import BaseUIMediator from "../aTGame/UI/FGUI/BaseUIMediator";
import { MiscData, MiscDataProxy } from "../Data/MiscDataProxy";
import { EUI } from "../dMyGame/Enum/EUI";
import UIManagerProxy from "../dMyGame/Manager/Proxy/UIManagerProxy";
import { Time } from "../lty/class/Time";
import MesManagerClass from "../dMyGame/Manager/MesManager";
import { EEventUI } from "../dMyGame/EventEnum/EEventUI";
import FGUI_PGameVideos from "../FGUI/Main/FGUI_PGameVideos";
import { GuideDataProxy } from "../Data/GuideDataProxy";
import PlatformManagerProxy from "../aTGame/Platform/PlatformManagerProxy";
import { Audio } from "../GameComponent/Audio";
import { Uma, UmaEvent } from "../Uma";
import { UIMediatorGameAd } from "./UIMediatorGameAd";
import VIVOData from "src/cFrameBridge/Platform/Data/VIVOData";
import VivoBannerAd from "src/dMyGame/vivoPlatform/VivoBannerAd";


export class UIMediatorAD extends BaseUIMediator<FGUI_PGameVideos> {
    private static m_instance: UIMediatorAD;
    private isGuide: any;
    /** 单例 */
    public static get instance(): UIMediatorAD {
        if (!this.m_instance) {
            this.m_instance = new UIMediatorAD();
            this.m_instance._classDefine = FGUI_PGameVideos;
        }
        return this.m_instance;
    }

    private constructor() {
        super();
    }

    //关闭倒计时
    _OnShow() {
        // this.isGameVidePanel = true;
        super.Show();
        this.ui.m_btn_close.onClick(this, this.Close);
        this.ui.m_btn_thanks.onClick(this, this.Close)

        this.ui.m_btn_Watch.onClick(this, this.OnClickWatchADBtn);
        this.UpdateUsableAdNum();
        this.ui.m_text_Time.text = Time.ToTimeString(MiscDataProxy.data.DoubleBuffTime);
        let diamondTimes = MiscDataProxy.data.OfflineAdTotalNum % MiscDataProxy.data.OfflineDiamondLoop
        this.ui.m_text_Times.text = diamondTimes + "/" + MiscDataProxy.data.OfflineDiamondLoop;
        this.SetProgressOne(MiscDataProxy.data.DoubleBuffTime, MiscDataProxy.data.MaxDoubleBuffTime);
        this.SetProgressTwo(diamondTimes, MiscDataProxy.data.OfflineDiamondLoop);
        MesManagerClass.instance.onUI(EEventUI.UpdateAdBuffTime, this, this.OnUpdateAdBuffTime);
        if (MiscDataProxy.data.OfflineAdDayNum >= MiscDataProxy.data.OfflineMaxDayNum) {
            this.DisableAdd();
        }
        //引导
        this.isGuide = !GuideDataProxy.IsCompleteAllGuide;
        Uma.trackEvent(UmaEvent.DoubleUI);
        /***原生广告调用******************* */
        UIMediatorGameAd.instance.ShowNativeAd();

        /**Baanner */
        let vivoObj = new VIVOData();
        VivoBannerAd.instance.showBannerFun(vivoObj);
    }

    protected _OnHide() {
        MesManagerClass.instance.offUI(EEventUI.UpdateAdBuffTime, this, this.OnUpdateAdBuffTime);
        VivoBannerAd.instance.DestroyAd();

    }


    private Close() {
        UIManagerProxy.instance.setUIState([
            {
                typeIndex: EUI.AD, state: false
            }
        ], false);
    }

    private SetProgressOne(curr: number, max: number) {
        this.ui.m_progress_1.value = curr / max * 100;
    }

    private SetProgressTwo(curr: number, max: number) {
        this.ui.m_text_Times.text = curr + "/" + max;
        this.ui.m_progress_2.value = curr / max * 100;
    }

    private OnClickWatchADBtn() {
        if (!GuideDataProxy.IsCompleteAllGuide) {
            this.WatchAd();
            return;
        }
        PlatformManagerProxy.instance.PlatformInstance.ShowRewardVideoAd(
            Laya.Handler.create(this, () => {
                Audio.PlayAudio();
                // 接入广告
                this.WatchAd();
                Uma.trackEvent(UmaEvent.DoubleAD);
            }),
            Laya.Handler.create(this, () => {
                PlatformManagerProxy.instance.PlatformInstance.ShowToast("未看完广告")
            }))
    }

    private WatchAd() {
        MiscDataProxy.AddDoubleBuffTime(30 * 60 * 1000);
        if (this.isShow) {
            this.ui.m_text_Time.text = Time.ToTimeString(MiscDataProxy.data.DoubleBuffTime);
            this.SetProgressOne(MiscDataProxy.data.DoubleBuffTime, MiscDataProxy.data.MaxDoubleBuffTime);
        }
        MiscDataProxy.data.OfflineAdTotalNum++;
        MiscDataProxy.data.OfflineAdDayNum++;
        if (MiscDataProxy.data.OfflineAdDayNum >= MiscDataProxy.data.OfflineMaxDayNum && this.isShow) {
            this.DisableAdd();
        }
        let dia = MiscDataProxy.data.OfflineAdTotalNum % MiscDataProxy.data.OfflineDiamondLoop;
        if (MiscDataProxy.data.OfflineAdTotalNum > 0 && dia == 0) {
            // 获取钻石30
            MiscDataProxy.Diamond += 30;
        }
        if (this.isShow) {
            this.SetProgressTwo(dia, MiscDataProxy.data.OfflineDiamondLoop);
            this.UpdateUsableAdNum()
        }
        //引导
        if (this.isGuide) {
            MesManagerClass.instance.eventUI(EEventUI.GuideToNext);
            //关闭自身
            this.Close();
        }
    }

    private UpdateUsableAdNum() {
        let num = 8 - MiscDataProxy.data.OfflineAdDayNum;
        num = num < 0 ? 0 : num;
        this.ui.m_text_Surplus.text = num.toString();
    }

    private DisableAdd() {
        this.ui.m_btn_Watch.grayed = true;
        this.ui.m_btn_Watch.touchable = false;
    }

    private OnUpdateAdBuffTime() {
        this.ui.m_text_Time.text = Time.ToTimeString(MiscDataProxy.data.DoubleBuffTime);
        this.SetProgressOne(MiscDataProxy.data.DoubleBuffTime, MiscDataProxy.data.MaxDoubleBuffTime);
    }


}
