import AllGlobal3DFFFFF from "../aTGame/3D/Global3D";
import BaseUIMediator from "../aTGame/UI/FGUI/BaseUIMediator";
import { EUILayer } from "../aTGame/UI/FGUI/EUILayer";
import { EEventScene } from "../dMyGame/EventEnum/EEventScene";
import MesManagerClass from "../dMyGame/Manager/MesManager";
import { Vector3 } from "../lty/class/Vector3";
import MouseManager = Laya.MouseManager;
import FGUI_EventLayer from "../FGUI/Main/FGUI_EventLayer";

export class UIMeditorEventLayer extends BaseUIMediator<FGUI_EventLayer>{

    touchCount: number;

    private static m_instance: UIMeditorEventLayer;
    /** 单例 */
    public static get instance(): UIMeditorEventLayer {
        if (!this.m_instance) {
            this.m_instance = new UIMeditorEventLayer();
            this.m_instance._classDefine = FGUI_EventLayer;
        }
        return this.m_instance;
    }
    protected _layer: EUILayer = EUILayer.EventLayer;
    private constructor() { super(); }
    private _lastFramePos = new Laya.Vector2();
    private _isDrag = false;//是否为拖拽状态
    private _point: Laya.Vector2 = new Laya.Vector2();

    private get mousePosition() {
        this._point.x = MouseManager.instance.mouseX;
        this._point.y = MouseManager.instance.mouseY;
        return this._point;
    }

    private _ray: Laya.Ray = new Laya.Ray(Vector3.zero, Vector3.zero);
    private _out: Laya.HitResult = new Laya.HitResult();
    private get _camera() {
        return AllGlobal3DFFFFF.camera;
    }

    private get _scene() {
        return AllGlobal3DFFFFF.s3d;
    }

    _OnShow() {
        this.ui.m_layer.on(Laya.Event.MOUSE_DOWN, this, this.onDown);
        this.ui.m_layer.on(Laya.Event.MOUSE_MOVE, this, this.onMove);
        this.ui.m_layer.on(Laya.Event.MOUSE_UP, this, this.onUp);
    }

    onDown() {
        this._lastFramePos.x = this.mousePosition.x;
        this._lastFramePos.y = this.mousePosition.y;
    }

    onMove() {
        if (Math.abs(this.mousePosition.x - this._lastFramePos.x) < 5 || Math.abs(this.mousePosition.y - this._lastFramePos.y) < 5)
            return;
        this._isDrag = true;
    }

    onUp() {
        if (this._isDrag && this._scene.input.touchCount() == 1) {
            this._isDrag = false;
            return;
        }
        if (this._isDrag == false) {
            this._camera.viewportPointToRay(this.mousePosition as Laya.Vector2, this._ray);
            if (this._scene.physicsSimulation.rayCast(this._ray, this._out)) {
                console.log("检测物", this._out.collider.owner.name);
                let name = this._out.collider.owner.name;
                MesManagerClass.instance.event3D(EEventScene.OnClickBuilding, [name]);
                // if (!GuideDataProxy.IsCompleteAllGuide&&(name=="femaleToilet_1"||name=="femaleToilet_1")) {
                //     MesManager.instance.eventUI(EEventUI.GuideToNext);
                //     console.log("射线检测到")
                // }
            }
            else {
                console.log("没有检测到box");
            }
        }
    }

}