/**
 * 游戏进入之前的加载操作类
 */
export default class GameLoadClass extends RootGameLoadUnitility {
    /** 白屏时显示的UI */
    private _emptyScreenShowUI: FGUI_EmptyScreen;
    /** 加载资源时显示的进度条 */
    private _loadingProgress: fgui.GProgressBar;
    /** 显示进度的文字 */
    private _loadingProgressText: fgui.GTextField;
    //加载时显示的UI
    private _loadShowUI: FGUI_splash;

    //初始化之前的操作
    protected _Init(): Promise<void> {
        GlobalStateManagerClass.instance.GameInit();
        //白屏时显示的包
        this._initEmptyScreen = new LoadUIPack(EssentialResUrlsUntility.FGUIPack('InitEmptyScreen'));
        //需要初始化加载的ui包。
        this._initUiPack = new LoadUIPack(EssentialResUrlsUntility.FGUIPack('InitLoad'), 0);
        //其他需要加载的UI包
        this._needLoadOtherUIPack = [
            //XXX:UI->加载的UI包
            // new LoadUIPack(EssentialResUrlsUntility.FGUIPack('GameCommon')),//游戏公共包
            new LoadUIPack(EssentialResUrlsUntility.FGUIPack('GameMain'), 0),//游戏主包
            new LoadUIPack(EssentialResUrlsUntility.FGUIPack('Main'), 0),//游戏主包
            new LoadUIPack(EssentialResUrlsUntility.FGUIPack('BuildUIPanel'), 0),//建筑slot升级UI
            new LoadUIPack(EssentialResUrlsUntility.FGUIPack('TopInfoUI'), 0),//顶部数值UI
            new LoadUIPack(EssentialResUrlsUntility.FGUIPack('EventPanel'), 0),//广告
            new LoadUIPack(EssentialResUrlsUntility.FGUIPack('Guide'), 0),
            new LoadUIPack(EssentialResUrlsUntility.FGUIPack('EffectLayer'), 0),
            new LoadUIPack(EssentialResUrlsUntility.FGUIPack('GameAd'), 0),
        ];
        //
        return;
    }

    //初始化
    protected _OnInitEmptyScreen() {
        //打开白屏UI
        this._emptyScreenShowUI = FGuiRootManager.AddUI(FGUI_EmptyScreen, EUILayer.Main) as FGUI_EmptyScreen;
    }

    //初始化UI加载完成 (在这里设置进度条)
    protected _OnInitUILoaded() {
        //隐藏白屏U
        this._emptyScreenShowUI.dispose();
        //添加UI
        this._loadShowUI = FGuiRootManager.AddUI(FGUI_splash, EUILayer.Loading) as FGUI_splash;
        this._loadShowUI.sortingOrder = Number.MAX_SAFE_INTEGER;
        //设置所属团队
        this._loadShowUI.m_text_explain.text = MainConfigClass.GameWhatTeam;
        //设置logo
        this._loadShowUI.m_text_logo.text = MainConfigClass.GameName_;
        //设置版本
        this._loadShowUI.m_text_v.text = MainConfigClass.versions;
        //设置说明
        this._loadShowUI.m_text_game_explain.text = MainConfigClass.GameExplain;
        //设置laya版本
        this._loadShowUI.m_text_laya_v.text = Laya.version;
        //获取加载进度条
        this._loadingProgress = this._loadShowUI.m_progress;
        //获取进度显示文字
        this._loadingProgressText = this._loadShowUI.m_loading_progress;
        //设置默认进度
        this._loadingProgress.value = 0;
    }

    //捆绑UI
    protected OnBindUI() {
        InitEmptyScreenBinder.bindAll();
        InitLoadBinder.bindAll();
        // GameCommonBinder.bindAll();
        GameMainBinder.bindAll();
        //XXX:UI->添加ui的加载包
        MainBinder.bindAll();
        BuildUIPanelBinder.bindAll();
        TopInfoUIBinder.bindAll()
        EventPanelBinder.bindAll()
        GuideBinder.bindAll()
        EffectLayerBinder.bindAll();
        GameAdBinder.bindAll();
    }

    // 注册表格
    protected OnSetLoadConfig() {
        CustomConfigManagerFFSSSsaaa.AddConfig(CameraConst);
        CustomConfigManagerFFSSSsaaa.AddConfig(EnvironmentConfig);
        CustomConfigManagerFFSSSsaaa.AddConfig(OtherEnvironmentConfig);
        CustomConfigManagerFFSSSsaaa.AddConfig(GameConst);
        CustomConfigManagerFFSSSsaaa.AddConfig(GameStateConst);
        CustomConfigManagerFFSSSsaaa.AddConfig(LevelConfig);
        CustomConfigManagerFFSSSsaaa.AddConfig(OtherLevelConfig);
        CustomConfigManagerFFSSSsaaa.AddConfig(LevelPropConfig);
        CustomConfigManagerFFSSSsaaa.AddConfig(LightingConst);
        CustomConfigManagerFFSSSsaaa.AddConfig(OtherConst);
        CustomConfigManagerFFSSSsaaa.AddConfig(SkinConfig);
        CustomConfigManagerFFSSSsaaa.AddConfig(TestConst);
        //加入slot的数据配置表
        CustomConfigManagerFFSSSsaaa.AddConfig(SlotUiConfig);
        //XXX:配置表初始化
        //旅客预制体配置，各个种类
        CustomConfigManagerFFSSSsaaa.AddConfig(GusetPrefabConfig);
        //ui建筑列表item
        CustomConfigManagerFFSSSsaaa.AddConfig(UIManagerBuildConfig);
        //建造配置表
        CustomConfigManagerFFSSSsaaa.AddConfig(BuildConfig);
        //默认的建筑
        CustomConfigManagerFFSSSsaaa.AddConfig(DefaultBuilding);
        //建筑默认的slot
        CustomConfigManagerFFSSSsaaa.AddConfig(BuildingDefaultSlotConfig);
        //SLot uid const
        CustomConfigManagerFFSSSsaaa.AddConfig(SlotUIDConst);
        // 聚焦位置配置表
        CustomConfigManagerFFSSSsaaa.AddConfig(FocusPositionConfig);
        //引导配置表
        CustomConfigManagerFFSSSsaaa.AddConfig(GuideInfo);
        //任务配置表
        CustomConfigManagerFFSSSsaaa.AddConfig(TaskConfig);
    }

    //注册数据
    protected loginData() {
        //游戏主要保存数据
        GameDataSave.instance.InitData();
        //游戏新手引导数据
        GameNewHandDataSave.instance.InitData();
        //游戏道具数据
        GamePropDataSave.instance.InitData();
        //游戏皮肤数据
        GameSkinDataSave.instance.InitData();
        //游戏签到数据
        GameSignDataSave.instance.InitData();
        //游戏临时数据
        GameShortDataSave.instance.InitData();
        //XXX:需要保存的数据初始化
        //厕所数据
        ToiletDataProxy.instance.InitData();
        //洗手池数据
        SinkDataProxy.instance.InitData();
        //建造的数据
        BuildDataProxy.instance.InitData();
        //无障碍卫生间的数据
        DisableRoomDataProxy.instance.InitData();
        //coffee间的数据
        // CoffeeDataProxy.instance.InitData();
        //清洁室
        CleanRoomDataProxy.instance.InitData();
        //婴儿房
        // BabyRoomDataProxy.instance.InitData();
        //等候区
        WaitAeraDataProxy.instance.InitData();
        //小便区
        PeeAreaDataProxy.instance.InitData();
        //图书馆
        LibraryDataProxy.instance.InitData();
        //便利店
        CanteenDataProxy.instance.InitData();
        //其他数据
        MiscDataProxy.instance.InitData();
        //水循环室
        WaterRoomDataProxy.instance.InitData();
        //货币数据
        MiscDataProxy.instance.InitData();
        //新手引导
        GuideDataProxy.instance.InitData();

    }

    // 获取其它游戏资源, 可以依赖游戏表格
    protected OnGameResPrepared(urls: string[]) {
        //把所有音效添加进预加载列表
        let _str: string;
        //背景音乐
        for (let _i in EnumEBGMs) {
            //
            _str = EnumEBGMs[_i];
            //判断是否是空元素
            if (_str == '') continue;
            //添加进列表
            urls.push(ResUrlClassffff.music_url(_str));
        }
        //音效
        for (let _i in EnumESounds) {
            //
            _str = EnumESounds[_i];
            //判断是否是空元素
            if (_str == '') continue;
            //添加进列表
            urls.push(ResUrlClassffff.sound_url(_str));
        }
        //TODO:加入json文件配置
        urls.push(EssentialResUrlsUntility.ConfigURL(StringName.GusetPool));
        urls.push(EssentialResUrlsUntility.ConfigURL(StringName.MapRoot));
        urls.push(EssentialResUrlsUntility.ConfigURL(StringName.EmployeeData));
        for (let name of TableName.list) {
            urls.push(EssentialResUrlsUntility.ConfigURL(TableName.NameUpper(name) + ".json"));
        }
    }

    private _progress = { 0: false, 30: false, 50: false, 80: false, 100: false };

    //游戏加载进度
    protected onLoading(_n: number) {
        this._loadingProgress.value = _n;//设置进度条
        let progress = Math.floor(_n);
        this._loadingProgressText.text = progress.toString() + '%';//设置文字进度
        this.Check(0, progress);
        this.Check(30, progress);
        this.Check(50, progress);
        this.Check(80, progress);
        this.Check(100, progress);

    }

    private Check(index: number, progress) {
        if (progress == index) {
            let res = this._progress[index]
            if (!res) {
                this._progress[index] = true;
                console.log("加载" + index);
                Uma.trackEvent(UmaEvent.LoadProgress, { "LoadProgress": index.toString() })
            }
        }
    }

    // 完成
    protected OnComplete() {
        //关闭加载时显示的UI
        this._loadShowUI.dispose();
        //游戏初始化完成
        GlobalStateManagerClass.instance.GameOnInit();
    }
}