import RootLocalStorageData from "../aTGame/Data/RootLocalStorageData";
import RootLocalStorageSaveNaWft from "../aTGame/Data/RootLocalStorageSave";
import { StringName } from "../Enum/StringName";
import BuildingData from "./BuildingData";
import { FunctionID } from "./FunctionID";

export class DisableRoomData extends RootLocalStorageData {
    list: BuildingData;
}

export class DisableRoomDataProxy extends RootLocalStorageSaveNaWft<DisableRoomData>{
    private static _instance: DisableRoomDataProxy;
    public static get instance() {
        if (this._instance == null) {
            this._instance = new DisableRoomDataProxy();
        }
        return this._instance;
    }
    private _data: DisableRoomData;
    public get data() {
        return this._data;
    }
    protected get _saveName(): string {
        return "DisableRoomData"
    }
    protected getNewData(): DisableRoomData {
        return new DisableRoomData();
    }
    public InitData(): void {
        this._data = this._ReadFromFile();
        if (this._data.list == null) {
            this._data.list = new BuildingData();
            this._data.list.active = false;
            this._data.list.functionID = FunctionID.DisableToilet;
            this._data.list.index = 0;
            this._data.list.title = "无障碍卫生间";
            this._data.list.name = StringName.disableRoom;
            //TODO:初始化SLOTS数据
            this._data.list.slots = [];
            this._SaveToDisk(this._data);
        }
    }

}