/**
 * UI管理器
 * UI从这里开始，注册所有UI调度者，再分配给UI状态管理器管理,不能再UI调度者中直接调用
 * 沟通外界，处理一些不能在UI中产生和处理的事件，通过这里中转
 */
export default class UIManagerClass extends BaseUIManager<UIManagerProxy> implements IRootManagerFFFFF {
    private static m_instance: UIManagerClass;
    /** 单例 */
    public static get instance(): UIManagerClass {
        if (!this.m_instance) {
            this.m_instance = new UIManagerClass();
        }
        return this.m_instance;
    }

    //
    private constructor() {
        super();
    }

    /** 初始化UI调度者列表和代理类 */
    protected initUIMediator() {
        //初始化ui状态管理器
        this.m_UIMediator = {
            [EUI.GameLoading]: PGameLoadingMediator.instance,
            [EUI.CustomsLoading]: PGameCustomsLoadingMediator.instance,
            //
            [EUI.TestMain]: PGameTestMainMediator.instance,
            [EUI.TestPlatform]: PGameTestPlatformMediator.instance,
            //
            [EUI.Set]: PGameSetMediator.instance,
            [EUI.Play]: PGamePlayMediator.instance,
            [EUI.Start]: PGameStartMediator.instance,
            [EUI.Pause]: PGamePauseMediator.instance,
            [EUI.Com]: PGameComMediator.instance,
            [EUI.End]: PGameEndMediator.instance,
            //XXX:UI->加入游戏UI的枚举
            [EUI.Main]: UIMediatorMain.instance,
            [EUI.Manager]: UIMediatorManagement.instance,
            [EUI.EventLayer]: UIMeditorEventLayer.instance,
            [EUI.AD]: UIMediatorAD.instance,
            [EUI.LuckyWheel]: UIMediatorLuckyWheel.instance,
            // [EUI.Offline]: UIMediatorOffline.instance,
            // [EUI.RemoveAD]: UIMediatorRemoveAD.instance,
            // [EUI.SuperDouble]: UIMediatorSuperDouble.instance,
            [EUI.Setting]: UIMediatorSetting.instance,
            [EUI.Task]: UIMediatorTask.instance,
            [EUI.Staff]: UIMediatorStaff.instance,
            [EUI.EventPanel]: UIMediatorEventPanel.instance,
            [EUI.TopPanel]: UIMediatorTopPanel.instance,
            [EUI.StaffMarket]: UIMediatorStaffMarket.instance,
            [EUI.StaffInfoPanel]: UIMediatorStaffInfoPanel.instance,
            [EUI.Guide]: UIMediatorGuide.instance,
            [EUI.OffLineIncomePanel]: UIMediatorOffLineIncomePanel.instance,
            [EUI.UIMoneyEffect]: UIMediatorEffectPanel.instance,
            [EUI.UIMediatorCheers]: UIMediatorCheers.instance,
            [EUI.UIMediatorInfluencer]: UIMediatorInfluencer.instance,
            [EUI.UIMeditorRepairGame]: UIMediatorRepairGame.instance,
            [EUI.UIMediatorSpeedUp]: UIMediatorSpeedUp.instance,
            [EUI.UIMediatorFlyPanel]: UIMediatorFlyPanel.instance,
            [EUI.DailySign]: UIMediatorDailySign.instance,
            [EUI.OpenCoffee]: UIMediatorOpenCoffee.instance,
            [EUI.CountDownTime]: UIMediatorCountTimePanel.instance,
            [EUI.RecordVideo]: UIMediatorRecordShare.instance,
            [EUI.TalkPanel]: UIMediatorTalkEventPanel.instance,
            [EUI.NativeAD]: UIMediatorGameAd.instance,
        };
        //注册代理类
        this.m_UIProxy = UIManagerProxy.instance;
    }

    /**
     * 初始化,UI的一切都从这里开始
     */
    public init() {
        //
    }

    /**
     * 开始
     */
    public Start() {
        this.m_UIProxy.Start();
    }
}