import BaseConfigDataProxy from "../aTGame/Config/RootDataProxy";
import { DefaultBuilding } from "../dMyGame/_config/DefaultBuilding";
/** 默认建筑配置表的代理 */
export class DefaultBuilingConfigProxy extends BaseConfigDataProxy<DefaultBuilding.config>{
    private static _instance: DefaultBuilingConfigProxy;
    /** 单例 */
    public static get instance(): DefaultBuilingConfigProxy {
        if (this._instance == null) {
            this._instance = new DefaultBuilingConfigProxy();
        }
        return this._instance;
    }
    //
    private constructor() {
        super();
    }

    public initData() {
        this.m_dataList = DefaultBuilding.dataList;
    }
}