import BaseUIMediator from "../aTGame/UI/FGUI/BaseUIMediator";
import { MiscDataProxy } from "../Data/MiscDataProxy";
import { UIFacade } from "./UIFacade";
import UIManagerProxy from "../dMyGame/Manager/Proxy/UIManagerProxy";
import { EUI } from "../dMyGame/Enum/EUI";
import FGUI_PGameMainDaily from "../FGUI/Main/FGUI_PGameMainDaily";
import { GuideDataProxy } from "../Data/GuideDataProxy";
import MesManagerClass from "../dMyGame/Manager/MesManager";
import { EEventUI } from "../dMyGame/EventEnum/EEventUI";
import PlatformManagerProxy from "../aTGame/Platform/PlatformManagerProxy";
import {Audio} from "../GameComponent/Audio";
import {ADUtility, UIMediatorMain} from "./UIMediatorMain";


export class UIMediatorDailySign extends BaseUIMediator<FGUI_PGameMainDaily> {
    private static m_instance: UIMediatorDailySign;
    private isGuide: boolean;

    /** 单例 */
    public static get instance(): UIMediatorDailySign {
        if (!this.m_instance) {
            this.m_instance = new UIMediatorDailySign();
            this.m_instance._classDefine = FGUI_PGameMainDaily;
        }
        return this.m_instance;
    }

    private constructor() {
        super();
    }

    protected _OnShow() {
        this.ui.m_btn_get.onClick(this, this.OnClickGet);
        this.ui.m_btn_close.onClick(this, this.OnClickClose);
        this.ui.m_btn_ad.onClick(this,this.OnClickAd)
        this.UpdateShow();
        if (!GuideDataProxy.IsCompleteAllGuide)
            this.isGuide = true;
        else
            this.isGuide = false;
    }

    UpdateShow() {
        let day = MiscDataProxy.data.signDay;
        this.ui.m_day_1.m_bgColor.selectedIndex = day <= 0 ? 0 : 1;
        this.ui.m_day_2.m_bgColor.selectedIndex = day <= 1 ? 0 : 1;
        this.ui.m_day_3.m_bgColor.selectedIndex = day <= 2 ? 0 : 1;
        this.ui.m_day_4.m_bgColor.selectedIndex = day <= 3 ? 0 : 1;
        this.ui.m_day_5.m_bgColor.selectedIndex = day <= 4 ? 0 : 1;
        this.ui.m_day_6.m_bgColor.selectedIndex = day <= 5 ? 0 : 1;
        this.ui.m_day_7.m_bgColor.selectedIndex = day <= 6 ? 0 : 1;
    }

    OnClickAd(){
        PlatformManagerProxy.instance.PlatformInstance.ShowRewardVideoAd(
            Laya.Handler.create(this, () => {
                Audio.PlayAudio();
                this.GetDailyReward(2)
            }),
            Laya.Handler.create(this, () => {
                PlatformManagerProxy.instance.PlatformInstance.ShowToast("未看完广告")
            }))
    }

    public OnClickGet() {
        this.GetDailyReward(1)
    }

    GetDailyReward(scale = 1){
        if(this.isShow){
            UIFacade.PlayDiamondEffect(this.ui.m_btn_get);
        }
        switch (MiscDataProxy.data.signDay) {
            case 1:
                MiscDataProxy.Diamond += 50 * scale;
                break;
            case 2:
                MiscDataProxy.Diamond += 50* scale;
                break;
            case 3:
                MiscDataProxy.Diamond += 60* scale;
                break;
            case 4:
                MiscDataProxy.Diamond += 70* scale;
                break;
            case 5:
                MiscDataProxy.Diamond += 80* scale;
                break;
            case 6:
                MiscDataProxy.Diamond += 100* scale;
                break;
            default:
                MiscDataProxy.Diamond += 30* scale;
        }
        MiscDataProxy.instance.AddSignDay();
        MiscDataProxy.data.hasSign = true;
        this.OnClickClose();
        //引导
        if (this.isGuide){
            MesManagerClass.instance.eventUI(EEventUI.GuideToNext);
        }
    }

    OnClickClose() {
        UIManagerProxy.instance.setUIState([{ typeIndex: EUI.DailySign, state: false }], false);
    }
}