import Building from "./api/Building";
import MesManagerClass from "../../dMyGame/Manager/MesManager";
import {EEventScene} from "../../dMyGame/EventEnum/EEventScene";
import {FacilityPosition} from "./FacilityPosition";
import {Worker} from "../Worker/Worker";
import ResLoadClass from "../../aTGame/Res/ResLoad";
import EssentialResUrlsUntility from "../../aTGame/Res/EssentialResUrls";
import {PrefabNamesTable} from "../../dMyGame/ResList/PrefabNames";
import {StringName} from "../../Enum/StringName";
import {NullReferenceException} from "../../lty/Exception/NullReferenceException";
import {UIMediatorEventPanel} from "../../UI/UIMediatorEventPanel";
import {SlotData} from "../../Data/SlotData";
import {MiscDataProxy} from "../../Data/MiscDataProxy";
import {IWorkerData} from "../Worker/IWorkerData";
import {UIMediatorMain} from "../../UI/UIMediatorMain";
import {EEventUI} from "../../dMyGame/EventEnum/EEventUI";
import {EffectManager} from "../EffectManager";
import {CameraManager} from "../CameraManager";
import {UIMediatorTalkEventPanel} from "../../UI/UIMediatorTalkEventPanel";

export class CleanRoom extends Building {
    //等待维修的设备列表,已被分配给维修工的不在此列表中
    private _waitRepairFac: FacilityPosition[] = [];
    //维修人员的列表
    private _workList: Worker[] = [];
    //workerDict
    private _workDict: Map<string, Worker> = new Map<string, Worker>()
    //椅子
    private _chairList: { chair: Laya.Sprite3D, worker: Worker }[] = [];
    private _runtimeExitPos: Laya.Vector3;
    //人员不足时，提示玩家点击或者增加雇员
    private _lastTipTime = 0;

    private get exitPos() {
        if (this._runtimeExitPos == null) {
            let tool = this.sprite3D.getChildByName("exit") as Laya.Sprite3D;
            this._runtimeExitPos = tool.transform.position;
        }
        return this._runtimeExitPos;
    }

    public get staffNum() {
        return this._workList.length;
    }

    private static _instance: CleanRoom;
    public static get instance() {
        return this._instance;
    }

    onAwake() {
        super.onAwake();
        CleanRoom._instance = this;
        //收到维修请求
        MesManagerClass.instance.on3D(EEventScene.RepairFacility, this, this.OnRequestRepair);
        MesManagerClass.instance.on3D(EEventScene.FacilityRepairComplete, this, this.OnFacilityRepairComplete);
        MesManagerClass.instance.on3D(EEventScene.AddStaff, this, this.AddWorker);
        MesManagerClass.instance.onUI(EEventUI.OnClickRepairBtn, this, this.OnClickRepairBtn);
    }

    onStart() {
        this.InitWorkers();
        this.entrance.active = false;
    }

    ShowOutlook() {
    }

    ShowPlaceholder() {
    }

    ClosePlaceholder() {
    }

    UpdateFacilityLevel(name: string, level: number, showEffect: boolean, slotData: SlotData) {
        super.UpdateFacilityLevel(name, level, showEffect, slotData);
        this.OnUpdateSlot();
    }

    //#region 派发人员流程
    //根据数据初始化所有雇员
    public InitWorkers() {
        this.InitChair();
        let prefab = ResLoadClass.Get(EssentialResUrlsUntility.prefab_url(PrefabNamesTable.prefab4.Female_worker));
        if (prefab) {
            let list = MiscDataProxy.instance.GetEmployeeList();
            for (let i = 0; i < list.length; i++) {
                let w = this.Instantiate(prefab, this.owner);
                let worker = w.addComponent(Worker) as Worker;
                worker.Init(list[i]);
                this._workDict.set(worker.Name, worker);
                this._workList.push(worker);
                this._chairList[i].worker = worker;
                worker.transform.position = this._chairList[i].chair.transform.position;
                worker.transform.rotationEuler = this._chairList[i].chair.transform.rotationEuler;
                w.active = true;
            }
        } else {
            throw new NullReferenceException('维修工获取失败');
        }
        this.UpdateStaffNumUI();
    }

    /**
     * 添加雇员实例
     * @param data 雇员信息
     * @param replaceName 被替换雇员的名字
     */
    private AddWorker(data: IWorkerData, replaceName?: string) {
        let index = -1;
        if (replaceName) {
            index = this.GetWorkerIndexByName(replaceName);
        }
        let prefab = ResLoadClass.Get(EssentialResUrlsUntility.prefab_url(PrefabNamesTable.prefab4.Female_worker));
        let w = this.Instantiate(prefab, this.owner);
        let worker = w.addComponent(Worker) as Worker;
        worker.Init(data);
        if (index > -1) {
            let temp = this._workDict.get(replaceName);
            //替换雇员
            this._workList[index] = worker;
            this._workDict.delete(replaceName);
            this._workDict.set(worker.Name, worker);
            this.TryRemoveWorkerFromChair(temp);
            //删除雇员之前让他完成任务
            temp.Disposed();
        } else {
            //增加雇员
            this._workDict.set(worker.Name, worker);
            this._workList.push(worker);
        }
        //把雇员放在椅子上
        let chair = this.TryGetEmptyChair(worker);
        worker.transform.position = chair.transform.position;
        worker.transform.rotationEuler = chair.transform.rotationEuler;
        w.active = true;
        this.UpdateStaffNumUI();
    }

    private GetWorkerIndexByName(name: string) {
        let index = -1;
        for (let i = 0; i < this._workList.length; i++) {
            if (this._workList[i].Name == name) {
                return i;
            }
        }
        return index;
    }

    //点击修理按钮,然后尝试派发人员
    private OnClickRepairBtn() {
        let facPos = this._waitRepairFac[0];
        if (facPos) {
            this.OnRequestRepair(facPos);
        }
        console.log("雇员正在来的路上");
    }

    /**
     * 添加入等待维修的列表
     * 如果有空闲的雇员，直接派发
     * 如果没有空闲的雇员，暂存事件，等待出现空闲的雇员再派发
     */
    private OnRequestRepair(facPos: FacilityPosition) {
        if (!this.TryDispatchWorker(facPos, this.exitPos)) {
            let index = this._waitRepairFac.indexOf(facPos);
            if (index < 0) {
                this._waitRepairFac.push(facPos);
                console.log("加入待维修的列表")
            } else {
                console.log("不能重复加入待维修的列表")
            }
        }
    }

    //尝试为损坏设备派发人员修理
    private TryDispatchWorker(facPos: FacilityPosition, exitPos: Laya.Vector3) {
        for (let w of this._workList) {
            if (!w.hasFacTarget) {
                w.SetFacTarget(facPos, exitPos);
                this.TryRemoveWorkerFromChair(w);
                //从等待维修的列表中移除这个设备，然后进行维修
                this.RemoveFacFromList(facPos);
                this.UpdateStaffNumUI();
                return true;
            }
        }
        let time = new Date().getTime();
        if(time - this._lastTipTime > 60000){
            let hasShow = UIMediatorTalkEventPanel.instance.ShowTalkPanel(2)
            if (hasShow) {
                this._lastTipTime = time;
            }
        }
        return false;
    }

    //把设备从损坏列表移除
    private RemoveFacFromList(facPos: FacilityPosition) {
        let index = this._waitRepairFac.indexOf(facPos);
        if (index > -1) {
            this._waitRepairFac.splice(index, 1);
        }
    }

    //设备维修完毕
    protected OnFacilityRepairComplete(facPos: FacilityPosition, worker: Worker) {
        if (worker.isDisposed) {
            return;
        }
        if (this._waitRepairFac.length > 0) {
            let facPos = this._waitRepairFac[0];
            this.TryDispatchWorker(facPos, this.exitPos);
        } else {
            //命令维修工返回座位
            let chair = this.TryGetEmptyChair(worker);
            worker.ReturnChairPos(chair);
            this.UpdateStaffNumUI();
        }
    }

    //初始化椅子
    private InitChair() {
        let chairParent = this.sprite3D.getChildByName(StringName.Chair);
        let count = chairParent.numChildren;
        this._chairList = new Array(count);
        for (let i = 0; i < count; i++) {
            let chair = chairParent.getChildAt(i) as Laya.Sprite3D;
            if (chair) {
                this._chairList[i] = {chair: chair, worker: null};
            } else {
                throw new Error("初始化椅子失败！" + i.toString());
            }
        }
    }

    //获取一个空椅子,并把雇员和椅子绑定
    private TryGetEmptyChair(worker: Worker): Laya.Sprite3D {
        let chair = null;
        for (let ele of this._chairList) {
            if (ele.worker == null) {
                chair = ele.chair;
                ele.worker = worker;
                break;
            }
        }
        if (chair == null) {
            throw "获取闲置的椅子失败！";
        }
        return chair;
    }

    //把人员从椅子上移除，数据层面
    private TryRemoveWorkerFromChair(worker: Worker) {
        for (let ele of this._chairList) {
            if (ele.worker == worker) {
                ele.worker = null;
            }
        }
    }

    //#endregion

    //#region 设备修理提示

    onUpdate() {
        this._waitRepairFac.forEach(ele => {
            UIMediatorEventPanel.instance.EventDistribute(ele.runtimeFirstChild.transform.position, ele.sprite.id, ele.eventID, 0);
        })
    }

    private OnUpdateSlot() {
        this._workList.forEach(ele => {
            ele.SetRepairTime(this.cleanTime);
        })
    }

    //更新雇员可用和最大数量
    private UpdateStaffNumUI() {
        let unused = 0;
        for (let w of this._workList) {
            if (!w.hasFacTarget) {
                unused++;
            }
        }
        let max = this._workList.length;
        UIMediatorMain.instance.UpdateStaffNumText(unused, max);
    }

    //#endregion
    //看广告立即修复设备
    RepairCompleteNow() {
        for(let fac of this._waitRepairFac){
            fac.SetBreakState(false);
            UIMediatorEventPanel.instance.RecycleBtn(fac.sprite.id,fac.eventID);
            EffectManager.ClearAll(fac.sprite.id);
        }
        this._waitRepairFac= [];
        for (let worker of this._workList){
            if(worker.hasFacTarget){
                worker.RepairCompleteNow();
            }
        }
    }

    OnClick() {
        super.OnClick();
        CameraManager.instance.FocusBuild(21)
    }
}
