import {LayaBehaviour} from "../lty/class/LayaBehaviour";
import {EffectName, PrefabNamesTable} from "../dMyGame/ResList/PrefabNames";
import EssentialResUrlsUntility from "../aTGame/Res/EssentialResUrls";
import ResLoadClass from "../aTGame/Res/ResLoad";
import AllGlobal3DFFFFF from "../aTGame/3D/Global3D";

export class EffectManager extends LayaBehaviour {
    private static _runtimeInstance: EffectManager;

    public static get instance() {
        return this._runtimeInstance;
    }

    private get prefabs() {
        return PrefabNamesTable.prefab5;
    }

    //所有特效的名字，依靠此数组在初始时获取所有预置体资源
    private _nameList: string[] = [
        EffectName.Caidai,
        EffectName.Cleaning,
        EffectName.Emoji_angry,
        EffectName.Emoji_cry,
        EffectName.Emoji_cute,
        EffectName.Emoji_dead,
        EffectName.Emoji_happy,
        EffectName.Emoji_poop,
        EffectName.Emoji_sick,
        EffectName.Emoji_money,
        EffectName.Hushen,
        EffectName.Money,
        EffectName.Shui,
        EffectName.Urine,
        EffectName.Yan,
        EffectName.Pee,
        EffectName.Pee_dirty,
        EffectName.Pee_Damage,
        EffectName.Toilet_dirty,
        EffectName.Toilet_damage,
        EffectName.Sink_Dirty,
        EffectName.Sink_Damage,
        EffectName.Cleaning,
        EffectName.ZS,
    ]
    //每个特效的对象池
    private _map: Map<string, Laya.Node[]> = new Map<string, Laya.Node[]>();
    //预置体资源
    private _prefab: { [name: string]: Laya.Sprite3D } = {};
    //用户字典 XXX 后期可能出现 用户 没有主动回收特效
    private _callerDict: Map<number, Map<string, Laya.Sprite3D>> = new Map<number, Map<string, Laya.Sprite3D>>();

    onAwake() {
        EffectManager._runtimeInstance = this;
    }

    //获取所有特效的预置体
    public InitAllPrefabs() {
        for (let name of this._nameList) {
            let url = EssentialResUrlsUntility.prefab_url(name);
            if (url) {
                let prefab = ResLoadClass.Get(url, true);
                if (prefab) {
                    this._prefab[name] = prefab;
                } else {
                    throw new Error("获取预置体失败：" + url);
                }
            } else {
                throw new Error("获取预置体url失败：" + name);
            }
        }
    }

    //通过名字从对象池获取特效
    private GetEffectByName(effectName: string) {
        let _pool = this._map.get(effectName);
        //在对象池搜索一个没有激活的特效
        let effect: Laya.Sprite3D = null;
        if (_pool && _pool.length > 0) {
            for (let element of _pool) {
                if (!element.active) {
                    effect = element as Laya.Sprite3D;
                    break;
                }
            }
        }
        //与之对应的对象池没有初始化的就进行初始化
        if (effect == null) {
            let prefab = this._prefab[effectName];
            if (prefab) {
                effect = this.Instantiate(prefab, AllGlobal3DFFFFF.s3d);
                //把新实例化的特效加入对象池
                if (_pool == null) {
                    _pool = [];
                    _pool.push(effect);
                    this._map.set(effectName, _pool);
                } else {
                    _pool.push(effect);
                }
            } else {
                throw new Error("获取特效预置体失败：" + effectName);
            }
        }
        return effect;
    }

    private RecycleEffect(effect:Laya.Sprite3D){
        if(effect.parent!=this.owner){
            AllGlobal3DFFFFF.s3d.addChild(effect);
        }
        effect.active = false;
    }


    /**
     * 播放特效
     * @param effectName 特效名字
     * @param callerID 使用特效的用户
     * @param position 播放位置
     * @param eulerAngle 播放角度
     * @param clearTime 回收时间，在设置回收时间后会自动进行回收
     * XXX 不能重复使用特效，会获取此用用户之前已经使用的特效
     */
    public static PlayEffect(effectName, callerID: number, clearTime?:number,position?: Laya.Vector3, eulerAngle?: Laya.Vector3,) {
        // console.log("play effect!",effectName);
        //获取用户的数据
        let user = this.instance._callerDict.get(callerID);
        if (user == null) {
            user = new Map<string, Laya.Sprite3D>();
            this.instance._callerDict.set(callerID, user);
        }
        //检测用户是否已经使用此名字的特效,防止重复获取
        let effect = user.get(effectName);
        if (effect == null) {
            effect = this.instance.GetEffectByName(effectName);
        }
        if (effect) {
            //成功获取特效，刷新用户字典
            user.set(effectName, effect);
            if (position) {
                effect.transform.position = position;
            }
            if (eulerAngle) {
                effect.transform.rotationEuler = eulerAngle;
            }
            if(clearTime){
                Laya.timer.once(clearTime,this,()=>this.Clear(callerID,effectName));
            }
            effect.active = true;
        }
        return effect;
    }

    

    /**
     * 回收特效
     * @param callerID 用户id
     * @param effectName 特效名字
     */
    public static Clear(callerID: number, effectName: string) {
        let caller = this.instance._callerDict.get(callerID);
        if (caller) {
            let effect = caller.get(effectName);
            if (effect) {
                EffectManager.instance.RecycleEffect(effect);
            }
            caller.delete(effectName);
            //用户使用的特效数量小于0 就删除这个用户
            if (caller.size <= 0) {
                caller.clear()
                this.instance._callerDict.delete(callerID);
            }
        }
    }

    /**
     * 回收所有特效
     * @param callID 用户id
     */
    public static ClearAll(callerID: number) {
        let caller = this.instance._callerDict.get(callerID);
        if (caller) {
            caller.forEach(ele => {
                EffectManager.instance.RecycleEffect(ele);
            })
            caller.clear();
        }
        this.instance._callerDict.delete(callerID);
    }
}

