import BaseConfigDataProxy from "../../aTGame/Config/RootDataProxy";
import { GusetPrefabConfig } from "../_config/GusetPrefabConfig";
/**旅客预制体配置，各个种类*/
export default class GuestPrefabConfigProxy extends BaseConfigDataProxy<GusetPrefabConfig.config>{
    private static _instance: GuestPrefabConfigProxy;
    /** 单例 */
    public static get instance(): GuestPrefabConfigProxy {
        if (this._instance == null) {
            this._instance = new GuestPrefabConfigProxy();
        }
        return this._instance;
    }
    //
    private constructor() { super(); }

    // 初始化
    protected initData() {
        //
        this.m_dataList = GusetPrefabConfig.dataList;
    }
}