import BaseConfigDataProxy from "../aTGame/Config/RootDataProxy"
import {FocusPositionConfig} from "../dMyGame/_config/FocusPositionConfig";

export class FocusPositionProxy extends BaseConfigDataProxy<FocusPositionConfig.config>{
    private static _instance: FocusPositionProxy;
    /** 单例 */
    public static get instance(): FocusPositionProxy {
        if (this._instance == null) {
            this._instance = new FocusPositionProxy();
        }
        return this._instance;
    }
    //
    private constructor() {
        super();
    }

    public initData() {
        this.m_dataList = FocusPositionConfig.dataList;
    }
}