import BaseUIMediator from "../aTGame/UI/FGUI/BaseUIMediator";
import { EUI } from "../dMyGame/Enum/EUI";
import UIManagerProxy from "../dMyGame/Manager/Proxy/UIManagerProxy";
import { TaskManager } from "../GameComponent/TaskManager";
import { MiscDataProxy } from "../Data/MiscDataProxy";
import { RewardType } from "../GameComponent/Task/ITask";
import MesManagerClass from "../dMyGame/Manager/MesManager";
import { EEventUI } from "../dMyGame/EventEnum/EEventUI";
import FGUI_PGameTask from "../FGUI/Main/FGUI_PGameTask";
import { UIFacade } from "./UIFacade";
import PlatformManagerProxy from "../aTGame/Platform/PlatformManagerProxy";
import { Audio } from "../GameComponent/Audio";
import { Uma, UmaEvent } from "../Uma";
import VIVOData from "src/cFrameBridge/Platform/Data/VIVOData";
import VivoBannerAd from "src/dMyGame/vivoPlatform/VivoBannerAd";

export class UIMediatorTask extends BaseUIMediator<FGUI_PGameTask> {
    private static m_instance: UIMediatorTask;
    // private isGameTask: boolean = false;
    // public get IsGameTask(): boolean {
    //     return this.isGameTask;
    // }
    /** 单例 */
    public static get instance(): UIMediatorTask {
        if (!this.m_instance) {
            this.m_instance = new UIMediatorTask();
            this.m_instance._classDefine = FGUI_PGameTask;
        }
        return this.m_instance;
    }

    private constructor() {
        super();
    }

    _OnShow() {
        // this.isGameTask = true;
        super.Show();
        this.ui.m_btn_close.onClick(this, this.Close);
        this.ui.m_close.onClick(this, this.Close);
        this.ui.m_btn_get.onClick(this, this.OnClick1x);
        this.ui.m_btn_getDouble.onClick(this, this.OnClick2x);
        this.ui.m_btn_go.onClick(this, () => {
            Uma.trackEvent(UmaEvent.Task, { "ClickGO": 'Go' })
            TaskManager.instance.currentTask.Go()
            this.Close();
        })
        MesManagerClass.instance.onUI(EEventUI.UpdateTaskProgress, this, this.UpdateTaskProgress);
        this.UpdateTaskProgress();
        Uma.trackEvent(UmaEvent.Task, { "OpenTaskUI": 'UI' })

        /**Baanner */
        let vivoObj = new VIVOData();
        VivoBannerAd.instance.showBannerFun(vivoObj);
    }

    private Close() {
        UIManagerProxy.instance.setUIState([
            {
                typeIndex: EUI.Task, state: false
            }
        ], false);
    }

    _OnHide() {
        MesManagerClass.instance.offUI(EEventUI.UpdateTaskProgress, this, this.UpdateTaskProgress);
        VivoBannerAd.instance.DestroyAd();
        // this.isGameTask = false;
    }

    private OnClick1x() {
        let task = TaskManager.instance.currentTask;
        this.AddReward(task.reward, task.rewardType);
        Uma.trackEvent(UmaEvent.Task, { "GetReward": "1X" });
    }

    private OnClick2x() {
        Uma.trackEvent(UmaEvent.Task, { "GetReward": "2X" });
        PlatformManagerProxy.instance.PlatformInstance.ShowRewardVideoAd(
            Laya.Handler.create(this, () => {
                Audio.PlayAudio();
                let task = TaskManager.instance.currentTask;
                this.AddReward(task.reward * 2, task.rewardType);
            }),
            Laya.Handler.create(this, () => {
                PlatformManagerProxy.instance.PlatformInstance.ShowToast("未看完广告")
            }))
    }

    private AddReward(value: number, type: RewardType) {
        if (type == RewardType.money) {
            MiscDataProxy.Money += value;
        } else if (type == RewardType.diamond) {
            MiscDataProxy.Diamond += value;
            if (this.isShow) {
                //播放钻石特效
                UIFacade.PlayDiamondEffect(this.ui.m_txt_reward)
            }
        }
        TaskManager.instance.DoNext();
    }

    UpdateTaskProgress() {
        let task = TaskManager.instance.currentTask;
        if (task == null) {
            this.Close();
            return;
        }
        if (!this.isShow) {
            return;
        }
        this.ui.m_taskProgress.value = task.progress;
        this.ui.m_text_progress.text = task.toProgressString();
        this.ui.m_text_taskDesc.text = task.describe;
        this.ui.m_title.text = task.name;
        this.ui.m_txt_reward.text = task.reward.toString();
        this.ui.m_achieve.selectedIndex = task.progress >= 100 ? 1 : 0;
    }
}