/**全局物体 */
export class GlobalObject {
    private static _instance: GlobalObject;
    public static get instance(): GlobalObject{
        if (this._instance == null) {
            this._instance = new GlobalObject();
        }
        return this._instance;
    }

    private _prefabs: { [key: string]: Laya.Sprite3D[] };
    public static get prefabs() {
        return this.instance._prefabs;
    }

    public SetPrefabs(prefab) {
        this._prefabs = prefab;
    }
}