import BaseConfigDataProxy from "../aTGame/Config/RootDataProxy";
import { BuildConfig } from "../dMyGame/_config/BuildConfig";

/**建造的配置表 */
export class BuildConfigProxy extends BaseConfigDataProxy<BuildConfig.config>{
    private static _instance: BuildConfigProxy;
    /** 单例 */
    public static get instance(): BuildConfigProxy {
        if (this._instance == null) {
            this._instance = new BuildConfigProxy();
        }
        return this._instance;
    }
    //
    private constructor() {
        super();
    }

    public initData() {
        this.m_dataList = BuildConfig.dataList;
    }
}