import BaseConfigDataProxy from "../aTGame/Config/RootDataProxy";
import { UIManagerBuildConfig } from "../dMyGame/_config/UIManagerBuildConfig";
import {SlotUiConfig} from "../dMyGame/_config/SlotUiConfig";
/**UI slot界面图片url配置以及对应的其他数据 ，伪总表*/
export default class UISlotConfigProxy extends BaseConfigDataProxy<SlotUiConfig.config>{
    private static _instance: UISlotConfigProxy;
    /** 单例 */
    public static get instance(): UISlotConfigProxy {
        if (this._instance == null) {
            this._instance = new UISlotConfigProxy();
        }
        return this._instance;
    }
    //
    private constructor() {
        super();
    }

    public initData() {
        this.m_dataList = SlotUiConfig.dataList;
    }
}