import BaseConfigDataProxy from "../aTGame/Config/RootDataProxy";
import { UIManagerBuildConfig } from "../dMyGame/_config/UIManagerBuildConfig";
/**UI管理界面建筑列表item的配置 */
export default class UIManagerBuildConfigProxy extends BaseConfigDataProxy<UIManagerBuildConfig.config>{
    private static _instance: UIManagerBuildConfigProxy;
    /** 单例 */
    public static get instance(): UIManagerBuildConfigProxy {
        if (this._instance == null) {
            this._instance = new UIManagerBuildConfigProxy();
        }
        return this._instance;
    }
    //
    private constructor() {
        super();
    }

    public initData() {
        this.m_dataList = UIManagerBuildConfig.dataList;
    }
}