import BaseUIMediator from "../aTGame/UI/FGUI/BaseUIMediator";
import {MiscDataProxy} from "../Data/MiscDataProxy";
import {Time} from "../lty/class/Time";
import MesManagerClass from "../dMyGame/Manager/MesManager";
import {ClockEvent} from "../lty/plugin/Clock/ClockEvent";
import {EEventUI} from "../dMyGame/EventEnum/EEventUI";
import FGUI_CountDownTime from "../FGUI/Main/FGUI_CountDownTime";
import AllGlobal3DFFFFF from "../aTGame/3D/Global3D";
import {UIMediatorOpenCoffee} from "./UIMediatorOpenCoffee";
import {EUILayer} from "../aTGame/UI/FGUI/EUILayer";
import {StringName} from "../Enum/StringName";
import UIManagerProxy from "../dMyGame/Manager/Proxy/UIManagerProxy";
import {EUI} from "../dMyGame/Enum/EUI";

export class UIMediatorCountTimePanel extends BaseUIMediator<FGUI_CountDownTime>{
    private static m_instance: UIMediatorCountTimePanel;

    /** 单例 */
    public static get instance(): UIMediatorCountTimePanel {
        if (!this.m_instance) {
            this.m_instance = new UIMediatorCountTimePanel();
            this.m_instance._classDefine = FGUI_CountDownTime;
        }
        return this.m_instance;
    }

    private constructor() {
        super();
    }

    protected _layer = EUILayer.EventLayer;

    protected _OnShow() {
        MesManagerClass.instance.on(ClockEvent.UpdateOnLineTime, this, this.UpdateLastTime);
        MesManagerClass.instance.onUI(EEventUI.CanteenTimeOver, this, this.OnCarteenTimeOver)
        MesManagerClass.instance.onUI(EEventUI.LibraryTimeOver, this, this.OnLibraryTimeOver)
        this.ui.m_libraryLastTime.onClick(this,this.OnClickLibrary);
        this.ui.m_canteenLastTime.onClick(this,this.OnClickCateen);

    }

    protected _OnHide() {
        MesManagerClass.instance.off(ClockEvent.UpdateOnLineTime, this, this.UpdateLastTime);
        MesManagerClass.instance.offUI(EEventUI.CanteenTimeOver, this, this.OnCarteenTimeOver)
        MesManagerClass.instance.offUI(EEventUI.LibraryTimeOver, this, this.OnLibraryTimeOver)
    }



    private UpdateLastTime() {
        if(!this.isShow){
            return;
        }
        let ct = MiscDataProxy.data.CanteenLastTime;
        if(ct >0) {
            this.ui.m_canteenLastTime.m_txt_lastTime.text = Time.ToTimeString(ct);
        }
        else {
            this.ui.m_canteenLastTime.m_txt_lastTime.text = "开发完成！"
        }
        let lt = MiscDataProxy.data.LibraryLastTime;
        if(lt >0) {
            this.ui.m_libraryLastTime.m_txt_lastTime.text = Time.ToTimeString(lt);
        }
        else {
            this.ui.m_libraryLastTime.m_txt_lastTime.text = "开发完成！"
        }
    }

    private ChangPos(text:fgui.GButton, position:Laya.Vector3,viewPos:Laya.Vector4){
        AllGlobal3DFFFFF.camera.worldToViewportPoint(position,viewPos);
        text.x = viewPos.x //- 30;
        text.y = viewPos.y
    }

    public OnCarteenTimeOver() {
        if(this.isShow) {
            this.ui.m_canteenLastTime.m_txt_lastTime.text = "开发完成！"
        }
    }

    public OnLibraryTimeOver() {
        if(this.isShow) {
            this.ui.m_libraryLastTime.m_txt_lastTime.text = "开发完成！"
        }
    }

    private _lV4Pos = new Laya.Vector4();
    UpdateLibraryTimePos(pos:Laya.Vector3) {
        if(this.isShow){
            this.ChangPos(this.ui.m_libraryLastTime,pos,this._lV4Pos);
        }
    }

    UpdateCanteenTimePos(position: laya.d3.math.Vector3) {
        if(this.isShow){
            this.ChangPos(this.ui.m_canteenLastTime,position,this._lV4Pos);
        }
    }

    private OnClickLibrary(){
        UIMediatorOpenCoffee.instance.ShowPanel(0);
        if(!MiscDataProxy.data.hasLibraryTime){
            MiscDataProxy.data.hasLibraryTime = true;
            MiscDataProxy.data.LibraryLastTime = 24 * 60 * 60 *1000;
        }

    }

    private OnClickCateen(){
        //没有触发倒计时就开始计时
        if(!MiscDataProxy.data.hasCanteenTime){
            MiscDataProxy.data.hasCanteenTime = true;
            MiscDataProxy.data.CanteenLastTime = 24 * 60 * 60 *1000;
        }
        //如果剩余时间小于0，就显示正在开发中
        if(MiscDataProxy.data.CanteenLastTime >0){
            UIMediatorOpenCoffee.instance.ShowPanel(1);
        }
        else{
            this.ui.m_canteenLastTime.m_txt_lastTime.text = "开发完成！"
            //todo 点击建造便利店
        }
        if(MiscDataProxy.data.CanteenLastTime <=0){
            this.BuildCanteen()
            this.Close();
        }
    }

    private BuildCanteen(){
        MesManagerClass.instance.eventUI(EEventUI.BuildConstruction, [EEventUI.BuildConstruction, StringName.canteen]);
    }

    private Close(){
        UIManagerProxy.instance.setUIState([{typeIndex:EUI.CountDownTime,state:false}],false);
    }
}