import { SystemException } from "./api/SystemException";

export class NullReferenceException extends SystemException{
    name='NullReferenceException';
    message = '依从一个空对象';
    data: any[];
    constructor(msg?: string,...data:any[]) {
        super(msg);
        if (msg) {
            this.message = msg;
        }
        this.data = data;
    }
    
}