import BaseUIMediator from "../aTGame/UI/FGUI/BaseUIMediator";
import { EUILayer } from "../aTGame/UI/FGUI/EUILayer";
import { GuideInfoConfigProxy } from "../Config/GuideInfoConfigProxy";
import { GuideDataProxy } from "../Data/GuideDataProxy";
import { EUI } from "../dMyGame/Enum/EUI";
import { EEventUI } from "../dMyGame/EventEnum/EEventUI";
import EnvironmentManager from "../dMyGame/Manager/EnvironmentManager";
import MesManagerClass from "../dMyGame/Manager/MesManager";
import UIManagerProxy from "../dMyGame/Manager/Proxy/UIManagerProxy";
import { GuideInfo } from "../dMyGame/_config/GuideInfo";
import FGUI_GuidePanel from "../FGUI/Guide/FGUI_GuidePanel";
import { StringUtility } from "../lty/class/String";
import { UIMediatorStaff } from "./UIMediatorStaff";


export class UIMediatorGuide extends BaseUIMediator<FGUI_GuidePanel> {
    private static m_instance: UIMediatorGuide;
    private allowClick = false;
    dataList: GuideInfo.config[];

    /** 单例 */
    public static get instance(): UIMediatorGuide {
        if (!this.m_instance) {
            this.m_instance = new UIMediatorGuide();
            this.m_instance._classDefine = FGUI_GuidePanel;

        }
        return this.m_instance;
    }

    private constructor() {
        super();
    }

    protected _layer: EUILayer = EUILayer.Guide;

    _OnShow() {
        //  console.log("=========打开引导遮罩=========")
        super.Show();
        //  console.log("=========注册点击事件========")
        // this.ui.m_window.on(Laya.Event.CLICK, this, () => {
        //     console.log("模拟点击"); UIMediatorStaff.instance.ui.m_midPanel.m_btn_up.fireClick(true);
        //     UIMediatorStaff.instance.ui.m_midPanel.m_btn_buy.fireClick(true);

        // });
        this.dataList = GuideInfoConfigProxy.instance.dataList;
    }

    private MaskClick() {
        //   console.log("=========点击MaskClick========")
        if (this.allowClick) {
            if (GuideDataProxy.instance.Data.guideID == 68) {
                MesManagerClass.instance.eventUI(EEventUI.GuideToEnd);
                console.log("发送关闭引导信号");
            }
            // console.log("UIMediatorGuide:执行下一步");
            this.allowClick = false;
            // console.log("关闭允许点击");
            MesManagerClass.instance.eventUI(EEventUI.GuideToNext);
        }
    }

    NextPanel(index: number) {
        //延迟设置引导面板信息
        let delayTime = this.dataList[index].delay;
        this.ui.m_click.off(Laya.Event.CLICK, this, this.MaskClick);
        if (delayTime != 0) {
            //  console.log("延迟{0}秒", delayTime);
            //全空白遮罩 不允许点击
            this.ui.m_layer.selectedIndex = 0;
            this.ui.m_guide.selectedIndex = 3;
            Laya.timer.once(delayTime, this, this.SetPanel, [index])
        } else {
            this.SetPanel(index);
        }
    }

    SetPanel(index: number) {
        Laya.timer.clearAll(this);
        this.ui.m_guide.selectedIndex = this.dataList[index].guideIndex;
        this.ui.m_layer.selectedIndex = this.dataList[index].layerIndex;
        if (this.ui.m_layer.selectedIndex == 1)
            this.ui.m_windowsPos.selectedIndex = this.dataList[index].posIndex;
        else {
            // console.log("关闭引导点击");
            // StringUtility.PopString(this.dataList[index].text, (subString) => {
            //     this.ui.m_text.text = subString;
            // }, 20)
            // Laya.timer.once(this.dataList[index].text.length * 50, this, () => {
            //     this.ui.m_click.on(Laya.Event.CLICK, this, this.MaskClick);
            //     this.allowClick = true;
            //     console.log("开启引导点击");
            // })
            //     .then(() => {
            //     this.ui.m_click.on(Laya.Event.CLICK, this, this.MaskClick);
            //     this.allowClick = true;
            //     console.log("开启引导点击");
            // });
            // if (index == 1) {
            //     this.ui.m_finger_npcLayer.visible = false;
            //     let position = new Laya.Vector3(2.5, 0.5, -9.6);
            //     Laya.timer.once(500, this, () => { this.ChangePos(position, -750, -100); this.ui.m_finger_npcLayer.visible = true; this.allowClick = true; });
            // }
            // else if (index == 24) {
            //     let position = new Laya.Vector3(-13, 1, -17);
            //     this.ChangePos(position, -700, 900);
            // }
            this.ui.m_text.text = this.dataList[index].text;
            Laya.timer.once(500, this, () => {
                this.allowClick = true; this.ui.m_click.on(Laya.Event.CLICK, this, this.MaskClick);
            })
            // this.allowClick = true;
            // this.ui.m_click.on(Laya.Event.CLICK, this, this.MaskClick);
            console.log(this.dataList[index].text);
        }
        switch (index) {
            case 31:
                //网红按钮引导适配
                this.ui.m_window.x = this.ui.m_pos.x;
                this.ui.m_window.y = this.ui.m_pos.y;
                break;
            case 34:
                //建造按钮引导适配  新的适配 pos2 左右 顶顶
                this.ui.m_window.x = this.ui.m_pos2.x;
                this.ui.m_window.y = this.ui.m_pos2.y;
                break;
            case 36:
                //每日登录按钮引导适配
                this.ui.m_window.x = this.ui.m_pos.x;
                this.ui.m_window.y = this.ui.m_pos.y;
                break;
            case 39:
                //右下角按钮引导适配   新的适配 pos3 右右 低低
                this.ui.m_window.x = this.ui.m_pos3.x;
                this.ui.m_window.y = this.ui.m_pos3.y;
                break;
            case 41:
                //看广告按钮引导适配   
                this.ui.m_window.x = this.ui.m_pos.x;
                this.ui.m_window.y = this.ui.m_pos.y;
                break;
            case 67:
                //任务按钮引导适配   
                this.ui.m_window.x = this.ui.m_pos4.x;
                this.ui.m_window.y = this.ui.m_pos4.y;
                break;
            default:
                break;
        }

    }

    // private ChangePos(position: Laya.Vector3, x: number, y: number) {
    //     let viewPos = new Laya.Vector4();
    //     EnvironmentManager.instance.camera.worldToViewportPoint(position, viewPos)
    //     this.ui.m_finger_npcLayer.x = viewPos.x + x;
    //     this.ui.m_finger_npcLayer.y = viewPos.y + y;
    //    // console.log("x:{0}  y:{1}", viewPos.x, viewPos.y);

    // }

    public Close() {
        UIManagerProxy.instance.setUIState([
            {
                typeIndex: EUI.Guide, state: false
            }
        ], false);
    }

    _OnHide() {
        super.Hide();
    }
}
