import RootLocalStorageData from "../aTGame/Data/RootLocalStorageData";
import RootLocalStorageSaveNaWft from "../aTGame/Data/RootLocalStorageSave";
import {StringName} from "../Enum/StringName";
import BuildingData from "./BuildingData";
import {FunctionID} from "./FunctionID";
import {SlotData} from "./SlotData";
import UISlotConfigProxy from "../Config/UISlotConfigProxy";
import {BuildDefaultSlotConfigProxy} from "../Config/BuildDefaultSlotConfigProxy";
import {UpdateSlotEventData} from "./UpdateEventData";
import MesManagerClass from "../dMyGame/Manager/MesManager";
import {EEventScene} from "../dMyGame/EventEnum/EEventScene";
import {EEventUI} from "../dMyGame/EventEnum/EEventUI";
import {SlotUIDConst} from "../dMyGame/_config/SlotUIDConst";

/**洗手区的数据 */
export class SData extends BuildingData {
    constructor(male: boolean, index: number) {
        super();
        this.isMale = male;
        this.active = true;
        this.functionID = this.isMale ? FunctionID.MaleSink : FunctionID.FemaleSink;
        this.index = index;
        this.name = male ? "maleSinkAera" : "femaleSinkAera";
        this.title = male ? "洗手区（男）" : "洗手区(女)";
    }
}

export class SinkData extends RootLocalStorageData {
    /**男女洗手区建筑列表 ,洗手池在洗手区里面*/
    list: SData[];
}

export class SinkDataProxy extends RootLocalStorageSaveNaWft<SinkData> {
    constructor() {
        super();
        MesManagerClass.instance.onUI(EEventUI.UpdateSlotData,this, this.UpdateSlotData);
    }
    private static _instance: SinkDataProxy;
    public static get instance() {
        if (this._instance == null) {
            this._instance = new SinkDataProxy();
        }
        return this._instance;
    }

    public static get data() {
        return this.instance._data;
    }

    public get maleSinkData() {
        return this.dict[StringName.maleSinkAera];
    }

    public get femaleSinkData() {
        return this.dict[StringName.femaleSinkAera];
    }

    private _data: SinkData;
    private dict: { [index: string]: SData } = {};

    protected get _saveName(): string {
        return "SinkData";
    }

    protected getNewData(): SinkData {
        return new SinkData();
    }

    public InitData(): void {
        this._data = this._ReadFromFile();
        if (this._data == null || this._data.list == null) {
            //第一次进行游戏初始化数据
            console.log("第一次进行游戏,初始洗手池数据...");
            this._data.list = [];
            this.InitSinkData();
            this._SaveToDisk(this._data);
        }
        for (let d of this._data.list) {
            this.dict[d.name] = d;
        }
    }

    private InitSinkData() {
        let maleSink = new SData(true, 0);
        let femaleSink = new SData(false, 1);
        this._data.list.push(maleSink);
        this._data.list.push(femaleSink);
        this.dict[StringName.maleSinkAera] = maleSink;
        this.dict[StringName.femaleSinkAera] = femaleSink;
        //初始化默认的slot
        this.InitSlot(true, maleSink);
        this.InitSlot(false, femaleSink);
    }

    private InitSlot(isMale: boolean, data: SData) {
        let count = 0;
        let dataList = BuildDefaultSlotConfigProxy.instance.dataList;
        for (let i = 0; i < dataList.length; i++) {
            if (isMale) {
                if (dataList[i].wash_man_Slot > -1) {
                    count++;
                }
            } else {
                if (dataList[i].wash_woman_Slot > -1) {
                    count++;
                }
            }
        }
        if (count > 0) {
            data.slots = new Array(count);
            for (let i = 0; i < count; i++) {
                let slotData = new SlotData();
                slotData.id = i;
                if (isMale) {
                    slotData.level = dataList[i].wash_man_Slot_Level;
                    slotData.uid = dataList[i].wash_man_Slot;
                    slotData.name = UISlotConfigProxy.instance.dataList[slotData.uid].name;
                    data.slots[i] = slotData;
                } else {
                    slotData.level = dataList[i].wash_woman_Slot_Level;
                    slotData.uid = dataList[i].wash_woman_Slot;
                    slotData.name = UISlotConfigProxy.instance.dataList[slotData.uid].name;
                    data.slots[i] = slotData;
                }
                //洗手区只有洗手台可以使用
                slotData.usable = slotData.uid == SlotUIDConst.data.slot_wash_basin_woman_config || slotData.uid == SlotUIDConst.data.slot_wash_basin_man_config;

            }
        } else {
            console.warn("洗手池没有可用的SLot，isMale：", isMale);
        }
    }

    private UpdateSlotData(mes:UpdateSlotEventData){
        let funcID = mes.building.functionID;
        if(funcID == FunctionID.FemaleSink || funcID == FunctionID.MaleSink) {
            let data = this.dict[mes.building.buildingName];
            //不在本信息代理中不管理
            if (data) {
                SlotData.UpdateSlotData(mes,data.slots);
                this._SaveToDisk(this._data)
            } else {
                console.error("获取数据失败！", mes);
            }
        }
    }
}

