export abstract class ITask {
    abstract index: number;

    abstract get progress(): number;

    abstract CheckProgress(data: any): number
    abstract reward: number;
    abstract rewardType: RewardType;
    abstract describe: string;
    abstract name: string;

    abstract Go()

    abstract toProgressString()
}

export enum RewardType {
    diamond = 0,
    money = 1,
}
