import UIMediatorFlyPanel from "../UI/UIMediatorFlyPanel";

export default class LTUI {
    public static FlyCoinsTo(fromObj: fgui.GObject, toObj: fgui.GObject, flyCount: number = 20,
        flyTime: number = 1.5, circleRadius: number = 120) {
        //UIMediatorFlyPanel.instance.Show();
        UIMediatorFlyPanel.instance.FlyCoins(fromObj, toObj, flyCount, flyTime, circleRadius);
    }

    public static FlyDiamondTo(fromObj: fgui.GObject, toObj: fgui.GObject, flyCount: number = 20,
        flyTime: number = 1.5, circleRadius: number = 100) {
        //UIMediatorFlyPanel.instance.Show();
        UIMediatorFlyPanel.instance.FlyDiamonds(fromObj, toObj, flyCount, flyTime, circleRadius);
    }
}