import BaseUIMediator from "../aTGame/UI/FGUI/BaseUIMediator";
import { EUI } from "../dMyGame/Enum/EUI";
import UIManagerProxy from "../dMyGame/Manager/Proxy/UIManagerProxy";
import FGUI_PGameSet from "../FGUI/Main/FGUI_PGameSet";
import AudioProxyClass from "../dMyGame/Proxy/AudioProxy";
import VIVOData from "src/cFrameBridge/Platform/Data/VIVOData";
import VivoBannerAd from "src/dMyGame/vivoPlatform/VivoBannerAd";
import { UIMediatorGameAd } from "./UIMediatorGameAd";

export class UIMediatorSetting extends BaseUIMediator<FGUI_PGameSet>{
    private static m_instance: UIMediatorSetting;
    /** 单例 */
    public static get instance(): UIMediatorSetting {
        if (!this.m_instance) {
            this.m_instance = new UIMediatorSetting();
            this.m_instance._classDefine = FGUI_PGameSet;
        }
        return this.m_instance;
    }
    private constructor() { super(); }
    public _musicToggle = true;
    private _effectToggle = true;
    _OnShow() {
        // this.isGameSetPanel = true;
        super.Show();
        this.ui.m_btn_close.onClick(this, this.Close);
        this.ui.m_close.onClick(this, this.Close);
        this.ui.m_musicBtn.m_state.selectedIndex = this._musicToggle ? 0 : 1;
        this.ui.m_musicBtn.onClick(this, this.ToggleMusic);

        /***原生广告调用******************* */
        UIMediatorGameAd.instance.ShowNativeAd();

        /**Baanner */
        let vivoObj = new VIVOData();
        VivoBannerAd.instance.showBannerFun(vivoObj);
    }
    private ToggleMusic() {
        this._musicToggle = !this._musicToggle;
        this.ui.m_musicBtn.m_state.selectedIndex = this._musicToggle ? 0 : 1;
        if (this._musicToggle) {
            AudioProxyClass.instance.BGMGoOn();
        }
        else {
            AudioProxyClass.instance.stopBGM();
        }
    }
    private Close() {
        UIManagerProxy.instance.setUIState([
            {
                typeIndex: EUI.Setting, state: false
            }
        ], false);
    }

    _OnHide() {
        super.Hide();
        VivoBannerAd.instance.DestroyAd();

    }
}