
import {Task, TaskEventData} from "./Task/Task";
import MesManagerClass from "../dMyGame/Manager/MesManager";
import {EEventUI} from "../dMyGame/EventEnum/EEventUI";
import {TaskZero} from "./Task/TaskZero";
import {MiscDataProxy} from "../Data/MiscDataProxy";
import {TaskOne} from "./Task/TaskOne";
import {TaskTwo} from "./Task/TaskTwo";
import {TaskThree} from "./Task/TaskThree";
import {TaskFour} from "./Task/TaskFour";
import {TaskFive} from "./Task/TaskFive";
import {TaskSix} from "./Task/TaskSix";
import {TaskSeven} from "./Task/TaskSeven";
import {TaskEight} from "./Task/TaskEight";
import {TaskNine} from "./Task/TaskNine";
import {TaskTen} from "./Task/TaskTen";
import {TaskEleven} from "./Task/TaskEleven";
import {TaskTwelve} from "./Task/TaskTwelve";
import {TaskThirteen} from "./Task/TaskThirteen";
import {Uma, UmaEvent} from "../Uma";

export class TaskManager {
    private static _instance: TaskManager;
    get hasTask(): boolean{
        return  this.currentTask!=null;
    }
    public static get instance(): TaskManager {
        if (TaskManager._instance == null) {
            TaskManager._instance = new TaskManager();
        }
        return TaskManager._instance;
    }

    constructor() {
        MesManagerClass.instance.on(EEventUI.TaskEvent,this,this.Check)
    }

    public currentTask:Task;

    public InitTask(){
        let data  = MiscDataProxy.instance.Data.task;
        if(data ==null){
            if(MiscDataProxy.data.allTaskComplete) {
                this.currentTask = null;
                console.log("当前没有任务！")
                return;
            }
            else{
                data = MiscDataProxy.data.task = this.GetNewTask(0);
                data.Init();
            }
        }
        let task:Task = this.GetNewTask(data.index);
        if(task){
            //还原任务记录
            this.currentTask = task;
            task.condition = data.condition;
            if(isNaN(data.progress)){
                data.progress = 0;
            }
            if(data.progress == null){
                data.progress = 0;
            }
            task.progress = data.progress;
            if(isNaN(data.record)){
                data.record = 0;
            }
            if(data.record == null){
                data.record = 0;
            }
            task.record = data.record;
            MiscDataProxy.instance.Data.task = task;
            MesManagerClass.instance.eventUI(EEventUI.UpdateTaskProgress);
        }
    }

    protected Check(data:TaskEventData){
        if(this.currentTask == null){return}
        this.currentTask.CheckProgress(data);
        MesManagerClass.instance.eventUI(EEventUI.UpdateTaskProgress);
    }

    DoNext() {
        Uma.trackEvent(UmaEvent.Task,{"Index":this.currentTask.index.toString()})
        this.currentTask = this.GetNewTask(this.currentTask.NextTask);
        MiscDataProxy.instance.Data.task = this.currentTask;
        if (this.currentTask) {
            this.currentTask.Init();
        }
        if(this.currentTask==null){
            MiscDataProxy.data.allTaskComplete = true;
        }
        MesManagerClass.instance.eventUI(EEventUI.UpdateTaskProgress);
    }

    GetNewTask(index:number):Task{
        switch (index){
            case 0:
                return  new TaskZero(0);
            case 1:
                return  new TaskOne(1);
            case 2:
                return  new TaskTwo(2);
            case 3:
                return  new TaskThree(3);
            case 4:
                return  new TaskFour(4);
            case 5:
                return  new TaskFive(5);
            case 6:
                return  new TaskSix(6);
            case 7:
                return  new TaskSeven(7);
            case 8:
                return  new TaskEight(8);
            case 9:
                return  new TaskNine(9);
            case 10:
                return  new TaskTen(10);
            case 11:
                return  new TaskEleven(11);
            case 12:
                return  new TaskTwelve(12);
            case 13:
                return  new TaskThirteen(13);
        }
    }
}