import { SlotData } from "./SlotData";
/**建筑数据的基类 */
export default class BuildingData {
    /**厕所是否建造完成，激活开放 */
    active = false;
    /**建筑功能ID */
    functionID: number;
    /**建筑的key,序号 */
    index: number;
    /**建筑的英文名字 */
    name: string;
    /**建筑的中文名字 */
    title: string;
    /**是否为男 */
    isMale: boolean;
    //每个设备的等级
    slots: SlotData[] = [];

    public Copy(table: SlotData[]) {
        for (let data of table) {
            let s = new SlotData();
            s.id = data.id;
            s.uid = data.uid;
            s.level = data.level;
            s.name = data.name;
            this.slots.push(s);
        }
    }
}