import {ITask, RewardType} from "./ITask";
import {TaskConfigProxy} from "../../Config/TaskConfigProxy";
import {TaskEvent} from "./TaskEvent";
import {MoneyConverter} from "../../zml/MoneyConverter";

export class Task implements ITask {

    index: number;
    reward: number;
    rewardType: RewardType;
    describe: string;
    name: string;
    progress: number;
    //满足条件
    condition:number;
    //当前记录个数
    record:number;

    CheckProgress(data: TaskEventData): number {
        return this.progress;
    }

    constructor(index: number) {
        this.index = index;
        this.name = TaskConfigProxy.instance.dataList[index].name;
        this.describe = TaskConfigProxy.instance.dataList[index].describe;
        this.reward = TaskConfigProxy.instance.dataList[index].reward;
        this.rewardType = TaskConfigProxy.instance.dataList[index].reward_type;
        this.record = 0;
        this.progress= 0;
        this.condition = TaskConfigProxy.instance.dataList[index].condition;
    }

    Init(){
        console.log("method(Init) not implement"!);
    }

    /**
     * 获取下个任务的索引
     * @constructor
     */
    get NextTask(): number {
        return this.index +1;
    }

    /**
     * 进度
     */
    toProgressString() {
        if(this.progress>=100){
            return "完成"
        }
        else{
            return Math.floor( this.record )+ "/" + this.condition;
        }
    }

    /**
     * 缩写的的进度
     */
    toSimpleProgressString(){
        if(this.progress>=100){
            return "完成"
        }
        else{
            return MoneyConverter.TaskDoConverter(this.record) + "/" + MoneyConverter.TaskDoConverter(this.condition);
        }
    }

    /**
     * 跳转到任务目标附近
     * @constructor
     */
    Go(){
        console.log("method(Go) not implement"!);
    }
}

export class TaskEventData {
    evt: TaskEvent;
    data?: any;
}

