import BaseUIMediator from "../aTGame/UI/FGUI/BaseUIMediator";
import { EUILayer } from "../aTGame/UI/FGUI/EUILayer";
import { MiscDataProxy } from "../Data/MiscDataProxy";
import UIManagerProxy from "../dMyGame/Manager/Proxy/UIManagerProxy";
import { EUI } from "../dMyGame/Enum/EUI";
import { MoneyConverter } from "../zml/MoneyConverter";
import { UIMediatorEffectPanel } from "./UIMediatorEffectPanel";
import { BuildingManager } from "../GameComponent/BuildingManager";
import { Time } from "../lty/class/Time";
import { UIFacade } from "./UIFacade";
import FGUI_PGameOfflineIncome from "../FGUI/Main/FGUI_PGameOfflineIncome";
import PlatformManagerProxy from "../aTGame/Platform/PlatformManagerProxy";
import { Audio } from "../GameComponent/Audio";
import VivoBannerAd from "src/dMyGame/vivoPlatform/VivoBannerAd";
import VIVOData from "src/cFrameBridge/Platform/Data/VIVOData";

//离线收益界面
export class UIMediatorOffLineIncomePanel extends BaseUIMediator<FGUI_PGameOfflineIncome> {

    private static m_instance: UIMediatorOffLineIncomePanel;

    /** 单例 */
    public static get instance(): UIMediatorOffLineIncomePanel {
        if (!this.m_instance) {
            this.m_instance = new UIMediatorOffLineIncomePanel();
            this.m_instance._classDefine = FGUI_PGameOfflineIncome;
        }
        return this.m_instance;
    }

    protected _layer: EUILayer = EUILayer.Main;

    private constructor() {
        super();
    }

    private _money = 0;

    private get hasMoney() {
        return this._money > 0;
    }

    public TryShowPanel() {
        let time = new Date().getTime() - MiscDataProxy.instance.Data.onLineTime;
        if (time < 5 * 60 * 1000) {
            return;
        }
        UIManagerProxy.instance.setUIState([{ typeIndex: EUI.OffLineIncomePanel, state: true }], false);
    }


    protected _OnShow() {
        let deltaTime = new Date().getTime() - MiscDataProxy.instance.Data.onLineTime;
        let maxOffTime = MiscDataProxy.instance.Data.maxOffLineTime * 60 * 60 * 1000;
        this.ui.m_text_time.text = Time.ToTimeString(deltaTime);
        this.ui.m_text_maxTime.text = "上限" + MiscDataProxy.instance.Data.maxOffLineTime + "个小时";
        let progress = deltaTime > maxOffTime ? maxOffTime : deltaTime;
        this.ui.m_progress.value = progress;
        this.ui.m_progress.max = maxOffTime
        this._money = BuildingManager.instance.GetTotalMoney();
        this._money = this._money * progress / 1000 / 60;
        this.ui.m_text_money.text = MoneyConverter.DoConverter(this._money);
        this.ui.m_btn_1x.onClick(this, this.OnClick1x);
        this.ui.m_btn_2x.onClick(this, this.OnClick2x);
        this.ui.m_btn_close.onClick(this, this.OnClickClose);
        /**Baanner */
        let vivoObj = new VIVOData();
        VivoBannerAd.instance.showBannerFun(vivoObj);

    }

    private OnClick1x() {
        this.AddMoney();
    }

    private OnClick2x() {
        PlatformManagerProxy.instance.PlatformInstance.ShowRewardVideoAd(
            Laya.Handler.create(this, () => {
                Audio.PlayAudio();
                this.AddMoney(true);
            }),
            Laya.Handler.create(this, () => {
                PlatformManagerProxy.instance.PlatformInstance.ShowToast("未看完广告")
            }))
    }

    private AddMoney(is2x = false) {
        if (is2x) {
            this._money += this._money;
        }
        MiscDataProxy.Money += this._money;
        //特效
        if (this.isShow) {
            UIFacade.PlayMoneyEffect(this.ui.m_text_money);
        }
        this._money = 0;
        this.OnClickClose();
    }


    private OnClickClose() {
        UIManagerProxy.instance.setUIState([{ typeIndex: EUI.OffLineIncomePanel, state: false }], false)
        VivoBannerAd.instance.DestroyAd();
    }

}
