import Building from "./api/Building";
import {Guest} from "../Guest/Guest";
import { WayPoint } from "../../lty/plugin/NetNavigation/NetNavigation";
import { Vector3 } from "../../lty/class/Vector3";
import {CameraManager} from "../CameraManager";

export class PeeArea extends Building{

    _standardTime = 15
    protected ShowOutlook(ismale: boolean, index: number, showEffect: boolean = false) {
    }

    UpdateFacOutLook(){
        let slots = this.slots;
        for(let s of slots){
            this.UpdateFacilityLevel(s.name + "_" + s.id.toString(), s.level, false, s);
        }
    }

    OnClick() {
        super.OnClick();
        CameraManager.instance.FocusBuild(38);
    }
}
