import {ClockEvent} from "./ClockEvent";
import MesManagerClass from "../../../dMyGame/Manager/MesManager";

/**
 * 计时器，一天仅执行一次
 */
export class Clock {


    //计时器执行时间
     localTimeString: string;
    //计时事件
     event: ClockEvent;
    //循环次数
     loops = 1;
    //执行时刻
     time: number = 0;
    //已经执行的次数
     runTimes = 0;

    constructor(date: Date, evt: ClockEvent) {
        this.time = date.getTime();
        this.localTimeString = date.toLocaleTimeString();
        this.event = evt;
        this.Immediately(false);
    }

    /**
     * 是否已经执行完毕
     */
    get isDone(): boolean {
        return this.loops >= 0 && this.runTimes >= this.loops;
    }


    /**
     * 刷新计时器的执行时间
     * @param nowTime 当前时间
     * @param isNext 是否为向未来顺延一天，否为向过去倒退一天
     * @constructor
     * @private
     */
    private UpdateClockTime(nowTime: number, isNext = true) {
        let now = new Date(nowTime);
        let ct = new Date(this.time);
        let day = isNext ? ct.getDate() + 1 : ct.getDate() - 1;
        let date = new Date(now.getFullYear(), now.getMonth(), day, ct.getHours(), ct.getMinutes(), ct.getSeconds());
        this.time = date.getTime();
    }

    /**
     * 检测是否已经抵达指定时间
     * @param now 当前时间,the time value in milliseconds.
     * @constructor
     */
    public Check(now: number) {
        if (now > this.time) {
            this.runTimes++;
            if (!this.isDone) {
                this.UpdateClockTime(now);
            }
            this.DispatchEvent();
        }
    }

    /**
     * 执行次数
     * @param times, -1表示无限循环使用
     * @constructor
     */
    public SetLoop(times: number) {
        this.loops = times;
    }

    /**
     * 派发计时器中存贮的事件
     * @constructor
     */
    public DispatchEvent() {
        console.log(this.localTimeString, "广播", this.event);
        MesManagerClass.instance.event(this.event);
    }

    /**
     * 重置计时器事件
     * @param evt
     * @constructor
     */
    ResetEvent(evt: ClockEvent) {
        this.event = evt;
        this.runTimes = 0;
    }

    /**
     * 设定计时器后，执行时刻小于当前时间是否在检测时立即派发事件
     * @constructor
     * @param isTrue
     */
    Immediately(isTrue: boolean = false) {
        if (isTrue) {
            let now = new Date().getTime();
            if (this.time > now) {
                this.UpdateClockTime(now,false);
            }
        }
        else{
            let now = new Date().getTime();
            if(this.time < now){
                this.UpdateClockTime(now);
            }
        }
        return this;
    }
}