import EssentialResUrlsUntility from "../aTGame/Res/EssentialResUrls";
import ResLoadClass from "../aTGame/Res/ResLoad";
import {IWorkerData} from "../GameComponent/Worker/IWorkerData";
import {Random} from "../lty/class/Random";
import {StringName} from "../Enum/StringName";
//雇员数据对象池，用于随机生产雇员
export class EmployeeDataPool{

    private static _instance:EmployeeDataPool;
    public static get instance():EmployeeDataPool{
        if(this._instance==null){
            this._instance = new EmployeeDataPool();
        }
        return  this._instance;
    }

    private  m_data:IWorkerData[];
    private _totalWeight  = 0;

    constructor() {
        //加载文件
        let _url: string = EssentialResUrlsUntility.ConfigURL(StringName.EmployeeData);
        //这里必须不使用克隆资源
        let config = ResLoadClass.Get(_url, true);
        //
        if (config) {
            this.m_data = config as IWorkerData[];
            for(let data of this.m_data){
                this._totalWeight  += data.Weight;
                data.Weight = this._totalWeight;
            }
        }
        //清理资源缓存只使用一次
        ResLoadClass.Unload(_url);
    }

    //获取一个随机的雇员数据
    public GetRandomData(){
        let num = Random.Range(0,this._totalWeight);
        let data:IWorkerData = null;
        for(let d of this.m_data){
            if(d.Weight > num){
                data = d;
                break;
            }
        }
        return  data;
    }

    //获取技能图标的url
    public static GetIconSprite(type:number){
        switch (type){
            case 0:
                return "res/sprite/skillicon/skill_1001.png";
            case 1:
                return "res/sprite/skillicon/skill_1002.png";
            case 2:
                return "res/sprite/skillicon/skill_1003.png";
            case 3:
                return "res/sprite/skillicon/skill_1004.png";
            case 4:
                return "res/sprite/skillicon/skill_1005.png";
        }
    }

    static GetEmployeeSpriteByName(Name: string) {
        return "";
    }

    static GetEmployeeSprite(data: IWorkerData):string {
        if(data.Gender ==0){
            return "res/sprite/employee/female_1.png";
        }
        else
        {
            return "res/sprite/employee/male_1.png";
        }
    }
}