import {Task, TaskEventData} from "./Task";
import {ToiletDataProxy} from "../../Data/ToiletDataProxy";
import SlotUIDConstProxy from "../../Data/SlotUIDConstProxy";
import {TaskTen} from "./TaskTen";
import {TaskEvent} from "./TaskEvent";
import {CameraManager} from "../CameraManager";
import {EffectManager} from "../EffectManager";
import {EffectName} from "../../dMyGame/ResList/PrefabNames";

export class TaskNine extends Task {
    CheckProgress(data: TaskEventData): number {
        if(data.evt == TaskEvent.UpdateTrash){
            this.Init()
        }
        return this.progress;
    }
    Init() {
        let ts = ToiletDataProxy.data.list;
        this.record ++;
        for(let t of ts){
            for(let s of t.slots){
                if(s.uid== SlotUIDConstProxy.instance.data.slot_mt_config && s.level >=4){
                    this.record ++;
                }
            }
        }
        this.progress = this.record/this.condition *100;
    }

    Go() {
        let data = CameraManager.instance.FocusPositionByIndex(0,false);
        let pos = new Laya.Vector3(-data.x,data.y,data.z);
        EffectManager.PlayEffect(EffectName.ZS,1111,10000,pos);
    }

}
