import VIVOData from "src/cFrameBridge/Platform/Data/VIVOData";
import { GuideDataProxy } from "src/Data/GuideDataProxy";

export default class VivoBannerAd {
    private static _instance: VivoBannerAd;
    /** 单例 */
    public static get instance(): VivoBannerAd {
        if (!this._instance) {
            this._instance = new VivoBannerAd();
        }
        return this._instance;
    }
    private bannerAd: IVivoBanner;
    /** 获取Banner广告组件 */
    public get m_bannerAd(): IVivoBanner {
        return this.bannerAd;
    }
    /**显示Banner广告 */
    public showBannerFun(vivoData: VIVOData) {
        if (!GuideDataProxy.IsCompleteAllGuide) {
            return;
        }
        try {
            let banner: IVivoBanner = window["qg"].createBannerAd({
                adUnitId: vivoData.bannerId,
            }) as any;

            this.bannerAd = banner;
            this.bannerAd.show();
            let onLoadFun = () => {
                console.log("视频播放成功");
                this.bannerAd.offLoad(onLoadFun);
                this.bannerAd.offError(onErrorFun);
            }
            let onErrorFun = () => {
                console.log("视频播放失败");
                this.bannerAd.offLoad(onLoadFun);
                this.bannerAd.offError(onErrorFun);
            }
            this.bannerAd.onLoad(onLoadFun);
            this.bannerAd.onError(onErrorFun);
        } catch (error) {
            console.error(error);

        }
    }
    /**销毁Banner广告 */
    public DestroyAd(): void {
        if (!GuideDataProxy.IsCompleteAllGuide) {
            return;
        }
        try {
            if (this.bannerAd) {
                this.bannerAd.destroy();
            }

        }
        catch (e) {
            console.error(e);

        }
    }

}

export interface IVivoBanner {
    /**
 * 调用  方法请求展示 banner，成功的时候回调 onShow，出错的时候回调 onError
 */
    show: () => void;
    /**
     * 隐藏 banner，成功回调 onHide, 出错的时候回调 onError
     */
    hide: () => void;
    /**
     * 设置 banner 成功展示回调
     */
    onLoad: (callback: Function) => void;
    /**
     * 移除 banner 成功展示回调
     */
    offLoad: (callback: Function) => void;
    /**
     * 设置 banner 隐藏回调
     */
    onHide: (callback: Function) => void;
    /**
     * 移除banner 隐藏回调
     */
    offHide: (callback: Function) => void;
    /**
     * 设置失败回调
     */
    onError: (callback: Function) => void;
    /**
     * 移除失败回调
     */
    offError: (callback: Function) => void;
    /**
     * 销毁Banner广告
     * */
    destroy: () => void;
}