import BaseUIMediator from "../../aTGame/UI/FGUI/BaseUIMediator";
import UIManagerProxy from "../../dMyGame/Manager/Proxy/UIManagerProxy";
import { EUI } from "../../dMyGame/Enum/EUI";
import { BuildingManager } from "../../GameComponent/BuildingManager";
import { MoneyConverter } from "../../zml/MoneyConverter";
import { UIFacade } from "../UIFacade";
import { ADUtility, UIMediatorMain } from "../UIMediatorMain";
import MesManagerClass from "../../dMyGame/Manager/MesManager";
import { EEventScene } from "../../dMyGame/EventEnum/EEventScene";
import FGUI_PGameMainInfluencer from "../../FGUI/Main/FGUI_PGameMainInfluencer";
import { EEventUI } from "../../dMyGame/EventEnum/EEventUI";
import { GuideDataProxy } from "../../Data/GuideDataProxy";
import PlatformManagerProxy from "../../aTGame/Platform/PlatformManagerProxy";
import { GuestPool } from "../../GameComponent/Guest/GuestPool";
import { Audio } from "../../GameComponent/Audio";
import { MiscDataProxy } from "../../Data/MiscDataProxy";
import { Uma, UmaEvent } from "../../Uma";
import VIVOData from "src/cFrameBridge/Platform/Data/VIVOData";
import VivoBannerAd from "src/dMyGame/vivoPlatform/VivoBannerAd";
import { UIMediatorGameAd } from "../UIMediatorGameAd";


export class UIMediatorInfluencer extends BaseUIMediator<FGUI_PGameMainInfluencer>{
    private static m_instance: UIMediatorInfluencer;
    private money = 0;
    private isGuide: any;
    private vivoObj: VIVOData;
    /** 单例 */
    public static get instance(): UIMediatorInfluencer {
        if (!this.m_instance) {
            this.m_instance = new UIMediatorInfluencer();
            this.m_instance._classDefine = FGUI_PGameMainInfluencer;
        }
        return this.m_instance;
    }


    private constructor() {
        super();
    }

    protected _OnShow() {
        // this.isInfluence = true;
        this.money = 8 * BuildingManager.instance.GetTotalMoney();
        this.ui.m_bg.m_btn_close.onClick(this, this.OnClickBtnClose);
        this.ui.m_btn_thanks.onClick(this, this.OnClickBtnClose)
        this.ui.m_btn_Start.onClick(this, this.OnClickWatchAD);
        this.ui.m_text_money.text = MoneyConverter.DoConverter(this.money);
        this.isGuide = !GuideDataProxy.IsCompleteAllGuide;
        Uma.trackEvent(UmaEvent.WorldEvent, { "influencer": "UI" });

        /**弹原生广告 */
        UIMediatorGameAd.instance.ShowNativeAd();

        this.vivoObj = new VIVOData();
        VivoBannerAd.instance.showBannerFun(this.vivoObj);

    }

    OnClickBtnClose() {
        UIManagerProxy.instance.setUIState([{ typeIndex: EUI.UIMediatorInfluencer, state: false }], false);

        UIMediatorMain.instance.RemoveADUtility(ADUtility.NetRed);

        MesManagerClass.instance.event3D(EEventScene.NetRedLeaveWC);
        VivoBannerAd.instance.DestroyAd();

    }

    OnClickWatchAD() {
        if (!GuideDataProxy.IsCompleteAllGuide) {
            UIFacade.PlayMoneyEffect(this.ui.m_btn_Start);

            UIMediatorMain.instance.RemoveADUtility(ADUtility.NetRed);


            this.OnClickBtnClose();
            MesManagerClass.instance.event3D(EEventScene.NetRedLeaveWC);
            //引导
            if (this.isGuide) {
                MesManagerClass.instance.eventUI(EEventUI.GuideToNext);
            }
            return;
        }
        console.log("看广告！+钱" + this.money)
        PlatformManagerProxy.instance.PlatformInstance.ShowRewardVideoAd(Laya.Handler.create(this, () => {
            Audio.PlayAudio();
            MiscDataProxy.Money += this.money;
            this.money = 0;
            Uma.trackEvent(UmaEvent.WorldEvtAD, { "influencer": "AD" });
            if (this.isShow) {
                UIFacade.PlayMoneyEffect(this.ui.m_btn_Start);
                this.OnClickBtnClose();
            }
            //引导
            if (this.isGuide) {
                MesManagerClass.instance.eventUI(EEventUI.GuideToNext);
            }
        }),
            Laya.Handler.create(this, () => {
                PlatformManagerProxy.instance.PlatformInstance.ShowToast("未看完广告")
            }))
    }
}
