    /**开启引导 */
    StartGuide() {
        CameraManager.instance.enabled = false;
        //   MesManager.instance.onUI(EEventUI.OtherGuide, this, this.OtherGuide);
        MesManagerClass.instance.onUI(EEventUI.GuideToNext, this, this.ToNext);
        MesManagerClass.instance.onUI(EEventUI.GuideToEnd, this, this.EndGuide);
        GuestPool.instance.PopNetRed();
        // 断点引导        废弃
        // if (this.data.guideID != 0) {
        //     this.breakPoint = this.data.guideID;
        // }
        if (this.data.isCompleteGuide1 == false) {
            this.data.guideID = 0;
            this.data.isCompleteGuide1 = true;
            GuideDataProxy.instance.SaveData();
        } else if (this.data.isCompleteGuide2 == false) {
            this.data.guideID = 19;
            this.data.isCompleteGuide2 = true;
            GuideDataProxy.instance.SaveData();
        } else if (this.data.isCompleteGuide3 == false) {
            this.data.guideID = 36;
            this.data.isCompleteGuide3 = true;
            GuideDataProxy.instance.SaveData();
        } else if (this.data.isCompleteGuide4 == false) {
            this.data.guideID = 43;
            this.data.isCompleteGuide4 = true;
            GuideDataProxy.instance.SaveData();
        } else {
            this.data.isCompleteGuide5 = true;
            GuideDataProxy.instance.SaveData();
            this.EndGuide();
            return;
        }
        // else if (this.data.isCompleteGuide5 == false) {
        //     this.data.guideID = 43;
        //     this.data.isCompleteGuide4 = true;
        //     GuideDataProxy.instance.SaveData();
        // }

        this.OpenGuide();
    }

