import RootLocalStorageData from "../aTGame/Data/RootLocalStorageData";
import RootLocalStorageSaveNaWft from "../aTGame/Data/RootLocalStorageSave";
import {IWorkerData} from "../GameComponent/Worker/IWorkerData";
import {EmployeeDataPool} from "../Config/EmployeeDataPool";
import MesManagerClass from "../dMyGame/Manager/MesManager";
import {EEventUI} from "../dMyGame/EventEnum/EEventUI";
import {EEventScene} from "../dMyGame/EventEnum/EEventScene";
import {BuildingManager} from "../GameComponent/BuildingManager";
import Building from "../GameComponent/Buildings/api/Building";
import {StringName} from "../Enum/StringName";
import {ClockManager} from "../lty/plugin/Clock/ClockManager";
import {ClockEvent} from "../lty/plugin/Clock/ClockEvent";
import {Task, TaskEventData} from "../GameComponent/Task/Task";
import {TaskZero} from "../GameComponent/Task/TaskZero";
import {TaskEvent} from "../GameComponent/Task/TaskEvent";
import {Uma, UmaEvent} from "../Uma";

export class MiscData extends RootLocalStorageData {

    /**现金  钻石  水滴*/
    money = 50000;
    // money = 2100000000000;

    diamond = 700;
    // diamond = 700000000;

    water = 80;
    MaxWater = 80;

    //雇员数据
    staffList: IWorkerData[];
    //雇员的空位,最大数量
    staffSize: number = 3;
    //招聘市场列表
    marketList: IWorkerData[];
    //雇员刷新时间倒计时
    workMarketTime = 43200000

    //在线时刻，逐秒刷新
    onLineTime = 0;
    isFirstGame = true;
    //最大可用累计离线时间,hour
    maxOffLineTime = 2;

    //离线收益广告观看总次数
    OfflineAdTotalNum = 0;
    //离线收益广告观看奖励钻石的循环,每5次奖励30钻石
    OfflineDiamondLoop = 5;
    //离线收益广告每日已看次数
    OfflineAdDayNum = 0;
    //离线收益广告每日最大次数
    OfflineMaxDayNum = 8;
    //转盘抽奖，每日已看次数
    LuckWheelAdDayNum: number = 0;
    //转盘抽奖，每日最大次数
    LuckWheelAdMaxDayNum = 8;
    //转盘抽奖，初始抽奖可用的测试
    LuckWheelStartCount = 2;

    //双倍收益广告buff剩余时间
    public DoubleBuffTime:number = 0;
    //双倍收益广告buff最大时间，仅本次游戏有效
    public MaxDoubleBuffTime:number = 0;

    //签到日0-6
    signDay:number = 0 ;
    //今日是否已经签到
    hasSign = false;
    //任务系统
    task:Task;
    allTaskComplete= false;
    //便利店剩余时间
    CanteenLastTime: number=86400000;
    LibraryLastTime:number = 172800000
    hasCanteenTime = false;
    hasLibraryTime = false;
}

export class MiscDataProxy extends RootLocalStorageSaveNaWft<MiscData> {
    constructor() {
        super();
        MesManagerClass.instance.onUI(EEventUI.AddStaff, this, this.AddStaff);
        MesManagerClass.instance.on3D(EEventScene.SceneLoadComplete, this, MiscDataProxy.RefreshWater);
        MesManagerClass.instance.onUI(EEventUI.CostMoney, this, this.CostMoney);
        MesManagerClass.instance.on3D(EEventScene.OnUpdateSlotComplete,this,this.UpdateIncomeMin);
        ClockManager.OnTick(ClockEvent.UpdateOnLineTime)
        ClockManager.OnNewDay(ClockEvent.NewDay).SetLoop(-1);
        ClockManager.AddListener(ClockEvent.UpdateOnLineTime, this, this.OnTick);
        ClockManager.AddListener(ClockEvent.NewDay, this, this.OnNewDay);
    }

    private static m_instance: MiscDataProxy;

    public static get data() {
        return this.instance._data;
    }

    //#region 运行时的数据，不需要保存
    //左下角双倍收益的buff
    public static get AdIncomeBuff() {
        if (this.instance._data.DoubleBuffTime > 0) {
            return 2;
        }
        return 1;
    }

    //所有人上厕所速度加速的时间
    public static speedUpTime = 0;

    // 上厕所速度
    public static get toiletSpeed() {
        if (this.speedUpTime > 0) {
            return 0.5;
        }
        return 1;
    }

    //#endregion

    /** 单例 */
    public static get instance(): MiscDataProxy {
        if (!this.m_instance) {
            this.m_instance = new MiscDataProxy();
        }
        return this.m_instance;
    }

    private _data: MiscData;

    public get Data() {
        return this._data;
    }

    protected get _saveName(): string {
        return "MiscData";
    }

    protected getNewData(): MiscData {
        return new MiscData();
    }

    public InitData(): void {
        this._data = this._ReadFromFile();
        if (this._data.staffList == null || this._data.staffList.length <= 0) {
            let staff: IWorkerData = {
                Gender: 0,
                Level: 2,
                Name: "安娜",
                Price: 5000,
                Weight: 0.5,
                MoveSpeed: 1.5,
            }
            this._data.staffList = [];
            this._data.staffList.push(staff);
        }
        if (this._data.isFirstGame) {
            this._data.onLineTime = new Date().getTime();
            this._data.isFirstGame = false;
        }
        //初始化任务记录
    }

    private UpdateWaterState() {
        MiscDataProxy.RefreshWater();
        let cleanRoom = BuildingManager.instance.GetBuildingByName(StringName.cleanRoom);
        MiscDataProxy.MaxWater = (cleanRoom as Building).waterAdd;
    }

    //#region 雇员管理
    //获取雇员索引依靠名字
    private GetEmployeeIndexByName(name: string) {
        for (let i = 0; i < this._data.staffList.length; i++) {
            let employee = this._data.staffList[i];
            if (employee.Name == name) {
                return i;
            }
        }
        return -1;
    }

    //增加雇员的可用数量
    AddStaffPosNum() {
        this._data.staffSize += 1;
    }

    //增加雇员
    AddStaff(index: number, replaceName: string) {
        let data = this._data.marketList[index];
        if (data) {
            let index = this.GetEmployeeIndexByName(replaceName);
            //替换雇员
            if (replaceName != null && index > -1) {
                this._data.staffList[index] = data;
                console.log("替换雇员")
            }
            //增加雇员
            else {
                this._data.staffList.push(data);
                console.log("增加雇员")
            }
            MiscDataProxy.Money -= data.Price;
            MesManagerClass.instance.event3D(EEventScene.AddStaff, [data, replaceName])
            MesManagerClass.instance.eventUI(EEventUI.RenderMarketStaffList);
            Uma.trackEvent(UmaEvent.Employee,{"Number":this._data.staffList.length.toString()})
        } else {
            throw Error("获取招聘市场雇员信息失败:" + index);
        }
    }

    //刷新招聘市场列表数据
    UpdateMarketList() {
        this._data.marketList = this.GetEmployeeDataFromPool();
    }

    //获取三个不重复的随机雇员数据
    private GetEmployeeDataFromPool() {
        let list: IWorkerData[] = []
        let employeeList = MiscDataProxy.instance.Data.staffList;
        while (list.length < 3) {
            let d = EmployeeDataPool.instance.GetRandomData();
            if (list.indexOf(d) < 0 && !MiscDataProxy.ContainEmployee(d, employeeList)) {
                list.push(d);
            }
        }
        return list;
    }

    //通过名字检测雇员是否已经被雇佣
    public static ContainEmployee(employee: IWorkerData, list: IWorkerData[]) {
        for (let data of list) {
            if (data.Name == employee.Name) {
                return true;
            }
        }
        return false;
    }

    public GetMarketEmployeeList() {
        if (!this._data.marketList || this._data.marketList.length < 0) {
            this._data.marketList = this.GetEmployeeDataFromPool();
        }
        return this._data.marketList;
    }

    GetEmployeeList() {
        return this._data.staffList;
    }

    //#endregion

    //region 经济管理
    /** 总金钱*/
    public static get Money() {
        return this.instance._data.money;
    }

    public static set Money(value: number) {
        if (isNaN(value)) {
            console.error("NaN!!!")
            return;
        }
        if (value > this.instance._data.money) {
            this.instance.DispatchMoneyTaskEvent(value - this.instance._data.money);
        }
        this.instance._data.money = value;
        MesManagerClass.instance.eventUI(EEventUI.UpdateMoneyNum, value);
    }

    public static get Diamond() {
        return this.instance._data.diamond;
    }

    public static set Diamond(value: number) {
        this.instance._data.diamond = value;
        MesManagerClass.instance.eventUI(EEventUI.UpdateDiamondNum, value);
    }

    //占用的水资源
    public static get Water() {
        return this.instance._data.water;
    }

    public static UpdateWaterState() {
        // UIMediatorTopPanel.UpdateDiamondNum();
    }

    //占用的水资源
    public static set Water(value) {
        this.instance._data.water = value;
        MesManagerClass.instance.eventUI(EEventUI.UpdateWaterState);
    }

    //提供的水资源
    public static get MaxWater() {
        let waterRoom = BuildingManager.instance.GetBuildingByName(StringName.waterRoom);
        this.instance._data.MaxWater = (waterRoom as Building).waterAdd;
        return this.instance._data.MaxWater;
    }

    public static set MaxWater(value) {
        this.instance._data.MaxWater = value;
        MesManagerClass.instance.eventUI(EEventUI.UpdateWaterState);
    }

    /**刷新water */
    public static RefreshWater() {
        let map = BuildingManager.instance.buildingMap;
        let total: number = 0;
        if (map == null) {
            throw "map is null!"
        }
        map.forEach(ele => {
            if (ele.active) {
                let buildele = ele as Building;
                total += buildele.wasteWater;
            }
        });
        MiscDataProxy.Water = total;
        return total;
    }

    private _runtimeTotalIncomeMin = 0;
    //刷新每分钟自动收入数值
    public UpdateIncomeMin(){
        this._runtimeTotalIncomeMin = BuildingManager.instance.GetTotalMoney()/60;
        if(isNaN(this._runtimeTotalIncomeMin))
        {
            this._runtimeTotalIncomeMin = 0
        }
        console.log("刷新每秒收入：",this._runtimeTotalIncomeMin)
    }
    //#endregion

    //#region 广告
    //更新在线时刻,更新双倍收益广时间,所有人急速上厕所的buff
    private OnTick() {
        //增加每分钟的自动收入
        if(this._runtimeTotalIncomeMin >0){
            MiscDataProxy.Money +=this._runtimeTotalIncomeMin;
        }
        //更新双倍收益广时间
        if (MiscDataProxy.data.DoubleBuffTime > 0) {
            MiscDataProxy.data.DoubleBuffTime -= 1000;
            if (MiscDataProxy.data.DoubleBuffTime < 0) {
                MiscDataProxy.data.DoubleBuffTime = 0;
                MiscDataProxy.data.MaxDoubleBuffTime = 0;
            }
            MesManagerClass.instance.eventUI(EEventUI.UpdateAdBuffTime);
        }
        //所有人急速上厕所的buff
        if (MiscDataProxy.speedUpTime > 0) {
            MiscDataProxy.speedUpTime -= 1000;
            if (MiscDataProxy.speedUpTime < 0) {
                MiscDataProxy.speedUpTime = 0;
            }
        }
        //更新图书馆剩余建造时间
        if (MiscDataProxy.data.LibraryLastTime > 0 && MiscDataProxy.data.hasLibraryTime) {
            MiscDataProxy.data.LibraryLastTime -= 1000;
            if (MiscDataProxy.data.LibraryLastTime < 0) {
                MiscDataProxy.data.LibraryLastTime = 0;
            }
            MesManagerClass.instance.eventUI(EEventUI.LibraryTimeOver);
        }
        //更新便利店剩余建造时间
        if (MiscDataProxy.data.CanteenLastTime > 0 && MiscDataProxy.data.hasCanteenTime) {
            MiscDataProxy.data.CanteenLastTime -= 1000;
            if (MiscDataProxy.data.CanteenLastTime < 0) {
                MiscDataProxy.data.CanteenLastTime = 0;
            }
            MesManagerClass.instance.eventUI(EEventUI.CanteenTimeOver);
        }
        if(MiscDataProxy.data.workMarketTime> 0){
            MiscDataProxy.data.workMarketTime -= 1000;
            if (MiscDataProxy.data.workMarketTime < 0) {
                MiscDataProxy.data.workMarketTime = 43200000;
                this.UpdateMarketList();
            }
        }
        //更新在线时刻,下次登录计算离线收益
        this._data.onLineTime = new Date().getTime();
        this._SaveToDisk(this._data);
    }

    private OnNewDay() {
        this._data.OfflineAdDayNum = 0;
        this._data.LuckWheelAdDayNum = 0;
        this._data.LuckWheelStartCount = 2;
        this._data.hasSign = false;
    }

    static AddDoubleBuffTime(number: number) {
        MiscDataProxy.data.MaxDoubleBuffTime += number
        MiscDataProxy.data.DoubleBuffTime += number
    }

    private CostMoney(value: number) {
        MiscDataProxy.Money -= value;
    }

    //#endregion
    private DispatchMoneyTaskEvent(value:number){
        if(this._data.allTaskComplete){
            return;
        }
        let data = new TaskEventData();
        data.evt = TaskEvent.Money;
        data.data = value;
        MesManagerClass.instance.eventUI(EEventUI.TaskEvent,data);
    }

    public AddSignDay(){
        this._data.signDay++;
        this._data.signDay = this._data.signDay %7;
    }
}
