import PlatformManagerProxy from "./aTGame/Platform/PlatformManagerProxy";
import {EPlatformType} from "./aTGame/Platform/T/EPlatformType";

type umaSDK = {
    setOpenid: (id: number) => void;
    trackEvent: (id: string, params?: { [key: string]: string }) => void;
    trackShare: (data: any) => any;
    shareAppMessage: (data: any) => void;
}

export class Uma {
    private  static _onWeb = true;
    private static _inst: umaSDK
    private static get inst(): umaSDK {
        return this.GetInstance();
    }

    public static InitUma(){
        if (Laya.Browser.onWeiXin) {
            try {
                Uma.GetInstance();
            } catch (e) {
                console.error(e);
            }
        }
        else{
            console.warn("不是微信平台，uma不进行初始化")
        }
    }

    static GetInstance() {
        if (this._inst == null) {
            if (Laya.Browser.onWeiXin) {
                this._inst = window['wx']['uma'];
            } else {
                this._inst = window['uma'];
            }
        }
        if (this._inst == null) {
            console.error("Uma初始化失败！");
        }
        return this._inst;
    }

    static setOpenid(id: number): void {
        if (this._inst == null) {
            return;
        }
        this._inst.setOpenid(id);
    }

    static trackEvent(id: UmaEvent, params?: { [key: string]: string; }) {
        if(this._onWeb){
            console.log(id, params)
        }
        if (this._inst == null) {
            return;
        }
        this._inst.trackEvent(id, params);
    }

    static trackShare(data: any) {
        if (this._inst == null) {
            return data;
        }
        return this._inst.trackShare(data);
    }

    static shareAppMessage(data: any) {
        if (this._inst == null) {
            return;
        }
        this._inst.shareAppMessage(data);
    }
}

