import IRootManagerFFFFF from "../aTGame/Manager/IRootManager";
import EssentialResUrlsUntility from "../aTGame/Res/EssentialResUrls";
import ResLoadClass from "../aTGame/Res/ResLoad";
import {FunctionID} from "../Data/FunctionID";
//Guest的对象池配置表
export default class GuestPoolConfig implements IRootManagerFFFFF {
    //
    private static m_instance: GuestPoolConfig;
    /** 单例 */
    public static get instance(): GuestPoolConfig {
        if (!this.m_instance) {
            this.m_instance = new GuestPoolConfig();
        }
        return this.m_instance;
    }

    public m_data: IGuestPoolConfigData[] = [];

    /**
     * 初始化
     */
    public init() { }

    private constructor() {
        //加载文件
        let _url: string = EssentialResUrlsUntility.ConfigURL('guestPool.json');
        //这里必须不使用克隆资源
        let config = ResLoadClass.Get(_url, true);
        //
        if (config) {
            this.m_data = config as IGuestPoolConfigData[];
        }
        //清理资源缓存只使用一次
        ResLoadClass.Unload(_url);
    }
}

export interface IGuestPoolConfigData {
    Id: number,
    Functions: {
        FunctionId: FunctionID,
        Animations: string[],
        SfxName: string,
        SfxSlotId: number,
    }[],
    Gender: number[],
    RandomAttachments: number[],
    MoveSpeed: number,
    MoveAnimation:string,
    Weight: number,
    point: number;
}