import BaseUIMediator from "../aTGame/UI/FGUI/BaseUIMediator";
import FGUI_StaffInfoPanel from "../FGUI/BuildUIPanel/FGUI_StaffInfoPanel";
import UIManagerProxy from "../dMyGame/Manager/Proxy/UIManagerProxy";
import {EUI} from "../dMyGame/Enum/EUI";
import {EUILayer} from "../aTGame/UI/FGUI/EUILayer";
import {IWorkerData} from "../GameComponent/Worker/IWorkerData";
import FGUI_skillPanel from "../FGUI/BuildUIPanel/FGUI_skillPanel";
import {EmployeeDataPool} from "../Config/EmployeeDataPool";
import {UIMediatorStaffMarket} from "./UIMediatorStaffMarket";

//雇员的信息面板 ，替换雇员
export class UIMediatorStaffInfoPanel extends BaseUIMediator<FGUI_StaffInfoPanel> {
    private static m_instance: UIMediatorStaffInfoPanel;

    /** 单例 */
    public static get instance(): UIMediatorStaffInfoPanel {
        if (!this.m_instance) {
            this.m_instance = new UIMediatorStaffInfoPanel();
            this.m_instance._classDefine = FGUI_StaffInfoPanel;
        }
        return this.m_instance;
    }

    protected _layer = EUILayer.StaffInfoPanel;

    private constructor() {
        super();
    }

    private get staff() {
        return this.ui.m_StaffInfo;
    }

    private _runtimeData: IWorkerData;

    protected _OnShow() {
        super._OnShow();
        this.ui.m_StaffInfoBg.onClick(this, this.Close);
    }

    protected _OnHide() {
        super._OnHide();
        this._runtimeData = null;
    }

    private InitPanel(data: IWorkerData) {
        this.staff.m_name.text = data.Name;
        this.staff.m_starNum.fillAmount = data.Level / 4;
        this.staff.m_cutBtn.selectedIndex = 0;
        this.staff.m_loader_character.url = EmployeeDataPool.GetEmployeeSprite(data);
        this.staff.m_sex.selectedIndex = data.Gender == 0 ? 0 : 1;
        this.staff.m_skill.selectedIndex = data.Skill && data.Skill.length > 0 ? 1 : 0;
        if (this.staff.m_skill.selectedIndex == 1) {
            this.staff.m_skillList.itemRenderer = Laya.Handler.create(this, (index: number, item: FGUI_skillPanel) => {
                let skillData = data.Skill[index];
                item.m_sprite.url = skillData.Icon;
                item.m_Title.text = skillData.Title;
                item.m_text_desc.text = skillData.Desc;
            }, null, false);
            this.staff.m_skillList.numItems = data.Skill.length;
        }
        this.staff.m_btn_staffReplace.onClick(this, this.OpenMarketPanel);
    }

    public ShowPanel(staffData: IWorkerData) {
        UIManagerProxy.instance.setUIState([{typeIndex: EUI.StaffInfoPanel, state: true}], false);
        this._runtimeData = staffData;
        this.InitPanel(staffData);
    }

    Close() {
        UIManagerProxy.instance.setUIState([{typeIndex: EUI.StaffInfoPanel, state: false}], false);
    }

    private OpenMarketPanel() {
        let temp = this._runtimeData.Name;
        UIManagerProxy.instance.setUIState([
                {typeIndex: EUI.StaffInfoPanel, state: false},
                {typeIndex: EUI.StaffMarket, state: true}],
            false);
        UIMediatorStaffMarket.instance.SetReplaceWorkerName(temp);
    }
}