import {UpdateSlotEventData} from "./UpdateEventData";
import {FunctionID} from "./FunctionID";
import UISlotConfigProxy from "../Config/UISlotConfigProxy";
import MesManagerClass from "../dMyGame/Manager/MesManager";
import {EEventScene} from "../dMyGame/EventEnum/EEventScene";
import {EEventUI} from "../dMyGame/EventEnum/EEventUI";
import {SlotConfigProxy} from "../Config/SlotConfigProxy";
import {MiscDataProxy} from "./MiscDataProxy";
import {UIMediatorTopPanel} from "../UI/UIMediatorTopPanel";
import {TaskManager} from "../GameComponent/TaskManager";
import {TaskEventData} from "../GameComponent/Task/Task";
import SlotUIDConstProxy from "./SlotUIDConstProxy";
import {TaskEvent} from "../GameComponent/Task/TaskEvent";
import {Uma, UmaEvent} from "../Uma";

export class SlotData {
    //序号,在本建筑UI页面的排序
    id: number;
    //ui配置表的UID,对应此slot的设备类型
    uid: number;
    //本插槽中的设备等级
    level: number
    //本插槽中的设备名字
    name: string;
    //设备是否损坏
    isBreaking: boolean = false;
    //设备是否可被使用,区分装饰品和马桶之类
    usable = false;

    public static UpdateSlotData(mes: UpdateSlotEventData, slots: SlotData[]) {
        if (slots && slots.length > 0) {
            let slot = slots[mes.slotIndex];
            if (slot.uid != mes.uid) {
                console.warn("Slot升级警告：信息UID和依靠索引索引获取的数据中的UID不一致！");
            }
            let nextLevel = slot.level + 1;
            if (nextLevel <= UISlotConfigProxy.instance.dataList[slot.uid].maxLevel) {
                slot.level = nextLevel;
                //减去消耗的资源
                let config = SlotConfigProxy.instance.GetSlotConfigByUID(slot.uid)[nextLevel];
                if (config.updateCostCoin!=null) {
                    MiscDataProxy.Money-= config.updateCostCoin;
                    Uma.trackEvent(UmaEvent.Update,{"Type":"Money","Update":mes.building.buildingName +'_'+ slot.name+"_" + slot.id+"_Level_"+slot.level});
                }
                if(config.updataCostDiamond!=null){
                    MiscDataProxy.Diamond -= config.updataCostDiamond;
                    Uma.trackEvent(UmaEvent.Update,{"Type":"Diamond","Update":mes.building.buildingName +'_'+ slot.name+"_" + slot.id+"_Level_"+slot.level});
                }
                if(config.updateCostWater!=null){
                    //刷新水资源消耗状态的显示
                    MiscDataProxy.UpdateWaterState();
                }
                MesManagerClass.instance.event3D(EEventScene.UpdateSlotOutLook, mes);
                MesManagerClass.instance.eventUI(EEventUI.UpdateSlotComplete);
                this.EventFacUpdate(slot);
            } else {
                console.warn("Slot升级警告：", mes.building.buildingName, slot.name, "已到达最大等级，无法升级！");
            }
        } else {
            console.error("获取数据失败！", mes);
        }
    }

    //派发设备升级事件，任务占用
    protected static EventFacUpdate(data:SlotData){
        if(!MiscDataProxy.data.allTaskComplete){
            let evtData = new TaskEventData();
            switch (data.uid){
                case SlotUIDConstProxy.instance.data.slot_flow_config:
                    evtData.evt = TaskEvent.UpdateFlow;
                    break;
                case SlotUIDConstProxy.instance.data.slot_wash_basin_woman_config:
                    evtData.evt = TaskEvent.UpdateFemaleSink;
                    break;
                case SlotUIDConstProxy.instance.data.slot_mt_config:
                    evtData.evt = TaskEvent.UpdateMt;
                    break;
                case SlotUIDConstProxy.instance.data.slot_sewage_config:
                    evtData.evt = TaskEvent.UpdateSewage;
                    break;
                case SlotUIDConstProxy.instance.data.slot_wash_basin_man_config:
                    evtData.evt = TaskEvent.UpdateMaleSink;
                    break;
                case SlotUIDConstProxy.instance.data.slot_urinal_config:
                    evtData.evt = TaskEvent.UpdateUrinal;
                    break;
                case SlotUIDConstProxy.instance.data.slot_paper_config:
                    evtData.evt = TaskEvent.UpdatePaper;
                    break;
                case SlotUIDConstProxy.instance.data.slot_trashCan_config:
                    evtData.evt = TaskEvent.UpdateTrash;
                    break;
                default:
                    break;
            }
            MesManagerClass.instance.event(EEventUI.TaskEvent,evtData);
        }
    }
}
