import {StringName} from "../../../Enum/StringName";
import {LayaBehaviour} from "../../../lty/class/LayaBehaviour";
import {IBuilding} from "./IBuilding";
import {SlotData} from "../../../Data/SlotData";
import BuildingData from "../../../Data/BuildingData";
import {FacilityPosition} from "../FacilityPosition";
import UISlotConfigProxy from "../../../Config/UISlotConfigProxy";
import {SlotConfigProxy} from "../../../Config/SlotConfigProxy";
import {EffectManager} from "../../EffectManager";
import {EffectName} from "../../../dMyGame/ResList/PrefabNames";
import {MiscDataProxy} from "../../../Data/MiscDataProxy";
import {FunctionID} from "../../../Data/FunctionID";
import {TaskEventData} from "../../Task/Task";
import {TaskManager} from "../../TaskManager";
import SlotUIDConstProxy from "../../../Data/SlotUIDConstProxy";
import MesManagerClass from "../../../dMyGame/Manager/MesManager";
import {EEventUI} from "../../../dMyGame/EventEnum/EEventUI";
import {TaskEvent} from "../../Task/TaskEvent";
import {BuildDataProxy} from "../../../Data/BuildDataProxy";

export default class Building extends LayaBehaviour implements IBuilding {

    private _isMale: boolean;
    functionID: number;
    size: number;
    count: number;
    queueSize: number;
    queueCount: any;
    active: boolean;
    slots: SlotData[];
    protected _index: number;
    protected _name: string;
    //管理设备位置与人的交互,动态刷新开放的设备的信息
    // KEY - slotName value - 设备位置的信息
    protected _facMap: Map<string, FacilityPosition> = new Map<string, FacilityPosition>();
    public slotPosList: FacilityPosition[] = [];
    //建筑排序
    public get index(){
        return this._index;
    }

    get isMale() {
        return this._isMale;
    }

    //建筑的英文名字
    get buildingName(): string {
        return this._name;
    }

    protected _title: string;
    //建筑的中文名字
    get title() {
        return this._title;
    }

    private _runtimeEquipmentNode;

    protected get _equipmentNode(): Laya.Node {
        if (this._runtimeEquipmentNode == null) {
            this._runtimeEquipmentNode = this.sprite3D.getChildByName(StringName.equipment);
            if (this._runtimeEquipmentNode == null) {
                console.error(this.sprite3D.name + "设备节点获取失败");
            }
        }
        return this._runtimeEquipmentNode;
    }

    public InitBuilding(data: BuildingData, showEffect: boolean) {
        this._isMale = data.isMale;
        this.active = data.active;
        this.functionID = data.functionID;
        this._index = data.index;
        this._name = data.name;
        this.slots = data.slots;
        this._title = data.title;
        //开关建筑
        if (data.active) {
            this.ShowOutlook(data.isMale, data.index, showEffect);
            //升级设备
            if (data.slots && data.slots.length > 0) {
                for (let i = 0; i < data.slots.length; i++) {
                    let s = data.slots[i];
                    s.isBreaking = false;
                    this.UpdateFacilityLevel(s.name + "_" + i.toString(), s.level, showEffect, s);
                }
            } else {
                console.warn("设备初始化失败,已关闭所有设备显示,没有插槽数据:", this.sprite3D.name);
                if (this._equipmentNode) {
                    this._equipmentNode.active = false;
                }
            }
        } else {
            this.ShowPlaceholder();
        }
    }


    /**
     * 更新设备的外观显示，使之与等级匹配
     * @param name 设备名字
     * @param level 设备的等级
     * @param showEffect 升级时是否显示特效
     * @param slotData 设备当前等级的插槽数据
     */
    UpdateFacilityLevel(name: string, level: number, showEffect: boolean, slotData: SlotData) {
        let fac = this._equipmentNode.getChildByName(name) as Laya.Sprite3D;
        if (fac) {
            fac.active = true;
            let next = fac.getChildByName(level.toString()) as Laya.Sprite3D;
            if (next) {
                this.ToggleAllChild(fac, false);
                if(slotData.uid== SlotUIDConstProxy.instance.data.slot_urinal_config){
                    let count = BuildDataProxy.instance.GetBuildDataByName(StringName.maleToilet).count +1;
                    //打开pee的隔板
                    let menban = fac.getChildByName("menban")
                    if(slotData.id < count*2){
                        next.active = true;
                        if (menban) {
                            menban.active = true;
                        }
                        if (showEffect) {
                            let id = next.id;
                            EffectManager.PlayEffect(EffectName.Yan, id, 2000, next.transform.position);
                        }
                    }
                    else{
                        console.log("关闭pee显示")
                    }
                }
                else {
                    next.active = true;
                    if (showEffect) {
                        let id = next.id;
                        EffectManager.PlayEffect(EffectName.Yan, id, 2000, next.transform.position);
                    }
                }
                if (slotData) {
                    this.AddSlotPositon(fac, name, slotData);
                }
                this.UpdateStatistic();
            } else {
                //0级默认关闭设备
                if (level != 0) {
                    console.warn("建筑", this.sprite3D.name, "的", name, "的等级", level, "寻找失败，无法改变设备外观模型,设备显示已经关闭.");
                } else {
                    this.ToggleAllChild(fac, false);
                }

            }
        } else {
            console.warn("试图在建筑", this.sprite3D.name, "中获取一个不存在的设备", name, ",请检查建筑设备节点equipmen下是否已正确放置此设备...")//,this._equipmentNode);
        }
    }

    /**
     * 添加可用的设备
     * @param sprite3d - 设备点
     * @param facName - 设备的名字
     * @param slotData - XXX:设备等级变化时这个可能不会更新
     */
    protected AddSlotPositon(sprite3d: Laya.Sprite3D, facName: string, slotData: SlotData) {
        let data = this._facMap.get(facName);
        if (data == null) {
            data = new FacilityPosition();
            data.slotData = slotData;
            data.sprite = sprite3d;
            data.belonger = this;
            this._facMap.set(facName, data);
            this.slotPosList.push(data);
        } else {
            console.log(facName);
        }
    }

    /**显示男女厕所外观,并关闭占位，显示设备节点 */
    protected ShowOutlook(ismale: boolean, index: number, showEffect: boolean = false) {
        console.error("ShowOutlook方法没有在子类中实现:", this.sprite3D.name);
    }
    //建造特效
    protected PlayBuildEffect() {
        EffectManager.PlayEffect(EffectName.Caidai, this.id, 2000, this.sprite3D.transform.position);
    }

    /**占位显示 */
    protected ShowPlaceholder() {
        let spr = this.sprite3D.getChildByName(StringName.placeholder);
        this.ToggleAllChild(this.sprite3D, false);
        if (spr) {
            spr.active = true;
        } else {
            console.warn("节点下没有占位节点：", this.sprite3D.name);
        }
    }

    /**关闭占位 */
    protected ClosePlaceholder() {
        let spr = this.sprite3D.getChildByName(StringName.placeholder);
        if (spr) {
            spr.active = false;
        } else {
            console.warn("节点下没有占位节点：", this.sprite3D.name);
        }
    }



    //#region 流程重构
    private _runtimeEntrance: Laya.Sprite3D;

    //入口
    public get entrance() {
        if (this._runtimeEntrance == null) {
            this._runtimeEntrance = this.sprite3D.getChildByName(StringName.entrance) as Laya.Sprite3D;
        }
        return this._runtimeEntrance;
    }

    private _runtimeExit: Laya.Sprite3D;

    public get exit() {
        if (this._runtimeExit == null) {
            this._runtimeExit = this.sprite3D.getChildByName(StringName.exit) as Laya.Sprite3D;
        }
        return this._runtimeExit;
    }

    //#endregion

    //#region 建筑统计
    //开发进度
    public developProgress: number = 0;
    //收入/分钟
    public income: number = 0;
    //使用时间
    public duration: number = 0;
    //耗费水
    public wasteWater: number = 0;
    //增加水，净水池
    public waterAdd: number = 0;
    public cleanTime: number = 32;
    public sweepTime: number = 32;
    //标准维修清洁时间
    protected _standardCleanTime = 25;
    //标准打扫时间
    protected _standardSweepTime = 27;
    //标准使用时间，在子类中重写
    protected _standardTime = 20;
    

    //更新统计信息
    protected UpdateStatistic() {
        let total = 0;
        let level = 0;
        let money = 0;
        let buffTime = 0;
        let _runtimeWasteWater = 0;
        //污水处理净化量
        let _runtimeWaterAdd = 0;
        let _runtimeRepairTime = 0;
        let _runtimeSweepTime = 0;
        for (let data of this.slots) {
            //等级信息
            let levelInfo = SlotConfigProxy.instance.GetSlotConfigByUID(data.uid)[data.level];
            //开发等级
            level += data.level
            let maxLevel = UISlotConfigProxy.instance.dataList[data.uid].maxLevel;
            total += maxLevel;
            if (data.level < 1) {
                //不统计0级的信息
                continue;
            }
            //每分钟收入
            let tempMoney = levelInfo.income;
            if (tempMoney) {
                money += tempMoney;
            }
            //时间buff
            let tempBuff = levelInfo.Buff;
            if (tempBuff) {
                buffTime += tempBuff;
            }
            //水的消耗
            let tempWater = levelInfo.updateCostWater;
            if (tempWater) {
                _runtimeWasteWater += tempWater;
            }
            //水的增加
            let tempWaterAdd = levelInfo.water_add;
            if (tempWaterAdd) {
                _runtimeWaterAdd += tempWaterAdd;
                if(this.functionID == FunctionID.WaterRoom){
                    MiscDataProxy.MaxWater = _runtimeWaterAdd;
                }
            }
            //维修时间
            let tempRepairBuff = levelInfo.repair_time;
            if (tempRepairBuff) {
                _runtimeRepairTime += tempRepairBuff;
            }
            //擦桌子时间
            let tempSweep = levelInfo.sweep_time;
            if (tempSweep) {
                _runtimeSweepTime += tempSweep;
            }
        }
        this.developProgress = level / total;
        this.income = money *3;
        this.duration = this._standardTime + buffTime;
        this.wasteWater = _runtimeWasteWater;
        this.waterAdd = _runtimeWaterAdd;
        MiscDataProxy.RefreshWater();
        this.cleanTime = this._standardCleanTime + _runtimeRepairTime;
        this.sweepTime = this._standardSweepTime + _runtimeSweepTime;
    }

    //#endregion

    //#region 收入计算
    /**
     * 产升收益
     * @param slot 设备信息
     * @constructor
     */
    CreateIncome(slot: SlotData) {
        let income = SlotConfigProxy.instance.GetSlotConfigByUID(slot.uid)[slot.level].income;
        if (income) {
            MiscDataProxy.Money += income * MiscDataProxy.AdIncomeBuff;
        }
        else {
            console.log(this.buildingName, "获取收入失败！");
        }
    }
    //#endregion

    OnClick(){

    }

    OffClick(){}
}