import BaseUIMediator from "../aTGame/UI/FGUI/BaseUIMediator";
import UIManagerBuildConfigProxy from "../Config/UIManagerBuildConfig";
import { BData, BuildDataProxy } from "../Data/BuildDataProxy";
import { FunctionID } from "../Data/FunctionID";
import { EUI } from "../dMyGame/Enum/EUI";
import { EEventUI } from "../dMyGame/EventEnum/EEventUI";
import MesManagerClass from "../dMyGame/Manager/MesManager";
import UIManagerProxy from "../dMyGame/Manager/Proxy/UIManagerProxy";
import { BuildingManager } from "../GameComponent/BuildingManager";
import Building from "../GameComponent/Buildings/api/Building";
import { NullReferenceException } from "../lty/Exception/NullReferenceException";
import { MoneyConverter } from "../zml/MoneyConverter";
import { UIFacade } from "./UIFacade";
import { FocusPositionProxy } from "../Config/FocusPositionProxy";
import { MiscDataProxy } from "../Data/MiscDataProxy";
import { CameraManager } from "../GameComponent/CameraManager";
import FGUI_Manager from "../FGUI/Main/FGUI_Manager";
import FGUI_Item_building from "../FGUI/Main/FGUI_Item_building";
import FGUI_Item_Statistics from "../FGUI/Main/FGUI_Item_Statistics";
import FGUI_ProgressBar1 from "../FGUI/Main/FGUI_ProgressBar1";
import { GuideDataProxy } from "../Data/GuideDataProxy";
import { CanteenDataProxy } from "../Data/CanteenDataProxy";
import { UIMediatorGameAd } from "./UIMediatorGameAd";

/**管理界面 */
export class UIMediatorManagement extends BaseUIMediator<FGUI_Manager> {
    private static m_instance: UIMediatorManagement;
    private isGuide: boolean;
    // private IsMgrPanel: boolean = false;
    // public get GetIsMgrPanel(): boolean {
    //     return this.IsMgrPanel
    // }
    /** 单例 */
    public static get instance(): UIMediatorManagement {
        if (!this.m_instance) {
            this.m_instance = new UIMediatorManagement();
            this.m_instance._classDefine = FGUI_Manager;
        }
        return this.m_instance;
    }

    private _buildItemList: Map<string, FGUI_Item_building>;
    private _statisticsItemList: Map<string, FGUI_Item_Statistics>;
    private _StatisticsArray: IStatisticsInfo[] = [];
    private _flowArray: number[] = new Array(7);
    public speed: number;

    private constructor() {
        super();
    }

    /**本地化保存的建造数据 */
    private get data() {
        return BuildDataProxy.instance.data.list;
    }


    /**数据的词典 */
    private get dict() {
        return BuildDataProxy.instance.dict;
    }

    /**建造的配置表 */
    private get config() {
        return UIManagerBuildConfigProxy.instance.dataList;
    }

    /**取得已建筑的信息 */
    private GetStatisticsInfo() {
        let map = BuildingManager.instance.buildingMap;
        this._StatisticsArray = [];
        map.forEach(ele => {
            if (ele.active) {
                let buildele = ele as Building;
                if (buildele.income > 0) {
                    let info: IStatisticsInfo = {
                        functionID: buildele.functionID,
                        title: buildele.title,
                        buildingName: buildele.buildingName,
                        income: buildele.income,
                        progress: buildele.developProgress
                    }
                    this._StatisticsArray.push(info);
                }
            }
        })
        return this._StatisticsArray;
    }

    private GetUrl(funcID: FunctionID) {
        switch (funcID) {
            case FunctionID.MaleToilet:
                return this.config[0].url;
            case FunctionID.FemaleToilte:
                return this.config[1].url;
            case FunctionID.FemaleSink:
                return this.config[3].url;
            case FunctionID.MaleSink:
                return this.config[4].url;
            case FunctionID.MalePee:
                return this.config[2].url;
            case FunctionID.Library:
                return this.config[5].url;
            case FunctionID.Canteen:
                return this.config[6].url;
            default:
                console.error("尚未为此功能建筑配置图标url", funcID);
                break;
        }
    }

    private GetGuestVolume() {
        /** now */
        this._flowArray[0] = BuildingManager.instance.flow;
        this._flowArray[1] = BuildingManager.instance.femaleQueueCount;
        this._flowArray[2] = BuildingManager.instance.peeQueueCount;
        this._flowArray[3] = BuildingManager.instance.maleQueueCount;
        //max flow
        this._flowArray[4] = BuildingManager.instance.MaxSizeFemale;
        this._flowArray[5] = BuildingManager.instance.MaxSizePee;
        this._flowArray[6] = BuildingManager.instance.MaxSizeMale;
    }

    _OnShow() {
        // this.IsMgrPanel = true;
        this.RenderBuildingList();
        this.ui.m_btn_close.onClick(this, this.Close);
        MesManagerClass.instance.onUI(EEventUI.BuildComplete, this, this.OnBuildComplete);
        this.RenderStatisticsItem();
        this.RenderTopInfo();
        Laya.timer.loop(1000, this, this.RenderTopInfo);
        this.TotalIncome();
        if (!GuideDataProxy.IsCompleteAllGuide)
            this.isGuide = true;
        else
            this.isGuide = false;
    }


    /**关闭自身的显示 */
    private Close() {
        this.ClosePanel();
        /**弹原生广告 */
        UIMediatorGameAd.instance.ShowNativeAd();

    }

    private ClosePanel() {
        UIFacade.instance.ResetCurrSelectedBuilding();
        Laya.timer.clearAll(this);
        UIManagerProxy.instance.setUIState([
            {
                typeIndex: EUI.Manager, state: false
            }
        ], false);
        // this.IsMgrPanel = false;
    }

    private RenderBuildingList() {
        this._buildItemList = new Map<string, FGUI_Item_building>();
        this.ui.m_Buildings.m_list.itemRenderer = Laya.Handler.create(this, (index: number, item: FGUI_Item_building) => {
            let data = this.data[index];
            if (data == null) {
                return;
            }
            //关闭便利店建造提示
            if (index >= 6 && !CanteenDataProxy.instance.data.list.active) {
                index += 1
            }
            let id = this.data[index].UID;
            item.m_txt_title.text = this.config[id].title;
            item.m_txt_infomation.text = this.config[id].describe;
            item.m_picture.url = this.config[id].url;
            let currData = this.data[index];
            this.UpdateBuildItem(currData, item);
            this._buildItemList.set(currData.name, item);
        }, null, false);
        this.ui.m_Buildings.m_list.numItems = CanteenDataProxy.instance.data.list.active ? this.data.length : this.data.length - 1;
    }

    /**刷新建筑列表的item */
    private UpdateBuildItem(currData: BData, item: FGUI_Item_building) {
        let maxCount = UIManagerBuildConfigProxy.instance.dataList[currData.UID].maxCount;
        if (currData.count >= maxCount) {
            //已经建成最大数量，显示完成UI
            item.m_btn_update.visible = false;
            item.m_costWater.visible = false;
            item.m_img_fins.visible = true;
            item.m_txt_progress.visible = false;
        } else {
            item.m_txt_progress.text = currData.count.toString() + "/" + maxCount.toString();
            item.m_img_fins.visible = false;
            let hasMoney = MiscDataProxy.Money >= currData.costMoney;
            item.m_btn_update.m_txt_cost.text = MoneyConverter.DoConverter(currData.costMoney);
            let max = MiscDataProxy.MaxWater;
            let water = MiscDataProxy.Water;
            let lastWater = max - water - currData.costWater;
            if (lastWater < 0) {
                item.m_txt_waterWarn.visible = true;
                item.m_txt_costWater.visible = false;
                item.m_spr_water.visible = false;
            } else {
                item.m_txt_waterWarn.visible = false;
                item.m_txt_costWater.visible = true;
                item.m_spr_water.visible = true;
                item.m_txt_costWater.text = currData.costWater.toString();
            }
            if (lastWater < 0 || !hasMoney) {
                item.m_btn_update.grayed = true;
                item.m_btn_update.touchable = false;
            } else {
                item.m_btn_update.grayed = false;
                item.m_btn_update.touchable = true;
                item.m_btn_update.onClick(this, this.BuildConstruction, [currData.name]);
            }
        }
    }

    /**建造操作 */
    private BuildConstruction(name: string) {
        MesManagerClass.instance.eventUI(EEventUI.BuildConstruction, [EEventUI.BuildConstruction, name]);
        //关闭面板 
        this.ClosePanel();
        //相机导航
        //   this.CameraNavigation(name);
        if (this.isGuide)
            MesManagerClass.instance.eventUI(EEventUI.GuideToNext);
    }

    private OnBuildComplete(name: string) {
        let item = this._buildItemList.get(name);
        if (item) {
            let data = this.dict.get(name);
            let item = this._buildItemList.get(name);
            this.UpdateBuildItem(data, item);
        } else {
            throw new NullReferenceException("刷新建造Item失败", name);
        }
        // this.Close();
    }

    /**统计 */
    private RenderStatisticsItem() {
        this.GetStatisticsInfo();
        if (this._StatisticsArray.length < 1)
            return;
        this._statisticsItemList = new Map<string, FGUI_Item_Statistics>();
        this.ui.m_Statistics.m_list.itemRenderer = Laya.Handler.create(this, (index: number, item: FGUI_Item_Statistics) => {
            let info = this._StatisticsArray[index];
            item.m_txt_title.text = info.title;
            item.m_txt_progress.text = Math.floor(info.progress * 100).toString() + "%";
            item.m_pro_blue.fillAmount = info.progress;
            item.m_txt_incomeRate.text = MoneyConverter.DoConverter(info.income) + "/分钟";
            item.m_itemIcon.url = this.GetUrl(info.functionID);
            item.m_btn_go.onClick(this, this.OnClick, [info.buildingName]);
        }, null, false);
        this.ui.m_Statistics.m_list.numItems = this._StatisticsArray.length;
    }

    private RenderTopInfo() {
        this.GetGuestVolume();
        this.ui.m_Statistics.m_text_flow.text = this._flowArray[0].toString();
        this.ui.m_Statistics.m_pro_2.value = this._flowArray[1] / this._flowArray[4] * 100;
        this.ChangeColor(this.ui.m_Statistics.m_pro_2);
        this.ui.m_Statistics.m_pro_3.value = this._flowArray[2] / this._flowArray[5] * 100;
        this.ChangeColor(this.ui.m_Statistics.m_pro_3);
        this.ui.m_Statistics.m_pro_4.value = this._flowArray[3] / this._flowArray[6] * 100;
        this.ChangeColor(this.ui.m_Statistics.m_pro_4);
        this.ui.m_Statistics.m_text_women.text = this._flowArray[1].toString() + "/ " + this._flowArray[4].toString();
        this.ui.m_Statistics.m_text_pee.text = this._flowArray[2].toString() + "/ " + this._flowArray[5].toString();
        this.ui.m_Statistics.m_text_men.text = this._flowArray[3].toString() + "/ " + this._flowArray[6].toString();
    }

    private ChangeColor(pro: FGUI_ProgressBar1) {
        if (pro.value > 75 && pro.value < 100)
            pro.m_c1.selectedIndex = 1;
        else if (pro.value >= 100)
            pro.m_c1.selectedIndex = 2;
        else
            pro.m_c1.selectedIndex = 0;
    }

    /**计算总收入 */
    private TotalIncome() {
        if (this._StatisticsArray.length > 0) {
            let totalIncome = 0;
            this._StatisticsArray.forEach(ele => {
                totalIncome += ele.income;
            })
            this.ui.m_Statistics.m_text_totalIncome.text = MoneyConverter.DoConverter(totalIncome) + "/分钟"
        } else
            this.ui.m_Statistics.m_text_totalIncome.text = "0/分钟"
    }

    _OnHide() {
        MesManagerClass.instance.offUI(EEventUI.BuildComplete, this, this.OnBuildComplete);

    }

    private OnClick(name: string) {
        this.CameraNavigation(name);
        this.ClosePanel();
    }

    /**相机导航 */
    private CameraNavigation(name: string) {
        let list = FocusPositionProxy.instance.dataList;
        let index = -1;
        console.log(name);
        for (let date of list) {
            if (date.name == name) {
                console.log(date.name);
                index = date.id;
                break;
            }
        }
        if (index == -1) {
            console.log("获取失败");
            return;
        }
        CameraManager.instance.FocusPositionByIndex(index)
    }

}

interface IStatisticsInfo {
    title: string,
    progress: number;
    income: number;
    buildingName: string;
    functionID: FunctionID;
}