export class MoneyConverter {
    private static moneyStr: string = "";
    private static unit: string[] = ["k", "M", "B", "T"]
    private constructor() {
    }

    /**金币换算 */
    public static DoConverter(num: number) {
        num = Math.floor(num);
        //number转string 
        this.moneyStr = num.toString().trim();
        // console.log(this.moneyStr);
        //3-6位 加逗号 小于3位直接返回
        if (this.moneyStr.length > 3 && this.moneyStr.length < 7) {
            return this.moneyStr.slice(0, this.moneyStr.length - 3) + "," + this.moneyStr.slice(this.moneyStr.length - 3);
        }
        //大于6位 单位换算
        if (this.moneyStr.length > 6) {
            //得到单位
            let temp: number = Math.floor((this.moneyStr.length - 6) / 3);
            //超过单位索引
            if (temp > 3) {
                return "NAN"
            }
            //根据单位来丢弃后几位
            let str1: string = this.moneyStr.substring(0, (this.moneyStr.length - (3 * (temp + 1))));
            //判断 是否满足四舍五入 取丢弃的第一位
            let str2: string = this.moneyStr[this.moneyStr.length - (3 * (temp + 1))]
            if (parseInt(str2) >= 5) {
                str1 = (parseInt(str1) + 1).toString();
                //四舍五入后再次判定  避免出现 99，999,999 >=100，000k
                if (str1.length > 5) {
                    return this.DoConverter((parseInt(str1) + 1) * 1000 * (temp + 1));
                }
            }
            //截取后 长度大于3 加逗号
            if (str1.length > 3) {
                return str1.slice(0, str1.length - 3) + "," + str1.slice(str1.length - 3).concat(this.unit[temp]);
            }
            return str1.concat(this.unit[temp]);
        }
        return this.moneyStr;
    }

    /**任务金币单位换算 */
    public static TaskDoConverter(num: number) {
        num = Math.floor(num);
        //number转string 
        this.moneyStr = num.toString().trim();

        if (this.moneyStr.length > 3) {
            let temp: number = Math.floor((this.moneyStr.length - 4) / 3);
            //超过单位索引
            if (temp > 3) {
                return "NAN"
            }
            //根据单位来丢弃后几位
            let str1: string = this.moneyStr.substring(0, (this.moneyStr.length - (3 * (temp + 1))));
            //判断 是否满足四舍五入 取丢弃的第一位
            let str2: string = this.moneyStr[this.moneyStr.length - (3 * (temp + 1))]
            if (parseInt(str2) >= 5) {
                str1 = (parseInt(str1) + 1).toString();
                //四舍五入后再次判定  避免出现 99，999,999 >=100，000k
                if (str1.length > 3) {
                    return this.TaskDoConverter((parseInt(str1) + 1) * 1000 * (temp + 1));
                }
            }
            return str1.concat(this.unit[temp]);
        }
        return this.moneyStr;
    }
}