import BaseUIMediator from "../aTGame/UI/FGUI/BaseUIMediator";
import FGUI_MoneyEffect from "../FGUI/EffectLayer/FGUI_MoneyEffect";
import UIManagerProxy from "../dMyGame/Manager/Proxy/UIManagerProxy";
import { EUI } from "../dMyGame/Enum/EUI";
import { EUILayer } from "../aTGame/UI/FGUI/EUILayer";

export class UIMediatorEffectPanel extends BaseUIMediator<FGUI_MoneyEffect>{
    private static m_instance: UIMediatorEffectPanel;
    /** 单例 */
    public static get instance(): UIMediatorEffectPanel {
        if (!this.m_instance) {
            this.m_instance = new UIMediatorEffectPanel();
            this.m_instance._classDefine = FGUI_MoneyEffect;
        }
        return this.m_instance;
    }
    private constructor() {
        super();
    }

    protected _layer = EUILayer.Top;


    protected _OnShow() {
        super._OnShow();
        this.ui.m_momeyEffect.play();
        Laya.timer.once(2200, this, this.ClosePanel);

    }

    public ClosePanel() {
        UIManagerProxy.instance.setUIState([{ typeIndex: EUI.UIMoneyEffect, state: false }], false);
    }

}