    /**
   * 等待一定帧数后回调
   * @param frameCount 
   */
    public static WaitFrame(frameCount: number): Promise<void> {
        return new Promise<void>(r => {
            Laya.timer.frameOnce(frameCount, null, r);
        })
    }

    /**
   * 等待一段时间
   * @param duration 毫秒
   */
    public static WaitTime(duration: number): Promise<void> {
        return new Promise<void>(r => {
            if (duration <= 0) {
                r();
            } else {
                setTimeout(r, duration);
            }
        })
    }

    public static WaitWithBlock(checkFunc: () => boolean): Promise<void> {
        return new Promise<void>(r => {
            var time = 0;
            var frameLoop = () => {
                time += Laya.timer.delta;
                if (checkFunc()) {
                    Laya.timer.clear(null, frameLoop);
                    r();
                }
            }
            Laya.timer.frameLoop(1, null, frameLoop);
        })
    }

    public static WaitTimeWithBlock(duration: number, checkFuc: () => boolean): Promise<void> {
        return new Promise<void>(r => {
            var time = 0;
            var frameLoop = () => {
                var d = Math.min(100, Laya.timer.delta);
                time += d;
                if (time >= duration || checkFuc()) {
                    Laya.timer.clear(null, frameLoop);
                    r();
                }
            }
            Laya.timer.frameLoop(1, null, frameLoop);
        })
    }

    public static WaitTimeWithDuartionCall(totalDuration: number, callDuration: number, cb: () => boolean): Promise<void> {
        return new Promise<void>(r => {
            var time = 0;
            var cTime = 0;
            cb();
            var frameLoop = () => {
                var d = Math.min(100, Laya.timer.delta);
                time += d;
                cTime += d;
                if (cTime >= callDuration) {
                    cTime -= callDuration;
                    var cr = cb();
                    if (cr == false) {
                        Laya.timer.clear(null, frameLoop);
                        r();
                    }
                }
                if (totalDuration > 0 && time >= totalDuration) {
                    Laya.timer.clear(null, frameLoop);
                    r();
                }
            }
            Laya.timer.frameLoop(1, null, frameLoop);
        })
    }

