    public static PromiseQueue(arg: any, ...promises: ((arg: any) => PromiseLike<any>)[]): Promise<void> {
        var p = Promise.resolve<any>(arg);
        if (promises) {
            promises.forEach(promise => {
                p = p.then(promise)
            })
        }
        return p;
    }

    public static SaveImgToTempPath(canvas: Laya.HTMLCanvas, imgSrc: string = null): Promise<string> {
        return new Promise<string>(r => {
            var inst = window['wx'] as any;
            if (inst == null) {
                r(null);
                return;
            }
            imgSrc = imgSrc == null ? canvas.toBase64('image/png', 1) : imgSrc;
            var startIdx = imgSrc.indexOf('base64,') + 7;
            var save = inst.getFileSystemManager();
            var number = MiscUtils.CurrentTime;
            var filePath = inst.env.USER_DATA_PATH + '/pic' + number + '.png';
            save.writeFile(
                {
                    filePath: filePath,
                    data: imgSrc.slice(startIdx),
                    encoding: 'base64',
                    success: res => {
                        r(filePath);
                    },
                    fail: err => {
                        console.log(err)
                        r(null);
                    }
                }
            )
        })
    }

