    /**点到直线距离 */
    public static DistancePointWithLine(pos: Laya.Vector3, linePos: Laya.Vector3, lineDirection: Laya.Vector3): number {
        return Math.abs(this.SignDistancePointWithLine(pos, linePos, lineDirection));
    }

    /**点到线段距离 */
    public static PointToSegmentDist(point: Laya.Vector2, segPont1: Laya.Vector2, segPont2: Laya.Vector2) {
        var cross = (segPont2.x - segPont1.x) * (point.x - segPont1.x) + (segPont2.y - segPont1.y) * (point.y - segPont1.y);
        if (cross <= 0) return Math.sqrt((point.x - segPont1.x) * (point.x - segPont1.x) + (point.y - segPont1.y) * (point.y - segPont1.y));

        var d2 = (segPont2.x - segPont1.x) * (segPont2.x - segPont1.x) + (segPont2.y - segPont1.y) * (segPont2.y - segPont1.y);
        if (cross >= d2) return Math.sqrt((point.x - segPont2.x) * (point.x - segPont2.x) + (point.y - segPont2.y) * (point.y - segPont2.y));

        var r = cross / d2;
        var px = segPont1.x + (segPont2.x - segPont1.x) * r;
        var py = segPont1.y + (segPont2.y - segPont1.y) * r;
        return Math.sqrt((point.x - px) * (point.x - px) + (py - point.y) * (py - point.y));
    }

    public static PointToSegmentDist2(x: number, y: number, x1: number, y1: number, x2: number, y2: number) {
        var cross = (x2 - x1) * (x - x1) + (y2 - y1) * (y - y1);
        if (cross <= 0) return Math.sqrt((x - x1) * (x - x1) + (y - y1) * (y - y1));

        var d2 = (x2 - x1) * (x2 - x1) + (y2 - y1) * (y2 - y1);
        if (cross >= d2) return Math.sqrt((x - x2) * (x - x2) + (y - y2) * (y - y2));

        var r = cross / d2;
        var px = x1 + (x2 - x1) * r;
        var py = y1 + (y2 - y1) * r;
        return Math.sqrt((x - px) * (x - px) + (py - y) * (py - y));
    }

    public static SignDistancePointWithLine(pos: Laya.Vector3, linePos: Laya.Vector3, lineDirection: Laya.Vector3): number {
        var proVec = new Laya.Vector3();
        Laya.Vector3.subtract(pos, linePos, proVec);
        var left = new Laya.Vector3(-lineDirection.z, 0, lineDirection.x);
        Laya.Vector3.cross(new Laya.Vector3(0, 0, 1), lineDirection, left);
        Laya.Vector3.normalize(left, left);
        return Laya.Vector3.dot(proVec, left);
    }

    /**直线线段是否相交 */
    public static CheckSegmentLineIntersect(segPos1: Laya.Vector3, segPos2: Laya.Vector3, linePos: Laya.Vector3, lineDirection: Laya.Vector3): boolean {
        var d1 = this.SignDistancePointWithLine(segPos1, linePos, lineDirection);
        var d2 = this.SignDistancePointWithLine(segPos2, linePos, lineDirection);
        if (Laya.MathUtils3D.isZero(d1) || Laya.MathUtils3D.isZero(d2)) { return true; }
        return Math.sign(d1) != Math.sign(d2);
    }

    /**直线与直线交点 
     * 只计算相交,保证传入的是相交的直线和线段
    */
    public static SegmentLineIntersectPos(p0: Laya.Vector3, d0: Laya.Vector3, p1: Laya.Vector3, d1: Laya.Vector3): Laya.Vector3 {
        var d11 = this.DistancePointWithLine(p0, p1, d1);
        var p11 = new Laya.Vector3();
        Laya.Vector3.add(p0, d0, p11);
        var d22 = this.DistancePointWithLine(p11, p1, d1);
        var k = d11 / (d11 + d22);
        var r = new Laya.Vector3();
        Laya.Vector3.scale(d0, k, r);
        Laya.Vector3.add(p0, r, r);
        return r;
    }

    public static LineIntersectPos(p0: Laya.Vector3, d0: Laya.Vector3, p1: Laya.Vector3, d1: Laya.Vector3): Laya.Vector3 {
        var e = new Laya.Vector3();
        Laya.Vector3.subtract(p1, p0, e);
        var kross = d0.x * d1.y - d0.y * d1.x;
        // var sqrLen0 = d0.x * d0.x + d0.y * d0.y;
        // var sqrLen1 = d1.x * d1.x + d1.y * d1.y;
        var s = (e.x * d1.y - e.y * d1.x) / kross;
        Laya.Vector3.scale(d0, s, d0);
        Laya.Vector3.add(p0, d0, e);
        return e;
    }

    /**0-360转圈圈匀速lerp */
    public static AngleLerpWithSpeed(src: number, dis: number, speed: number, t: number) {
        var delta = dis - src;
        if (delta < -180) {
            delta += 360;
        } else if (delta > 180) {
            delta -= 360;
        }
        var _speed = speed;
        if (delta < 0) {
            _speed = -speed;
        }
        src = src + _speed * t;
        if (src > 360) {
            src -= 360;
        }
        if (src < 0) {
            src += 360;
        }
        return src;
    }

    /**0-360转圈圈lerp */
    public static AngleLerp(src: number, dis: number, t: number) {
        var delta = dis - src;
        if (delta < -180) {
            delta += 360;
        } else if (delta > 180) {
            delta -= 360;
        }

        src = src + delta * t;
        if (src > 360) {
            src -= 360;
        }
        if (src < 0) {
            src += 360;
        }
        return src;
    }

    public static GetAllSKinMeshRender(node: Laya.Node, mr: Laya.SkinnedMeshRenderer[]) {
        if (node instanceof Laya.SkinnedMeshSprite3D) {
            var m = node.skinnedMeshRenderer;
            mr.push(m);
        }
        for (let i = 0; i < node.numChildren; i++) {
            var child = node.getChildAt(i);
            this.GetAllSKinMeshRender(child, mr);
        }
    }

    public static GetAllMeshRenderer(node: Laya.Node, mr: Laya.MeshRenderer[]) {
        if (node instanceof Laya.MeshSprite3D) {
            var m = node.meshRenderer;
            mr.push(m);
        }
        for (let i = 0; i < node.numChildren; i++) {
            var child = node.getChildAt(i);
            this.GetAllMeshRenderer(child, mr);
        }
    }

    public static SetAllMeshRendererMat(node: Laya.Node, mat: Laya.Material) {
        if (node instanceof Laya.MeshSprite3D) {
            var m = node.meshRenderer;
            m.sharedMaterial = mat;
        }
        for (let i = 0; i < node.numChildren; i++) {
            var child = node.getChildAt(i);
            this.SetAllMeshRendererMat(child, mat);
        }
    }

    public static GetChildNode(node: Laya.Node, name: string): Laya.Sprite3D {
        if (node.name == name) {
            return node as Laya.Sprite3D;
        } else {
            if (node.numChildren > 0) {
                for (let i = 0; i < node.numChildren; i++) {
                    var n = this.GetChildNode(node.getChildAt(i), name);
                    if (n != null) {
                        return n;
                    }
                }
            } else {
                return null;
            }
        }
    }

    public static SetPhysicGroup(node: Laya.Node, group: number) {
        var pc = node.getComponent(Laya.PhysicsCollider) as Laya.PhysicsCollider;
        if (pc != null) {
            pc.collisionGroup = group;
        }
        var r = node.getComponent(Laya.Rigidbody3D) as Laya.Rigidbody3D;
        if (r != null) {
            r.collisionGroup = group;
        }
        for (var i = 0; i < node.numChildren; i++) {
            this.SetPhysicGroup(node.getChildAt(i), group);
        }
    }

    public static ParseVector3(str: string): Laya.Vector3 {
        var strs = str.split(',');
        return new Laya.Vector3(Number(strs[0]), Number(strs[1]), Number(strs[2]));
    }
