import AudioProxyClass from "../dMyGame/Proxy/AudioProxy";
import { EnumEBGMs } from "../dMyGame/ResList/EBGMs";
import { UIMediatorSetting } from "../UI/UIMediatorSetting";

export class Audio {
    private static bgm1 = "res/bgm/bgm1.mp3"
    private static bgm2 = "res/bgm/bgm2.mp3"
    private static currBgm: string = null;

    static PlayAudio() {
        if (this.currBgm == null) {
            this.currBgm = this.bgm1;
        } else {
            if (this.currBgm == this.bgm1) {
                this.currBgm = this.bgm2;
                if (UIMediatorSetting.instance._musicToggle) {
                    AudioProxyClass.instance.playBGM(EnumEBGMs.bgm2, 10);
                }

            }
            else if (this.currBgm == this.bgm2) {
                this.currBgm = this.bgm1;
                if (UIMediatorSetting.instance._musicToggle) {
                    AudioProxyClass.instance.playBGM(EnumEBGMs.bgm1, 10);
                }

            }
            return;
        }
        if (UIMediatorSetting.instance._musicToggle) {
            AudioProxyClass.instance.playBGM(EnumEBGMs.bgm1, 10);
        }
    }
}