import {LayaBehaviour} from "../lty/class/LayaBehaviour";
import AllGlobal3DFFFFF from "../aTGame/3D/Global3D";
import CameraConstProxy from "../dMyGame/ConfigProxy/CameraConstProxy";
import {Mathf} from "../lty/class/Mathf";
import {FocusPositionProxy} from "../Config/FocusPositionProxy";
import {UIMediatorEventPanel} from "../UI/UIMediatorEventPanel";

export class CameraManager extends LayaBehaviour {
    private static _instance: CameraManager;
    public static get instance() {
        return this._instance;
    }

    private parent: Laya.Transform3D;
    private _rigidBody: Laya.Rigidbody3D;
    private _maxForce = 5;
    private _minForce = 1;
    private _damp = 0;
    private _appleForce = new Laya.Vector3();
    private _maxSize = 0;
    private _minSize = 1;
    private _scaleSpeed = 1;
    //初始相对位置
    private localPosition: Laya.Vector3 = new Laya.Vector3(0, -10, 0);
    private oneTouch = false;
    private twoTouch = false;
    private hasTwoTouch = false;
    private posOne = new Laya.Vector2();
    private posTwo = new Laya.Vector2();
    //开始滑动
    private _onDrag = true;
    //开始缩放
    private _onScale = true;

    public get Input() {
        return this._scene.input;
    }

    private get _unTouch() {
        return !this.oneTouch && !this.twoTouch;
    }

    private get isOneTouch() {
        return this.oneTouch && !this.twoTouch;
    }

    private get isTwoTouch() {
        return this.oneTouch && this.twoTouch && this.hasTwoTouch;
    }

    private get _scene(): Laya.Scene3D {
        return AllGlobal3DFFFFF.s3d;
    }

    onAwake() {
        CameraManager._instance = this;
    }


    public Init(limit: Laya.Sprite3D) {
        let cube = limit.getChildByName("Cube") as Laya.Sprite3D;
        this.parent = cube.transform;
        this._damp = CameraConstProxy.instance.data.damp;
        this._maxForce = CameraConstProxy.instance.data.maxForce;
        this._minForce = CameraConstProxy.instance.data.minForce;
        this._rigidBody = cube.getComponent(Laya.Rigidbody3D);
        this._rigidBody.linearDamping = CameraConstProxy.instance.data.damp;
        this._rigidBody.linearFactor = new Laya.Vector3(1, 0, 1);
        this._rigidBody.angularFactor = new Laya.Vector3(0, 0, 0);
        cube.addChild(this.owner);
        this.transform.localPosition = this.localPosition;
        AllGlobal3DFFFFF.camera.transform.rotationEuler = new Laya.Vector3(-45, 0, 0);
        this._maxSize = CameraConstProxy.instance.data.orthographic_MaxSize;
        this._minSize = CameraConstProxy.instance.data.orthographic_MinSize;
        this._scaleSpeed = CameraConstProxy.instance.data.scaleSpeed;
    }

    //移动相机
    MoveByForce(x: number, y: number) {
        if ((x ^ x + y * y) < 0.3) {
            //开启摩擦
            if (this._rigidBody.linearDamping <= 0) {
                this._rigidBody.linearDamping = this._damp;
            }
        } else {
            //关闭摩擦
            if (this._rigidBody.linearDamping > 0) {
                this._rigidBody.linearDamping = 0;
            }
        }
        //根据相机size缩放力
        let t = (AllGlobal3DFFFFF.camera.orthographicVerticalSize - this._minSize) / (this._maxSize - this._minSize);
        let scale = Mathf.Lerp(this._minForce, this._maxForce, t);
        this._appleForce.x = x * scale;
        this._appleForce.z = y * scale;
        this._rigidBody.wakeUp()
        // this._rigidBody.applyForce(this._appleForce);
        this._rigidBody.linearVelocity = this._appleForce;
    }

    //缩放相机
    Scale(delta: number) {
        delta *= this._scaleSpeed;
        let size = AllGlobal3DFFFFF.camera.orthographicVerticalSize + delta;
        size = Mathf.Clamp(this._minSize, this._maxSize, size);
        AllGlobal3DFFFFF.camera.orthographicVerticalSize = size;
        UIMediatorEventPanel.scale = 0.8 * (this._maxSize / size) + 0.2;

    }

    onUpdate() {
        this.TouchCountCheck();
        this.Run();

    }

    onLateUpdate() {
    }

    TouchCountCheck() {
        let count = this._scene.input.touchCount()
        if (count == 0) {
            this.oneTouch = false;
            this.twoTouch = false;
            this.hasTwoTouch = false;
            this._onDrag = true;
            this._onScale = true;
        } else if (count == 1) {
            this.oneTouch = true;
            this.hasTwoTouch = false;
            this._onScale = true;
        } else if (count == 2) {
            this.oneTouch = true;
            this.twoTouch = true;
            this.hasTwoTouch = true;
        }
    }

    private Run() {
        if (this.isTwoTouch) {
            //缩放
            if (this._onScale) {
                let pos = this.Input.getTouch(0).position;
                this.SetPos(this.posOne, pos)
                let pos1 = this.Input.getTouch(1).position;
                this.SetPos(this.posTwo, pos1);
                this._onScale = false;
            } else {
                let pos0 = this.Input.getTouch(0).position;
                let pos1 = this.Input.getTouch(1).position;
                let d1 = this.GetDistance(pos0, pos1);
                let d2 = this.GetDistance(this.posOne, this.posTwo);
                this.SetPos(this.posOne, pos0);
                this.SetPos(this.posTwo, pos1);
                this.Scale(d2 - d1);
            }

        } else if (this.isOneTouch) {
            //滑动
            if (this._onDrag) {
                let pos = this.Input.getTouch(0).position;
                this.SetPos(this.posOne, pos)
                this._onDrag = false;
            } else {
                let pos = this.Input.getTouch(0).position;
                let dx = pos.x - this.posOne.x;
                let dy = pos.y - this.posOne.y;
                this.SetPos(this.posOne, pos)
                this.MoveByForce(-dx, -dy);
            }
        }

        if (this._unTouch || (this.oneTouch && this.twoTouch)) {
            //开启摩擦
            if (this._rigidBody.linearDamping <= 0) {
                this._rigidBody.linearDamping = this._damp;
            }
        }
    }

    private SetPos(pos: Laya.Vector2, target: Laya.Vector2) {
        pos.x = target.x;
        pos.y = target.y;
    }

    private GetDistance(a: Laya.Vector2, b: Laya.Vector2) {
        let x = a.x - b.x;
        let y = a.y - b.y;
        return Math.sqrt(x * x + y * y);
    }

    //相机与地面的距离(11.24,26.66,54.94);
    private offSet = new Laya.Vector3(0, 26.66, 50);
    private _targetPos = new Laya.Vector3();

    public FocusPositionByIndex(index: number,scale = true) {
        let data = FocusPositionProxy.instance.dataList[index]
        this.FocusPosition(data,scale);
        return data;
    }

    public FocusBuild(index: number){
        let data = FocusPositionProxy.instance.dataList[index]
        this.FocusPosition({x:data.x,y:data.y,z:data.z +1.5 },false);
    }

    public FocusPosition(position: { x: number, y: number, z: number },scale = true,onComplete:Laya.Handler = null) {
        let target = new Laya.Vector3(-position.x, position.y, position.z);
        let targetPos = this.parent.position;
        Laya.Vector3.add(target, this.offSet, target);
        if(scale) {
            AllGlobal3DFFFFF.camera.orthographicVerticalSize = CameraConstProxy.instance.data.orthographic_MinSize;
            UIMediatorEventPanel.scale = 0.8 * (this._maxSize / AllGlobal3DFFFFF.camera.orthographicVerticalSize) + 0.2;
        }
        Laya.Tween.to(targetPos, {
            x: target.x, z: target.z, update: new Laya.Handler(this, (temp) => {
                this.parent.position = temp;
            }, [targetPos])
        }, 200, Laya.Ease.sineOut,onComplete)
        // console.log(position,this.offSet,targetPos);
    }

    private ComOffSet() {
        let point = new Laya.Vector2();
        let ray = new Laya.Ray(new Laya.Vector3(), new Laya.Vector3());
        AllGlobal3DFFFFF.camera.viewportPointToRay(point, ray);
        let hit = new Laya.HitResult();
        AllGlobal3DFFFFF.s3d.physicsSimulation.rayCast(ray, hit, 1000, 2);
        Laya.Vector3.subtract(this.transform.position, hit.point, this.offSet);
        // console.log("相机与地面的距离", this.offSet);
    }

    private _lookPos = new Laya.Vector3();

    LookAt(position: laya.d3.math.Vector3) {
        this._lookPos.setValue(position.x,0,position.z);
        Laya.Vector3.add(this._lookPos, this.offSet, this._lookPos);
        this.parent.position.x = this._lookPos.x
        this.parent.position.z = this._lookPos.z
        this.parent.position = this.parent.position;
        // console.log(this.parent.position);
    }


}