import BaseUIMediator from "../aTGame/UI/FGUI/BaseUIMediator";
import { EUI } from "../dMyGame/Enum/EUI";
import UIManagerProxy from "../dMyGame/Manager/Proxy/UIManagerProxy";
import FGUI_Main from "../FGUI/Main/FGUI_Main";
import { UIFacade } from "./UIFacade";
import { Random } from "../lty/class/Random";
import MesManagerClass from "../dMyGame/Manager/MesManager";
import { EEventUI } from "../dMyGame/EventEnum/EEventUI";
import { MiscDataProxy } from "../Data/MiscDataProxy";
import { GuestPool } from "../GameComponent/Guest/GuestPool";
import { TaskManager } from "../GameComponent/TaskManager";
import { GuideDataProxy } from "../Data/GuideDataProxy";
import { Time } from "../lty/class/Time";
import { ClockEvent } from "../lty/plugin/Clock/ClockEvent";
import { UIMediatorRecordShare } from "./UIMediatorRecordShare";
import PlatformManagerProxy from "../aTGame/Platform/PlatformManagerProxy";
import { EPlatformType } from "../aTGame/Platform/T/EPlatformType";
import { UIMediatorTalkEventPanel } from "./UIMediatorTalkEventPanel";
import { EUILayer } from "../aTGame/UI/FGUI/EUILayer";
import { UIMediatorGameAd } from "./UIMediatorGameAd";
import { UIMediatorManagement } from "./UIMediatorManager";
import { UIMediatorLuckyWheel } from "./UIMediatorLuckyWheel";
import { UIMediatorTask } from "./UIMediatorTask";
import { UIMediatorAD } from "./UIMediatorAD";
import { UIMediatorSetting } from "./UIMediatorSetting";
import { UIMediatorInfluencer } from "./AdUtility/UIMediatorInfluencer";
import { UIMediatorRepairGame } from "./AdUtility/UIMediatorRepairGame";
import { UIMediatorSpeedUp } from "./AdUtility/UIMediatorSpeedUp";
import { UIMediatorCheers } from "./AdUtility/UIMediatorCheers";


//主界面两侧的按钮
export class UIMediatorMain extends BaseUIMediator<FGUI_Main> {
    private static m_instance: UIMediatorMain;
    protected _layer = EUILayer.Main;
    private _adUtilityList = [];
    private isGuide: boolean;
    private CanShowNative: boolean;


    /** 单例 */
    public static get instance(): UIMediatorMain {
        if (!this.m_instance) {
            this.m_instance = new UIMediatorMain();
            this.m_instance._classDefine = FGUI_Main;
        }
        return this.m_instance;
    }

    private constructor() {
        super();
        if (!GuideDataProxy.IsCompleteAllGuide) this.isGuide = true;
        else this.isGuide = false;
    }
    _OnShow() {

        Laya.timer.loop(35000, this, this.RandomAdBtn);

        // Laya.timer.once(3000, this, () => {
        //     this.AddAdUtility(0);
        //     this.AddAdUtility(1);
        //     this.AddAdUtility(2);
        //     this.AddAdUtility(3);
        // }, null, false);


        this.ShowPanel(EUI.EventPanel, true, false);
        this.ui.m_btn_statistics.onClick(this, this.ShowManager);
        //看广告
        this.ui.m_btn_video.onClick(this, this.ADClick);
        // //抽奖
        this.ui.m_turntableBtn.onClick(this, () =>
            this.ShowPanel(EUI.LuckyWheel, true, false)
        );
        // //离线
        // this.ui.m_offlineBtn.onClick(this, () =>
        //     this.ShowPanel(EUI.Offline, true, false)
        // );
        // //去除广告
        // this.ui.m_removeAdBtn.onClick(this, () =>
        //     this.ShowPanel(EUI.RemoveAD, true, false)
        // );
        // //超级加倍
        // this.ui.m_supperDoubleBtn.onClick(this, () =>
        //     this.ShowPanel(EUI.SuperDouble, true, false)
        // );
        //游戏设置
        this.ui.m_setBtn.onClick(this, () =>
            this.ShowPanel(EUI.Setting, true, false)
        );
        //游戏任务
        this.ui.m_taskBtn.onClick(this, this.TaskOnClick);
        //游戏雇员
        this.ui.m_staffBtn.onClick(this, () => UIFacade.instance.ShowCleanRoom());
        this.ui.m_btn_speedUp.onClick(this, () => {
            this.ShowUtilityPanel(EUI.UIMediatorSpeedUp);
        });
        this.ui.m_btn_netRed.onClick(this, () => {
            this.ShowUtilityPanel(EUI.UIMediatorInfluencer);
        });
        this.ui.m_btn_cheers.onClick(this, () => {
            this.ShowUtilityPanel(EUI.UIMediatorCheers);
        });
        this.ui.m_btn_repair.onClick(this, () => {
            this.ShowUtilityPanel(EUI.UIMeditorRepairGame);
        });

        this.RenderRightUtility();
        this.HideTaskBtn();
        MesManagerClass.instance.onUI(
            EEventUI.UpdateTaskProgress,
            this,
            this.UpdateTaskProgress
        );
        MesManagerClass.instance.onUI(
            EEventUI.AddAdUtilityNetRed,
            this,
            this.OnAddNetRed
        );

        MesManagerClass.instance.on(ClockEvent.UpdateOnLineTime, this, this.UpdateDoubleTimeLast);
        this.ShowRecordBtn(true);
        this.ui.m_btn_record.onClick(this, this.ShowRecordVideoPanel);
        MesManagerClass.instance.onUI(EEventUI.OnStartRecord, this, this.ShowRecordStop);
        MesManagerClass.instance.onUI(EEventUI.OnStopRecord, this, this.ShowRecordIdle);


    }

    private TaskOnClick() {
        // //双重判定 减少数据查询
        // if (this.isGuide) {
        //     if (!GuideDataProxy.IsCompleteAllGuide) {
        //         MesManager.instance.eventUI(EEventUI.GuideToEnd);
        //         this.isGuide = false;
        //     }
        // }
        this.ShowPanel(EUI.Task, true, false);
    }

    protected _OnHide() {
        MesManagerClass.instance.off(
            EEventUI.UpdateTaskProgress,
            this,
            this.UpdateTaskProgress
        );
        MesManagerClass.instance.off(
            EEventUI.AddAdUtilityNetRed,
            this,
            this.OnAddNetRed
        );
        Laya.timer.clearAll(this);
        // this.CanShowNative = false;
    }

    private ShowPanel(uiStates: EUI, ifUnify: boolean, unifyState: boolean) {
        UIManagerProxy.instance.setUIState(
            [
                {
                    typeIndex: uiStates,
                    state: ifUnify,
                },
            ],
            unifyState
        );
    }

    private ADClick() {
        this.ShowPanel(EUI.AD, true, false);
        if (this.isGuide) {
            if (!GuideDataProxy.IsCompleteAllGuide) {
                MesManagerClass.instance.eventUI(EEventUI.GuideToNext);
                this.isGuide = false;
            }
        }
    }

    /**
     * 显示雇员的闲置和最大数量
     * @param unused 闲置数量
     * @param max 最大数量
     * @constructor
     * @private
     */
    public UpdateStaffNumText(unused: number, max: number) {
        if (this.isShow) {
            this.ui.m_staffBtn.m_txt_num.text = unused + "/" + max;
        }
    }

    private ShowManager() {
        UIManagerProxy.instance.setUIState(
            [
                {
                    typeIndex: EUI.Manager,
                    state: true,
                },
            ],
            false
        );
    }

    private RenderRightUtility() {
        this.ui.m_btn_cheers.visible = true;
        this.ui.m_btn_cheers.touchable = true;
        this.ui.m_btn_netRed.visible = true;
        this.ui.m_btn_netRed.touchable = true;
        this.ui.m_btn_repair.visible = true;
        this.ui.m_btn_repair.touchable = true;
        this.ui.m_btn_speedUp.visible = true;
        this.ui.m_btn_speedUp.touchable = true;
        for (let type of this._adUtilityList) {
            switch (type) {
                case ADUtility.Cheers:
                    this.ui.m_btn_cheers.visible = true;
                    this.ui.m_btn_cheers.touchable = true;
                    break;
                case ADUtility.NetRed:
                    this.ui.m_btn_netRed.visible = true;
                    this.ui.m_btn_netRed.touchable = true;
                    break;
                case ADUtility.RepairGame:
                    this.ui.m_btn_repair.visible = true;
                    this.ui.m_btn_repair.touchable = true;
                    break;
                case ADUtility.SpeedUp:
                    this.ui.m_btn_speedUp.visible = true;
                    this.ui.m_btn_speedUp.touchable = true;
                    break;
            }
        }
    }

    private GetRightBtnSpriteUrl(type: ADUtility) {
        switch (type) {
            case ADUtility.NetRed:
                return "/res/sprite/utility/NetRed.png";
            case ADUtility.Cheers:
                return "/res/sprite/utility/Party.png";
            case ADUtility.RepairGame:
                return "/res/sprite/utility/Repair.png";
            case ADUtility.SpeedUp:
                return "/res/sprite/utility/SpeedUp.png";
            default:
                return "/res/sprite/utility/SpeedUp.png";
        }
    }

    private OnClickRightUtilityBtn(type: ADUtility) {
        switch (type) {
            case ADUtility.NetRed:
                this.ShowUtilityPanel(EUI.UIMediatorInfluencer);
                break;
            case ADUtility.Cheers:
                this.ShowUtilityPanel(EUI.UIMediatorCheers);
                break;
            case ADUtility.RepairGame:
                this.ShowUtilityPanel(EUI.UIMeditorRepairGame);
                break;
            case ADUtility.SpeedUp:
                this.ShowUtilityPanel(EUI.UIMediatorSpeedUp);
                break;
            default:
                console.error("打开面板失败！" + type);
                break;
        }
    }

    //显示功能面板
    private ShowUtilityPanel(panel: EUI) {
        UIManagerProxy.instance.setUIState(
            [
                {
                    typeIndex: panel,
                    state: true,
                },
            ],
            false
        );
    }

    private _lastTipTime = 0;
    //添加右边的功能按钮
    public AddAdUtility(btn: ADUtility) {
        let index = this._adUtilityList.indexOf(btn);
        if (index < 0) {
            this._adUtilityList.push(btn);
            this.RenderRightUtility();
            let time = new Date().getTime();
            if (time - this._lastTipTime > 60000) {
                let hasShow = UIMediatorTalkEventPanel.instance.ShowTalkPanel(3)
                if (hasShow) {
                    this._lastTipTime = time;
                }
            }
        }
    }

    //移除右边的功能按钮
    RemoveADUtility(btn: ADUtility) {
        let index = this._adUtilityList.indexOf(btn);
        if (index > -1) {
            this._adUtilityList.splice(index, 1);
            this.RenderRightUtility();
        }
    }

    //随机一个btn
    private RandomAdBtn() {
        let type = Random.RangeInt(0, 4);
        if (type == ADUtility.NetRed) {
            if (GuestPool.instance) {
                GuestPool.instance.PopNetRed();
            }
        } else {
            this.AddAdUtility(type);
        }
        console.log("引导是否结束：" + GuideDataProxy.IsCompleteAllGuide);
        console.log("管理界面是否打开：" + UIMediatorManagement.instance.isShow);
        console.log("转盘界面是否打开：" + UIMediatorLuckyWheel.instance.isShow);
        console.log("任务界面是否打开：" + UIMediatorTask.instance.isShow);
        console.log("双倍奖励界面是否打开：" + UIMediatorAD.instance.isShow);
        console.log("设置界面是否打开：" + UIMediatorSetting.instance.isShow);
        console.log("影响界面是否打开：" + UIMediatorInfluencer.instance.isShow);
        console.log("修理界面是否打开：" + UIMediatorRepairGame.instance.isShow);
        console.log("加速界面是否打开：" + UIMediatorSpeedUp.instance.isShow);
        console.log("干杯界面是否打开:" + UIMediatorCheers.instance.isShow);
        if (GuideDataProxy.IsCompleteAllGuide && !UIMediatorManagement.instance.isShow && !UIMediatorLuckyWheel.instance.isShow
            && !UIMediatorTask.instance.isShow && !UIMediatorAD.instance.isShow && !UIMediatorSetting.instance.isShow
            && !UIMediatorInfluencer.instance.isShow && !UIMediatorRepairGame.instance.isShow && !UIMediatorSpeedUp.instance.isShow
            && !UIMediatorCheers.instance.isShow) {
            /***原生广告调用******************* */
            UIMediatorGameAd.instance.ShowNativeAd();
            console.warn("============>>>>>随机原生广告调用");
        }
        console.log("============>>>>>RandomAdBtn");
    }

    HideTaskBtn() {
        this.ui.m_taskBtn.visible = false;
        this.ui.m_taskBtn.touchable = false;
    }

    //更新任务进度
    private UpdateTaskProgress() {
        if (!MiscDataProxy.instance.Data.allTaskComplete) {
            this.ui.m_taskBtn.visible = true;
            this.ui.m_taskBtn.touchable = true;
            let task = TaskManager.instance.currentTask;
            this.ui.m_taskBtn.m_progress.value = task.progress;
            this.ui.m_taskBtn.m_progress.m_text_progress.text = task.toSimpleProgressString();
            this.ui.m_taskBtn.m_progress.m_text_task.text = task.name;
        } else {
            this.HideTaskBtn();
        }
    }

    private OnAddNetRed() {
        this.AddAdUtility(ADUtility.NetRed);
    }

    private UpdateDoubleTimeLast() {
        let t = MiscDataProxy.data.DoubleBuffTime;
        if (t > 0) {
            this.ui.m_txt_doubleTime.text = "2x " + Time.ToTimeString(t);
        } else {
            this.ui.m_txt_doubleTime.text = "2x"
        }
    }

    private ShowRecordVideoPanel() {
        if (this.ui.m_btn_record.m_state.selectedIndex == 0) {
            UIMediatorRecordShare.instance.ShowRecord()
        }
        else {
            this.ShowRecordIdle()
            PlatformManagerProxy.instance.PlatformInstance.recordManager.StopRecord(Laya.Handler.create(this, () => {
                PlatformManagerProxy.instance.PlatformInstance.ShowToast("录制结束")
                UIMediatorRecordShare.instance.ShowShare()
            }))
        }
    }

    public ShowRecordBtn(value: boolean) {
        let isTT = PlatformManagerProxy.instance.PlatformInstance.platform == EPlatformType.TT
        this.ui.m_btn_record.visible = value && isTT;
    }

    ShowRecordState(index: number) {
        this.ui.m_btn_record.m_state.selectedIndex = index;
    }

    ShowRecordIdle() {
        this.ShowRecordState(0);
    }

    ShowRecordStop() {
        this.ShowRecordState(2);
        this.ui.m_btn_record.offClick(this, this.ShowRecordVideoPanel)
        this.ui.m_btn_record.grayed = true;
        for (let i = 3; i >= 0; i--) {
            let num = i;
            Laya.timer.once(3000 - num * 1000, this, () => {
                this.ui.m_btn_record.m_number.text = i.toString()
                if (num <= 0) {
                    this.ui.m_btn_record.m_number.text = ""
                    this.ui.m_btn_record.onClick(this, this.ShowRecordVideoPanel)
                    this.ui.m_btn_record.grayed = false;
                }
            })
        }
    }





}

export enum ADUtility {
    NetRed = 0,
    Cheers = 1,
    RepairGame = 2,
    SpeedUp = 3,
}
