import BaseUIMediator from "../aTGame/UI/FGUI/BaseUIMediator";
import FGUI_PGameMarket from "../FGUI/BuildUIPanel/FGUI_PGameMarket";
import { EmployeeDataPool } from "../Config/EmployeeDataPool";
import FGUI_PGameStaffInfo from "../FGUI/BuildUIPanel/FGUI_PGameStaffInfo";
import FGUI_skillPanel from "../FGUI/BuildUIPanel/FGUI_skillPanel";
import { EUILayer } from "../aTGame/UI/FGUI/EUILayer";
import UIManagerProxy from "../dMyGame/Manager/Proxy/UIManagerProxy";
import { EUI } from "../dMyGame/Enum/EUI";
import { MiscDataProxy } from "../Data/MiscDataProxy";
import MesManagerClass from "../dMyGame/Manager/MesManager";
import { EEventUI } from "../dMyGame/EventEnum/EEventUI";
import { GuideDataProxy } from "../Data/GuideDataProxy";
import PlatformManagerProxy from "../aTGame/Platform/PlatformManagerProxy";
import {Audio} from "../GameComponent/Audio";
import {Time} from "../lty/class/Time";
import {ClockManager} from "../lty/plugin/Clock/ClockManager";
import {ClockEvent} from "../lty/plugin/Clock/ClockEvent";
import {Uma, UmaEvent} from "../Uma";

//招聘市场面板
export class UIMediatorStaffMarket extends BaseUIMediator<FGUI_PGameMarket> {
    private static m_instance: UIMediatorStaffMarket;
    private isGuide: boolean;

    /** 单例 */
    public static get instance(): UIMediatorStaffMarket {
        if (!this.m_instance) {
            this.m_instance = new UIMediatorStaffMarket();
            this.m_instance._classDefine = FGUI_PGameMarket;
        }
        return this.m_instance;
    }

    private constructor() {
        super();
    }

    protected _layer = EUILayer.MarketPanel;
    //运行时要替换的雇员
    protected _runtimeReplaceWorkName: string = null;

    protected _OnShow() {
        super._OnShow();
        this.ui.m_btn_close.onClick(this, this.Close);
        this.ui.m_btn_refresh.onClick(this, this.OnWatchAdUpdateStaffInfo);
        this.RenderMarketStaffList();
        MesManagerClass.instance.onUI(EEventUI.RenderMarketStaffList, this, this.RenderMarketStaffList);
        this.isGuide = !GuideDataProxy.IsCompleteAllGuide;
        MesManagerClass.instance.on(ClockEvent.UpdateOnLineTime,this,this.UpdateCountDownTime);
        Uma.trackEvent(UmaEvent.StaffUI)
    }

    protected _OnHide() {
        super._OnHide();
        this._runtimeReplaceWorkName = null;
        MesManagerClass.instance.offUI(EEventUI.RenderMarketStaffList, this, this.RenderMarketStaffList);
        MesManagerClass.instance.off(ClockEvent.UpdateOnLineTime,this,this.UpdateCountDownTime);

    }

    //渲染招聘市场的雇员
    private RenderMarketStaffList() {
        let list = MiscDataProxy.instance.GetMarketEmployeeList();
        //已经雇佣的雇员
        this.ui.m_list_Market.itemRenderer = Laya.Handler.create(this, (index: number, item: FGUI_PGameStaffInfo) => {
            let data = list[index];
            item.m_loader_character.url = EmployeeDataPool.GetEmployeeSprite(data);
            item.m_name.text = data.Name;
            item.m_starNum.fillAmount = data.Level / 4;
            item.m_sex.selectedIndex = data.Gender == 0 ? 0 : 1;
            item.m_skill.selectedIndex = data.Skill.length == 0 ? 0 : 1;
            item.m_btn_staffBuy.m_text_price.text = data.Price.toString();
            //显示技能
            if (data.Skill && data.Skill.length > 0) {
                item.m_skillList.itemRenderer = Laya.Handler.create(this, (index: number, skill: FGUI_skillPanel) => {
                    let skillData = data.Skill[index];
                    skill.m_Title.text = skillData.Title;
                    skill.m_text_desc.text = skillData.Desc;
                    skill.m_sprite.url = EmployeeDataPool.GetIconSprite(skillData.Type);
                }, null, false);
                item.m_skillList.numItems = data.Skill.length;
            }
            //是否已经雇佣此人
            let has = MiscDataProxy.ContainEmployee(data, MiscDataProxy.instance.Data.staffList);
            item.m_cutBtn.selectedIndex = has ? 2 : 1;
            //如果金钱小于雇佣价格，按钮变灰，不可点击
            if (!has) {
                if (data.Price <= MiscDataProxy.instance.Data.money) {
                    item.m_btn_staffBuy.grayed = false;
                    item.m_btn_staffBuy.touchable = true;
                    item.m_btn_staffBuy.onClick(this, this.AddStaff, [index])
                } else {
                    item.m_btn_staffBuy.grayed = true;
                    item.m_btn_staffBuy.touchable = false;
                }
            } else {
                item.m_btn_staffBuy.offClick(this, this.AddStaff);
            }

        }, null, false);
        this.ui.m_list_Market.numItems = list.length;
    }


    private Close() {
        if (this.isGuide) {

            MesManagerClass.instance.eventUI(EEventUI.GuideToNext);
        }
        UIManagerProxy.instance.setUIState([{ typeIndex: EUI.StaffMarket, state: false }], false);
    }

    private AddStaff(index: number) {
        let temp = this._runtimeReplaceWorkName;
        this._runtimeReplaceWorkName = MiscDataProxy.instance.GetMarketEmployeeList()[index].Name;
        MesManagerClass.instance.eventUI(EEventUI.AddStaff, [index, temp]);
    }

    public SetReplaceWorkerName(name: string) {
        this._runtimeReplaceWorkName = name;
    }


    //观看广告立即刷新雇员信息
    private OnWatchAdUpdateStaffInfo() {
        console.log("观看广告立即刷新雇员信息");
        PlatformManagerProxy.instance.PlatformInstance.ShowRewardVideoAd(
            Laya.Handler.create(this, () => {
                Audio.PlayAudio();
                MiscDataProxy.instance.UpdateMarketList();
                Uma.trackEvent(UmaEvent.MarkertAD);
                if(this.isShow){
                    this.RenderMarketStaffList();
                }
            }),
            Laya.Handler.create(this, () => {
                PlatformManagerProxy.instance.PlatformInstance.ShowToast("未看完广告")
            }))
    }

    UpdateCountDownTime(){
        this.ui.m_txx_countdownTime.text = Time.ToTimeString(MiscDataProxy.data.workMarketTime);
    }


}