import BaseConfigDataProxy from "../aTGame/Config/RootDataProxy";
import {BuildConfig} from "../dMyGame/_config/BuildConfig";
import {TableName} from "./TableName";
import EssentialResUrlsUntility from "../aTGame/Res/EssentialResUrls";
import ResLoadClass from "../aTGame/Res/ResLoad";
import UISlotConfigProxy from "./UISlotConfigProxy";

/**Slot的配置表们的代理,手动初始化 */
export class SlotConfigProxy {

    private static _instance: SlotConfigProxy;
    /** 单例 */
    public static get instance(): SlotConfigProxy {
        if (this._instance == null) {
            this._instance = new SlotConfigProxy();
        }
        return this._instance;
    }
    /**key - slotID,value- slot数据*/
    private _map: { [slotID: number]: any } ={};
    /**key - slotName,value- slot数据*/
    private _dict:{[slodName:string]:any}={};

    public initData() {
        let length = UISlotConfigProxy.instance.dataList.length;
        for (let i = 0; i < length; i++) {
            let slot = UISlotConfigProxy.instance.dataList[i];
            //读取数据
            let url = EssentialResUrlsUntility.ConfigURL(TableName.NameUpper(slot.name) + ".json");
            let table = ResLoadClass.Get(url, true);
            if (table) {
                if (this._map[i]) {
                    console.error("Slot配置表添加失败，id重复：",i,url,this._map);
                } else {
                    this._map[i] = table;
                    this._dict[slot.name] = table;
                }
                ResLoadClass.Unload(url);
            } else {
                console.error("Slot配置表读取失败：", url,slot.name);
            }
        }
        // console.log("所有slot数据加载完毕：",this._map);
    }

    public GetSlotConfigByUID(uid:number){
        let data = this._map[uid];
        if(data){
            return data;
        }else{
            console.error("获取UID对应的config失败：",uid);
        }
        return null;
    }

    public GetSlotConfigByName(slotName:string){
        let data = this._dict[slotName];
        if(data){
            return data;
        }else{
            console.error("获取SlotName对应的config失败：",slotName);
        }
        return null;
    }
}