import {TUtils} from "../../aTGame/Utils/TUtils";
import {Mathf} from "./Mathf";

export class Time {
    /**两帧之间的时间间隔,单位秒。 */
    public static get deltaTime(): number {
        return TUtils.deltaTimeSec;
    }

    /**时针缩放。 */
    public static get scale(): number {
        return Laya.timer.scale;
    }

    /**时针缩放。 */
    public static set scale(value: number) {
        Laya.timer.scale = value;
    }

    /**
     * 把从date获取的time转为24小时格式 00：00：00
     * @param value
     * @constructor
     */
    public static ToTimeString(value: number) {
        value = value / 1000;
        let h = Math.floor(value / 60 / 60);
        let m = Math.floor((value - h * 60 * 60) / 60);
        m = m < 0 ? 0 : m;
        let s = Math.floor(value - h * 60 * 60 - m * 60);
        s = s < 0 ? 0 : s;
        let txt = (h + ":" + m + ":" + s)
            .replace(/[0-9]+/g, (i) => {
                if (Number(i) < 10) {
                    return '0' + i
                } else {
                    return i
                }
            });
        return txt;
    }

}