import {Task, TaskEventData} from "./Task";
import {WaterRoomDataProxy} from "../../Data/WaterRoomDataProxy";
import {TaskTwelve} from "./TaskTwelve";
import {TaskEvent} from "./TaskEvent";
import {CameraManager} from "../CameraManager";
import {EffectManager} from "../EffectManager";
import {EffectName} from "../../dMyGame/ResList/PrefabNames";

export class TaskEleven extends Task {
    CheckProgress(data: TaskEventData): number {
        if (data.evt == TaskEvent.UpdateSewage) {
            this.Init();
        }
        return this.progress;
    }

    Init() {
        let slot = WaterRoomDataProxy.instance.data.list.slots[0];
        this.record = slot.level;
        this.progress = this.record/this.condition*100;
    }

    Go() {
       let data=  CameraManager.instance.FocusPositionByIndex(20,false);
        let pos = new Laya.Vector3(-data.x,data.y,data.z);
        EffectManager.PlayEffect(EffectName.ZS,1111,10000,pos);
    }

}

