import BaseUIMediator from "../aTGame/UI/FGUI/BaseUIMediator";
import {EUILayer} from "../aTGame/UI/FGUI/EUILayer";
import {MiscData, MiscDataProxy} from "../Data/MiscDataProxy";
import FGUI_PGameTopUI from "../FGUI/TopInfoUI/FGUI_PGameTopUI";
import {MoneyConverter} from "../zml/MoneyConverter";
import MesManagerClass from "../dMyGame/Manager/MesManager";
import {EEventUI} from "../dMyGame/EventEnum/EEventUI";
import {EEventScene} from "../dMyGame/EventEnum/EEventScene";


export class UIMediatorTopPanel extends BaseUIMediator<FGUI_PGameTopUI>{
    private static m_instance: UIMediatorTopPanel;
    private _data: MiscData;
    /** 单例 */
    public static get instance(): UIMediatorTopPanel {
        if (!this.m_instance) {
            this.m_instance = new UIMediatorTopPanel();
            this.m_instance._classDefine = FGUI_PGameTopUI;
        }
        return this.m_instance;
    }
    protected _layer: EUILayer = EUILayer.Main;

    public UpdateMoneyNum(value:number){
        if (this.isShow) {
            this.ui.m_text_money.text = MoneyConverter.DoConverter(Math.floor(value));
        }
    }

    public  UpdateDiamondNum(value:number){
        if (this.isShow) {
            this.ui.m_text_diamond.text = MoneyConverter.DoConverter(Math.floor(value));
        }
    }

    public  UpdateWaterState(){
        if(this.isShow){
            this.ui.m_text_water.text = this._data.water + "/" + this._data.MaxWater;
        }
    }

    private constructor() { super(); }

    _OnShow() {
        super.Show();
        MesManagerClass.instance.onUI(EEventUI.UpdateDiamondNum, this,this.UpdateDiamondNum);
        MesManagerClass.instance.onUI(EEventUI.UpdateMoneyNum, this,this.UpdateMoneyNum);
        MesManagerClass.instance.onUI(EEventUI.UpdateWaterState,this,this.UpdateWaterState);
        MesManagerClass.instance.on3D(EEventScene.SceneLoadComplete, this,this.UpdateMoneyDiamondShow);
        this._data = MiscDataProxy.instance.Data;

    }

    private UpdateMoneyDiamondShow(){
        this.ui.m_text_money.text = MoneyConverter.DoConverter(this._data.money);
        this.ui.m_text_diamond.text = MoneyConverter.DoConverter(this._data.diamond);
    }



    /**花钱 */
    Expend(type: TopDataType, value: number): boolean {
        if (value < 0)
            return false;
        switch (type) {
            case TopDataType.Money:
                if (MiscDataProxy.Money >= value) {
                    MiscDataProxy.Money = MiscDataProxy.Money - value;
                }
                else
                    return false;
                break;
            case TopDataType.Diamond:
                if (MiscDataProxy.Diamond >= value)
                    MiscDataProxy.Diamond -=value;
                else
                    return false;
                break;
            case TopDataType.Water:
                if (MiscDataProxy.Water >= value){
                    //todo 刷新水资源显示
                }
                else {
                    return false;
                }
                break;
            default:
                return false;
        }
        return true;
    }
}
export enum TopDataType {
    Money = "Money",
    Diamond = "Diamond",
    Water = "Water",
}