import {BuildDataProxy} from "../Data/BuildDataProxy";
import {StringName} from "../Enum/StringName";
import MesManagerClass from "../dMyGame/Manager/MesManager";
import {EEventScene} from "../dMyGame/EventEnum/EEventScene";

//建造区的格子，占位显示
export class BuildBlock {
    private static _instance: BuildBlock;
    public static get instance(): BuildBlock {
        if (this._instance == null) {
            this._instance = new BuildBlock();
        }
        return this._instance;
    }

    constructor() {
        MesManagerClass.instance.on3D(EEventScene.OpenBuildBlock, this, this.PlayState);
    }

    //男厕所
    private maleAnimator: Laya.Animator;
    //女厕所
    private femaleAnimator: Laya.Animator;

    //初始化格子的animator
    public InitBlockAnimator(male: Laya.Node, female: Laya.Node) {
        this.maleAnimator = male.getComponent(Laya.Animator);
        this.femaleAnimator = female.getComponent(Laya.Animator);
        let ma = BuildDataProxy.instance.dict.get(StringName.maleToilet);
        this.PlayState(ma.count, true);
        let fema = BuildDataProxy.instance.dict.get(StringName.femaleToilet);
        this.PlayState(fema.count, false);
    }

    /**
     * 切换等级状态
     * @param level 格子等级
     * @param isMale 是否为男厕所
     */
    public PlayState(level: number, isMale: boolean) {
        level += 1;
        level = level >= 6 ? 6 : level;
        if (0 <= level && level <= 6) {
            if (isMale) {
                this.maleAnimator.play(level.toString())
            } else {
                this.femaleAnimator.play(level.toString())
            }
        } else {
            console.error("厕所格子等级显示超限！");
        }
    }
}

