import {LayaBehaviour} from "../lty/class/LayaBehaviour";
import {Time} from "../lty/class/Time";
import {EffectManager} from "../GameComponent/EffectManager";
import {EffectName} from "../dMyGame/ResList/PrefabNames";
import {Vector3} from "../lty/class/Vector3";
import {MiscDataProxy} from "../Data/MiscDataProxy";
import {UIFacade} from "../UI/UIFacade";

export class VendingMachine extends LayaBehaviour {
    private _time: number
    private _money: number
    private _speed: number = 100
    private _star: number = 0;
    private _isAllowOnclick = false

    onStart(): void {
        this._time = 0;
        this._money = 0;
    }

    onUpdate(): void {
        this._time += Time.deltaTime;
        this._money += Time.deltaTime * this._speed * ((this._star + 1) * (this._star + 1));
        if (this._time > 10.0) {
            // console.warn("click money!")
            this._time = 0;
            let effect = EffectManager.PlayEffect(EffectName.Emoji_money, this.id)
            this.sprite3D.addChild(effect);
            effect.transform.localPosition = Vector3.zero;
            this._isAllowOnclick = true;
            effect.active = false;
            effect.active = true;
        }
    }

    private OnclickCollectMoney() {
        EffectManager.Clear(this.id, EffectName.Emoji_money);
        //玩家money增加
        let money = Math.floor(this._money);
        MiscDataProxy.Money += money;
        this._money = 0;
        this._time = 0;
        this._isAllowOnclick = false;
        let effect = EffectManager.PlayEffect(EffectName.Money, this.id, 2000);
        this.owner.addChild(effect);
        effect.transform.localPosition = Vector3.zero;
        effect.transform.rotationEuler = this.transform.rotationEuler;
        // UIFacade.PlayMoneyEffect();
    }

    onMouseDown() {
        if (this._isAllowOnclick) {
            // console.log("click vending!!!!")
            this.OnclickCollectMoney();
        }
    }
}