import EssentialResUrlsUntility from "../../aTGame/Res/EssentialResUrls";
import ResLoadClass from "../../aTGame/Res/ResLoad";
import GuestPoolConfig, { IGuestPoolConfigData } from "../../Config/GusetPoolConfig";
import GuestPrefabConfigProxy from "../../dMyGame/ConfigProxy/GuestPrefabConfigProxy";
import { EEventScene } from "../../dMyGame/EventEnum/EEventScene";
import MesManagerClass from "../../dMyGame/Manager/MesManager";
import { LayaBehaviour } from "../../lty/class/LayaBehaviour";
import { Random } from "../../lty/class/Random";
import { Time } from "../../lty/class/Time";
import { NullReferenceException } from "../../lty/Exception/NullReferenceException";
import { Guest } from "./Guest";
import { GuideUncle } from "./GuideUncle";
import { NetRed } from "./NetRed";

/**
 * 根据旅客的性别匹配男女的预制体
 * 根据挂件的类型附加挂件
 * 根据挂件推断动画
 */
export class GuestPool extends LayaBehaviour {
    /**所有人物的父节点 */
    private _parentNode: Laya.Node;
    private _data: IGuestPoolConfigData[];
    private _totalWeight = 0;
    /**旅客的对象池 */
    private _pool: Guest[] = [];//所有人物的对象池
    private _attachmentPool: Laya.Sprite3D[] = [];//所有附件的对象池,按名称get附件
    private _malePrefabs: Laya.Sprite3D[] = [];//男性预制体
    private _femalePrefabs: Laya.Sprite3D[] = [];//女性预制体
    private _attachmentPrefabs: Laya.Sprite3D[] = [];//附件的预制体
    private static _instance: GuestPool;
    public static get instance(): GuestPool {
        return this._instance;
    }

    private _exits: Laya.Sprite3D[];//机场出口
    private _entrances: Laya.Sprite3D[];//机场入口
    private _interval = 5;//s
    private _timer = 0;
    private _popToggle = false;//生成人物的开关
    private _flow = 80;
    /**
     * 客流量
     */
    public get flow() {
        return this._flow;
    }

    private get count() {
        let num = 0;
        for (let ele of this._pool) {
            if (ele.sprite3D.active) {
                num++;
            }
        }
        return num;
    }

    onAwake() {
        GuestPool._instance = this;
    }

    onLateUpdate() {
        if (this._popToggle) {
            this._timer += Time.deltaTime;
            if (this._timer > this._interval) {
                this._timer = 0;
                if (this.count < this._flow) {
                    this.Pop();
                }
            }
        }
    }

    /**
     * 打开或者关闭生成人物的开关
     * @param value
     * @constructor
     */
    SetToggle(value: boolean) {
        this._popToggle = value;
    }

    public InitPool(entrances: Laya.Sprite3D[], exits: Laya.Sprite3D[]) {
        MesManagerClass.instance.on3D(EEventScene.UpdateMaxFlow, this, this.SetInterval);
        this._entrances = entrances;
        this._exits = exits;
        console.log("旅客对象池初始化...");
        this._data = GuestPoolConfig.instance.m_data;
        if (this._data == null || this._data.length <= 0) {
            console.warn("对象池初始化失败，Json文件为空");
            return;
        }
        for (const ele of this._data) {
            this._totalWeight += ele.Weight;
            ele.point = this._totalWeight;
        }
        this.LoadPrefabs();
        this.InitNetRed();
        this.InitRandomNumPeople();
    }

    public SetParentNode(node: Laya.Node) {
        this._parentNode = node;
    }

    /**加载人物和附件的预制体资源 */
    private LoadPrefabs() {
        let list = GuestPrefabConfigProxy.instance.dataList;
        if (list == null || list.length <= 0) {
            console.warn("加载指定旅客预制体失败，旅客预制体配置表为空");
            return;
        }
        for (const data of list) {
            let url = EssentialResUrlsUntility.prefab_url(data.name);
            if (url && url.length > 0) {
                let spr = ResLoadClass.Get(url) as Laya.Sprite3D;
                if (spr) {
                    if (!data.isAttachment) {
                        if (data.isMale) {
                            this._malePrefabs.push(spr);
                        } else {
                            this._femalePrefabs.push(spr);
                        }
                        // console.log("对象池资源添加", spr);
                    } else {
                        this._attachmentPrefabs.push(spr);
                    }
                } else {
                    console.warn("获取预制体失败:", data.name, url);
                }
            } else {
                console.warn("获取url失败:", data.name, url);
            }
        }
    }

    /**随机实例化一个人物 */
    private RandomInstantiate(isMale: boolean) {
        let prefab: Laya.Sprite3D;
        let index: number;
        if (isMale) {
            index = Random.RangeInt(0, this._malePrefabs.length);
            prefab = this._malePrefabs[index];
        } else {
            index = Random.RangeInt(0, this._femalePrefabs.length);
            prefab = this._femalePrefabs[index];
        }
        if (prefab) {
            let node = this.Instantiate(prefab, this._parentNode)
            let script = node.addComponent(Guest) as Guest;
            this._pool.push(script);
            return script;
        } else {
            console.warn("随机获取旅客预制体失败，导致实例化失败:", index, "isMale,", isMale, "预制体数组", isMale ? this._malePrefabs : this._femalePrefabs);
        }
        return null;
    }


    /**随机一个旅客行为模板 */
    private RandomGuestData() {
        let num = Random.Range(0, this._totalWeight);
        for (const d of this._data) {
            if (num < d.point) {
                return d;
            }
        }
        return null;
    }

    /**在对象池中搜索实例,没有找到就随机实例化*/
    private SearchPool(isMale: boolean) {
        let guset: Guest = null;
        for (let i = 0; i < this._pool.length; i++) {
            let temp = this._pool[i];
            if (!temp.sprite3D.active && temp.isMale == isMale) {
                guset = temp;
                break;
            }
        }
        if (guset == null) {
            guset = this.RandomInstantiate(isMale);
        }
        return guset;
    }

    /**实例化附件 */
    private InstantiateAttachment(name: string) {
        let spr = null;
        for (const item of this._attachmentPrefabs) {
            if (item.name == name) {
                spr = item;
                break;
            }
        }
        if (spr) {
            return this.Instantiate(spr as Laya.Sprite3D, this._parentNode);
        } else {
            console.warn("获取附件的预制体失败,实例化失败:", name);
        }
        return null;
    }

    /**查找可以用的附件实例 */
    private SearchAttachment(name: string) {
        let attachment: Laya.Sprite3D = null;
        if (this._attachmentPool && this._attachmentPool.length > 0) {
            for (let i = 0; i < this._attachmentPool.length; i++) {
                if (this._attachmentPool[i].name == name) {
                    attachment = this._attachmentPool[i];
                    break;
                }
            }
        }
        if (attachment == null) {
            attachment = this.InstantiateAttachment(name);
        }
        return attachment;
    }

    /**生成附件 */
    private SpawnAttachment(name: string) {
        return this.SearchAttachment(name);
    }

    /**实例化一个旅客 */
    private SpawnGuest() {
        let data = this.RandomGuestData();
        let guest: Guest = null;
        if (data.Gender[0] == 0) {
            //寻找一个女性实例
            guest = this.SearchPool(false)
        } else if (data.Gender[0] == 1) {
            //寻找一个男性实例
            guest = this.SearchPool(true)
        } else {
            console.warn("目标是个人吗？", data);
        }
        if (guest) {
            let att = null;
            //重置旅客行为，初始化附件数据
            if (data.RandomAttachments && data.RandomAttachments.length > 0) {
                let count = data.RandomAttachments.length;
                let index = Random.RangeInt(0, count);
                let attName = data.RandomAttachments[index].toString();
                att = this.SpawnAttachment(attName);

            } else {
                //TODO 随机附件
                // console.warn("无随机附件信息:", data);
            }
            guest.ReInit(data, att);
        } else {
            console.error("对象池生成旅客失败，跳过本次");
        }
        return guest;
    }

    /**弹出一个人 */
    public Pop(): Guest {
        let guest = this.SpawnGuest();
        if (guest) {
            //随机一个入口        
            let index = Random.random > 0.5 ? 0 : 1;
            let enter = this.GetEntranceByName("Entrance_" + index)
            guest.transform.position = enter.transform.position.clone();
        }
        return guest;
    }

    GetEntranceByName(name: string) {
        for (let e of this._entrances) {
            if (e.name == name) {
                return e;
            }
        }
    }


    /**回收附件 */
    public RecycleAttachment(attachment: Laya.Sprite3D) {
        if (attachment == null) {
            throw new NullReferenceException("被回收的附件为空.");
        }
        attachment.active = false;
        this._parentNode.addChild(attachment);
        if (this._attachmentPool.indexOf(attachment) < 0) {
            this._attachmentPool.push(attachment);
        } else {
            console.warn("人物附件回收失败", attachment);
        }
    }

    //设置生成间隔
    SetInterval(length: number) {
        //开启最大生成速度
        // length = 120;
        // this._interval = 60 / length;
        // console.warn("当前生成频率", this._interval, "/s", length, "/min");
        // return;
        //  this._flow = length;
        this._interval = 60 / length;
        console.warn("当前生成频率", length, "/min");
    }


    //观看广告增加人数
    AddPeopleOnWatchAD(peoples: number) {
        let e = this.GetEntranceByName("Entrance_2");
        for (let i = 0; i < peoples; i++) {
            Laya.timer.once(i * 600, this, () => {
                let g = this.Pop();
                g.transform.position = e.transform.position;
            })
        }
    }

    /**
     * 随机生成一定数量的旅客
     * @constructor
     * @private
     */
    private InitRandomNumPeople() {
        if (this._popToggle) {
            let num = Random.RangeInt(10, 20);
            for (let i = 0; i < num; i++) {
                let x = Random.Range(-3, 3);
                let z = Random.Range(-3, 3);
                let pos = new Laya.Vector3(-12 + x, 0, 12 + z);
                let g = this.Pop();
                g.transform.position = pos;
            }
        }
    }



    /**
     * 开始生成怪物
     * @constructor
     */
    public StartPop() {
        this.SetToggle(true);
        if (this._pool.length < 20) {
            this.InitRandomNumPeople();
        }
    }

    //#region 特殊人物管理
    private _netRedSprite: Laya.Sprite3D;
    private _netRed: NetRed;

    private InitNetRed() {
        let url = EssentialResUrlsUntility.prefab_url("woman_wh");
        this._netRedSprite = ResLoadClass.Get(url);
        this._netRed = this._netRedSprite.addComponent(NetRed) as NetRed;
        this._parentNode.addChild(this._netRedSprite);
        this._netRedSprite.active = false;
    }

    public PopNetRed() {
        if (!this._netRed.sprite3D.active) {
            let index = Random.random > 0.5 ? 0 : 1;
            let enter = this.GetEntranceByName("Entrance_" + index)
            this._netRed.transform.position = enter.transform.position.clone();
            this._netRed.SetActive(true);


        }
    }

    /**
     * 生成引导大叔
     * @constructor
     */
    public PopGuideUncle() {
        let index = Random.RangeInt(0, this._malePrefabs.length);
        let prefab = this._malePrefabs[index];
        let node = this.Instantiate(prefab, this._parentNode)
        let g = node.addComponent(GuideUncle) as GuideUncle;
        let d = this._data[this._data.length - 1];
        g.ReInit(d, null);
        g.transform.position = new Laya.Vector3(3.6, 0, 24.9);
    }

    //#endregion

}