import RootLocalStorageData from "../aTGame/Data/RootLocalStorageData";
import RootLocalStorageSaveNaWft from "../aTGame/Data/RootLocalStorageSave";
import { StringName } from "../Enum/StringName";
import BuildingData from "./BuildingData";
import { FunctionID } from "./FunctionID";
import {BuildDefaultSlotConfigProxy} from "../Config/BuildDefaultSlotConfigProxy";
import {SlotData} from "./SlotData";
import UISlotConfigProxy from "../Config/UISlotConfigProxy";
import {SlotUIDConst} from "../dMyGame/_config/SlotUIDConst";
import {UpdateBuildingEventData, UpdateSlotEventData} from "./UpdateEventData";
import MesManagerClass from "../dMyGame/Manager/MesManager";
import {EEventScene} from "../dMyGame/EventEnum/EEventScene";
import {EEventUI} from "../dMyGame/EventEnum/EEventUI";
import UIManagerProxy from "../dMyGame/Manager/Proxy/UIManagerProxy";
import {EUI} from "../dMyGame/Enum/EUI";
import SlotUIDConstProxy from "./SlotUIDConstProxy";
//小卖部的数据
export class CanteenData extends RootLocalStorageData{
    list: BuildingData;
}
export class CanteenDataProxy extends RootLocalStorageSaveNaWft<CanteenData>{
    private static _instance: CanteenDataProxy;
    constructor() {
        super();
        MesManagerClass.instance.on3D(EEventScene.UpdateBuildingData, this, this.UpdateBuildData);
        MesManagerClass.instance.onUI(EEventUI.UpdateSlotData, this, this.UpdateSlotData);
    }
    public static get instance() {
        if (this._instance == null) {
            this._instance = new CanteenDataProxy();
        }
        return this._instance;
    }
    private _data: CanteenData;
    public get data() {
        return this._data;
    }
    protected get _saveName(): string {
        return "CanteenData";
    }
    protected getNewData(): CanteenData {
        return new CanteenData;
    }
    public InitData(): void {
        this._data = this._ReadFromFile();
        if (this._data.list == null) {
            this._data.list = new BuildingData();
            this._data.list.active = false;
            this._data.list.functionID = FunctionID.Canteen;
            this._data.list.name = StringName.canteen;
            this._data.list.index = 0;
            this._data.list.title = "便利店";
            //TODO:初始化SLOTS数据
            this.InitSlot(this._data.list);
            this._SaveToDisk(this._data);
        }
    }

    //todo 初始化slot
    private InitSlot(data: BuildingData) {
        let count = 0;
        let dataList = BuildDefaultSlotConfigProxy.instance.dataList;
        for (let i = 0; i < dataList.length; i++) {
            if (dataList[i].canteen_slot > -1) {
                count++;
            }
        }
        if (count > 0) {
            data.slots = new Array(count);
            for (let i = 0; i < count; i++) {
                let slotData = new SlotData();
                slotData.level = dataList[i].canteen_slot_level;
                slotData.uid = dataList[i].canteen_slot;
                slotData.name = UISlotConfigProxy.instance.dataList[slotData.uid].name;
                slotData.id = i;
                slotData.usable =
                    SlotUIDConstProxy.Data.slot_cashier_config != slotData.uid
                    && SlotUIDConstProxy.Data.slot_onewindow_config!=slotData.uid
                    && SlotUIDConstProxy.Data.slot_shelf_config!=slotData.uid;
                data.slots[i] = slotData;
            }
        } else {
            console.warn("便利店没有可用的Slot");
        }
    }

    private UpdateBuildData(data: { Name: string, Count: number }) {
        if(data.Name == StringName.canteen) {
            console.log("便利店建造完成开放")
            this._data.list.active = true;
            let buildData: UpdateBuildingEventData = {buildingName: data.Name, buildingData: this._data.list};
            MesManagerClass.instance.event3D(EEventScene.UpdateBuildingOutlookEvent, buildData);
            this._SaveToDisk(this._data)
            UIManagerProxy.instance.setUIState([{typeIndex:EUI.CountDownTime,state:false}],false);
        }
    }

    private UpdateSlotData(mes: UpdateSlotEventData) {
        let funcID = mes.building.functionID;
        if (funcID == FunctionID.Canteen) {
            let data = this.data.list;
            //不在本信息代理中不管理
            if (data) {
                SlotData.UpdateSlotData(mes, data.slots);
                this._SaveToDisk(this._data);
            } else {
                console.error("获取数据失败！", mes);
            }
        }
    }

}
