export namespace TaskConfig {
    export class config {
        /** 建造数量 */ readonly id: number;
        /** 厕所任务名称 */ readonly name: string;
        /** 厕所任务描述 */ readonly describe: string;
        /** 完成条件 */ readonly condition: number;
        /** 厕所任务奖励 */ readonly reward: number;
        /** 奖励类型 */ readonly reward_type: number;
        /** 备注 */ readonly remark: number;
    }
    export var data : {[key: number]: TaskConfig.config};
    export var dataList : TaskConfig.config[];
    export var lastData : TaskConfig.config;
    export const path = "res/config/TaskConfig.json";
}


