import BaseConfigDataProxy from "../aTGame/Config/RootDataProxy";
import {BuildingDefaultSlotConfig} from "../dMyGame/_config/BuildingDefaultSlotConfig";
import {SlotUiConfig} from "../dMyGame/_config/SlotUiConfig";

export class BuildDefaultSlotConfigProxy extends BaseConfigDataProxy<BuildingDefaultSlotConfig.config>{
    private static _instance: BuildDefaultSlotConfigProxy;
    /** 单例 */
    public static get instance(): BuildDefaultSlotConfigProxy {
        if (this._instance == null) {
            this._instance = new BuildDefaultSlotConfigProxy();
        }
        return this._instance;
    }
    //
    private constructor() {
        super();
    }

    public initData() {
        this.m_dataList = BuildingDefaultSlotConfig.dataList;
    }
}
