import {LayaBehaviour} from "../../lty/class/LayaBehaviour";
import {FacilityPosition} from "../Buildings/FacilityPosition";
import {NetNavigation, WayPoint} from "../../lty/plugin/NetNavigation/NetNavigation";
import {Vector3} from "../../lty/class/Vector3";
import {Time} from "../../lty/class/Time";
import {MiscUtils} from "../../lty/class/MiscUtils";
import MesManagerClass from "../../dMyGame/Manager/MesManager";
import {EEventScene} from "../../dMyGame/EventEnum/EEventScene";
import {UIMediatorEventPanel} from "../../UI/UIMediatorEventPanel";
import {PeopleEvent} from "../Misc/PeopleEvent";
import {EEventUI} from "../../dMyGame/EventEnum/EEventUI";
import {SlotUIDConst} from "../../dMyGame/_config/SlotUIDConst";
import {EffectManager} from "../EffectManager";
import {EffectName} from "../../dMyGame/ResList/PrefabNames";
import {FunctionID} from "../../Data/FunctionID";
import {BuildingManager} from "../BuildingManager";
import {IWorkerData} from "./IWorkerData";
import {Random} from "../../lty/class/Random";
import {MiscDataProxy} from "../../Data/MiscDataProxy";

//机场内工作人员，打扫卫生
export class Worker extends LayaBehaviour {
    //是否使用带有动画的Poly模型
    public isPoly = false;
    private _toTarget: Vector3 = Vector3.zero;
    private _wpIndex: number = 0;
    private _wayPoints: WayPoint[];
    private _transitionFunc: () => void;
    private OnReach: () => void;
    private _transition: string;
    private moveSpeed: number = 1.5;
    private facPos: FacilityPosition;
    private _target: Laya.Vector3;
    private _runtimeAnimator: Laya.Animator;
    //名字
    Name: string;

    //是否已经外出
    private isOut = false;
    private _toolCar: Laya.Sprite3D;
    //正在维修
    private _isRepairing = false;
    //事件id
    private eventID: PeopleEvent = PeopleEvent.Repair;
    //维修时间
    private repairTime = 40;
    //打扫时间
    private sweepTime: number;
    //小费技能
    private moneySkill = false;
    private moneySeed = 0;
    //获取的小费
    private _runtimeMoney = 0;
    //捡钻石技能
    private diamondSkill = false;
    private diamondSeed = 0;
    //捡到的钻石
    private _runtimeDiamonds = 0;


    //工具车
    private get toolCar() {
        if (this._toolCar == null) {
            this._toolCar = this.sprite3D.getChildByName("Car") as Laya.Sprite3D;
        }
        return this._toolCar;
    }

    private _toolMop: Laya.Sprite3D;

    //工具扫把，拖把
    private get toolMop() {
        if (this._toolMop == null) {
            this._toolMop = this.DeepFind("Mop", this.owner) as Laya.Sprite3D;
        }
        return this._toolMop;
    }

    private get _animator() {
        if (this._runtimeAnimator == null) {
            this._runtimeAnimator = this.owner.getComponent(Laya.Animator);
        }
        return this._runtimeAnimator;
    }

    private get _agent() {
        return NetNavigation.NetMap.instance;
    }

    //是否有修理目标
    get hasFacTarget() {
        return this.facPos != null;
    }

    onAwake() {
        MesManagerClass.instance.onUI(EEventUI.OnClickTip, this, this.OnClickTip);
        MesManagerClass.instance.onUI(EEventUI.RepairProgressComplete, this, this.OnRepairComplete);
        this.PlayAnimationByState(StateName.Sit);
    }

    public SetRepairTime(value: number) {
        this.repairTime = value;
    }

    //运行时的动画状态
    private _runtimeAnimatorStateName: string = null;

    //通过名字播放动画
    private PlayAnimationByState(state: StateName) {
        try {
            if (this._runtimeAnimatorStateName != state) {
                let s: string = state;
                switch (state) {
                    case StateName.CaZhuoZi:
                    case StateName.DaSao:
                        s = "clean"
                        break;
                }
                this._animator.play(s);
                this._runtimeAnimatorStateName = state;
                this.ShowToolByState(state);
            }
        } catch (e) {
            console.error("播放动画失败：", state);
            throw e;
        }
    }

    //通过状态显示不同的工具
    private ShowToolByState(state: StateName) {
        //关闭显示对应的工具
        switch (state) {
            case StateName.DaSao:
                this.toolMop.active = true;
                this.toolCar.active = false;
                break;
            case StateName.Car:
                this.toolMop.active = false;
                this.toolCar.active = true;
                break;
            case StateName.CaZhuoZi:
                this.toolMop.active = false;
                this.toolCar.active = false;
                break;
            default:
                this.toolMop.active = false;
                this.toolCar.active = false;
                break;
        }
    }

    //设置修理目标
    SetFacTarget(facPos: FacilityPosition, exitPos: Laya.Vector3) {
        this.facPos = facPos;
        if (this.isOut) {
            this.MoveToTarget();
        } else {
            this.MoveToExit(exitPos);
        }
        this.isOut = true;
    }

    //移动到出口位置拿工具
    private MoveToExit(exitPos: Laya.Vector3) {
        console.log("移动到出口位置拿工具")
        //拿工具的动画
        let wps = this._agent.TrySearchByNearestPos(this.transform.position, exitPos);
        this.SetWayPoints(wps).OnComplete(this.GetTool, "GetTool");
        this.PlayAnimationByState(StateName.Walk);
    }

    private GetTool() {
        this.PlayAnimationByState(StateName.Idle);
        //TODO 播放拿工具的动画
        console.log("维修工变出工具！")
        BuildingManager.instance.TryOpenDoor(this.transform.position);
        Laya.timer.once(500, this, this.MoveToTarget);
    }

    //向目标移动，如果目标建筑在厕所内，就请求开门，然后再进入
    private MoveToTarget() {
        if (this.facPos==null || this.facPos.belonger==null) {
            console.error(this.facPos);
        }
        this.PlayAnimationByState(StateName.Car);
        if (this.facPos.belonger.functionID == FunctionID.FemaleToilte || this.facPos.belonger.functionID == FunctionID.MaleToilet) {
            let entrance = this.facPos.belonger.entrance;
            let wps = this._agent.TrySearchByNearestPos(this.transform.position, entrance.transform.position);
            this.SetWayPoints(wps).OnComplete(this.WaitOpenDoor, "WaitOpenDoor");
        } else {
            let wps = this._agent.TrySearchByNearestPos(this.transform.position, this.facPos.sprite.transform.position);
            this.SetWayPoints(wps).OnComplete(this.DoRepair, "DoRepair");
            this.PlayAnimationByState(StateName.Car);
        }
    }

    private WaitOpenDoor() {
        BuildingManager.instance.TryOpenDoor(this.transform.position);
        Laya.timer.once(600, this, () => {
            if (this.facPos==null) {
                return;
            }
            let wps = this._agent.TrySearchByNearestPos(this.transform.position, this.facPos.sprite.transform.position);
            this.SetWayPoints(wps).OnComplete(this.DoRepair, "DoRepair");
            this.PlayAnimationByState(StateName.Car);
        })
    }


    public SetWayPoints(wps: WayPoint[]) {
        if (wps && wps.length > 0) {
            this._wayPoints = wps;
            this._target = this._wayPoints[0].position;
            this._wpIndex = 0;
        } else {
            console.log("路径点数据为空或者数量为0")
        }
        return this;
    }

    private _step: Vector3 = new Vector3();

    onUpdate() {
        //技能显示判定
        if (this._runtimeMoney > 0) {
            UIMediatorEventPanel.instance.EventDistribute(this.transform.position, this.id, PeopleEvent.Money, 0);
        }
        if (this._runtimeDiamonds > 0) {
            UIMediatorEventPanel.instance.EventDistribute(this.transform.position, this.id, PeopleEvent.Diamond, 0);
        }
        if (this.hasFacTarget) {
            UIMediatorEventPanel.instance.EventDistribute(this.facPos.runtimeFirstChild.transform.position, this.facPos.sprite.id, this.facPos.eventID,
                this.facPos.eventID == PeopleEvent.Repair ? this.repairTime : this.sweepTime);
        }
        if (this._target) {
            Laya.Vector3.subtract(this._target, this.transform.position, this._toTarget);
            if (this._toTarget.SqrMagnitude < 0.01) {
                this._wpIndex++;
                if (this._wpIndex >= this._wayPoints.length) {
                    this._target = null;
                    if (this._transitionFunc) {
                        console.log("执行转换：", this._transition);
                        this.OnReach = this._transitionFunc;
                        this.OnReach();
                        if (this.OnReach == this._transitionFunc) {
                            //在回调中设置了转换方法，如果还是上一个方法就清空回调
                            this._transition = null;
                        }
                    } else {
                        console.error("抵达没有回调,或者切换方法为空:", this._transition);
                    }
                } else {
                    this._target = this._wayPoints[this._wpIndex].position;
                }
            } else {
                Laya.Vector3.normalize(this._toTarget, this._step);
                Laya.Vector3.scale(this._step, this.moveSpeed * Time.deltaTime, this._step);
                this.transform.localRotationEulerY = MiscUtils.updateRotation(this._step) - 45;
                Laya.Vector3.add(this.transform.position, this._step, this.transform.position);
                this.transform.position = this.transform.position;
            }
        }
    }

    //移动完毕的回调
    private OnComplete(func: () => void, transition?: string) {
        this._transitionFunc = func;
        this._transition = transition;
    }

    //抵达设备位置，进行维修
    private DoRepair() {
        if (this.facPos.slotData.uid == SlotUIDConst.data.slot_mt_config) {
            this.PlayAnimationByState(StateName.DaSao);
        } else {
            this.PlayAnimationByState(StateName.CaZhuoZi);
        }
        EffectManager.ClearAll(this.facPos.sprite.id);
        //修正位置位置
        if (this.facPos.belonger.functionID == FunctionID.MaleToilet || this.facPos.belonger.functionID == FunctionID.FemaleToilte) {
            let fwd = new Laya.Vector3();
            this.facPos.sprite.transform.getForward(fwd);
            Laya.Vector3.scale(fwd, 0.5, fwd);
            fwd.y = 0;
            Laya.Vector3.subtract(this.facPos.sprite.transform.position, fwd, fwd);
            this.transform.position = fwd;
        }
        UIMediatorEventPanel.instance.EventDistribute(this.facPos.sprite.transform.position, this.facPos.sprite.id, PeopleEvent.StartRepair, 0);
        this._isRepairing = true;
    }

    //维修完毕
    private OnRepairComplete(id: number) {
        if (this.hasFacTarget) {
            if (this.facPos.sprite.id == id) {
                Laya.timer.clearAll(this);
                //技能,小费和钻石
                if (this.moneySkill) {
                    this._runtimeMoney += Random.RangeInt(1, this.moneySeed);
                }
                if (this.diamondSkill) {
                    this._runtimeDiamonds += Random.RangeInt(1, this.diamondSeed);
                }
                if(this.facPos.slotData.uid == SlotUIDConst.data.slot_mt_config){
                    //如果在厕所内就请求开门
                    BuildingManager.instance.TryOpenDoor(this.transform.position);
                }
                this._isRepairing = false;
                let temp = this.facPos;
                let id = this.facPos.sprite.id;
                let e = EffectManager.PlayEffect(EffectName.Cleaning, id, 2000);
                this.facPos.runtimeFirstChild.addChild(e);
                e.transform.localPosition = Vector3.zero;
                temp.SetBreakState(false);
                this.facPos = null;
                MesManagerClass.instance.event3D(EEventScene.FacilityRepairComplete, [temp, this]);
                if (this.isDisposed) {
                    this.sprite3D.destroy(true);
                }
            }
        }
    }

    private _runtimeChair: Laya.Sprite3D;

    //返回座位
    ReturnChairPos(chair: Laya.Sprite3D) {
        if (chair == null) {
            throw Error("获取椅子失败，维修工无法返回休息椅子！")
        }
        let wps = null;
        this._runtimeChair = chair;
        this.PlayAnimationByState(StateName.Car);
        let entrance = BuildingManager.instance.GetBuildingEntrance(FunctionID.CleanRoom);
        wps = this._agent.TrySearchByNearestPos(this.transform.position, entrance.transform.position);
        this.SetWayPoints(wps).OnComplete(() => {
            //到达入口 等待开门
            BuildingManager.instance.TryOpenDoor(this.transform.position);
            Laya.timer.once(600, this, () => {
                wps = this._agent.TrySearchByNearestPos(this.transform.position, chair.transform.position);
                this.SetWayPoints(wps).OnComplete(() => {
                    this.isOut = false;
                    this.PlayAnimationByState(StateName.Sit);
                    this.transform.position = this._runtimeChair.transform.position;
                    this.transform.rotationEuler = this._runtimeChair.transform.rotationEuler;

                });
            })
        })

    }

    //初始化
    Init(data: IWorkerData) {
        this.Name = data.Name;
        this.moveSpeed = data.MoveSpeed;
        this.repairTime = 40;
        this.sweepTime = 25;
        if (data.Skill && data.Skill.length > 0) {
            for (let skill of data.Skill) {
                switch (skill.Type) {
                    case 0:
                        //修理加速
                        this.repairTime = this.repairTime * (1 - skill.parameter[0]);
                        break;
                    case  1:
                        //清洁打扫加速
                        this.sweepTime = this.sweepTime * (1 - skill.parameter[0]);
                        break;
                    case 2:
                        //移动加速
                        this.moveSpeed = this.moveSpeed * (1 + skill.parameter[0]);
                        break;
                    case 3:
                        //小费技能
                        this.moneySkill = true;
                        this.moneySeed = skill.parameter[0];
                        break;
                    case 4:
                        //捡钻石技能
                        this.diamondSkill = true;
                        this.diamondSeed = skill.parameter[0];
                        break;
                    default:
                        console.log("没有技能")
                        break;

                }
            }
        }
    }

    public isDisposed = false;

    //删除这个雇员,如果雇员有修理目标，则在修理完成后自动销毁
    Disposed() {
        if (this.hasFacTarget) {
            this.isDisposed = true;
        } else {
            this.sprite3D.destroy(true);
        }
    }

    //点击收取小费
    private OnClickTip(id: number) {
        if (id == this.id) {
            if (this.moneySkill) {
                console.log("收取小费" + this._runtimeMoney);
                MiscDataProxy.Money+= this._runtimeMoney;
                this._runtimeMoney = 0;
            }
            if (this.diamondSkill) {
                console.log("收取钻石" + this._runtimeDiamonds);
                MiscDataProxy.Diamond+= this._runtimeDiamonds;
                this._runtimeDiamonds = 0;
            }
        }
    }

    //观看广告立即修复设备，回收特效和按钮
    RepairCompleteNow() {
        EffectManager.ClearAll(this.facPos.sprite.id);
        UIMediatorEventPanel.instance.RecycleBtn(this.facPos.sprite.id, this.facPos.eventID);
    }
}

//动画状态
enum StateName {
    Walk = "zou",
    CaZhuoZi = "clean",
    DaSao = "clean",
    Sit = "daiji",
    Car = "tuiche",
    Idle = "daiji",
}