import BaseUIMediator from "../../aTGame/UI/FGUI/BaseUIMediator";
import UIManagerProxy from "../../dMyGame/Manager/Proxy/UIManagerProxy";
import { EUI } from "../../dMyGame/Enum/EUI";
import { MiscDataProxy } from "../../Data/MiscDataProxy";
import { ADUtility, UIMediatorMain } from "../UIMediatorMain";
import FGUI_PGameMainSpeedUp from "../../FGUI/Main/FGUI_PGameMainSpeedUp";
import PlatformManagerProxy from "../../aTGame/Platform/PlatformManagerProxy";
import { UIFacade } from "../UIFacade";
import MesManagerClass from "../../dMyGame/Manager/MesManager";
import { EEventScene } from "../../dMyGame/EventEnum/EEventScene";
import { EEventUI } from "../../dMyGame/EventEnum/EEventUI";
import { Audio } from "../../GameComponent/Audio";
import { Uma, UmaEvent } from "../../Uma";
import { UIMediatorGameAd } from "../UIMediatorGameAd";
import VivoBannerAd from "src/dMyGame/vivoPlatform/VivoBannerAd";
import VIVOData from "src/cFrameBridge/Platform/Data/VIVOData";

export class UIMediatorSpeedUp extends BaseUIMediator<FGUI_PGameMainSpeedUp> {
    private static m_instance: UIMediatorSpeedUp;

    /** 单例 */
    public static get instance(): UIMediatorSpeedUp {
        if (!this.m_instance) {
            this.m_instance = new UIMediatorSpeedUp();
            this.m_instance._classDefine = FGUI_PGameMainSpeedUp;
        }
        return this.m_instance;
    }

    private constructor() {
        super();
    }

    protected _OnShow() {

        this.ui.m_bg.m_btn_close.onClick(this, this.Close);
        this.ui.m_btn_thanks.onClick(this, this.Close)
        this.ui.m_btn_start.onClick(this, this.OnWatchAd);
        Uma.trackEvent(UmaEvent.WorldEvent, { "speedUp": "UI" });
        /**弹原生广告 */
        UIMediatorGameAd.instance.ShowNativeAd();

        /**Baanner */
        let vivoObj = new VIVOData();
        VivoBannerAd.instance.showBannerFun(vivoObj);
    }

    Close() {
        UIManagerProxy.instance.setUIState([{
            typeIndex: EUI.UIMediatorSpeedUp,
            state: false
        }], false)

        UIMediatorMain.instance.RemoveADUtility(ADUtility.SpeedUp);

        VivoBannerAd.instance.DestroyAd();

    }

    OnWatchAd() {
        PlatformManagerProxy.instance.PlatformInstance.ShowRewardVideoAd(
            Laya.Handler.create(this, () => {
                Audio.PlayAudio();
                MiscDataProxy.speedUpTime = 2 * 60 * 1000;
                Uma.trackEvent(UmaEvent.WorldEvtAD, { "speedUp": "AD" });
                this.Close();
            }),
            Laya.Handler.create(this, () => {
                PlatformManagerProxy.instance.PlatformInstance.ShowToast("未看完广告")
            }))
    }
}