import BaseUIMediator from "../aTGame/UI/FGUI/BaseUIMediator";
import {EUI} from "../dMyGame/Enum/EUI";
import UIManagerProxy from "../dMyGame/Manager/Proxy/UIManagerProxy";
import FGUI_PGameToiletPanel from "../FGUI/BuildUIPanel/FGUI_PGameToiletPanel";
import FGUI_commButton from "../FGUI/BuildUIPanel/FGUI_commButton";
import UISlotConfigProxy from "../Config/UISlotConfigProxy";
import {UIFacade} from "./UIFacade";
import {NullReferenceException} from "../lty/Exception/NullReferenceException";
import {SlotConfigProxy} from "../Config/SlotConfigProxy";
import MesManagerClass from "../dMyGame/Manager/MesManager";
import {EEventUI} from "../dMyGame/EventEnum/EEventUI";
import {UpdateSlotEventData} from "../Data/UpdateEventData";
import Building from "../GameComponent/Buildings/api/Building";
import {FunctionID} from "../Data/FunctionID";
import {CleanRoom} from "../GameComponent/Buildings/CleanRoom";
import {WaitAera} from "../GameComponent/Buildings/WaitAera";
import {MiscDataProxy} from "../Data/MiscDataProxy";
import FGUI_staffSkillicon from "../FGUI/BuildUIPanel/FGUI_staffSkillicon";
import FGUI_Employee from "../FGUI/BuildUIPanel/FGUI_Employee";
import {EmployeeDataPool} from "../Config/EmployeeDataPool";
import {UIMediatorStaffInfoPanel} from "./UIMediatorStaffInfoPanel";
import {IWorkerData} from "../GameComponent/Worker/IWorkerData";
import {BuildDataProxy} from "../Data/BuildDataProxy";
import {StringName} from "../Enum/StringName";
import {GuideDataProxy} from "../Data/GuideDataProxy";
import {EUILayer} from "../aTGame/UI/FGUI/EUILayer";
import {MoneyConverter} from "../zml/MoneyConverter";
import PlatformManagerProxy from "../aTGame/Platform/PlatformManagerProxy";
import {Audio} from "../GameComponent/Audio";
import {Uma, UmaEvent} from "../Uma";

export class UIMediatorStaff extends BaseUIMediator<FGUI_PGameToiletPanel> {
    private static m_instance: UIMediatorStaff;

    private isGuide: boolean = false;
    protected _layer: EUILayer = EUILayer.ToiletPanel;

    /** 单例 */
    public static get instance(): UIMediatorStaff {
        if (!this.m_instance) {
            this.m_instance = new UIMediatorStaff();
            this.m_instance._classDefine = FGUI_PGameToiletPanel;
        }
        return this.m_instance;
    }

    private constructor() {
        super();
    }

    //当前点击选择的建筑
    private get _currBuilding() {
        return UIFacade.instance.building;
    }

    //中部面板
    private get midPanel() {
        return this.ui.m_midPanel;
    }

    public Close() {
        UIManagerProxy.instance.setUIState(
            [
                {
                    typeIndex: EUI.Staff,
                    state: false,
                },
            ],
            false
        );
        //Next Guide
        if (this.isGuide) {
            MesManagerClass.instance.eventUI(EEventUI.GuideToNext);
            console.log("Close Next Guide");
        }
    }

    public GuideClose() {
        UIManagerProxy.instance.setUIState(
            [
                {
                    typeIndex: EUI.Staff,
                    state: false,
                },
            ],
            false
        );
    }

    /**当前被选中的slot*/
    private get _runtimeSelectedSlot() {
        return this._currBuilding.slots[this._currentSelectSlotIndex];
    }

    /**当前被选中的slot的index*/
    private _currentSelectSlotIndex = 0;

    _OnShow() {
        super.Show();
        this.ui.m_close.onClick(this, this.Close);
        this.UpdateTitle();
        this.ShowStaffOptionPanel();
        this.UpdateTopPanel();
        this.RenderSlotList();
        this.midPanel.m_btn_up.onClick(this, this.OnClickUpdateBtn);
        this.midPanel.m_btn_buy.onClick(this, this.OnClickUpdateBtn);
        MesManagerClass.instance.onUI(
            EEventUI.UpdateSlotComplete,
            this,
            this.OnUpdateSlotComplete
        );
        MesManagerClass.instance.onUI(
            EEventUI.RenderMarketStaffList,
            this,
            this.RenderStaffList
        );
        MesManagerClass.instance.onUI(
            EEventUI.ShowCleanRoomStaff,
            this,
            this.ShowCleanRoomStaff
        );
        // if (this._currBuilding.buildingName == "cleanRoom" && GuideDataProxy.instance.Data.isCompleteGuide2 == false) {
        //     console.log("打开了CleanRoom")
        //     MesManager.instance.eventUI(EEventUI.OtherGuide, [2]);
        // }
        // if (this._currBuilding.buildingName == "waterRoom" && GuideDataProxy.instance.Data.isCompleteGuide3 == false) {
        //     console.log("打开了waterRoom")
        //     MesManager.instance.eventUI(EEventUI.OtherGuide, [3]);
        // }
        //引导
        this.isGuide = !GuideDataProxy.IsCompleteAllGuide;

        this.ui.m_btn_employee.on(Laya.Event.CLICK, this, () => {
            if (this.isGuide) {
                MesManagerClass.instance.eventUI(EEventUI.GuideToNext);
                //console.log("btn_employee:下一步");
            }
        });

        if (GuideDataProxy.IsCompleteAllGuide) {
            (this._currBuilding as Building).OnClick();
        }
    }

    _OnHide() {
        this._currentSelectSlotIndex = 0;
        super.Hide();
        MesManagerClass.instance.offUI(
            EEventUI.UpdateSlotComplete,
            this,
            this.RenderSlotList
        );
        MesManagerClass.instance.offUI(
            EEventUI.RenderMarketStaffList,
            this,
            this.RenderStaffList
        );
        if (GuideDataProxy.IsCompleteAllGuide) {
            (this._currBuilding as Building).OffClick();
        }
    }

    //设备升级的回调，刷新ui显示
    private OnUpdateSlotComplete() {
        this.RenderSlotList();
        this.UpdateTopPanel();
    }

    //渲染设备列表
    private RenderSlotList() {
        if (this._currBuilding) {
            let slotsData = this._currBuilding.slots;
            if (slotsData && slotsData.length > 0) {
                let margin = new fgui.Margin();
                margin.top = 20;
                margin.left = 20;
                this.ui.m_slotList.margin = margin;
                this.ui.m_slotList.itemRenderer = Laya.Handler.create(
                    this,
                    (index: number, item: FGUI_commButton) => {
                        let data = slotsData[index];
                        item.m_image.url =
                            UISlotConfigProxy.instance.dataList[data.uid].url;
                        let maxLevel =
                            UISlotConfigProxy.instance.dataList[data.uid].maxLevel;
                        item.m_bg.selectedIndex = data.level <= 0 ? 1 : 0; //背景颜色
                        if (data.level <= 0) {
                            //左上角表
                            item.m_level.selectedIndex = 0;
                            //左下角标
                            item.m_state.selectedIndex = 0;
                        } else if (data.level < maxLevel) {
                            item.m_level.selectedIndex = 1;
                            item.m_state.selectedIndex = 1;
                            item.m_levelLable.m_title.text = data.level.toString();
                        }
                        if (data.level >= maxLevel) {
                            item.m_level.selectedIndex = 2;
                            item.m_state.selectedIndex = 2;
                        }
                        //左下角标，锁定的图标
                        if (data.isBreaking) {
                            item.m_state.selectedIndex = 3;
                        }
                        item.offClick(this, this.SlotListButtonEvent);
                        item.onClick(this, this.SlotListButtonEvent, [index]);
                    },
                    null,
                    false
                );
                //根据建造等级开放小便池的升级插槽
                if (this._currBuilding.functionID == FunctionID.MalePee) {
                    let bLevel = BuildDataProxy.instance.GetBuildDataByName(
                        StringName.maleToilet
                    ).count;
                    this.ui.m_slotList.numItems = bLevel * 2;
                } else {
                    this.ui.m_slotList.numItems = slotsData.length;
                }
            } else {
                console.error("建筑slot数据为空：", this._currBuilding);
            }
        } else {
            throw new NullReferenceException("当前所选建筑为空");
        }
        if (this._currentSelectSlotIndex < 0) {
            this._currentSelectSlotIndex = 0;
        }
        this.UpdateMidPanel(this._currentSelectSlotIndex);
    }

    private SlotListButtonEvent(index: number) {
        this.UpdateMidPanel(index);
        if (this.isGuide) {
            MesManagerClass.instance.eventUI(EEventUI.GuideToNext);
            // console.log("列表按钮发送下一步指令");
        }
    }

    //显示雇员设置界面
    private ShowStaffOptionPanel() {
        this.ui.m_cleanRoomSwitch.selectedIndex =
            this._currBuilding.functionID == FunctionID.CleanRoom ? 1 : 0;
        if (this.ui.m_cleanRoomSwitch.selectedIndex == 1) {
            this.RenderStaffList();
        }
    }

    //渲染雇员列表
    private RenderStaffList() {
        let list = MiscDataProxy.instance.Data.staffList;
        let size = MiscDataProxy.instance.Data.staffSize;
        this.ui.m_staffList.itemRenderer = Laya.Handler.create(
            this,
            (index: number, item: FGUI_Employee) => {
                if (index < size) {
                    let data = list[index];
                    if (data) {
                        item.m_state.selectedIndex = data.Gender == 0 ? 1 : 2;
                        item.m_Titile.text = data.Name;
                        item.m_img_starNum.fillAmount = data.Level / 4;
                        if (data.Skill && data.Skill.length > 0) {
                            item.m_list_skillicon.itemRenderer = Laya.Handler.create(
                                this,
                                (skillIndex: number, icon: FGUI_staffSkillicon) => {
                                    icon.m_sprite.url = EmployeeDataPool.GetIconSprite(
                                        data.Skill[skillIndex].Type
                                    );
                                },
                                null,
                                false
                            );
                            item.m_list_skillicon.numItems = data.Skill.length;
                        }
                        //替换按钮
                        item.m_sprite.url = EmployeeDataPool.GetEmployeeSprite(data);
                        item.m_sprite.onClick(this, this.OpenStaffInfoPanel, [data]);
                    } else {
                        //此索引没有数据，等待加人
                        item.m_state.selectedIndex = 3;
                        item.m_Titile.text = "";
                        item.m_btn_AddStaff.onClick(this, this.OpenStaffMarket, ["Open"]);
                    }
                    //取消增加空位的广告
                    item.m_btn_AD.offClick(this, this.OnWatchADAddStaffPosNum);
                } else {
                    //没有空位了，扩招
                    item.m_state.selectedIndex = 0;
                    item.m_Titile.text = "扩招";
                    console.log("注册事件");
                    item.m_btn_AD.onClick(this, this.OnWatchADAddStaffPosNum);
                }
            },
            null,
            false
        );
        this.ui.m_staffList.numItems = 8;
    }

    //观看广告增加雇员的可用数量
    public OnWatchADAddStaffPosNum() {
        console.log("观看广告增加雇员的可用数量");
        PlatformManagerProxy.instance.PlatformInstance.ShowRewardVideoAd(Laya.Handler.create(this, () => {
                Audio.PlayAudio();
                MiscDataProxy.instance.AddStaffPosNum();
                Uma.trackEvent(UmaEvent.AddStaffPosition);
                if (this.isShow) {
                    this.RenderStaffList();
                }
            }),
            Laya.Handler.create(this, () => {
                PlatformManagerProxy.instance.PlatformInstance.ShowToast("未看完广告")
            }))
    }

    //打开雇员市场
    OpenStaffMarket() {
        UIManagerProxy.instance.setUIState(
            [{typeIndex: EUI.StaffMarket, state: true}],
            false
        );
        if (this.isGuide) {
            MesManagerClass.instance.eventUI(EEventUI.GuideToNext);
            // console.log("staff Next Guide");
        }
    }

    OpenStaffInfoPanel(data: IWorkerData) {
        UIMediatorStaffInfoPanel.instance.ShowPanel(data);
    }

    //更新中部面板
    private UpdateMidPanel(index: number) {
        this.midPanel.m_txt_needWater.visible = false;
        this._currentSelectSlotIndex = index;
        let slotsData = this._currBuilding.slots;
        let data = slotsData[index];
        if (data == null) {
            throw {one: slotsData, two: index};
        }
        let level = data.level;
        if (level == null) {
            throw data;
        }
        let slotUID = data.uid;
        let maxLevel = UISlotConfigProxy.instance.dataList[slotUID].maxLevel;
        this.ui.m_midPanel.m_picture.url =
            UISlotConfigProxy.instance.dataList[slotUID].url;
        this.midPanel.m_leftLevel.text = level.toString();
        this.midPanel.m_levelProgress.text =
            level.toString() + "/" + maxLevel.toString();
        this.midPanel.m_levelSlider.fillAmount = level / maxLevel;
        let slotInfos = SlotConfigProxy.instance.GetSlotConfigByUID(slotUID);
        let nextLevel = level == 0 ? 1 : level + 1;
        nextLevel = nextLevel > maxLevel ? maxLevel : nextLevel;
        let nextLevelSlotInfo = slotInfos[nextLevel];
        //更新buff状态
        let currLevelSlotInfo = slotInfos[level];
        if (currLevelSlotInfo || nextLevelSlotInfo) {
            currLevelSlotInfo = currLevelSlotInfo ? currLevelSlotInfo : {};
            nextLevelSlotInfo = nextLevelSlotInfo ? nextLevelSlotInfo : {};
            this.UpdateBuffStateShow(
                level,
                nextLevel,
                currLevelSlotInfo,
                nextLevelSlotInfo
            );
        } else {
            console.error("获取设备等级相关信息失败！", slotsData);
        }
        this.midPanel.m_btn_up.grayed = false;
        this.midPanel.m_btn_up.touchable = true;
        if (nextLevelSlotInfo) {
            this.midPanel.m_text_mid_title.text = nextLevelSlotInfo.Name;
            this.midPanel.m_text_mid_title_desc.text = nextLevelSlotInfo.Describe;
            if (level >= maxLevel) {
                this.midPanel.m_upState.selectedIndex = 2;
            } else {
                if (nextLevelSlotInfo.updateCostCoin) {
                    this.midPanel.m_upState.selectedIndex = 0;
                    this.midPanel.m_btn_up.m_text_upCast.text = MoneyConverter.DoConverter(
                        nextLevelSlotInfo.updateCostCoin
                    );
                    //金钱不足关闭按钮
                    this.midPanel.m_btn_up.grayed = MiscDataProxy.Money < nextLevelSlotInfo.updateCostCoin;
                    this.midPanel.m_btn_up.touchable = MiscDataProxy.Money >= nextLevelSlotInfo.updateCostCoin;
                    //水资源不足关闭按钮 ，显示红色提示
                    let maxWater = MiscDataProxy.MaxWater;
                    let water = MiscDataProxy.Water;
                    let currWater = currLevelSlotInfo.updateCostWater ? currLevelSlotInfo.updateCostWater : 0;
                    let nextWater = nextLevelSlotInfo.updateCostWater ? nextLevelSlotInfo.updateCostWater : 0;
                    let lastWater = maxWater - (water - currWater + nextWater);
                    // console.log(lastWater);
                    if (lastWater < 0 && this.midPanel.m_btn_up.touchable && this._currBuilding.functionID != FunctionID.WaterRoom) {
                        this.midPanel.m_btn_up.grayed = true;
                        this.midPanel.m_btn_up.touchable = false;
                        this.midPanel.m_txt_needWater.visible = true;
                    }
                } else if (nextLevelSlotInfo.updataCostDiamond) {
                    this.midPanel.m_upState.selectedIndex = 1;
                    this.midPanel.m_btn_buy.m_text_price.text = nextLevelSlotInfo.updataCostDiamond.toString();
                    this.midPanel.m_btn_buy.m_btn_bg.grayed = MiscDataProxy.Diamond < nextLevelSlotInfo.updataCostDiamond;
                    this.midPanel.m_btn_buy.touchable = MiscDataProxy.Diamond >= nextLevelSlotInfo.updataCostDiamond;
                } else {
                    console.error("配置表单元格名字错误，检查", data.name, nextLevelSlotInfo);
                }
            }
        }

        if (index == this._currentSelectSlotIndex) {
            //刷新损坏显示
            if (this.midPanel.m_btn_up.touchable) {
                if (data.isBreaking) {
                    this.midPanel.m_btn_up.grayed = true;
                    this.midPanel.m_btn_up.touchable = false;
                } else {
                    this.midPanel.m_btn_up.grayed = false;
                    this.midPanel.m_btn_up.touchable = true;
                }
            }
        }
    }

    //刷新增益和下一级增益buff的显示
    private UpdateBuffStateShow(
        level: number,
        nextLevel: number,
        currLevelInfo,
        nextLevelInfo
    ) {
        //更新消耗水的状态
        if (currLevelInfo.updateCostWater || nextLevelInfo.updateCostWater) {
            this.midPanel.m_group_water.visible = true;
            this.midPanel.m_text_water.text =
                currLevelInfo.updateCostWater != null
                    ? currLevelInfo.updateCostWater.toString()
                    : "0";
            //下一水量的增减
            let currWater =
                currLevelInfo.updateCostWater != null
                    ? currLevelInfo.updateCostWater
                    : 0;
            let nextWater =
                nextLevelInfo.updateCostWater != null
                    ? nextLevelInfo.updateCostWater
                    : 0;
            let water = currWater - nextWater;
            this.UpdateBuffAddState(
                level,
                nextLevel,
                this.midPanel.m_text_water_WaterAdd,
                -water
            );
        } else {
            this.midPanel.m_group_water.visible = false;
        }
        //收入
        if (currLevelInfo.income != null || nextLevelInfo.income != null) {
            this.midPanel.m_bottomState.selectedIndex = BottomState.Money;
            this.midPanel.m_text_money.text =
                currLevelInfo.income != null ? currLevelInfo.income.toString() : "0";
            this.UpdateBuffAddState(
                level,
                nextLevel,
                this.midPanel.m_text_money_MoneyAdd,
                nextLevelInfo.income
            );
        } else {
            //使用时间减少
            if (currLevelInfo.Buff != null || nextLevelInfo.Buff != null) {
                this.midPanel.m_bottomState.selectedIndex = BottomState.time;
                this.midPanel.m_text_time.text =
                    currLevelInfo.Buff != null ? currLevelInfo.Buff.toString() : "0";
                this.UpdateBuffAddState(
                    level,
                    nextLevel,
                    this.midPanel.m_text_time_timeSub,
                    nextLevelInfo.Buff
                );
            }
            //打扫时间
            else if (
                currLevelInfo.sweep_time != null ||
                nextLevelInfo.sweep_time != null
            ) {
                this.midPanel.m_bottomState.selectedIndex = BottomState.clean;
                this.midPanel.m_text_clean_Money.text =
                    currLevelInfo.sweep_time != null
                        ? currLevelInfo.sweep_time.toString()
                        : "0";
                this.UpdateBuffAddState(
                    level,
                    nextLevel,
                    this.midPanel.m_text_clean_MoneyAdd,
                    nextLevelInfo.sweep_time
                );
            }
            //排队人数增加
            else if (currLevelInfo.length != null || nextLevelInfo.length != null) {
                this.midPanel.m_bottomState.selectedIndex = BottomState.queue;
                this.midPanel.m_text_volume.text =
                    currLevelInfo.length != null ? currLevelInfo.length.toString() : "0";
                this.UpdateBuffAddState(
                    level,
                    nextLevel,
                    this.midPanel.m_text_volume_volumeAdd,
                    nextLevelInfo.length
                );
            }
            //水循环处理
            else if (
                currLevelInfo.water_add != null ||
                nextLevelInfo.water_add != null
            ) {
                this.midPanel.m_group_water.visible = true;
                this.midPanel.m_text_water_WaterAdd.visible = false;
                this.midPanel.m_text_water.text =
                    currLevelInfo.water_add != null
                        ? currLevelInfo.water_add.toString()
                        : "0";
                this.midPanel.m_text_money.text = "0";
                this.midPanel.m_text_money_MoneyAdd.text = "";
            }
            //修理时间
            else if (
                currLevelInfo.repair_time != null ||
                nextLevelInfo.repair_time != null
            ) {
                this.midPanel.m_bottomState.selectedIndex = BottomState.repair;
                this.midPanel.m_text_replace_time.text =
                    currLevelInfo.repair_time != null
                        ? currLevelInfo.repair_time.toString()
                        : "0";
                this.UpdateBuffAddState(
                    level,
                    nextLevel,
                    this.midPanel.m_text_replace_timeSub,
                    nextLevelInfo.repair_time
                );
            } else {
                console.log(nextLevelInfo.sweep_time);
                //TODO:其他buff的显示
                console.log("尚未完成..");
            }
        }
    }

    private OnClickUpdateBtn() {
        let _uid = this._runtimeSelectedSlot.uid;
        let mes: UpdateSlotEventData = {
            slotIndex: this._currentSelectSlotIndex,
            building: this._currBuilding,
            uid: _uid,
        };
        MesManagerClass.instance.eventUI(EEventUI.UpdateSlotData, mes);
        if (this.isGuide) {
            console.log("UpdateBtn Next Guide");
            MesManagerClass.instance.eventUI(EEventUI.GuideToNext);
        }
    }

    //更新buff图标的状态
    private UpdateBuffAddState(
        level: number,
        maxLevel: number,
        text: fairygui.GTextField,
        value: number
    ) {
        if (level < maxLevel) {
            text.visible = true;
            text.text = value >= 0 ? "+" + value.toString() : value.toString();
        } else {
            text.visible = false;
        }
    }

    //更新顶部面板显示建筑统计信息
    private UpdateTopPanel() {
        let building = this._currBuilding as Building;
        let funcID = this._currBuilding.functionID;
        //开发进度
        let progress = building.developProgress;
        if (progress >= 1) {
            this.ui.m_topPanel.m_progress.selectedIndex = 1;
            this.ui.m_topPanel.m_topProgress.value = 100;
        } else {
            this.ui.m_topPanel.m_progress.selectedIndex = 0;
            this.ui.m_topPanel.m_topProgress.value = progress * 100;
        }
        switch (funcID) {
            case FunctionID.MaleToilet:
            case FunctionID.FemaleToilte:
            case FunctionID.MalePee:
            case FunctionID.FemaleSink:
            case FunctionID.MaleSink:
            case FunctionID.Library:
            case FunctionID.Canteen:
                this.ui.m_topPanel.m_c1.selectedIndex = 0;
                this.ui.m_topPanel.m_text_top_moneyAdd.text = building.income.toString() + "/分钟";
                this.ui.m_topPanel.m_text_top_time.text = building.duration.toString() + "s";
                this.ui.m_topPanel.m_text_top_water.text = building.wasteWater.toString();
                break;
            case FunctionID.CleanRoom:
                let cleanRoom = building as CleanRoom;
                this.ui.m_topPanel.m_c1.selectedIndex = 1;
                this.ui.m_topPanel.m_text_top_repairTime.text = cleanRoom.cleanTime.toString();
                this.ui.m_topPanel.m_text_top_cleanTime.text = cleanRoom.sweepTime.toString();
                this.ui.m_topPanel.m_text_top_stuffNum.text = cleanRoom.staffNum.toString();
                break;
            case FunctionID.WaterRoom:
                this.ui.m_topPanel.m_c1.selectedIndex = 2;
                this.ui.m_topPanel.m_text_top_expendWater.text = building.waterAdd.toString();
                break;
            case FunctionID.WaitAera:
                this.ui.m_topPanel.m_c1.selectedIndex = 3;
                let waitArea = building as WaitAera;
                this.ui.m_topPanel.m_text_top_ridership.text = waitArea.flow.toString();
                this.ui.m_topPanel.m_text_top_queueMan.text = waitArea.maleQueueLength.toString();
                this.ui.m_topPanel.m_text_top_queueWoman.text = waitArea.femaleQueueLength.toString();
                this.ui.m_topPanel.m_text_top_queuePee.text = waitArea.peeQueueLength.toString();
                break;
            default:
                console.error("尚未此建筑设置更新统计显示!", funcID);
                break;
        }
    }

    //更新建筑名字
    private UpdateTitle() {
        this.ui.m_top_Title.text = (this._currBuilding as Building).title;
    }

    //显示雇员界面
    private ShowCleanRoomStaff() {
        this.ui.m_stuffSwitch.selectedIndex = 1;
        this.ui.m_cleanRoomSwitch.selectedIndex = 1;
    }
}

/**
 * 底部buff的状态的枚举和，排序和控制器中序号一一对应
 */
enum BottomState {
    Money,
    time,
    clean,
    repair,
    queue,
    women,
    pee,
    man,
}
