import {StringName} from "../../Enum/StringName";
import Building from "./api/Building";
import BuildingData from "../../Data/BuildingData";
import {SlotData} from "../../Data/SlotData";
import {SlotConfigProxy} from "../../Config/SlotConfigProxy";
import {CameraManager} from "../CameraManager";

/**等候区 */
export class WaitAera extends Building {
    public get flow() {
        let slot = this.slots[0];
        return SlotConfigProxy.instance.GetSlotConfigByUID(slot.uid)[slot.level].length;
    }

    public get femaleQueueLength() {
        let slot = this.slots[1];
        return SlotConfigProxy.instance.GetSlotConfigByUID(slot.uid)[slot.level].length;
    }

    public get maleQueueLength() {
        let slot = this.slots[2];
        return SlotConfigProxy.instance.GetSlotConfigByUID(slot.uid)[slot.level].length;
    }

    public get peeQueueLength() {
        let slot = this.slots[3];
        return SlotConfigProxy.instance.GetSlotConfigByUID(slot.uid)[slot.level].length;
    }

    private _peeLine: { [index: number]: Laya.Sprite3D } = {};
    private _maleLine: { [index: number]: Laya.Sprite3D } = {};
    private _femaleLine: { [index: number]: Laya.Sprite3D } = {};

    public InitBuilding(data: BuildingData, showEffect: boolean) {
        this.active = data.active;
        this.functionID = data.functionID;
        this._index = data.index;
        this._name = data.name;
        this._title = data.title;
        this.slots = data.slots;
        this.InitAllLine();
        for (let i = 0; i < data.slots.length; i++) {
            let slot = data.slots[i];
            this.UpdateFacilityLevel(slot.name + "_" + i.toString(), slot.level, false, slot);
        }
    }

    UpdateFacilityLevel(name: string, level: number, showEffect: boolean, slotData?: SlotData) {
        this.UpdateStatistic();
        if (name == StringName.slot_flow_config_0) {
            return;
        }
        //更新外观显示
        let line = this._equipmentNode.getChildByName(name) as Laya.Sprite3D;
        if (line) {
            let length = this.GetLengthByLevel(slotData.uid, slotData.level);
            line.active = true;
            this.ToggleAllChild(line, false);
            for (let i = 0; i < length; i++) {
                let child = line.getChildByName(i.toString());
                if (child) {
                    child.active = true;
                } else {
                    console.log("获取面片失败！" + line.name, i);
                }
            }
        } else {
            console.warn("试图在建筑", this.sprite3D.name, "中获取一个不存在的设备", name, ",请检查场景中是否已正确放置此设备...");
        }
    }

    private GetLengthByLevel(uid: number, level: number) {
        let config = SlotConfigProxy.instance.GetSlotConfigByUID(uid);
        if (config) {
            if (config[level]) {
                return config[level].length
            } else {
                throw new Error(uid + "-" + level + "的配置表等级信息获取失败！")
            }
        } else {
            throw new Error(uid + "-" + level + "的配置表获取失败！")
        }
    }

    private InitAllLine() {
        this.InitLineUp(StringName.slot_femaleQueue_config_1, this._femaleLine);
        this.InitLineUp(StringName.slot_peeQueue_config_2, this._peeLine);
        this.InitLineUp(StringName.slot_maleQueue_config_3, this._maleLine);
    }

    private InitLineUp(lineUpName: string, table: { [strName: number]: Laya.Sprite3D }) {
        let spr = this._equipmentNode.getChildByName(lineUpName) as Laya.Sprite3D;
        let count = spr.numChildren;
        for (let i = 0; i < count; i++) {
            let child = spr.getChildAt(i);
            table[child.name] = child;
        }
    }

    ShowOutlook() {
    }

    onStart() {
        super.onStart();
        this.ToggleQueueShow(false)
    }

    OnClick() {
        super.OnClick();
        this.ToggleQueueShow(true)
        CameraManager.instance.FocusBuild(24);
    }

    OffClick() {
        super.OffClick();
        this.ToggleQueueShow(false);
    }

    ToggleQueueShow(value: boolean) {
        this._equipmentNode.getChildByName("slot_femaleQueue_config_1").active = value;
        this._equipmentNode.getChildByName("slot_peeQueue_config_2").active = value;
        this._equipmentNode.getChildByName("slot_maleQueue_config_3").active = value;
    }
}