import {NotImplementedException} from "../Exception/NotImplementedException";
import {Time} from "./Time";

export class Mathf {
    /**将值限制在 0 与 1 之间并返回值。 */
    public static Clamp01(t: number): number {
        return Mathf.Clamp(0, 1, t);
    }

    /**在给定的最小浮点值和最大浮点值之间钳制给定值。如果在最小和最大范围内，则返回给定值。 */
    public static Clamp(min: number, max: number, value: number): number {
        if (value < min) {
            return min;
        } else if (value > max) {
            return max;
        } else {
            return value;
        }
    }

    /**    在 min 与 max 之间按 t 进行线性插值。
     * 参数 t 限制在范围 [0, 1] 内。
     * 当 t = 0 时，返回 min。\ 当 t = 1 时，返回 max。\ 当 t = 0.5 时，返回 min 和 max 的中点。
     */
    public static Lerp(min: number, max: number, t: number): number {
        t = Mathf.Clamp01(t);
        return t * (max - min) + min;
    }

    /**在 min 与 max 之间按 t 进行线性插值。
     * 注意参数 t 无限制，可以在min和max之外进行线形插值。这意味着 t 可以小于 0 或大于 1。
     */
    public static LerpUnclamped(min: number, max: number, t: number): number {
        return t * (max - min) + min;
    }

    /**随时间推移将一个值逐渐改变为所需目标。 */
    public static SmoothDamp(current: number, target: number, smoothTime: number, maxSpeed: number = Infinity,
                             deltaTime: number = Laya.timer.delta): number {
        let n = smoothTime / deltaTime;
        let t = 0.998228611243 - 0.994433814875 * Math.exp(-4.68826970093 * Math.pow(n, -1.00200917568));
        t = Mathf.Clamp01(t);//当前帧需要移动的百分比
        let dir = target - current;
        let currentVelocity = dir * t / deltaTime * 1000;//秒速度
        if (currentVelocity > maxSpeed)//转为秒速度判定不超过最大速度
        {
            currentVelocity = maxSpeed;
        }
        let step = currentVelocity / 1000 * deltaTime;
        step = current + step;
        return step;
    }

    /**计算两个给定圆周角度（以度为单位给定）之间的最短差异，即最小夹角 */
    public static DeltaAngle(from: number, target: number): number {
        from = from % 360;
        target = target % 360;
        let delta = 0;
        if (from + 180 < target && target <= from + 360) {
            delta = from + 360 - target;
        } else {
            delta = target - from;
        }
        return delta;
    }

    /**
     * 向目标值匀速移动，速度过快，达到目标值时会产设震荡
     * @param from
     * @param to
     * @param speedAbs 速度的绝对值
     * @constructor
     */
    public static StepMove(from: number, to: number, speedAbs: number) {
        let delta = to - from;
        if(delta == 0){
            return  to;
        }
        if(delta < 0 ){
            speedAbs = -speedAbs;
        }
        let step = speedAbs * Time.deltaTime;
        from += step;
        return  from;
    }
}