import { AllPrefabsNames } from "../../cFrameBridge/Config/ELevelSceneName";
import FrameCDNURL from "../../cFrameBridge/FrameCDNURL";
import FrameSubpackages from "../../cFrameBridge/FrameSubpackages";
import ConsoleExGalababn from "../Console/ConsoleEx";
import { EKeyResNameEnum } from "./EKeyResName";

/**
 * 关键资源点列表管理器
 */
export default class KeyResManagerWFT {
    //
    private static _instance: KeyResManagerWFT;
    /** 单例 */
    public static get instance(): KeyResManagerWFT {
        if (this._instance == null) {
            this._instance = new KeyResManagerWFT();
        }
        //
        return this._instance;
    }

    //关键资源列表
    private m_KeyResList: { [index: string]: string } = {};

    //副本
    private m_KeyResList_: { [index: string]: string } = {};

    private constructor() {
        //注册关键资源目录
        this.m_KeyResList = {
            [EKeyResNameEnum.RootRes]: EKeyResNameEnum.RootRes + '/',
            [EKeyResNameEnum.Config]: EKeyResNameEnum.RootRes + '/' + EKeyResNameEnum.Config + '/',
            [EKeyResNameEnum.FGUI]: EKeyResNameEnum.RootRes + '/' + EKeyResNameEnum.FGUI + '/',
            [EKeyResNameEnum.LvConfig]: EKeyResNameEnum.RootRes + '/' + EKeyResNameEnum.LvConfig + '/',
            [EKeyResNameEnum.Font]: EKeyResNameEnum.RootRes + '/' + EKeyResNameEnum.Font + '/',
            [EKeyResNameEnum.Other]: EKeyResNameEnum.RootRes + '/' + EKeyResNameEnum.Other + '/',
            [EKeyResNameEnum.icon]: EKeyResNameEnum.RootRes + '/' + EKeyResNameEnum.Other + '/' + EKeyResNameEnum.icon + '/',
            [EKeyResNameEnum.img]: EKeyResNameEnum.RootRes + '/' + EKeyResNameEnum.Other + '/' + EKeyResNameEnum.img + '/',
            [EKeyResNameEnum.music]: EKeyResNameEnum.RootRes + '/' + EKeyResNameEnum.Other + '/' + EKeyResNameEnum.music + '/',
            [EKeyResNameEnum.sound]: EKeyResNameEnum.RootRes + '/' + EKeyResNameEnum.Other + '/' + EKeyResNameEnum.sound + '/',
            [EKeyResNameEnum.skin]: EKeyResNameEnum.RootRes + '/' + EKeyResNameEnum.Other + '/' + EKeyResNameEnum.skin + '/',
        };
        //注入预制体目录
        let _AllPrefabNames: AllPrefabsNames = new AllPrefabsNames();
        for (let _i in _AllPrefabNames) {
            EKeyResNameEnum[_i] = _i;
            this.m_KeyResList[EKeyResNameEnum[_i]] = EKeyResNameEnum.RootRes + '/' + EKeyResNameEnum[_i] + '/';
            //
            console.log(...ConsoleExGalababn.packLogLight('注入预制体资源路径', this.m_KeyResList[EKeyResNameEnum[_i]]));
        }
        // console.log(this.m_KeyResList);
        //复制一个副本
        for (let _i in this.m_KeyResList) {
            this.m_KeyResList_[_i] = this.m_KeyResList[_i];
        }
        //
        this.setRes();
    };

    //重置资源路径
    private setRes() {
        //重置分包资源路径
        for (let _o of FrameSubpackages.subpackages) {
            this.editKeyResList(_o.name, _o.root);
        }
        //重置cdn资源路径
        for (let _o of FrameCDNURL.CDNURLs) {
            this.editKeyResList(_o.name, _o.root);
        }
    }

    /**
     * 判断是否存在某个关键点key
     * @param _key 关键点key
     */
    public ifKeyRes(_key: string): boolean {
        let _if: boolean = false;
        for (let _i in EKeyResNameEnum) {
            if (_key == EKeyResNameEnum[_i]) {
                _if = true;
                break;
            }
        }
        return _if;
    }

    /**
     * 获取一个关键点资源的路径
     * @param _key 关键点key
     */
    public getResURL(_key: EKeyResNameEnum): string {
        return this.m_KeyResList[_key];
    }

    /**
     * 修改关键点资源路径
     * @param _key 关键点key
     * @param _str 路径
     */
    public editKeyResList(_key: string, _str: string) {
        let _replace: string = this.m_KeyResList_[_key];
        if (!_replace) {
            console.log(...ConsoleExGalababn.packWarn('修改资源路径失败，没有' + _key + '这个关键路径！'));
            return;
        } else {
            console.log(...ConsoleExGalababn.packLog('修改关键点资源路径', _replace, '替换成', _str));
        }
        //替换所有资源路径中的关键点路径
        for (let _i in this.m_KeyResList) {
            this.m_KeyResList[_i] = this.m_KeyResList[_i].replace(_replace, _str);
        }
        //
        this.m_KeyResList_[_key] = _str;
    }
}