import { NZsSdk } from './NZsSdk';
import zsSdk from './ZsSdk';

/**
 * sdk开始
 */
export default class ZsSdkStart {
    private static m_instance: ZsSdkStart;
    /** 单例 */
    public static get instance(): ZsSdkStart {
        if (!this.m_instance) {
            this.m_instance = new ZsSdkStart();
        }
        //
        return this.m_instance;
    }
    //
    private constructor() { }

    public version: string = '1.09999';

    /** sdk配置数据 */
    private m_data: NZsSdk.IZsSdkCfg;

    /** 获取sdk配置数据 */
    public get data(): NZsSdk.IZsSdkCfg {
        return this.m_data;
    }

    /**
     * 开始
     */
    public start(): Promise<void> {
        return new Promise<void>((r) => {
            zsSdk.loadCfg((data) => {
                this.m_data = data;
                console.log('加载配置成功================>>>>>>', data);

                r();
            }, (mes) => {
                this.m_data = {} as any;
                console.log('加载配置失败================>>>>>>>', mes);
                r();
            });
        });
    }




}