/**游戏启动类，负责给所有物体挂脚本，初始化所有建筑 */
export class GameRoot {
    private _buildManager: BuildingManager;
    private _prefabs: { [key: string]: Laya.Sprite3D[] };

    public Init(sceneSpr: Laya.Sprite3D, prefabs: { [key: string]: Laya.Sprite3D[] }) {
        if (sceneSpr == null || prefabs == null) {
            throw new NullReferenceException();
        }
        let camera = EnvironmentManager.instance.camera;
        this._prefabs = prefabs;
        GlobalObject.instance.SetPrefabs(prefabs);
        //初始化售货机
        this.InitVendingMachine();
        //特效对象池池
        let effectManager = camera.addComponent(EffectManager) as EffectManager;
        effectManager.InitAllPrefabs();
        //建筑格子初始化
        BuildBlock.instance.InitBlockAnimator(this._prefabs[PrefabNamesTable.prefab5.Male_wall][0], this._prefabs[PrefabNamesTable.prefab5.Female_wall][0])
        //建筑管理者初始化
        this._buildManager = camera.addComponent(BuildingManager);
        this._buildManager.SetPrefabs(prefabs);
        let pool = camera.addComponent(GuestPool) as GuestPool;
        //slot数据初始化
        SlotConfigProxy.instance.initData();
        //射线机移动脚本添加
        let cm = camera.addComponent(CameraManager) as CameraManager;
        cm.Init(this._prefabs[PrefabNamesTable.prefab5.Limit][0]);
        //初始化UI中介者
        UIFacade.instance.Init();
        //初始化旅客模板对象池
        GuestPoolConfig.instance.init();
        //初始化导航数据
        NetNavigation.NetMap.instance.InitData(StringName.MapRoot);
        pool.SetParentNode(sceneSpr);
        //初始化旅客对象池
        pool.InitPool(prefabs[PrefabNamesTable.prefab5.Entrance], prefabs[PrefabNamesTable.prefab5.Exit]);
        if (GuideDataProxy.IsCompleteAllGuide) {
            pool.StartPop();
        }
        //任务系统
        TaskManager.instance.InitTask();
        //初始化所有厕所
        this.InitBuildings<Toilet>(Toilet, PrefabNamesTable.prefab0.Toilet, ToiletDataProxy.data.list);
        //初始化女洗手区
        this.InitBuildings<Sink>(Sink, PrefabNamesTable.prefab0.FamaleSink, [SinkDataProxy.instance.femaleSinkData]);
        //初始化男洗手区
        this.InitBuildings<Sink>(Sink, PrefabNamesTable.prefab0.MaleSink, [SinkDataProxy.instance.maleSinkData]);
        //初始化无障碍卫生间
        // this.InitBuildings<DisableRoom>(DisableRoom, PrefabNamesTable.prefab0.DisableRoom, [DisableRoomDataProxy.instance.data.list]);
        //初始化清洁室
        this.InitBuildings<CleanRoom>(CleanRoom, PrefabNamesTable.prefab5.CleanRoom, [CleanRoomDataProxy.instance.data.list]);
        //初始化咖啡厅
        // this.InitBuildings<CoffeeRoom>(CoffeeRoom, PrefabNamesTable.prefab0.CoffeeRoom, [CoffeeDataProxy.instance.data.list]);
        //初始化婴儿房
        // this.InitBuildings<BabyRoom>(BabyRoom, PrefabNamesTable.prefab0.BabyRoom, [BabyRoomDataProxy.instance.data.list])
        //初始化等候区
        this.InitBuildings<WaitAera>(WaitAera, PrefabNamesTable.prefab0.WaitAera, [WaitAeraDataProxy.instance.data.list]);
        //小便区
        this.InitBuildings<PeeArea>(PeeArea, PrefabNamesTable.prefab0.PeeArea, [PeeAreaDataProxy.instance.data.list]);
        //图书馆
        this.InitBuildings<Library>(Library, PrefabNamesTable.prefab0.Library, [LibraryDataProxy.instance.data.list]);
        //便利店
        this.InitBuildings<Canteen>(Canteen, PrefabNamesTable.prefab0.Canteen, [CanteenDataProxy.instance.data.list]);
        //水循环室
        this.InitBuildings<WaterRoom>(WaterRoom, PrefabNamesTable.prefab0.WaterRoom, [WaterRoomDataProxy.instance.data.list]);
        //加载完成
        MesManagerClass.instance.event3D(EEventScene.SceneLoadComplete);
        //引导
        this.InitGuide();
        //计时器
        ClockManager.instance.Start();
        Laya.timer.once(200, this, () => {
            this.ShowDailyPanel();
            //离线收益面板
            this.ShowOfflineIncomePanel();
            //倒计时面板
            this.ShowCountDownTime()
        })
        Audio.PlayAudio();
        if (GuideDataProxy.IsCompleteAllGuide) {
            MiscDataProxy.instance.UpdateIncomeMin();
        }
    }

    //获取数组中名为name的sprite3d
    private GetSpriteByName(name: string, array: Laya.Sprite3D[]) {
        if (name == null || array == null || array.length == 0) {
            return null;
        }
        for (const item of array) {
            if (item.name == name) {
                return item;
            }
        }
    }

    //TIP:初始化的流程，从prefabs获取对应的所有物体，按名字来匹配物体添加脚本
    /**
     * 初始化建筑
     * @param component 脚本类
     * @param prefabName 建筑对应的预制体名字
     * @param dataList 建筑信息列表
     */
    private InitBuildings<T extends IBuilding>(component: any, prefabName: string, dataList: BuildingData[]) {
        let array = this._prefabs[prefabName];
        if (array == null || array.length <= 0) {
            console.warn("场景中没有", prefabName, ",跳过此项初始化");
            return;
        }
        if (dataList && dataList.length > 0) {
            for (let data of dataList) {
                let building = this.GetSpriteByName(data.name, array);
                if (building) {
                    let script = building.addComponent(component) as T;
                    script.InitBuilding(data, false);
                    this._buildManager.AddBuild(script, data.functionID);
                } else {
                    console.warn("添加脚本失败,预制体名字", prefabName, ",建筑名字:", data.name, "预制体数组:", array);
                }
            }
        } else {
            throw new NullReferenceException(prefabName + "数据为null");
        }
    }

    //初始化自动售货机
    private InitVendingMachine() {
        let machine = this._prefabs[PrefabNamesTable.prefab5.VendingMachine][0];
        machine.addComponent(VendingMachine);
    }

    //初始化引导
    private InitGuide() {
        if (!GuideDataProxy.IsCompleteAllGuide) {
            console.log("开始引导");
            Uma.trackEvent(UmaEvent.GuideStart);
            GuideManager.instance.StartGuide();
        } else {
            console.log("引导完成", GuideDataProxy.IsCompleteAllGuide)
        }
    }

    public ShowOfflineIncomePanel() {
        if (GuideDataProxy.IsCompleteAllGuide) {
            UIMediatorOffLineIncomePanel.instance.TryShowPanel();
        }
    }

    public ShowDailyPanel() {
        if (!GuideDataProxy.IsCompleteAllGuide) {
            return;
        }
        if (MiscDataProxy.data.hasSign) {
            return;
        }
        UIManagerProxy.instance.setUIState([{ typeIndex: EUI.DailySign, state: true }], false);
    }

    public ShowCountDownTime() {
        let active = CanteenDataProxy.instance.data.list.active;
        if (!GuideDataProxy.IsCompleteAllGuide || active) {
            return;
        }
        UIManagerProxy.instance.setUIState([{ typeIndex: EUI.CountDownTime, state: true }], false);
    }
}