import {Guest, StateName} from "./Guest";
import MesManagerClass from "../../dMyGame/Manager/MesManager";
import {EEventUI} from "../../dMyGame/EventEnum/EEventUI";
import {EEventScene} from "../../dMyGame/EventEnum/EEventScene";
import {MiscUtils} from "../../lty/class/MiscUtils";
import {EffectManager} from "../EffectManager";
import {EffectName} from "../../dMyGame/ResList/PrefabNames";
import {Vector3} from "../../lty/class/Vector3";
import {UIFacade} from "../../UI/UIFacade";
import {EUI} from "../../dMyGame/Enum/EUI";
import {GuideDataProxy} from "../../Data/GuideDataProxy";

export class NetRed extends Guest{
    private _endPoint = new Laya.Vector3(-6,0,11)
    private _isWait = false;
    public SetActive(value:boolean){
        this.sprite3D.active = value;
        this.GoWc();
    }
    onAwake() {
        MesManagerClass.instance.on3D(EEventScene.NetRedLeaveWC,this,this.LeaveWC);
    }

    public GoWc(){
        let wps = this._agent.TrySearchByNearestPos(this.transform.position,this._endPoint);
        this.SetWayPoints(wps).OnComplete(this.OnReachWC);
        this.moveSpeed = 3;
    }

    public OnReachWC(){
        // 通知ui
        MesManagerClass.instance.eventUI(EEventUI.AddAdUtilityNetRed);
        this.PlayAnimationByState(StateName.Idle);
        this.transform.localRotationEulerY = MiscUtils.updateRotation(new Laya.Vector3(-1,0,1))
        let e= EffectManager.PlayEffect(EffectName.Hushen,this.id);
        this.sprite3D.addChild(e);
        e.transform.localPosition = Vector3.zero;
        e.transform.localScale = new Laya.Vector3(0.2,0.2,0.2);
        e.transform.rotationEuler = Vector3.zero;
        this._isWait = true;
    }

    public LeaveWC(){
        this._isWait = false;
        EffectManager.ClearAll(this.id);
        super.ExitScene();
    }

    onMouseClick() {
        if(GuideDataProxy.IsCompleteAllGuide && this._isWait){
            UIFacade.ShowPanel(EUI.UIMediatorInfluencer);
        }
    }

}