//插槽位置管理，管理人物可使用的slot
import {SlotData} from "../../Data/SlotData";
import Building from "./api/Building";
import {PeopleEvent} from "../Misc/PeopleEvent";

export class FacilityPosition{
    //所归属的建筑
    belonger: Building;
    //slot的信息
    slotData:SlotData;
    //设备
    sprite:Laya.Sprite3D;
    //使用设备的人
    peopleSprite:Laya.Sprite3D;
    //设备是否损坏
    isBreaking = false;
    //设置设备是否损坏
    public SetBreakState(value:boolean,breakEventID?:PeopleEvent){
        this.slotData.isBreaking = this.isBreaking = value;
        if(!value){
            this.eventID = null;
        }
        else{
            this.eventID = breakEventID;
        }
    }

    public eventID:PeopleEvent;

    //第一个子物体
    private _tempChild:Laya.Sprite3D;
    public get runtimeFirstChild(){
        if(this._tempChild==null ){
            this._tempChild = this.sprite.getChildAt(0) as Laya.Sprite3D;
        }
        return this._tempChild;
    }
}
