import {FunctionID} from "../Data/FunctionID";
import {EEventScene} from "../dMyGame/EventEnum/EEventScene";
import MesManagerClass from "../dMyGame/Manager/MesManager";
import {LayaBehaviour} from "../lty/class/LayaBehaviour";
import {ArgumentNullException} from "../lty/Exception/ArgumentNullException";
import {NullReferenceException} from "../lty/Exception/NullReferenceException";
import {IBuilding} from "./Buildings/api/IBuilding";
import {UpdateBuildingEventData, UpdateSlotEventData} from "../Data/UpdateEventData";
import {Guest} from "./Guest/Guest";
import {FacilityPosition} from "./Buildings/FacilityPosition";
import SlotUIDConstProxy from "../Data/SlotUIDConstProxy";
import {SlotConfigProxy} from "../Config/SlotConfigProxy";
import {StringName} from "../Enum/StringName";
import {Random} from "../lty/class/Random";
import {EffectManager} from "./EffectManager";
import {EffectName, PrefabNamesTable} from "../dMyGame/ResList/PrefabNames";
import {Vector3} from "../lty/class/Vector3";
import {BuildDataProxy} from "../Data/BuildDataProxy";
import Building from "./Buildings/api/Building";
import {PeopleEvent} from "./Misc/PeopleEvent";
import {EEventUI} from "../dMyGame/EventEnum/EEventUI";
import {PeeArea} from "./Buildings/PeeArea";
import {GuideDataProxy} from "../Data/GuideDataProxy";
import {Uma, UmaEvent} from "../Uma";

/**建造和建筑管理者 */
export class BuildingManager extends LayaBehaviour {

    /**场景中所有预制体的实例 */
    private _prefabs: { [key: string]: Laya.Sprite3D[] };
    /**存贮了建筑的key对应建筑的脚本 */
    private _array: { [functionID: number]: IBuilding[] } = {};
    /**map存储了建筑的名字对应建筑的脚本 key -建筑名字 value - 建筑脚本*/
    private _dict: Map<string, IBuilding> = new Map<string, IBuilding>();

    private static _instance: BuildingManager;
    public static get instance(): BuildingManager {
        return this._instance;
    }

    //建筑字典
    public get buildingMap() {
        return this._dict;
    }

    //男厕所，女厕所,统一管理进入,然后再由其自身管理进入

    onAwake() {
        BuildingManager._instance = this;
        //监听建造事件
        MesManagerClass.instance.on3D(EEventScene.UpdateBuildingOutlookEvent, this, this.UpdateBuildingOutlookEvent);
        //监听设备升级事件
        MesManagerClass.instance.on3D(EEventScene.UpdateSlotOutLook, this, this.UpdateSlotOutLookEvent);
        //延迟2秒后启动检测
        Laya.timer.once(1500, this, this.UpdateCheck);
        MesManagerClass.instance.onUI(EEventUI.DamageSlot, this, this.DamageSlot);
    }

    private UpdateCheck() {
        Laya.timer.loop(500, this, this.CheckQueue);
    }

    public SetPrefabs(arr: { [key: string]: Laya.Sprite3D[] }) {
        this._prefabs = arr;
        this.InitDoorManager(arr);
    }

    /**增加建筑 */
    public AddBuild(building: IBuilding, functionID: number) {
        let id = functionID;
        let arr = this._array[id];
        if (arr) {
            let index = arr.indexOf(building);
            if (index < 0) {
                arr.push(building);
            } else {
                console.error("重复添加建筑:", building);
            }
        } else {
            this._array[id] = [];
            this._array[id].push(building);
        }
        //加入字典
        let temp = this._dict.get(building.buildingName);
        if (temp == null) {
            this._dict.set(building.buildingName, building);
        } else {
            console.error("字典中已经包含相同键");
        }
        //在扩建区域外不显示
        this.UpdateBlockState(functionID, building as Building);
        //建筑的设备加入代理
        this.AddFacsByList(functionID, building.slotPosList);
        this.AddEndPoint(functionID, building);
        this.AddPosPlane(functionID, building);
        this.UpdateWaitAreaSize(building);
    }

    /**为这个功能ID服务的建筑是否存在 */
    ExistBuilding(functionID: number): boolean {
        let bs = this._array[functionID];
        return bs && bs.length > 0;
    }

    //通过buildName获取建筑脚本
    public GetBuildingByName(name: string) {
        return this._dict.get(name);
    }

    //通过功能获取某一类型的建筑列表
    public GetBuildingListByFunctionID(funcID: FunctionID) {
        return this._array[funcID];
    }

    /**建造完成，关闭施工围栏，开启建筑 */
    private UpdateBuildingOutlookEvent(mes: UpdateBuildingEventData) {
        if (mes) {
            let building = this._dict.get(mes.buildingName);
            if (building) {
                building.InitBuilding(mes.buildingData, true);
                this.AddFacsByList(building.functionID, building.slotPosList);
                this.UpdateWaitAreaSize(building);
                if (building.functionID == FunctionID.MaleToilet) {
                    let pee = this.GetBuildingByName(StringName.PeeArea);
                    (pee as PeeArea).UpdateFacOutLook();
                }
            } else {
                throw new NullReferenceException("建筑脚本获取失败!" + mes.buildingName, mes);
            }
        } else {
            throw new ArgumentNullException("建筑脚本获取失败，参数不应为null");
        }
        let list = this.GetBuildingListByFunctionID(mes.buildingData.functionID);
        for (let ele of list) {
            this.UpdateBlockState(mes.buildingData.functionID, ele as Building);
        }
    }

    //更新建筑格子内建筑状态，不显示
    private UpdateBlockState(funcID: FunctionID, building: Building) {
        switch (funcID) {
            case FunctionID.MaleToilet:
                let md = BuildDataProxy.instance.GetBuildDataByName(StringName.maleToilet);
                building.sprite3D.active = building.index <= md.count + 1;
                break;
            case FunctionID.FemaleToilte:
                let fd = BuildDataProxy.instance.GetBuildDataByName(StringName.femaleToilet);
                building.sprite3D.active = building.index <= fd.count * 2 + 2;
                break;
        }

    }

    /**升级slot外观*/
    private UpdateSlotOutLookEvent(mes: UpdateSlotEventData) {
        // console.log(mes);
        let building = this._dict.get(mes.building.buildingName);
        if (building) {
            let slot = building.slots[mes.slotIndex];
            building.UpdateFacilityLevel(slot.name + "_" + mes.slotIndex.toString(), slot.level, true, slot);
            this.AddFacsByList(building.functionID, building.slotPosList);
            this.UpdateWaitAreaSize(building);
            MesManagerClass.instance.event3D(EEventScene.OnUpdateSlotComplete)
            let progress = (building as Building).developProgress;
            Uma.trackEvent(UmaEvent.BuildingStatistic, {"progress": building.buildingName + '_' + Math.floor(progress * 100)})
        } else {
            console.error("建筑管理者获取建筑失败，无法升级slot！");
        }
    }

    //#region 流程重构
    get flow() {
        return this._flow;
    }

    get MaxSizeMale(): number {
        return this._MaxSizeMale;
    }

    get MaxSizePee(): number {
        return this._MaxSizePee;
    }

    get MaxSizeFemale(): number {
        return this._MaxSizeFemale;
    }

    /**
     * 女厕所排队人数
     */
    public get femaleQueueCount() {
        return this.GetCount(this._femaleList, this._MaxSizeFemale);
    }

    /**
     * 男厕所排队人数
     */
    public get maleQueueCount() {
        return this.GetCount(this._maleList, this._MaxSizeMale);
    }

    /**
     * 小便区排队人数
     */
    public get peeQueueCount() {
        return this.GetCount(this._peeList, this._MaxSizePee);
    }

    //设备表，按照建筑功能区分
    private _facTable: Map<FunctionID, FacilityPosition[]> = new Map<FunctionID, FacilityPosition[]>();
    //所有设备的列表
    private _facList: FacilityPosition[] = [];
    //旅客使用的设备 key -guest.ID value - fac 设备
    private _guestToFac: Map<number, FacilityPosition> = new Map<number, FacilityPosition>();
    //男厕所队列
    private _maleList: Guest[] = new Array(20);
    //女厕所队列
    private _femaleList: Guest[] = new Array(20);
    //小便队列
    private _peeList: Guest[] = new Array(20);
    //女洗手队列
    private _femaleSinkList: Guest[] = new Array(1);
    //男洗手队列
    private _maleSinkList: Guest[] = new Array(1);
    //图书馆队列
    private _libraryList: Guest[] = new Array(1);
    //便利店队列
    private _canteenList:Guest[] = new Array(1)

    private _MaxSizePee = 4;
    private _MaxSizeMale = 4;
    private _MaxSizeFemale = 4;
    //每个建筑等待队列的入口
    private _endPointTable: { [funId: number]: Laya.Sprite3D } = {};
    //每个功能的队伍中的位置的面片
    private _queuePosTable: { [funcID: number]: Laya.Sprite3D[] } = {};

    private _flow: number = 0;

    //批量向表中加入设备
    private AddFacsByList(funID: FunctionID, facPosList: FacilityPosition[]) {
        if (facPosList && facPosList.length > 0) {
            for (let ele of facPosList) {
                this.AddFac(funID, ele);
            }
        }
    }

    //向表中添加设备
    private AddFac(funcId: FunctionID, facPos: FacilityPosition) {
        let list = this._facTable.get(funcId);
        if (list == null) {
            list = [];
            this._facTable.set(funcId, list);
        }
        if (!this.Contain(list, facPos)) {
            list.push(facPos);
            this._facList.push(facPos);
        } else {
            console.error("重复添加设备", facPos);
        }
    }

    //加入建筑的时候加入
    private AddEndPoint(funcID: FunctionID, building: IBuilding) {
        switch (funcID) {
            case FunctionID.WaitAera:
                this.AddEndPointForWaitArea(building);
                break;
            case FunctionID.MalePee:
            case FunctionID.FemaleToilte:
            case FunctionID.MaleToilet:
                // console.log("跳过：", building.buildingName);
                break;
            default:
                this.AddEndPointForNormalBuilding(funcID, building);
                break;
        }
    }

    //添加队列的入口,在添加建筑的时候为建筑同时设置队列入口
    private AddEndPointForNormalBuilding(funcID: FunctionID, building: IBuilding) {
        let temp = this._endPointTable[funcID];
        if (temp == null) {
            let entrance = building.owner.getChildByName(StringName.entrance) as Laya.Sprite3D;
            if (entrance) {
                let index = entrance.numChildren - 1;
                let ep = entrance.getChildAt(index) as Laya.Sprite3D;
                if (ep) {
                    this._endPointTable[funcID] = ep;
                } else {
                    console.log("此功能的建筑没有设置队列入口", building);
                }
            }
        } else {
            console.error("此功能建筑的队列入口已经存在：", funcID);
        }
    }

    //把等候区的三个队伍的入口加入管理
    private AddEndPointForWaitArea(building: IBuilding) {
        let female = building.owner.getChildByName(StringName.EP_Female) as Laya.Sprite3D;
        let male = building.owner.getChildByName(StringName.EP_Male) as Laya.Sprite3D;
        let pee = building.owner.getChildByName(StringName.EP_Pee) as Laya.Sprite3D;
        this._endPointTable[FunctionID.FemaleToilte] = female;
        this._endPointTable[FunctionID.MaleToilet] = male;
        this._endPointTable[FunctionID.MalePee] = pee;
    }

    //为建筑添加等待位置的面片
    private AddPosPlane(funcID: FunctionID, builing: IBuilding) {
        switch (funcID) {
            case FunctionID.MaleToilet:
            case FunctionID.FemaleToilte:
            case FunctionID.MalePee:
                break;
            case FunctionID.WaitAera:
                this.AddPlaneFromWaitArea(builing);
                break;
            default:
                this.AddPlaneFromNormalBuilding(funcID, builing);
                break;
        }
    }

    private AddPlaneFromWaitArea(building: IBuilding) {
        let equipment = building.owner.getChildByName(StringName.equipment);
        let female = equipment.getChildByName(StringName.slot_femaleQueue_config_1) as Laya.Sprite3D;
        let femaleList: Laya.Sprite3D[] = [];
        this.AddPlaneForFunction(FunctionID.FemaleToilte, femaleList, female)
        let male = equipment.getChildByName(StringName.slot_maleQueue_config_3) as Laya.Sprite3D;
        let maleList: Laya.Sprite3D[] = [];
        this.AddPlaneForFunction(FunctionID.MaleToilet, maleList, male)
        let pee = equipment.getChildByName(StringName.slot_peeQueue_config_2) as Laya.Sprite3D;
        let peeList: Laya.Sprite3D[] = [];
        this.AddPlaneForFunction(FunctionID.MalePee, peeList, pee)
    }

    //在普通建筑的entrance下寻找排队面片
    private AddPlaneFromNormalBuilding(funcID: FunctionID, building: IBuilding) {
        let node = building.owner.getChildByName(StringName.entrance) as Laya.Sprite3D;
        let list: Laya.Sprite3D[] = [];
        this.AddPlaneForFunction(funcID, list, node);
    }

    //为功能添加排队的位置的面片的数组
    private AddPlaneForFunction(funcID: FunctionID, arr: Laya.Sprite3D[], entrance: Laya.Sprite3D) {
        let count = entrance.numChildren;
        if (count > 0) {
            for (let i = 0; i < count; i++) {
                let child = entrance.getChildByName(i.toString()) as Laya.Sprite3D;
                arr.push(child);
            }
        }
        this._queuePosTable[funcID] = arr;
    }

    //判断列表中是否包含某个设备
    private Contain(list: FacilityPosition[], element: FacilityPosition) {
        list.forEach(ele => {
            if (ele.sprite.id == element.sprite.id) {
                return true;
            }
        })
        return false;
    }

    //初始化等候区，为 厕所和小便池 代理等候区,刷新最大容量
    private UpdateWaitAreaSize(building: IBuilding) {
        let slotData = building.slots;
        //刷新等候区3个队伍的最大收纳人数
        for (let data of slotData) {
            switch (data.uid) {
                case SlotUIDConstProxy.Data.slot_peeQueue_config:
                    let lp = SlotConfigProxy.instance.GetSlotConfigByUID(data.uid)[data.level].length;
                    if (lp) {
                        this._MaxSizePee = lp;
                    } else {
                        throw "无法获取小便区当前等级排队人数"
                    }
                    break;
                case SlotUIDConstProxy.Data.slot_maleQueue_config:
                    let lm = SlotConfigProxy.instance.GetSlotConfigByUID(data.uid)[data.level].length;
                    if (lm) {
                        this._MaxSizeMale = lm;
                    } else {
                        throw "无法获取男厕所当前等级排队人数"
                    }
                    break;
                case SlotUIDConstProxy.Data.slot_femaleQueue_config:
                    let lfm = SlotConfigProxy.instance.GetSlotConfigByUID(data.uid)[data.level].length;
                    if (lfm) {
                        this._MaxSizeFemale = lfm;
                    } else {
                        throw "无法获取女厕所当前等级排队人数"
                    }
                    break;
                case SlotUIDConstProxy.Data.slot_flow_config:
                    let lf = SlotConfigProxy.instance.GetSlotConfigByUID(data.uid)[data.level].length;
                    if (lf) {
                        this._flow = lf;
                        MesManagerClass.instance.event3D(EEventScene.UpdateMaxFlow, lf);
                    } else {
                        throw "无法获取当前等级人流量"
                    }
                    break;
                default:
                    break;
            }
        }
    }

    //XXX:新加入的建筑的等待列表加入派送
    //每500ms检测一次队伍中的人，如果有可用的功能位置就把队首的人派发出去
    private CheckQueue() {
        this.DispatchGuest(FunctionID.MaleToilet, this._maleList);
        this.DispatchGuest(FunctionID.MalePee, this._peeList);
        this.DispatchGuest(FunctionID.FemaleToilte, this._femaleList);
        this.DispatchGuest(FunctionID.FemaleSink, this._femaleSinkList);
        this.DispatchGuest(FunctionID.MaleSink, this._maleSinkList);
        this.DispatchGuest(FunctionID.Library, this._libraryList);
        this.DispatchGuest(FunctionID.Canteen, this._canteenList);
    }

    //如果有空建筑而且有排队的人就把队首的人派发出去
    private DispatchGuest(funcID: FunctionID, queue: Guest[]) {
        let emptyFac = this.GetEmptyFuncPos(funcID);
        if (emptyFac) {
            let guest = this.PopGuest(funcID, queue);
            if (guest) {
                //通知队首的人去厕所
                let entrancePos = emptyFac.belonger.entrance.transform.position;
                let exitPos = emptyFac.belonger.exit.transform.position;
                emptyFac.peopleSprite = guest.sprite3D;
                this._guestToFac.set(guest.sprite3D.id, emptyFac);
                guest.MoveToEntrancePos({
                    entrance: entrancePos,
                    exit: exitPos,
                    funcPos: emptyFac.sprite.transform.position,
                    slot: emptyFac.slotData,
                    fac: emptyFac
                });
            }
        }
    }

    //在队伍的最大容量内获取（弹出）一个旅客
    private PopGuest(funcID: FunctionID, queue: Guest[]) {
        let maxSize = this.GetMaxSizeByFunction(funcID);
        for (let i = 0; i < maxSize; i++) {
            if (queue[i]) {
                let guest = queue[i];
                queue[i] = null;
                return guest;
            }
        }
        return null;
    }

    //获取一个空的设备
    private GetEmptyFuncPos(funcID: FunctionID) {
        let facList = this._facTable.get(funcID);
        if (facList == null || facList.length <= 0) {
            return null;
        }
        for (let fac of facList) {
            //没人使用，建筑开放，等级大于一，设备是可用设备，设备不是损坏状态
            if (fac.peopleSprite == null && fac.belonger.active && fac.slotData.level > 0 && fac.slotData.usable && !fac.isBreaking) {
                return fac;
            }
        }
        return null;
    }


    //获取funcID对应的排队的队伍数组
    private GetQueue(funID: FunctionID) {
        switch (funID) {
            case FunctionID.MaleToilet:
                return this._maleList;
            case FunctionID.FemaleToilte:
                return this._femaleList;
            case FunctionID.MalePee:
                return this._peeList;
            case FunctionID.FemaleSink:
                return this._femaleSinkList;
            case FunctionID.MaleSink:
                return this._maleSinkList;
            case FunctionID.Library:
                return this._libraryList;
            case FunctionID.Canteen:
                return this._canteenList;
            default:
                console.error("获取等待列表失败:", funID);
                return null;
        }
    }

    //获取funcI对应的队伍的最大容量
    private GetMaxSizeByFunction(funcID: FunctionID) {
        switch (funcID) {
            case FunctionID.MalePee:
                return this._MaxSizePee;
            case FunctionID.FemaleToilte:
                return this._MaxSizeFemale;
            case FunctionID.MaleToilet:
                return this._MaxSizeMale;
            default:
                return 1;
        }
    }

    //获取队伍中位置的面片的数组
    private GetPlaneList(funcID: FunctionID) {
        return this._queuePosTable[funcID];
    }

    //获取队列中maxSize以前的数量
    private GetCount(list: any[], maxSize: number) {
        let count = 0;
        for (let i = 0; i < maxSize; i++) {
            if (list[i]) {
                count++;
            }
        }
        return count;
    }

    //#endregion

    //#region旅客与建筑管理的交互
    //获取某个功能的建筑的排队的队伍的入口endPoint
    public GetEndPoint(funcID: FunctionID) {
        switch (funcID) {
            case FunctionID.Library:
            case FunctionID.Canteen:
                let building = this.GetBuildingListByFunctionID(funcID)[0];
                if (building != null && building.active) {
                    break;
                } else {
                    return null;
                }
            default:
                break;
        }
        return this._endPointTable[funcID];
    }

    //旅客尝试入队，进入其需求功能的建筑的队列
    public TrtEnqueue(funcID: FunctionID, guest: Guest, out: Laya.Vector3): boolean {
        try {
            let queue = this.GetQueue(funcID);
            if (queue) {
                let maxSize = this.GetMaxSizeByFunction(funcID);
                let list = this.GetPlaneList(funcID);
                let queuePos = list[maxSize].transform.position;
                out.setValue(queuePos.x, queuePos.y, queuePos.z);
                let count = this.GetCount(queue, maxSize);
                return queue && count < maxSize;
            }
        } catch (e) {
            console.error(funcID, guest, out);
            throw e;
        }
    }

    //判断旅客存在在队列中
    ExistInQueue(funcId: FunctionID, guest: Guest) {
        let queue: Guest[] = this.GetQueue(funcId);
        return queue.indexOf(guest) > -1;
    }

    //在队伍中逐步前进，直到不能前进
    public GetNextPointInQueue(funcId: FunctionID, guest: Guest) {
        let queue: Guest[] = this.GetQueue(funcId);
        let maxSize = this.GetMaxSizeByFunction(funcId);
        let planeList = this.GetPlaneList(funcId);
        if (queue) {
            let index = queue.indexOf(guest);
            if (index > -1) {
                //在队中
                let next = index - 1;
                if (next >= 0) {
                    if (queue[next] == null) {
                        queue[index] = null;
                        queue[next] = guest;
                        return planeList[next].transform.position;
                    }
                }
            } else {
                let next = maxSize - 1;
                if (queue[next] == null) {
                    queue[next] = guest;
                    return planeList[next].transform.position;
                }
            }

        }
        return null;
    }

    //旅客请求开门
    public RequestOpenTheDoor(guest: Guest) {
        //TODO:寻找一个最近的门，或者距离之内的门
        return this.TryOpenDoor(guest.transform.position);
    }


    //旅客退出设备，清空设备的使用人
    public ResetFac(guest: Guest) {
        let fac = this._guestToFac.get(guest.sprite3D.id);
        if (fac) {
            fac.peopleSprite = null;
            this._guestToFac.delete(guest.sprite3D.id);
            this.RandomBreakFacility(fac);
        }
    }

    //#endregion

    //随机损坏设备，只损坏马桶，洗手台，小便区
    private RandomBreakFacility(facPos: FacilityPosition) {
        if (!GuideDataProxy.IsCompleteAllGuide) {
            return;
        }
        let damage = Random.Range(0, 100) < 7;
        // let damage = true;
        if (damage) {
            let isSweep = Random.random > 0.5;
            //损坏的是否为可使用建筑
            let isUsableFac = false;
            switch (facPos.slotData.uid) {
                case SlotUIDConstProxy.Data.slot_mt_config:
                    let td = EffectManager.PlayEffect(isSweep ? EffectName.Toilet_dirty : EffectName.Toilet_damage, facPos.sprite.id);
                    facPos.sprite.addChild(td);
                    td.transform.localPosition = Vector3.zero;
                    td.transform.rotationEuler = facPos.sprite.transform.rotationEuler;
                    isUsableFac = true;
                    break;
                case SlotUIDConstProxy.Data.slot_urinal_config:
                    let sd = EffectManager.PlayEffect(isSweep ? EffectName.Pee_dirty : EffectName.Pee_Damage, facPos.sprite.id);
                    facPos.sprite.addChild(sd);
                    sd.transform.localPosition = Vector3.zero;
                    sd.transform.rotationEuler = facPos.sprite.transform.rotationEuler;
                    isUsableFac = true;
                    break;
                case SlotUIDConstProxy.Data.slot_wash_basin_man_config:
                case SlotUIDConstProxy.Data.slot_wash_basin_woman_config:
                    let se = EffectManager.PlayEffect(isSweep ? EffectName.Sink_Dirty : EffectName.Sink_Damage, facPos.sprite.id);
                    facPos.sprite.addChild(se);
                    se.transform.localPosition = Vector3.zero;
                    se.transform.rotationEuler = facPos.sprite.transform.rotationEuler;
                    isUsableFac = true;
                    break;
                default:
                    isUsableFac = false;
                    break;

            }
            if (isUsableFac) {
                facPos.SetBreakState(true, isSweep ? PeopleEvent.Sweep : PeopleEvent.Repair);
                MesManagerClass.instance.event3D(EEventScene.RepairFacility, facPos);
                // UIMediatorMain.instance.AddAdUtility(ADUtility.RepairGame);
            }
        }
    }

    private DamageSlot() {
        let building = this.GetBuildingListByFunctionID(FunctionID.MaleSink)[0] as Building;
        let facPos = building.slotPosList[0];
        if (facPos) {
            let td = EffectManager.PlayEffect(EffectName.Toilet_damage, facPos.sprite.id);
            facPos.sprite.addChild(td);
            td.transform.localPosition = Vector3.zero;
            td.transform.rotationEuler = facPos.sprite.transform.rotationEuler;
            facPos.SetBreakState(true, PeopleEvent.Repair);
            MesManagerClass.instance.event3D(EEventScene.RepairFacility, facPos);
        } else {
            console.error("派发损坏失败!");
        }
        MesManagerClass.instance.offUI(EEventUI.DamageSlot, this, this.DamageSlot);
    }

    //#region门的管理
    private _doorList: Laya.Sprite3D[] = [];

    private InitDoorManager(arr: { [p: string]: Laya.Sprite3D[] }) {
        let doors = arr[PrefabNamesTable.prefab5.Door];
        if (doors && doors.length > 0) {
            this._doorList = doors;
        } else {
            throw new Error("建筑管理者初始化门失败！")
        }
        //监听门的激活事件
        MesManagerClass.instance.on3D(EEventScene.SetDoorActive, this, this.SetDoorActive);
    }

    private GetDoorByPos(postion: Laya.Vector3) {
        let dist = Infinity;
        let door: Laya.Sprite3D = null;
        for (let d of this._doorList) {
            let temp = Laya.Vector3.distanceSquared(d.transform.position, postion);
            if (temp < dist) {
                dist = temp;
                door = d;
            }
        }
        return door;
    }

    public TryOpenDoor(guestPosition: Laya.Vector3, autoClose = true) {
        //查找一个最近的门
        let door = this.GetDoorByPos(guestPosition);
        let dist = Laya.Vector3.distanceSquared(guestPosition, door.transform.position);
        //开关门
        if (door) {
            if (dist > 100) {
                console.error("距离门太远了", dist, guestPosition);
                //到底是谁？在呼唤开门
                // EffectManager.PlayEffect(EffectName.ZS,this.id,10000,guestPosition);
                return false;
            } else {
                let ani = door.getComponent(Laya.Animator) as Laya.Animator;
                ani.play("open");
                if (autoClose) {
                    Laya.timer.clearAll(ani);
                    Laya.timer.once(3500, ani, () => ani.play("close"));
                }
                return true;
            }
        }
        return false;
    }

    public TryCloseDoor(guestPosition: Laya.Vector3) {
        //查找一个最近的门
        let door = this.GetDoorByPos(guestPosition);
        let dist = Laya.Vector3.distanceSquared(guestPosition, door.transform.position);
        //开关门
        if (door) {
            if (dist > 100) {
                console.error("距离门太远了", dist, guestPosition);
                return false;
            } else {
                let ani = door.getComponent(Laya.Animator) as Laya.Animator;
                ani.play("close")
                return true;
            }
        }
        return false;
    }


    //门的显示激活设置
    private SetDoorActive(pos: Laya.Vector3, isActive: boolean) {
        let door = this.GetDoorByPos(pos);
        if (door) {
            door.active = isActive;
        }
    }

    //#endregion


    //获取建筑的入口
    GetBuildingEntrance(CleanRoom: FunctionID) {
        let building = this._array[CleanRoom][0] as Building;
        return building.entrance;
    }

    //金钱统计
    _StatisticsArray = [];

    /**取得已建筑的信息 */
    private GetStatisticsInfo() {
        let map = BuildingManager.instance.buildingMap;
        this._StatisticsArray = [];
        map.forEach(ele => {
            if (ele.active) {
                let buildele = ele as Building;
                if (buildele.income > 0) {
                    let info = {
                        functionID: buildele.functionID,
                        title: buildele.title,
                        buildingName: buildele.buildingName,
                        income: buildele.income,
                        progress: buildele.developProgress
                    }
                    this._StatisticsArray.push(info);
                }
            }
        })
        return this._StatisticsArray;
    }

    /**
     * 所有建筑每分钟的收入
     * @constructor
     */
    public GetTotalMoney() {
        this.GetStatisticsInfo()
        let totalIncome = 0;
        if (this._StatisticsArray.length > 0) {
            this._StatisticsArray.forEach(ele => {
                totalIncome += ele.income;
            })
        }
        return totalIncome;
    }

}
