import BaseUIMediator from "../aTGame/UI/FGUI/BaseUIMediator";
import { EUILayer } from "../aTGame/UI/FGUI/EUILayer";
import { MiscDataProxy } from "../Data/MiscDataProxy";
import { EUI } from "../dMyGame/Enum/EUI";
import UIManagerProxy from "../dMyGame/Manager/Proxy/UIManagerProxy";
import FGUI_PGameMainLuckyWheel from "../FGUI/Main/FGUI_PGameMainLuckyWheel";
import { Random } from "../lty/class/Random";
import { UIFacade } from "./UIFacade";
import PlatformManagerProxy from "../aTGame/Platform/PlatformManagerProxy";
import { Audio } from "../GameComponent/Audio";
import { Uma, UmaEvent } from "../Uma";
import VIVOData from "src/cFrameBridge/Platform/Data/VIVOData";
import FGUI_GameAd from "src/FGUI/GameAd/FGUI_GameAd";
import { UIMediatorGameAd } from "./UIMediatorGameAd";
import VivoBannerAd from "src/dMyGame/vivoPlatform/VivoBannerAd";



export class UIMediatorLuckyWheel extends BaseUIMediator<FGUI_PGameMainLuckyWheel> {
    private static m_instance: UIMediatorLuckyWheel;

    private itemList: WheelItem[] = [];
    private totalWeight: number = 0;
    private testAddUI: FGUI_GameAd;

    /** 单例 */
    public static get instance(): UIMediatorLuckyWheel {
        if (!this.m_instance) {
            this.m_instance = new UIMediatorLuckyWheel();
            this.m_instance._classDefine = FGUI_PGameMainLuckyWheel;
        }
        return this.m_instance;
    }

    private constructor() {
        super();

    }

    protected _layer: EUILayer = EUILayer.Main;

    private InitArray() {
        this.itemList.push({ weight: 10, progress: 0, type: TypeName.Money, count: 86664 });
        this.itemList.push({ weight: 10, progress: 0, type: TypeName.Diamond, count: 20 });
        this.itemList.push({ weight: 10, progress: 0, type: TypeName.MuchMoney, count: 173328 });
        this.itemList.push({ weight: 10, progress: 0, type: TypeName.Diamond, count: 40 });
        this.itemList.push({ weight: 10, progress: 0, type: TypeName.Money, count: 21666 });
        this.itemList.push({ weight: 10, progress: 0, type: TypeName.MuchDiamond, count: 100 });
        this.itemList.push({ weight: 10, progress: 0, type: TypeName.Diamond, count: 80 });
        this.itemList.push({ weight: 10, progress: 0, type: TypeName.LotsMoney, count: 256666 });


        for (let item of this.itemList) {
            this.totalWeight += item.weight;
            item.progress = this.totalWeight;
        }
    }

    private GetRandomItemIndex() {
        let random = Random.RangeInt(0, this.totalWeight)
        for (let i = 0; i < this.itemList.length; i++) {
            if (this.itemList[i].progress > random) {
                return i;
            }
        }
    }

    private GetAngle(num: number) {
        return num * -45;
    }

    _OnShow() {
        // this.isLuckPanel = true;
        super.Show();
        this.InitArray();
        // this.ui.m_close.onClick(this, this.Close);
        this.ui.m_btn_thanks.onClick(this, this.Close)
        this.ui.m_btn_WatchAd.on(Laya.Event.CLICK, this, this.OnWatchAd);
        this.ui.m_btn_start.on(Laya.Event.CLICK, this, this.OnClickBtnStart);
        this.RenderWheel();
        this.UpdatePanel();
        Uma.trackEvent(UmaEvent.LuckWheel);

        ///***原生广告调用******************* */
        UIMediatorGameAd.instance.ShowNativeAd();

        /**Baanner */
        let vivoObj = new VIVOData();
        VivoBannerAd.instance.showBannerFun(vivoObj);

    }

    UpdatePanel() {
        //读取ui状态信息
        let hasNum = MiscDataProxy.instance.Data.LuckWheelAdDayNum < MiscDataProxy.instance.Data.LuckWheelAdMaxDayNum
        if (!hasNum) {
            this.ui.m_State.selectedIndex = 2
        } else {
            this.ui.m_State.selectedIndex = MiscDataProxy.data.LuckWheelAdDayNum >= 2 ? 0 : 1;
        }
        this.ui.m_btn_start.m_text_num.text = MiscDataProxy.data.LuckWheelStartCount + "/2";
        let lastNum = MiscDataProxy.data.LuckWheelAdMaxDayNum - MiscDataProxy.data.LuckWheelAdDayNum;
        lastNum = lastNum < 0 ? 0 : lastNum;
        this.ui.m_text_residue.text = lastNum.toString()
    }

    private RenderWheel() {
        this.ui.m_wheel.m_item_0.m_type.selectedIndex = this.itemList[0].type;
        this.ui.m_wheel.m_item_0.m_num.text = "x" + this.itemList[0].count.toString()
        this.ui.m_wheel.m_item_1.m_type.selectedIndex = this.itemList[1].type;
        this.ui.m_wheel.m_item_1.m_num.text = "x" + this.itemList[1].count.toString()
        this.ui.m_wheel.m_item_2.m_type.selectedIndex = this.itemList[2].type;
        this.ui.m_wheel.m_item_2.m_num.text = "x" + this.itemList[2].count.toString()
        this.ui.m_wheel.m_item_3.m_type.selectedIndex = this.itemList[3].type;
        this.ui.m_wheel.m_item_3.m_num.text = "x" + this.itemList[3].count.toString()
        this.ui.m_wheel.m_item_4.m_type.selectedIndex = this.itemList[4].type;
        this.ui.m_wheel.m_item_4.m_num.text = "x" + this.itemList[4].count.toString()
        this.ui.m_wheel.m_item_5.m_type.selectedIndex = this.itemList[5].type;
        this.ui.m_wheel.m_item_5.m_num.text = "x" + this.itemList[5].count.toString()
        this.ui.m_wheel.m_item_6.m_type.selectedIndex = this.itemList[6].type;
        this.ui.m_wheel.m_item_6.m_num.text = "x" + this.itemList[6].count.toString()
        this.ui.m_wheel.m_item_7.m_type.selectedIndex = this.itemList[7].type;
        this.ui.m_wheel.m_item_7.m_num.text = "x" + this.itemList[7].count.toString()
    }


    //关闭
    private Close() {
        UIManagerProxy.instance.setUIState([
            {
                typeIndex: EUI.LuckyWheel, state: false
            }
        ], false);
    }

    //看视频抽奖
    private OnWatchAd() {
        PlatformManagerProxy.instance.PlatformInstance.ShowRewardVideoAd(
            Laya.Handler.create(this, () => {
                Audio.PlayAudio();
                Uma.trackEvent(UmaEvent.LuckWheelAD);
                if (this.isShow) {
                    this.Turn();
                }
            }),
            Laya.Handler.create(this, () => {
                PlatformManagerProxy.instance.PlatformInstance.ShowToast("未看完广告")
            }))
    }

    //开始抽奖
    private OnClickBtnStart() {
        this.Turn();
    }

    private _runtimeIndex = -1;
    private Turn() {
        this._runtimeIndex = this.GetRandomItemIndex();
        let angle = this.GetAngle(this._runtimeIndex) + (360 * 5) + Random.RangeInt(-20, 20);
        this.ui.m_wheel.rotation %= 360;
        Laya.Tween.clearAll(this.ui.m_wheel);
        Laya.Tween.to(this.ui.m_wheel, { rotation: angle }, 3000, Laya.Ease.sineOut, new Laya.Handler(this, this.CallBackTurn));
        //注销按钮监听事件
        this.ui.m_btn_start.off(Laya.Event.CLICK, this, this.OnClickBtnStart);
        this.ui.m_close.off(Laya.Event.CLICK, this, this.Close);
    }

    private CallBackTurn() {
        if (this._runtimeIndex == -1) {
            return;
        }
        MiscDataProxy.data.LuckWheelStartCount--;
        MiscDataProxy.data.LuckWheelAdDayNum++;
        let item = this.itemList[this._runtimeIndex];
        if (item.type == TypeName.Money || item.type == TypeName.MuchMoney || item.type == TypeName.LotsMoney) {
            UIFacade.PlayMoneyEffect(this.ui.m_wheel);
            MiscDataProxy.Money += item.count;
        }
        else {
            UIFacade.PlayDiamondEffect(this.ui.m_wheel);
            MiscDataProxy.Diamond += item.count;
        }
        this._runtimeIndex = -1;
        this.UpdatePanel();
        this.ui.m_btn_start.on(Laya.Event.CLICK, this, this.OnClickBtnStart);
        this.ui.m_close.on(Laya.Event.CLICK, this, this.Close);
        Laya.Tween.clearAll(this.ui.m_wheel);
    }


    _OnHide() {
        Laya.Tween.clearAll(this.ui.m_wheel);
        VivoBannerAd.instance.DestroyAd();

    }
}

interface WheelItem {
    weight: number;
    progress: number;
    type: number;
    count: number;
}

enum TypeName {
    Money = 0,
    MuchMoney,
    Diamond,
    MuchDiamond,
    LotsMoney,
}