/**
 * UI分层,按顺序显示
 */
export enum EUILayer {
    /** 背景页面 */
    Bg = 'EUILayer_Bg',
    /**Event，摄像机、点击、拖动事件层级 */
    EventLayer = "EventLayer",
    /**
     * 各种进度条
     */
    EventBar = "EventBar",
    /** 主界面  永远存在 */
    Main = "EUILayer_Main",
    /** 单UI 只能存在一个 */
    OneUI = 'EUILayer_OneUI',
    //人物对话面板
    TalkPanel = "TalkPanel",
    /** 面板  可以有很多 */
    Panel = "EUILayer_Panel",
    /**
     * 雇员信息面板，替换面板
     */
    ToiletPanel="ToiletPanel",
    StaffInfoPanel = "StaffInfoPanel",
    /**招聘市场 */
    MarketPanel = "MarketPanel",
    /** 弹窗 */
    Popup = "EUILayer_Popup",
    /** 道具 */
    Prop = 'EUILayer_Prop',
    /** 小部件 */
    Tip = "EUILayer_Tip",
    /** 暂停 */
    Pause = 'EUILayer_Pause',
    /** 设置 */
    Set = 'EUILayer_Set',
    /** 最高 */
    Top = 'EUILayer_Top',
    /** 加载页面 */
    Loading = 'EUILayer_Loading',

    Guide = "Guide",
    NativeAD = "NativeAD",
}