import Building from "./api/Building";
import MesManagerClass from "../../dMyGame/Manager/MesManager";
import {EEventScene} from "../../dMyGame/EventEnum/EEventScene";
import {UIMediatorCountTimePanel} from "../../UI/UIMediatorCountTimePanel";
import {CameraManager} from "../CameraManager";

export class Canteen extends Building {
    onLateUpdate() {
        UIMediatorCountTimePanel.instance.UpdateCanteenTimePos(this.transform.position);
    }

    protected ShowOutlook(ismale: boolean, index: number, showEffect: boolean = false) {
        let ol = this.sprite3D.getChildByName("outlook")
        ol.active = true;
        super.ClosePlaceholder();
        this._equipmentNode.active = true;
        if (showEffect) {
            this.PlayBuildEffect();
        }
    }
    OnClick() {
        super.OnClick();
        CameraManager.instance.FocusBuild(23);

    }
}