import RootLocalStorageData from "../aTGame/Data/RootLocalStorageData";
import RootLocalStorageSaveNaWft from "../aTGame/Data/RootLocalStorageSave";
import UIManagerBuildConfigProxy from "../Config/UIManagerBuildConfig";
import {EEventScene} from "../dMyGame/EventEnum/EEventScene";
import MesManagerClass from "../dMyGame/Manager/MesManager";
import {StringName} from "../Enum/StringName";
import {NullReferenceException} from "../lty/Exception/NullReferenceException";
import BuildingData from "./BuildingData";
import {FunctionID} from "./FunctionID";
import {BuildDefaultSlotConfigProxy} from "../Config/BuildDefaultSlotConfigProxy";
import {SlotData} from "./SlotData";
import UISlotConfigProxy from "../Config/UISlotConfigProxy";
import {EEventUI} from "../dMyGame/EventEnum/EEventUI";
import {UpdateBuildingEventData, UpdateSlotEventData} from "./UpdateEventData";
import {SlotUIDConst} from "../dMyGame/_config/SlotUIDConst";

export class TData extends BuildingData {
    constructor(male: boolean, index: number) {
        super();
        this.isMale = male;
        this.index = index;
        if (male) {
            this.title = "男厕所No." + index;
            this.name = "maleToilet_" + index;
            this.functionID = FunctionID.MaleToilet;
        } else {
            this.title = "女厕所No." + index;
            this.name = "femaleToilet_" + index;
            this.functionID = FunctionID.FemaleToilte;
        }
        this.slots = [];
        let count = 0;
        let datas = BuildDefaultSlotConfigProxy.instance.dataList;
        if (datas && datas.length > 0) {
            //遍历一遍列表，拿到可用的slot数量
            for (let i = 0; i < datas.length; i++) {
                if (male) {
                    if (datas[i].maleToiletSolt > -1) {
                        count++;
                    }
                }
                else{
                    if (datas[i].femaleToiletSolt > -1) {
                        count++;
                    }
                }

            }
            //初始化默认的slot
            if (count > 0) {
                this.slots = new Array(count);
                for(let i =0;i<count;i++){
                    let slot = new SlotData();
                    let data = datas[i];
                    slot.id = i;
                    if(male){
                        slot.uid = data.maleToiletSolt;
                        slot.name = UISlotConfigProxy.instance.dataList[slot.uid].name;
                        slot.level = data.male_Slot_Level;
                        //厕所里面只有马桶可以使用
                        slot.usable = SlotUIDConst.data.slot_mt_config == slot.uid;
                    }
                    else{
                        slot.uid = data.femaleToiletSolt;
                        slot.name = UISlotConfigProxy.instance.dataList[slot.uid].name;
                        slot.level = data.female_Slot_Level;
                        //厕所里面只有马桶可以使用
                        slot.usable = SlotUIDConst.data.slot_mt_config == slot.uid;
                    }
                    this.slots[i]= slot;
                }
            } else {
                console.warn(this.name, "没有可用的SLot");
            }
        }
    }
}

export class ToiletData extends RootLocalStorageData {
    /**所有厕所的数据列表 */
    list: BuildingData[];
}

export class ToiletDataProxy extends RootLocalStorageSaveNaWft<ToiletData> {
    constructor() {
        super();
        MesManagerClass.instance.on3D(EEventScene.UpdateBuildingData, this, this.UpdateBuildData);
        MesManagerClass.instance.onUI(EEventUI.UpdateSlotData,this, this.UpdateSlotData);
    }

    private static _instance: ToiletDataProxy;
    public static get instance() {
        if (this._instance == null) {
            this._instance = new ToiletDataProxy();
        }
        return this._instance;
    }

    private _data: ToiletData;
    private _dict: Map<string, TData> = new Map<string, TData>();

    public static get data() {
        return this.instance._data;
    }

    protected get _saveName(): string {
        return "TolietData";
    }

    protected getNewData(): ToiletData {
        return new ToiletData();
    }

    public InitData(): void {
        this._data = this._ReadFromFile();
        if (this._data == null || this._data.list == null) {
            //第一次进行游戏初始化数据
            console.log("第一次进行游戏,初始化厕所数据...");
            this._data.list = [];
            this.InitTolietData();
            this._SaveToDisk(this._data);
        }
        for (let data of this._data.list) {
            this._dict.set(data.name, data);
        }
    }

    /**第一次游戏初始化厕所 */
    private InitTolietData() {
        let minFe = UIManagerBuildConfigProxy.instance.dataList[1].minCount;
        let maxFe = UIManagerBuildConfigProxy.instance.dataList[1].maxCount;
        let minMa = UIManagerBuildConfigProxy.instance.dataList[0].minCount;
        let maxMa = UIManagerBuildConfigProxy.instance.dataList[0].maxCount;
        //开放女厕所
        let num = minFe * 2;
        for (let i = 1; i <= num; i++) {
            let t = new TData(false, i);
            t.active = true;
            this._data.list.push(t);
        }
        //开放男厕所
        for (let i = 1; i <= minMa; i++) {
            let t = new TData(true, i);
            t.active = true;
            this._data.list.push(t);
        }
        //关闭女厕所
        let size = (maxFe - minFe) * 2;
        for (let i = 1; i <= size; i++) {
            let t = new TData(false, i + num);
            t.active = false;
            this._data.list.push(t);
        }
        size = maxMa - minMa;
        //关闭男厕所
        for (let i = 1; i <= size; i++) {
            let t = new TData(true, i + minMa);
            t.active = false;
            this._data.list.push(t);
        }
    }

    /**更新建造信息*/
    private UpdateBuildData(data: { Name: string, Count: number }) {
        if (data.Name == StringName.femaleToilet) {
            let sec = data.Count * 2;
            let fst = sec - 1;
            let fstName = data.Name + "_" + fst.toString();
            let secName = data.Name + "_" + sec.toString();
            let fstBuilding = this._dict.get(fstName);
            let secBuilding = this._dict.get(secName);
            if (fstBuilding) {
                fstBuilding.active = true;
                let buildData:UpdateBuildingEventData = {buildingName:fstBuilding.name,buildingData:fstBuilding};
                MesManagerClass.instance.event3D(EEventScene.UpdateBuildingOutlookEvent, buildData);
            } else {
                throw new NullReferenceException("获取厕所信息失败:" + fstName);
            }
            if (secBuilding) {
                secBuilding.active = true;
                let buildData:UpdateBuildingEventData = {buildingName:secBuilding.name,buildingData:secBuilding};
                MesManagerClass.instance.event3D(EEventScene.UpdateBuildingOutlookEvent, buildData);
            } else {
                throw new NullReferenceException("获取厕所信息失败:" + secName);
            }
        } else if (data.Name == StringName.maleToilet) {
            let buildingName = data.Name + "_" + data.Count.toString();
            let build = this._dict.get(buildingName);
            if (build) {
                build.active = true;
                let buildData:UpdateBuildingEventData = {buildingName:build.name,buildingData:build};
                MesManagerClass.instance.event3D(EEventScene.UpdateBuildingOutlookEvent, buildData);
            } else {
                throw new NullReferenceException("获取厕所信息失败:" + buildingName);
            }
        }
        this._SaveToDisk(this._data);
    }

    private UpdateSlotData(mes:UpdateSlotEventData){
        let funcID = mes.building.functionID;
        if(funcID == FunctionID.MaleToilet || funcID == FunctionID.FemaleToilte) {
            let tdata = this._dict.get(mes.building.buildingName);
            //不在本信息代理中不管理
            if (tdata) {
                SlotData.UpdateSlotData(mes,tdata.slots);
                this._SaveToDisk(this._data);
            } else {
                console.error("获取数据失败！", mes);
            }
        }
    }
}