import FGuiRootManager from "src/aTGame/UI/FGUI/FGuiRootManager";
import VIVOData from "src/cFrameBridge/Platform/Data/VIVOData";
import { GuideDataProxy } from "src/Data/GuideDataProxy";
import VivoPlatform, { INativeAd, INativeAdOnLoadData } from "src/dMyGame/vivoPlatform/VivoPlatform";
import { NZsSdk } from "src/zsSDk/NZsSdk";
import ZsSdkStart from "src/zsSDk/ZsSdkStart";
import BaseUIMediator from "../aTGame/UI/FGUI/BaseUIMediator";
import { EUILayer } from "../aTGame/UI/FGUI/EUILayer";
import FGUI_GameAd from "../FGUI/GameAd/FGUI_GameAd";
//import ad from '@service.ad';

export class UIMediatorGameAd extends BaseUIMediator<FGUI_GameAd> {
    private static m_instance: UIMediatorGameAd;
    /** 单例 */
    public static get instance(): UIMediatorGameAd {
        if (!this.m_instance) {
            this.m_instance = new UIMediatorGameAd();
            this.m_instance._classDefine = FGUI_GameAd;
        }
        return this.m_instance;
    }
    vivoObj: VIVOData = null;

    TempFGUI_GameAd: FGUI_GameAd;
    TempNativeAd: INativeAdOnLoadData;
    private SDKdata: NZsSdk.IZsSdkCfg;

    private CanShow: Boolean = true;
    private constructor() {
        super();
        this.SDKdata = ZsSdkStart.instance.data;
    }
    protected _layer = EUILayer.NativeAD;

    private m_data: INativeAdOnLoadData;

    public ShowNativeAd() {
        if (!GuideDataProxy.IsCompleteAllGuide) {
            console.warn("引导未结束，不显示原生广告！");
            return;
        }
        if (!this.CanShow) {
            console.warn("原生广是否可显示: ", this.CanShow);
            return;
        }
        console.log("调用原生广告");

        VivoPlatform.instance.destroy();
        VivoPlatform.instance.nativeAdLoadAsync().then((data) => {
            console.log('原生广告数据', data);
            this.m_data = data[0];
            /**判断版本号 是否一致，不一致时 误触 和 展示为 关闭0 */
            if (this.SDKdata.zs_version == ZsSdkStart.instance.version) {
                if (this.SDKdata.zs_native_limit == 1) {

                    VivoPlatform.instance.nativeAd.reportAdShow({
                        adId: data[0].adId,
                    });
                    this.TempFGUI_GameAd = FGuiRootManager.AddUI(FGUI_GameAd, EUILayer.NativeAD) as FGUI_GameAd;
                    this.setAdData(this.TempFGUI_GameAd, this.m_data);
                    this.CanShow = false;
                }
            }

        })

    }
    /** */
    private setAdData(GameAdUI: FGUI_GameAd, nativeAd: INativeAdOnLoadData) {

        this.TempFGUI_GameAd = GameAdUI;
        this.TempNativeAd = nativeAd;


        if (!nativeAd.imgUrlList[0]) {
            //纯文本时
            GameAdUI.m_AdIcon.m_FIeld.text = nativeAd.title;

            //获取广告logo
            GameAdUI.m_logo.url = nativeAd.logoUrl;
            console.warn("纯文本时", !nativeAd.imgUrlList[0]);
        } else {
            GameAdUI.m_title.text = nativeAd.title;
            // //获取广告UI图片
            let _url = nativeAd.imgUrlList[0];
            // if (/(%25)/g.test(_url)) {
            //     _url = _url.replace(/%25/g, '%');
            // }
            // Laya.loader.load(_url, Laya.Handler.create(this, (t: Laya.Texture) => {
            //     GameAdUI.m_AdIcon.m_loaderIcon.displayObject.texture = t;
            // }), null);

            GameAdUI.m_AdIcon.m_loaderIcon.url = _url;
            //获取广告logo
            GameAdUI.m_logo.url = nativeAd.logoUrl;
            console.log("广告UI图片===>>>>// 3：图文混合" + nativeAd.imgUrlList[0]);
        }

        //“zs_native_click_switch”为0则误触为 关闭。误触关闭时， 按钮文字为“点击跳 过”，点击后可以关 闭广告

        if (this.SDKdata.zs_native_click_switch == 0) {
            GameAdUI.m_JumpBtn.m_BtnTitle.text = "点击跳过"

        } else {
            GameAdUI.m_JumpBtn.m_BtnTitle.text = "点击查看"
        }

        /*
        X按钮放至广告图片上， 接入zs_native_touch_switch原生广告小关闭按钮 开关（0关，1开），
        关 闭时关闭按钮大小为 38*38；开启时关闭按 钮大小仍为38*38，点 击区域变为35*35
        */
        //关闭按钮
        if (this.SDKdata.zs_native_click_switch == 0) {
            GameAdUI.m_forkClickArea.width = 38;
            GameAdUI.m_forkClickArea.height = 38;
        } else {
            GameAdUI.m_forkClickArea.width = 35;
            GameAdUI.m_forkClickArea.height = 35;

        }
        GameAdUI.m_forkClickArea.onClick(this, this.onLeftUpCloseBtn);

        //添加看广告点击事件
        GameAdUI.m_AdIcon.onClick(this, () => {
            /**关闭 */
            this.onLeftUpCloseBtn();
            VivoPlatform.instance.nativeAd.reportAdClick({
                adId: nativeAd.adId,
            });

        });
        //跳过按钮
        GameAdUI.m_JumpBtn.onClick(this, this.onJumpAdBtn);

    }
    onLeftUpCloseBtn() {
        try {
            this.TempFGUI_GameAd && !this.TempFGUI_GameAd.isDisposed && this.TempFGUI_GameAd.dispose();
            this.CanShow = true;

        } catch (e) {
            console.log("”关闭onLeftUpCloseBtn===+++++++++++++++++>>>>" + e);
        }
    }

    onJumpAdBtn() {
        if (this.SDKdata.zs_native_click_switch == 0) {
            //按钮文字为“点击跳 过”
            try {
                this.onLeftUpCloseBtn();
            } catch (e) {
                console.log("点击跳 过”===+++++++++++++++++>>>>" + e);
            }
        }
        else {
            //点击查看广告按钮
            try {
                this.onLeftUpCloseBtn();
                VivoPlatform.instance.nativeAd.reportAdClick({
                    adId: this.TempNativeAd.adId,
                });
            } catch (e) {
                console.log("查看广告按钮===+++++++++++++++>>>>" + e);
            }

        }
    }


}
