import Awaiters from "../aTGame/Async/Awaiters";
import BaseUIMediator from "../aTGame/UI/FGUI/BaseUIMediator";
import { EUILayer } from "../aTGame/UI/FGUI/EUILayer";
import MathEx from "../aTGame/Utils/MathEx";
import FGUI_FlyPanel from "../FGUI/EffectLayer/FGUI_FlyPanel";
import FGUI_view_fly_diamond from "../FGUI/EffectLayer/FGUI_view_fly_diamond";
import FGUI_view_fly_Money from "../FGUI/EffectLayer/FGUI_view_fly_Money";
import FGUI_GuidePanel from "../FGUI/Guide/FGUI_GuidePanel";
import { Random } from "../lty/class/Random";

export default class UIMediatorFlyPanel extends BaseUIMediator<FGUI_FlyPanel> {
    protected _layer = EUILayer.Tip;
    private static _instance: UIMediatorFlyPanel;
    public static get instance(): UIMediatorFlyPanel {
        if (this._instance == null) {
            this._instance = new UIMediatorFlyPanel();
            this._instance._classDefine = FGUI_FlyPanel;
        }
        return this._instance;
    }
    private constructor() {
        super();
    }

    /**
   * 先往外扩,然后飞到指定位置
   * @param fromObj 
   * @param toObj 
   * @param flyIcon 
   * @param flyCount 
   * @param flyTime 
   */
    public FlyCoins(fromObj: fgui.GObject, toObj: fgui.GObject, flyCount: number = 100,
        flyTime: number = 1, circleRadius: number = 10) {
        let startPos = fromObj.localToGlobal();
        let stopPos = toObj.localToGlobal();
        let cacheTime = flyTime * 0.6 * 1000;
        let finalTime = flyTime * 0.4 * 1000;
        for (let i = 0; i < flyCount; ++i) {
            let flyCoin = FGUI_view_fly_Money.createInstance();
            this.ui.addChild(flyCoin);
            // if (flyIcon != null) {
            //     flyCoin.m_icon.url = flyIcon;
            // }
            flyCoin.setXY(startPos.x, startPos.y);
            let cachePos = new Laya.Vector2(startPos.x + MathEx.Random(-circleRadius, circleRadius),
                startPos.y + MathEx.Random(-circleRadius, circleRadius));
            cacheTime = Random.Range(0.85 * cacheTime, cacheTime);
            Laya.Tween.to(flyCoin, { x: cachePos.x, y: cachePos.y }, cacheTime, Laya.Ease.quadInOut, Laya.Handler.create(this, () => {
                Laya.Tween.to(flyCoin, { x: stopPos.x, y: stopPos.y }, finalTime, Laya.Ease.quadInOut, Laya.Handler.create(this, () => {
                    flyCoin.dispose();
                }));
            }))
        }
        Awaiters.Seconds(flyTime);
    }

    public FlyDiamonds(fromObj: fgui.GObject, toObj: fgui.GObject, flyCount: number = 100,
        flyTime: number = 1, circleRadius: number = 10) {
        let startPos = fromObj.localToGlobal();
        let stopPos = toObj.localToGlobal();
        let cacheTime = flyTime * 0.6 * 1000;
        let finalTime = flyTime * 0.4 * 1000;
        for (let i = 0; i < flyCount; ++i) {
            let flyDiamond = FGUI_view_fly_diamond.createInstance();
            this.ui.addChild(flyDiamond);
            flyDiamond.setXY(startPos.x, startPos.y);
            let cachePos = new Laya.Vector2(startPos.x + MathEx.Random(-circleRadius, circleRadius),
                startPos.y + MathEx.Random(-circleRadius, circleRadius));
            cacheTime = Random.Range(0.85 * cacheTime, cacheTime);
            Laya.Tween.to(flyDiamond, { x: cachePos.x, y: cachePos.y }, cacheTime, Laya.Ease.quadInOut, Laya.Handler.create(this, () => {
                Laya.Tween.to(flyDiamond, { x: stopPos.x, y: stopPos.y }, finalTime, Laya.Ease.quadInOut, Laya.Handler.create(this, () => {
                    flyDiamond.dispose();
                }));
            }))
        }
        Awaiters.Seconds(flyTime);
    }
}
