import {ArgumentNullException} from "../Exception/ArgumentNullException";

export class LayaBehaviour extends Laya.Script3D {

    private _sprite3D: Laya.Sprite3D;
    public get sprite3D(): Laya.Sprite3D {
        if (this._sprite3D == null) {
            this._sprite3D = this.owner as Laya.Sprite3D;
        }
        return this._sprite3D;
    }
    private _transform: Laya.Transform3D;
    public get transform(): Laya.Transform3D {
        if (this._transform == null) {
            this._transform = (this.owner as Laya.Sprite3D).transform;
        }
        return this._transform;
    }    

    /**在 delay 毫秒后调用 method 方法。 */
    public Invoke(delay: number, method: Function, args?: any[], coverBefore?: boolean) {
        Laya.timer.once(delay, this, method, args, coverBefore);
    }

    /**在 delay 毫秒后调用 method 方法，然后每 repeatRate 毫秒调用一次。 
     * 注意：如果将repeatRate时间刻度设置0，该函数不起作用。
    */
    public InvokeRepeating(delay: number, repeatRate: number, method: Function, args?: any[], coverBefore?: boolean,
        jumpFrame?: boolean) {
        if (repeatRate > 0) {
            Laya.timer.once(delay, this, () =>
                Laya.timer.loop(repeatRate, this, method, args, coverBefore, jumpFrame));
        }
    }

    /**取消该 Behaviour 上的所有 method Invoke调用。 */
    public CancelAllInvoke() {
        Laya.timer.clearAll(this);
    }

    /**取消该 Behaviour 上的所有 method Invoke调用。 */
    public CancelInvoke(method: Function) {
        Laya.timer.clear(this, method);
    }

    public GetChildByName<T extends laya.display.Node>(name:string):T{
        if(name == null || name ==''){
            throw new ArgumentNullException();
        }
        else{
            return this.sprite3D.getChildByName(name) as T;
        }
    }

    public GetSpriteChildByName(name){
        return this.GetChildByName(name) as Laya.Sprite3D;
    }
    
    public Instantiate(sprite3D: Laya.Sprite3D, parent?: Laya.Node) {
        return Laya.Sprite3D.instantiate(sprite3D, parent);
    }

    /**开关物体下所有子物体的显示 */
    public ToggleAllChild(sprite3D: Laya.Sprite3D, active: boolean) {
        if (sprite3D.numChildren > 0) {
            for (let i = 0; i < sprite3D.numChildren; i++) {
                let child = sprite3D.getChildAt(i);
                child.active = active;
            }
        }
    }

    //深度遍历，获取搜索到的第一个物体
    public DeepFind(childName:string,node:Laya.Node){
        let count = node.numChildren;
        let target:Laya.Node= null;
        if(count > 0){
            target = node.getChildByName(childName);
            if (target) {
                return target;
            }
            else{
                for(let i = 0;i< count;i++){
                    let c = node.getChildAt(i);
                    target = this.DeepFind(childName,c);
                    if(target){
                        break;
                    }
                }
            }
        }
        return target;
    }

}