import { ArgumentException } from "./ArgumentException";

export class ArgumentNullException extends ArgumentException{
    name:string = 'ArgumentNullException';
    message:string="参数为空（不允许）";
    data;
    constructor(msg?: string,...data) {
        super(msg);
        if (msg) {
            this.message = msg;
        }
        this.data = data;
    }
}