import BaseUIMediator from "../aTGame/UI/FGUI/BaseUIMediator";
import FGUI_PGameMainRecordShare from "../FGUI/Main/FGUI_PGameMainRecordShare";
import { EUI } from "../dMyGame/Enum/EUI";
import { UIFacade } from "./UIFacade";
import MesManagerClass from "../dMyGame/Manager/MesManager";
import { EEventUI } from "../dMyGame/EventEnum/EEventUI";
import PlatformManagerProxy from "../aTGame/Platform/PlatformManagerProxy";
import { MiscDataProxy } from "../Data/MiscDataProxy";
import VivoBannerAd from "src/dMyGame/vivoPlatform/VivoBannerAd";
import VIVOData from "src/cFrameBridge/Platform/Data/VIVOData";

export class UIMediatorRecordShare extends BaseUIMediator<FGUI_PGameMainRecordShare>{
    private static m_instance: UIMediatorRecordShare;

    /** 单例 */
    public static get instance(): UIMediatorRecordShare {
        if (!this.m_instance) {
            this.m_instance = new UIMediatorRecordShare();
            this.m_instance._classDefine = FGUI_PGameMainRecordShare;
        }
        return this.m_instance;
    }

    private constructor() {
        super();
    }

    protected _OnShow() {
        super._OnShow();
        this.ui.m_bg.m_btn_close.onClick(this, () => {
            UIFacade.ClosePanel(EUI.RecordVideo);
        })
        /**Baanner */
        let vivoObj = new VIVOData();
        VivoBannerAd.instance.showBannerFun(vivoObj);
    }

    private ShowPanel() {
        UIFacade.ShowPanel(EUI.RecordVideo)
    }

    private Close() {
        UIFacade.ClosePanel(EUI.RecordVideo)
        VivoBannerAd.instance.DestroyAd();
    }

    public ShowRecord() {
        this.ShowPanel()
        this.ui.m_state.selectedIndex = 0;
        this.ui.m_btn_record.onClick(this, this.StartRecord)
    }

    public ShowShare() {
        this.ShowPanel()
        this.ui.m_state.selectedIndex = 1;
        this.ui.m_btn_record.onClick(this, this.ShareVideo)
    }

    public StartRecord() {
        console.log("StartRecord")
        MesManagerClass.instance.eventUI(EEventUI.OnStartRecord);
        this.Close();
        PlatformManagerProxy.instance.PlatformInstance.recordManager.StartRecord(
            Laya.Handler.create(this, () => {
                PlatformManagerProxy.instance.PlatformInstance.ShowToast("录制开始")
            }),
            Laya.Handler.create(this, () => {
                PlatformManagerProxy.instance.PlatformInstance.ShowToast("录制结束")
                MesManagerClass.instance.eventUI(EEventUI.OnStopRecord);
                this.ShowShare()
            })
        )
    }

    //TODO 分享视频
    public ShareVideo() {
        console.log("ShareVideo")
        // MesManagerClass.instance.eventUI(EEventUI.OnStopRecord);
        PlatformManagerProxy.instance.PlatformInstance.recordManager.ShareVideo(
            Laya.Handler.create(this, () => {
                MiscDataProxy.Diamond += 300;
                UIFacade.PlayDiamondEffect(this.ui.m_btn_record);
                UIFacade.ClosePanel(EUI.RecordVideo)
            }),
            Laya.Handler.create(this, () => {
                PlatformManagerProxy.instance.PlatformInstance.ShowToast("分享取消")

            }),
            Laya.Handler.create(this, () => {
                PlatformManagerProxy.instance.PlatformInstance.ShowToast("分享失败")
            })
        )
    }
}
