import { SData } from "../../Data/SinkDataProxy";
import Building from "./api/Building";
import {Mathf} from "../../lty/class/Mathf";
import {FunctionID} from "../../Data/FunctionID";
import {CameraManager} from "../CameraManager";
/**女洗手区 */
export class Sink extends Building {
    _standardTime = 12;
    ShowOutlook() { }
    ShowPlaceholder() { }
    ClosePlaceholder() { }
    OnClick() {
        if(this.functionID == FunctionID.FemaleSink){
            CameraManager.instance.FocusBuild(19)
        }
        else{
            CameraManager.instance.FocusBuild(18)
        }
    }

    onStart() {
        super.onStart();
        this.entrance.active = false;
    }

}