import {_RuntimeFacPosInfo, Guest, StateName} from "./Guest";
import {FunctionID} from "../../Data/FunctionID";
import {Vector3} from "../../lty/class/Vector3";
import {BuildingManager} from "../BuildingManager";
import {CameraManager} from "../CameraManager";
import {IGuestPoolConfigData} from "../../Config/GusetPoolConfig";
import MesManagerClass from "../../dMyGame/Manager/MesManager";
import {EEventUI} from "../../dMyGame/EventEnum/EEventUI";


export class GuideUncle extends Guest {
    public static get instance(): GuideUncle {
        return this._instance;
    }

    private static _instance: GuideUncle;
    private _inGuide = false;


    onAwake() {
        GuideUncle._instance = this;
        super.onAwake()
    }

    onUpdate() {
        super.onUpdate();
        if (this._inGuide) {
            CameraManager.instance.LookAt(this.transform.position);
        }
    }

    onLateUpdate() {
        super.onLateUpdate();
    }

    protected DispatchEvent(time?: number) {
        super.DispatchEvent(6);
    }

    protected PlayMoveAnimation() {
        super.PlayMoveAnimation();
        this.moveSpeed = 3.5;
    }

    ReInit(data: IGuestPoolConfigData, attachment: Laya.Sprite3D) {
        Laya.timer.clearAll(this);
        this.sprite3D.active = true;
        this.animator.enabled = true;
        this._wpIndex = 0;
        this._data = data;
        this.gender = data.Gender[0];
        this.moveSpeed = data.MoveSpeed;
        this._currFuncIndex = 0;
        Laya.timer.once(1000, this, () => {
            this.GetEndPoint();
        });
    }

    protected HandleFunction() {
        super.HandleFunction();
        if (this._runtimeFuncPosInfo.fac.belonger.functionID == FunctionID.MaleSink) {
            //TODO 派发洗手事件
            this._inGuide = false;
            MesManagerClass.instance.eventUI(EEventUI.GuideToNext);
        }
    }


    private _hasCompleteToilet = false;

    protected ExitBuilding() {
        if (this._runtimeFuncPosInfo.fac.belonger.functionID == FunctionID.MaleToilet) {
            this.SetWayPoints([{position: this._runtimeFuncPosInfo.fac.belonger.exit.transform.position as Vector3}]).OnComplete(() => {
                super.PlayAnimationByState(StateName.Idle);
            }, "等待")
            BuildingManager.instance.ResetFac(this);
            BuildingManager.instance.TryOpenDoor(this.transform.position, false);
            this._hasCompleteToilet = true;
            //TODO 写信号
            if (this._inGuide) {
                MesManagerClass.instance.eventUI(EEventUI.GuideToNext, 5);
            }
            return;
        } else {
            super.ExitBuilding();
            this._inGuide = false;
        }
    }

    /**
     * 开始
     * @constructor
     */
    public Start() {
        // super.GetEndPoint()
        this._inGuide = true;
        if (this._hasCompleteToilet) {
            MesManagerClass.instance.eventUI(EEventUI.GuideToNext, 5);
        }
    }

    /**
     * 继续执行下一步，退出厕所，进行洗手
     * @constructor
     */
    public Continue() {
        this.SetWayPoints([{position: this._runtimeFuncPosInfo.fac.belonger.entrance.transform.position as Vector3}]).OnComplete(() => {
            super.DoNext();
        }, "执行下一步")
        Laya.timer.once(600, this, () => BuildingManager.instance.TryCloseDoor(this.transform.position));
    }


}