import {StringName} from "../../Enum/StringName";
import Building from "./api/Building";
import MesManagerClass from "../../dMyGame/Manager/MesManager";
import {EEventScene} from "../../dMyGame/EventEnum/EEventScene";
import {FunctionID} from "../../Data/FunctionID";
import {CameraManager} from "../CameraManager";

export class Toilet extends Building {

    _standardTime = 15;
    print() {
        console.log(this.buildingName, this.slotPosList);
    }


    /**显示男女厕所外观,并关闭占位，显示设备节点 */
    protected ShowOutlook(ismale: boolean, index: number, showEffect: boolean = false) {
        this.ClosePlaceholder();
        if (ismale) {
            this.sprite3D.getChildByName(StringName.cs_nan).active = true;
            this.sprite3D.getChildByName(StringName.cs_nv).active = false;
            this.sprite3D.getChildByName(StringName.toiletWall).active = false;
        } else {
            this.sprite3D.getChildByName(StringName.cs_nan).active = false;
            this.sprite3D.getChildByName(StringName.cs_nv).active = true;
            //根据位置显示墙壁
            let openL = index % 2 == 0;
            this.sprite3D.getChildByName(StringName.toiletWall).active = !openL;
        }
        this._equipmentNode.active = true;
        if(showEffect){
            this.PlayBuildEffect();
        }
        MesManagerClass.instance.event3D(EEventScene.SetDoorActive,[this.transform.position,true]);
    }

    protected ShowPlaceholder() {
        super.ShowPlaceholder();
        MesManagerClass.instance.event3D(EEventScene.SetDoorActive,[this.transform.position,false]);
    }

    OnClick() {
        super.OnClick();
        if(this.functionID== FunctionID.MaleToilet){
            CameraManager.instance.FocusBuild(this._index-1);
        }
        else{
            CameraManager.instance.FocusBuild(this._index+5);
        }
    }


}