import MesManagerClass from "../dMyGame/Manager/MesManager";
import { IBuilding } from "../GameComponent/Buildings/api/IBuilding";
import { EEventScene } from "../dMyGame/EventEnum/EEventScene";
import { BuildingManager } from "../GameComponent/BuildingManager";
import UIManagerProxy from "../dMyGame/Manager/Proxy/UIManagerProxy";
import { EUI } from "../dMyGame/Enum/EUI";
import { FunctionID } from "../Data/FunctionID";
import { EEventUI } from "../dMyGame/EventEnum/EEventUI";
import LTUI from "../zml/LTUI";
import { UIMediatorTopPanel } from "./UIMediatorTopPanel";
import Building from "../GameComponent/Buildings/api/Building";
import { StringName } from "../Enum/StringName";
import { UIMediatorGameAd } from "./UIMediatorGameAd";

/**UI中介 */
export class UIFacade {
    private static _instance: UIFacade;
    public static get instance(): UIFacade {
        if (this._instance == null) {
            this._instance = new UIFacade();
        }
        return this._instance;
    }

    constructor() {
        MesManagerClass.instance.on3D(EEventScene.OnClickBuilding, this, this.OnClickBuilding);
    }

    Init() {
        console.log("UI中介初始化...")
    }

    //当前选择的建筑
    private _runtimecurrbuilding: IBuilding;
    public get building() {
        return this._runtimecurrbuilding;
    }

    private set _currentSelecedBuilding(value: IBuilding) {
        this._runtimecurrbuilding = value;
    }

    private get _currentSelectedBuilding() {
        return this._runtimecurrbuilding;
    }

    OnClickBuilding(buildingName: string) {
        let building = BuildingManager.instance.GetBuildingByName(buildingName);
        this._runtimecurrbuilding = building;
        if (this._currentSelecedBuilding) {
            console.log("已经选择了建筑，避免重复显示UI");
            return;
        }
        if (this._currentSelecedBuilding == building) {
            console.log("选择了同一个建筑，避免重复显示UI");
            return;
        }
        if (building) {
            if (building.active) {
                console.log("显示建筑对应的升级UI", building.buildingName);
                //XXX:显示建筑对应的升级UI
                UIManagerProxy.instance.setUIState([{ typeIndex: EUI.Staff, state: true }], false);
            } else {
                //显示管理UI
                UIManagerProxy.instance.setUIState([{ typeIndex: EUI.Manager, state: true }], false);
            }
        } else {
            console.error("获取建筑脚本失败", buildingName);
        }
    }

    public ResetCurrSelectedBuilding() {
        this._currentSelecedBuilding = null;
    }

    //打开清洁室面板
    ShowCleanRoom() {
        let cleanRoom = BuildingManager.instance.GetBuildingListByFunctionID(FunctionID.CleanRoom)[0];
        this.OnClickBuilding(cleanRoom.buildingName);
        MesManagerClass.instance.eventUI(EEventUI.ShowCleanRoomStaff);
    }
    ShowRoom(name: FunctionID) {
        let room = BuildingManager.instance.GetBuildingListByFunctionID(name)[0];
        this.OnClickBuilding(room.buildingName);
    }

    //钱的全屏特效
    static PlayMoneyEffect(fromObj: fgui.GObject) {
        // this.ShowPanel(EUI.UIMoneyEffect);
        let m = UIMediatorTopPanel.instance.ui.m_text_money;
        LTUI.FlyCoinsTo(fromObj, m);
    }
    static PlayDiamondEffect(fromObj: fgui.GObject) {
        let d = UIMediatorTopPanel.instance.ui.m_text_diamond;
        LTUI.FlyDiamondTo(fromObj, d);
    }

    static ShowPanel(eui: EUI) {
        UIManagerProxy.instance.setUIState([{
            typeIndex: eui,
            state: true
        }], false)
    }

    static ClosePanel(panel: EUI) {
        UIManagerProxy.instance.setUIState([{ typeIndex: panel, state: false }], false);
    }

    static ShowNativeAD() {
        UIMediatorGameAd.instance.Show();
    }

    static HideNativeAD() {
        UIMediatorGameAd.instance.Hide();
    }
}