import BaseUIMediator from "../aTGame/UI/FGUI/BaseUIMediator";
import FGUI_PGameOpenCafe from "../FGUI/Main/FGUI_PGameOpenCafe";
import UIManagerProxy from "../dMyGame/Manager/Proxy/UIManagerProxy";
import { EUI } from "../dMyGame/Enum/EUI";
import MesManagerClass from "../dMyGame/Manager/MesManager";
import { ClockEvent } from "../lty/plugin/Clock/ClockEvent";
import { MiscDataProxy } from "../Data/MiscDataProxy";
import { Time } from "../lty/class/Time";
import PlatformManagerProxy from "../aTGame/Platform/PlatformManagerProxy";
import { Audio } from "../GameComponent/Audio";
import { Uma, UmaEvent } from "../Uma";
import VivoBannerAd from "src/dMyGame/vivoPlatform/VivoBannerAd";
import VIVOData from "src/cFrameBridge/Platform/Data/VIVOData";

export class UIMediatorOpenCoffee extends BaseUIMediator<FGUI_PGameOpenCafe> {
    private static m_instance: UIMediatorOpenCoffee;

    /** 单例 */
    public static get instance(): UIMediatorOpenCoffee {
        if (!this.m_instance) {
            this.m_instance = new UIMediatorOpenCoffee();
            this.m_instance._classDefine = FGUI_PGameOpenCafe;
        }
        return this.m_instance;
    }

    private constructor() {
        super();
    }

    protected _OnShow() {
        this.ui.m_btn_close.onClick(this, this.Close);
        this.ui.m_btn_thanks.onClick(this, this.Close);
        MesManagerClass.instance.on(ClockEvent.UpdateOnLineTime, this, this.OnTick);
        this.ui.m_btn_Ad.onClick(this, this.OnClickAd);
        Uma.trackEvent(UmaEvent.OpenCoffeeUI);
        /**Baanner */
        let vivoObj = new VIVOData();
        VivoBannerAd.instance.showBannerFun(vivoObj);
    }

    protected _OnHide() {
        MesManagerClass.instance.off(ClockEvent.UpdateOnLineTime, this, this.OnTick);
        VivoBannerAd.instance.DestroyAd();
    }

    ShowPanel(number: number) {
        UIManagerProxy.instance.setUIState([{ typeIndex: EUI.OpenCoffee, state: true }], false)
        if (number == 0) {
            this.ui.m_type.selectedIndex = 0;
            this.ui.m_spirte.url = 'res/sprite/ui_build_icon/icon_library.png'

        } else {
            this.ui.m_type.selectedIndex = 1;
            this.ui.m_spirte.url = 'res/sprite/ui_build_icon/icon_canteen.png'
        }
        this.OnTick();
    }

    Close() {
        UIManagerProxy.instance.setUIState([{ typeIndex: EUI.OpenCoffee, state: false }], false);
    }

    private OnTick() {
        if (this.ui.m_type.selectedIndex == 0) {
            let ct = MiscDataProxy.data.LibraryLastTime;
            if (ct > 0) {
                this.ui.m_text_countDown.text = Time.ToTimeString(ct);
            } else {
                this.ui.m_text_countDown.text = "00:00:00"
            }

        } else {
            let lt = MiscDataProxy.data.CanteenLastTime;
            if (lt > 0) {
                this.ui.m_text_countDown.text = Time.ToTimeString(lt);
                this.ui.m_cafeBulidProgress.value = lt / 24 / 60 / 60 / 10;
            } else {
                this.ui.m_cafeBulidProgress.value = 0
                this.ui.m_text_countDown.text = "00:00:00"
            }
        }
    }

    OnClickAd() {
        let index = this.ui.m_type.selectedIndex;
        PlatformManagerProxy.instance.PlatformInstance.ShowRewardVideoAd(
            Laya.Handler.create(this, () => {
                Audio.PlayAudio();
                if (index == 0) {
                    MiscDataProxy.data.LibraryLastTime = MiscDataProxy.data.LibraryLastTime - 3 * 60 * 60 * 1000
                } else {
                    MiscDataProxy.data.CanteenLastTime = MiscDataProxy.data.CanteenLastTime - 3 * 60 * 60 * 1000
                }
                Uma.trackEvent(UmaEvent.CoffeeAD);
                if (this.isShow) {
                    this.OnTick();
                }
            }),
            Laya.Handler.create(this, () => {
                PlatformManagerProxy.instance.PlatformInstance.ShowToast("未看完广告")
            }))

    }

}