import { AllPrefabsNames } from '../../cFrameBridge/Config/ELevelSceneName';
import ConsoleExGalababn from '../Console/ConsoleEx';
import { EKeyResNameEnum } from './EKeyResName';
import KeyResManagerWFT from './KeyResManager';
/**
 * 必要的游戏资源路径
 * 游戏必须要得资源，会在游戏加载时被默认加载
 */
export default class EssentialResUrlsUntility {

    /**
     * 获取所有的其他资源列表
     * 资源列表必须在EssentialResUrls类中获取，便于清理
     */
    public static EssentialOtherResUrl(): string[] {
        let _URLs: string[] = [];
        //添加资源
        //
        //
        return _URLs;
    }

    //* ----------单个资源---------- *//

    /**
     * 获取关卡配置文件
     * @param _name 场景名字
     */
    public static levelConfigURL(_name: string): string {
        return KeyResManagerWFT.instance.getResURL(EKeyResNameEnum.LvConfig) + _name + '.json';
    }

    /**
     * 获取配置表RUL
     * @param _name 配置表名字
     */
    public static ConfigURL(_name: string): string {
        return KeyResManagerWFT.instance.getResURL(EKeyResNameEnum.Config) + _name;
    }

    /**
     * FGUI包
     * @param _name 包名
     */
    public static FGUIPack(_name: string): string {
        return KeyResManagerWFT.instance.getResURL(EKeyResNameEnum.FGUI) + _name;
    }

    //所有预制体名字列表
    private static _AllPrefabsNames: AllPrefabsNames = new AllPrefabsNames();
    /**
     * 预制体资源路径
     * @param prefab 预制体名字
     */
    public static prefab_url(prefab: string): string {
        //判断该预制体在那个场景中被导出的
        for (let _i in this._AllPrefabsNames) {
            if (this._AllPrefabsNames[_i].indexOf('@' + prefab + '@') != -1) {
                return KeyResManagerWFT.instance.getResURL(EKeyResNameEnum[_i]) + 'Conventional/' + prefab + '.lh';
            }
        }
        //
        console.log(...ConsoleExGalababn.packError('没有在场景找到预制体', prefab, '可能是没有导出场景预制体列表导致的。'));
    }
}