import { BaseConstDataProxy } from "../aTGame/Config/RootDataProxy"
import { SlotUIDConst } from "../dMyGame/_config/SlotUIDConst"

export default class SlotUIDConstProxy extends BaseConstDataProxy<SlotUIDConst.config> {
    private static _instance: SlotUIDConstProxy;
    /** 单例 */
    public static get instance(): SlotUIDConstProxy {
        if (this._instance == null) {
            this._instance = new SlotUIDConstProxy();
        }
        return this._instance;
    }
    //
    private constructor() { super(); }

    //初始化
    protected initData() {
        this.m_data = SlotUIDConst.data;
    }

    public static get Data() {
        return this.instance.data;
    }
}
