import RootLocalStorageData from "../aTGame/Data/RootLocalStorageData";
import RootLocalStorageSaveNaWft from "../aTGame/Data/RootLocalStorageSave";
import {BuildConfigProxy} from "../Config/BuildConfigProxy";
import {DefaultBuilingConfigProxy} from "../Config/DefaultBuilingConfigProxy";
import UIManagerBuildConfigProxy from "../Config/UIManagerBuildConfig";
import {EEventScene} from "../dMyGame/EventEnum/EEventScene";
import {EEventUI} from "../dMyGame/EventEnum/EEventUI";
import MesManagerClass from "../dMyGame/Manager/MesManager";
import {StringName} from "../Enum/StringName";
import {NullReferenceException} from "../lty/Exception/NullReferenceException";
import {FocusPositionProxy} from "../Config/FocusPositionProxy";
import {CameraManager} from "../GameComponent/CameraManager";
import {TaskEventData} from "../GameComponent/Task/Task";
import {TaskEvent} from "../GameComponent/Task/TaskEvent";
import {Uma, UmaEvent} from "../Uma";

//建筑管理者数据
export class BData {
    constructor(count, name, uid, money, water) {
        this.count = count;
        this.name = name;
        this.UID = uid;
        this.costWater = water;
        this.costMoney = money;
    }

    /**当前建造数量 */
    count: number;
    /**建筑名字 */
    name: string;
    /**建筑对应UI建筑配置表的ID */
    UID: number;
    /**当前等级升级花费 */
    costMoney: number;
    costWater: number;
}

export class BuildData extends RootLocalStorageData {
    list: BData[];

    //降序排列
    public ReSort() {
        this.list.sort((a: BData, b: BData) => {
            if (a.costMoney < b.costMoney) {
                return 1;
            } else if (a.costMoney == b.costMoney) {
                return 0;
            } else {
                return -1;
            }
        });
    }

}

/**建造的数据 */
export class BuildDataProxy extends RootLocalStorageSaveNaWft<BuildData> {
    private static _instance: BuildDataProxy;
    dict: Map<string, BData>;

    public static get instance() {
        if (this._instance == null) {
            this._instance = new BuildDataProxy();
        }
        return this._instance;
    }

    private constructor() {
        super();
        this.InitData();
        MesManagerClass.instance.onUI(EEventUI.BuildConstruction, this, this.OnBuildConstruction);
    }

    private Save() {
        console.log("保存数据");
        this._SaveToDisk(this._data);
    }

    public static get data() {
        return this.instance._data;
    }

    private _data: BuildData;
    public get data(): BuildData {
        return this._data;
    }

    protected get _saveName(): string {
        return "BuildData";
    }

    protected getNewData(): BuildData {
        return new BuildData();
    }

    public GetBuildDataByName(name: string) {
        return this.dict.get(name);
    }

    public InitData(): void {
        this._data = this._ReadFromFile();
        this.dict = new Map<string, BData>();
        //初始list,建造配置
        if (this._data.list == null || this._data.list.length <= 0) {
            this._data.list = [];
            for (let data of DefaultBuilingConfigProxy.instance.dataList) {
                this._data.list.push(new BData(data.defaultCount, data.name, data.UID, data.money, data.water));
            }
        }
        for (let data of this._data.list) {
            this.dict.set(data.name, data);
        }
    }

    OnBuildConstruction(BuildConstruction: EEventUI, buildingName: any) {
        let data = this.dict.get(buildingName);
        if (data) {
            let config = UIManagerBuildConfigProxy.instance.dataList[data.UID];
            let maxCount = config.maxCount;
            let money = null;
            let water = null;
            if (data.count <= maxCount) {
                //建造等级升级
                data.count += 1;
                let isMale = data.name == StringName.maleToilet;
                let nextCount = data.count + 1;
                //下一数量大于最大数量时不更新下一数量花费
                if(data.count +1 <= maxCount) {
                    switch (buildingName) {
                        case StringName.maleToilet:
                            money = BuildConfigProxy.instance.dataList[nextCount].maleMoney;
                            water = BuildConfigProxy.instance.dataList[nextCount].maleWater;
                            break;
                        case StringName.femaleToilet:
                            money = BuildConfigProxy.instance.dataList[nextCount].femaleMoney;
                            water = BuildConfigProxy.instance.dataList[nextCount].femaleWater;
                            break;
                        case StringName.disableRoom:
                            money = BuildConfigProxy.instance.dataList[nextCount].disableMoney;
                            water = BuildConfigProxy.instance.dataList[nextCount].disableWater;
                            break;
                        case StringName.babyRoom:
                            money = BuildConfigProxy.instance.dataList[nextCount].babyMoney;
                            water = BuildConfigProxy.instance.dataList[nextCount].babyWater;
                            break;
                        default:
                            console.log("没有设置case：", buildingName);
                            break;
                    }
                }
                console.log(buildingName, "下一级花费", money, water);
                MesManagerClass.instance.eventUI(EEventUI.CostMoney, data.costMoney);
                //更新下一级花费
                data.costMoney = money ? money : 0;
                data.costWater = water ? water : 0;
                this._tempDataCount = data.count;
                this._tempBuidlingName = buildingName;
                this._tempIsMale = isMale;
                //派发统计事件
                Uma.trackEvent(UmaEvent.Build,{"Building_name":data.name+"_Building_count_"+data.count.toString()});
                //完成建造,回调刷新UI
                MesManagerClass.instance.eventUI(EEventUI.BuildComplete, buildingName);
                //厕所建造时间进度条
                if(buildingName == StringName.maleToilet || buildingName == StringName.femaleToilet){
                    let evtData = new TaskEventData();
                    evtData.evt = buildingName == StringName.maleToilet? TaskEvent.BuildMaleToilet:TaskEvent.BuildFemaleToilet;
                    MesManagerClass.instance.eventUI(EEventUI.TaskEvent,evtData)
                    let config = this.GetFocusPos(data.count, isMale);
                    let index = this.GetFocusIndex(data.count, isMale);
                    let pos = new Laya.Vector3(config.x, 0, config.z);
                    CameraManager.instance.FocusPositionByIndex(index);
                }
                else{
                    if(buildingName == StringName.Library){
                        CameraManager.instance.FocusPositionByIndex(22);
                    }
                    else if(buildingName == StringName.canteen){
                        CameraManager.instance.FocusPositionByIndex(23);
                    }
                }
                Laya.timer.once(500,this,this.EventBuildingComplete);

                this._SaveToDisk(this._data);

            } else {
                console.log("当前建筑已经达到最大级:", buildingName, data.count, data);
            }
        } else {
            throw new NullReferenceException("按照名字获取Data失败，导致升级失败", BuildConstruction, buildingName);
        }
    }

    private _tempDataCount: number;
    private _tempIsMale: boolean;
    private _tempBuidlingName: string;

    private EventBuildingComplete() {
        //通知建筑管理者建造操作
        //开放下一级的block
        MesManagerClass.instance.event3D(EEventScene.OpenBuildBlock, [this._tempDataCount, this._tempIsMale])
        //完成建造，回调刷新对应的数据
        MesManagerClass.instance.event3D(EEventScene.UpdateBuildingData, {
            Name: this._tempBuidlingName,
            Count: this._tempDataCount
        });
        Laya.timer.clearAll(this);
    }

    private GetFocusPos(count: number, isMale: boolean) {
        let index = this.GetFocusIndex(count, isMale);
        return FocusPositionProxy.instance.dataList[index];
    }

    GetFocusIndex(count: number, isMale: boolean) {
        if (isMale) {
            return 24 + count;
        } else {
            return 30 + count;
        }
    }

}