    public static NameUpper(name: string) {
        name = name.replace(/^([a-z])/, (r) => {
            return r.toUpperCase();
        });
        name = name.replace(/_[a-zA-Z]/g, (r) => {
            return r.replace('_', '').toUpperCase();
        });
        return name;
    }

