import {IGuestPoolConfigData} from "../../Config/GusetPoolConfig";
import {EEventScene} from "../../dMyGame/EventEnum/EEventScene";
import MesManagerClass from "../../dMyGame/Manager/MesManager";
import {EffectName, PrefabNamesTable} from "../../dMyGame/ResList/PrefabNames";
import {LayaBehaviour} from "../../lty/class/LayaBehaviour";
import {Random} from "../../lty/class/Random";
import {Vector3} from "../../lty/class/Vector3";
import {NetNavigation, WayPoint} from "../../lty/plugin/NetNavigation/NetNavigation";
import {BuildingManager} from "../BuildingManager";
import {GlobalObject} from "../GlobalObject";
import {Time} from "../../lty/class/Time";
import {MiscUtils} from "../../lty/class/MiscUtils";
import {UIMediatorEventPanel} from "../../UI/UIMediatorEventPanel";
import {SlotData} from "../../Data/SlotData";
import {FunctionID} from "../../Data/FunctionID";
import {FacilityPosition} from "../Buildings/FacilityPosition";
import {PeopleEvent} from "../Misc/PeopleEvent";
import {EffectManager} from "../EffectManager";
import SlotUIDConstProxy from "../../Data/SlotUIDConstProxy";
import {MiscDataProxy} from "../../Data/MiscDataProxy";
import {TaskManager} from "../TaskManager";
import {TaskEventData} from "../Task/Task";
import {TaskEvent} from "../Task/TaskEvent";
import {EEventUI} from "../../dMyGame/EventEnum/EEventUI";
import {UIMediatorTalkEventPanel} from "../../UI/UIMediatorTalkEventPanel";

//路径管理
//在最右边进入
/**旅客 */
export class Guest extends LayaBehaviour {
    protected _data: IGuestPoolConfigData;

    /**性别  */
    gender: number;
    /**人物速度 */
    moveSpeed: number = 4;
    /**当前路径点 */
    private _wayPoints: WayPoint[];
    /**当前目标 */
    private _target: Vector3;
    /**当前目标 在路径点数组的 索引*/
    protected _wpIndex = 0;
    /**预制体key */
    key: any;

    eventID: PeopleEvent;

    get isMale() {
        return this.gender == 1;
    }

    private _runtimeAttachment: Laya.Sprite3D;

    /**当前所执行的功能模块的index */
    protected _currFuncIndex: number = 0;
    private isIntoToilet: boolean = false;

    /**当前执行的功能*/
    private get _currFun() {
        if (this._currFuncIndex >= this._data.Functions.length) {
            console.error("功能模块索引越界,旅客退出场景", this._currFuncIndex, this._data.Functions.length);
            throw "功能模块索引越界";
        }
        return this._data.Functions[this._currFuncIndex];
    }

    protected get _agent() {
        return NetNavigation.NetMap.instance;
    }

    private _runtimeAnimator: Laya.Animator;

    protected get animator() {
        if (this._runtimeAnimator == null) {
            this._runtimeAnimator = this.sprite3D.getComponent(Laya.Animator) as Laya.Animator
        }
        return this._runtimeAnimator;
    }

    private OnReach: () => void
    private _transitionFunc: () => void;//切换状态的方法
    private _transition: string

    protected OnComplete(func: () => void, transition?: string) {
        this._transitionFunc = func;
        this._transition = transition;
    }

    /**重置旅客行为
     * @param data 来自数据对象池
     * @param attachment 旅客携带的附件
     */
    ReInit(data: IGuestPoolConfigData, attachment: Laya.Sprite3D) {
        Laya.timer.clearAll(this);
        this.sprite3D.active = true;
        this.animator.enabled = true;
        this._wpIndex = 0;
        this._data = data;
        this.gender = data.Gender[0];
        this.moveSpeed = Random.Range(1.68, 4);
        this._currFuncIndex = 0;
        if (attachment) {
            //TODO:根据附件的名字放置位置和选择人物的动画
            this._runtimeAttachment = attachment
        } else {
            this._runtimeAttachment = null;
        }
        Laya.timer.once(1000, this, () => {
            this.GetEndPoint();
        });
    }

    onStart() {
        MesManagerClass.instance.on3D(EEventScene.DoNext, this, this.OnDoHandleFuncComplete);
    }

    //通过权重播放行走和奔跑的动画
    protected PlayMoveAnimation() {
        this.PlayAnimationByState(StateName.Walk);
    }

    private _currAniState: StateName;

    //通过动画状态的名字播放动画
    protected PlayAnimationByState(state: StateName) {
        if (state) {
            if (state != this._currAniState) {
                let sn: string = state;
                switch (state) {
                    case StateName.Idle:
                        sn = "daiji"
                        break;
                    case StateName.Walk:
                        sn = "zou"
                        break;
                    case StateName.WashHand:
                        break;
                    default:
                        sn = "daiji"
                        // console.warn("播放不存在的状态,转为" + sn)
                        return;
                }
                this.animator.play(sn);
                this._currAniState = state;
            }
        } else {
            console.error("参数为空，动画转换失败")
        }
    }

    //移动需要的变量
    private _toTarget: Vector3 = new Vector3();
    private _step: Vector3 = new Vector3();
    private _t = 0;
    private _walkTime = 0;
    private _origin = new Laya.Vector3();

    //逐帧计算下一步位置
    private StepMove() {
        Laya.Vector3.subtract(this._target, this.transform.position, this._toTarget);
        if (this._toTarget.SqrMagnitude < 0.01) {
            this._wpIndex++;
            if (this._wpIndex >= this._wayPoints.length) {
                this.OnCompleteWPMove()
            } else {
                this._target = this._wayPoints[this._wpIndex].position;
                this._target.y = 0.1;
            }
        } else {
            Laya.Vector3.normalize(this._toTarget, this._step);
            Laya.Vector3.scale(this._step, this.moveSpeed * Time.deltaTime, this._step);
            Laya.Vector3.add(this.transform.position, this._step, this.transform.position);
            this.transform.position = this.transform.position;
            this.transform.localRotationEulerY = MiscUtils.updateRotation(this._step);
            // let dist = Laya.Vector3.scalarLengthSquared(this._toTarget)
            // if(dist > 0.5){
            //     let y = MiscUtils.updateRotation(this._step);
            //     let currY = this.transform.localRotationEulerY;
            //     this.transform.localRotationEulerY = Mathf.Lerp(currY,y,0.2);
            // }
        }
    }

    //lerp插值计算下一帧位置
    private LerpMove() {
        if (this._t > this._walkTime) {
            this._wpIndex++;
            if (this._wpIndex >= this._wayPoints.length) {
                this.OnCompleteWPMove()
            } else {
                this._target = this._wayPoints[this._wpIndex].position;
                this._target.y = 0.1;
                this.UpdateRotation()
            }
        } else {
            this._t += Time.deltaTime;
            let t = this._t / this._walkTime;
            Laya.Vector3.lerp(this._origin, this._target, t, this.transform.position)
            this.transform.position = this.transform.position;
        }
    }

    //更新朝向和预计的移动时间
    private UpdateRotation() {
        Laya.Vector3.subtract(this._target, this.transform.position, this._toTarget);
        let l = Laya.Vector3.scalarLength(this._toTarget);
        this._walkTime = l / this.moveSpeed;
        this._t = 0;
        this._origin = this.transform.position.clone();
        if (l > 0.5) {
            this.transform.localRotationEulerY = MiscUtils.updateRotation(this._toTarget);
        }
    }

    //完成路径点移动
    private OnCompleteWPMove() {
        this._target = null;
        if (this._transitionFunc) {
            // 执行转换
            this.OnReach = this._transitionFunc;
            this.OnReach();
            if (this.OnReach == this._transitionFunc) {
                //在回调中设置了转换方法，如果还是上一个方法就清空回调
                this._transition = null;
            }
        } else {
            console.error("抵达没有回调,或者切换方法为空:", this._transition);
        }
    }

    onUpdate() {
        if (this._target) {
            this.LerpMove();
        }
    }

    onLateUpdate() {
        this.DispatchEvent()
    }

    protected DispatchEvent(time?: number) {
        if (this.isIntoToilet) {
            time = time == null ? this._runtimeFuncPosInfo.fac.belonger.duration * MiscDataProxy.toiletSpeed : time;
            time *= 0.7;
            UIMediatorEventPanel.instance.EventDistribute(this.transform.position, this.sprite3D.id, this.eventID, time);
        }
    }


    public SetWayPoints(wps: WayPoint[]) {
        if (wps && wps.length > 0) {
            this._wayPoints = wps;
            this._target = this._wayPoints[0].position as Vector3;
            this._wpIndex = 0;
            this.UpdateRotation();
            //播放行走动画
            this.PlayMoveAnimation();
        } else {
            console.error("路径点数量为0");
        }
        return this;
    }

    //#region 重构移动逻辑
    //获取队伍的入口endpoint
    protected GetEndPoint() {
        let func = this._currFun;
        let ep = BuildingManager.instance.GetEndPoint(func.FunctionId);
        if (ep) {
            let wps = this._agent.TrySearchByNearestPos(this.transform.position, ep.transform.position);
            this.SetWayPoints(wps).OnComplete(this.TrtEnqueue, "尝试入队");
        } else {
            this.ExitScene();
        }
    }

    private _runtimeNextPos = new Laya.Vector3();

    //抵达队伍入口endpoint时获取下一个点，在队伍中逐步向前移动
    private TrtEnqueue() {
        this._runtimeNextPos.toDefault();
        let canEnqueue = BuildingManager.instance.TrtEnqueue(this._currFun.FunctionId, this, this._runtimeNextPos);
        let wps = this._agent.TrySearchByNearestPos(this.transform.position, this._runtimeNextPos);
        if (canEnqueue) {
            //可以入队，尝试在队伍中移动
            this.isInqueue = true;
            this.SetWayPoints(wps).OnComplete(this.MoveInQueue, "进入队伍");
        } else {
            this.PlayAnimationByState(StateName.Idle)
            this.PlayAngryEmoji();
            Laya.timer.once(1000, this, () => {
                //不能入队，在队尾徘徊然后离开
                this.SetWayPoints(wps).OnComplete(this.ExitScene, "ExitScene");
            })
        }
    }

    private isInqueue = false;

    //在队伍中前进
    private MoveInQueue() {
        if (!this.isInqueue) {
            return;
        }
        let next = BuildingManager.instance.GetNextPointInQueue(this._currFun.FunctionId, this);
        if (next) {
            this.SetWayPoints([{position: next as Vector3}]).OnComplete(this.MoveInQueue, "前进一格");
        } else {
            let hasEnter = BuildingManager.instance.ExistInQueue(this._currFun.FunctionId, this);
            if (hasEnter) {
                //如果已经进入设置等待动画
                this.PlayAnimationByState(StateName.Idle);
                this.PlayWaitEmoji();
                Laya.timer.once(1000, this, this.MoveInQueue);
            } else {
                //尚未进入，就退出场景
                this.PlayAnimationByState(StateName.Idle);
                this.PlayAngryEmoji();
                Laya.timer.once(1000, this, this.ExitScene);
            }
        }

    }

    protected _runtimeFuncPosInfo: _RuntimeFacPosInfo;

    //处理功能的执行,走向建筑的入口
    public MoveToEntrancePos(info: _RuntimeFacPosInfo) {
        Laya.timer.clearAll(this);
        this.isInqueue = false;
        this._runtimeFuncPosInfo = info;
        let wps = this._agent.TrySearchByNearestPos(this.transform.position, info.entrance);
        // wps.push({ position: info.entrance as Vector3 });
        this.SetWayPoints(wps).OnComplete(this.KnockTheDoor, "敲门");
    }

    //敲门
    private KnockTheDoor() {
        switch (this._runtimeFuncPosInfo.fac.belonger.functionID) {
            //小便，洗手 不需要开门
            case FunctionID.MalePee:
            case FunctionID.FemaleSink:
            case FunctionID.MaleSink:
            case FunctionID.Canteen:
                this.MoveToFuncPos();
                break;
            default:
                let hasOpen = BuildingManager.instance.RequestOpenTheDoor(this);
                if (hasOpen) {
                    this.WaitOpenDoor(true, 600);
                } else {
                    this.WaitOpenDoor(false);
                }
                break;
        }
    }

    /**
     * 等待开门
     * @param wait 等待开门吗，不等待的话直接进入
     * @param waitTime 等待时间
     */
    public WaitOpenDoor(wait: boolean, waitTime: number = 1000) {
        if (wait) {
            this.PlayAnimationByState(StateName.Idle);
            Laya.timer.once(waitTime, this, this.MoveToFuncPos)
        } else {
            this.MoveToFuncPos();
        }
    }

    //派发事件开门后，然后bs找最近的门，如果有门开就延迟0.5s派发开门成功的事件，通知这边的旅客你可以进入了,然后移动到设备的位置
    private MoveToFuncPos() {
        let wps = this._agent.TrySearchByNearestPos(this.transform.position, this._runtimeFuncPosInfo.funcPos);
        wps.push({position: this._runtimeFuncPosInfo.funcPos as Vector3});
        this.SetWayPoints(wps).OnComplete(this.HandleFunction, "执行功能");
    }

    //执行功能动画
    protected HandleFunction() {
        this.PlayAnimationByState(StateName.Idle);
        this.isIntoToilet = true;
        let funcId = this._currFun.FunctionId;
        let slot = this._runtimeFuncPosInfo.slot;
        this.transform.rotation = this._runtimeFuncPosInfo.fac.sprite.transform.rotation;
        this.transform.position = this._runtimeFuncPosInfo.fac.sprite.transform.position;
        switch (funcId) {
            case FunctionID.MaleToilet:
            case FunctionID.FemaleToilte:
                this.eventID = PeopleEvent.Toilet;
                //根据马桶的等级选择动画
                if (slot.level <= 5) {
                    this.PlayAnimationByState(StateName.Verb);
                } else {
                    this.PlayAnimationByState(StateName.Sit);
                    let fwd = new Laya.Vector3();
                    this._runtimeFuncPosInfo.fac.sprite.transform.getForward(fwd);
                    Laya.Vector3.scale(fwd, 0.35, fwd);
                    fwd.y = -0.3;
                    Laya.Vector3.subtract(this.transform.position, fwd, this.transform.position);
                    this.transform.position = this.transform.position;
                }
                break;
            case FunctionID.MalePee:
                let pee = EffectManager.PlayEffect(EffectName.Pee, this.sprite3D.id);
                this.owner.addChild(pee);
                pee.transform.rotationEuler = this.transform.rotationEuler;
                pee.transform.localPosition = new Laya.Vector3(0, -0.5, 0)
                this.eventID = PeopleEvent.Normal;
                this.PlayAnimationByState(StateName.Pee);
                break;
            case FunctionID.MaleSink:
            case FunctionID.FemaleSink:
                this.eventID = PeopleEvent.Normal;
                this.PlayAnimationByState(StateName.WashHand);
                break;
            case FunctionID.Canteen:
            case FunctionID.Library:
                this.eventID = PeopleEvent.Normal;
                this.PlayAnimationByState(StateName.Idle);
                break;
            default:
                this.eventID = PeopleEvent.Normal;
                console.error("此功能还没有匹配动作", funcId);
                break;
        }
    }


    /**
     * 功能执行完毕
     * @param id 人物唯一识别ID
     * @param isTalk 是否触发对话事件
     */
    private OnDoHandleFuncComplete(id: number, isTalk = false) {
        if (this.sprite3D.id == id) {
            if (isTalk) {
                UIMediatorTalkEventPanel.instance.ShowTalkPanel(this.isMale ? 1 : 0);
            }
            EffectManager.ClearAll(this.sprite3D.id);
            this._runtimeFuncPosInfo.fac.belonger.CreateIncome(this._runtimeFuncPosInfo.slot);
            let money = EffectManager.PlayEffect(EffectName.Money, this.id, 3000);
            this._runtimeFuncPosInfo.fac.sprite.addChild(money);
            money.transform.localPosition = Vector3.zero;
            money.transform.rotationEuler = this._runtimeFuncPosInfo.fac.sprite.transform.rotationEuler;
            Laya.timer.once(1500, this, this.PlayHappyEmoji);
            this.isIntoToilet = false;
            this.ExitBuilding();
            this.DispatchTaskEvent();
        }
    }

    //执行完功能后退出建筑
    protected ExitBuilding() {
        switch (this._runtimeFuncPosInfo.fac.belonger.functionID) {
            //小便，洗手 不需要开门
            case FunctionID.MalePee:
            case FunctionID.FemaleSink:
            case FunctionID.MaleSink:
            case FunctionID.Canteen:
                this.OnExitBuilding()
                break;
            default:
                this.OnExitBuildingAfterOpenDoor()
                break;
        }
    }

    //退出建筑时先开门
    protected OnExitBuildingAfterOpenDoor(){
        BuildingManager.instance.ResetFac(this);
        let wps = this._agent.TrySearchByNearestPos(this.transform.position, this._runtimeFuncPosInfo.exit);
        this.SetWayPoints(wps).OnComplete(()=>{
            let hasOpen = BuildingManager.instance.RequestOpenTheDoor(this);
            if (hasOpen) {
                this.PlayAnimationByState(StateName.Idle)
                Laya.timer.once(600, this, this.DoNext);
            } else {
                this.DoNext();
            }
        }, "开启下一轮");
    }

    //不开门直接退出建筑
    protected OnExitBuilding() {
        BuildingManager.instance.ResetFac(this);
        let wps = this._agent.TrySearchByNearestPos(this.transform.position, this._runtimeFuncPosInfo.exit);
        this.SetWayPoints(wps).OnComplete(this.DoNext, "开启下一轮");
    }

    /**开启下一轮，没有下一轮退出场景 */
    protected DoNext() {
        this._runtimeFuncPosInfo = null;
        this._currFuncIndex++;
        if (this._currFuncIndex < this._data.Functions.length) {
            this.GetEndPoint();
        } else {
            this.ExitScene();
        }
    }

    /**退出场景 */
    protected ExitScene() {
        //找另外一个出口走出机场
        let es = GlobalObject.prefabs[PrefabNamesTable.prefab5.Exit];
        let index = Random.RangeInt(0, es.length);
        let exitPos = es[index].transform.position;
        let wps = this._agent.TrySearchByNearestPos(this.transform.position, exitPos);
        if (wps && wps.length > 0) {
            this.SetWayPoints(wps).OnComplete(
                this.SetActiveFalse, "退出场景");
        } else {
            console.error("退出场景失败！");
        }
    }

    private SetActiveFalse() {
        this.sprite3D.active = false;
        this.animator.enabled = false;
        this._currAniState = null;
    }

    //endregion

    //表情管理
    private PlayWaitEmoji() {
        if (Random.random > 0.1) {
            return;
        }
        let effect: Laya.Sprite3D = null;
        let num = Random.random;
        if (num < 0.25) {
            effect = EffectManager.PlayEffect(EffectName.Emoji_sick, this.id, 3000, this.sprite3D.transform.position);
        } else if (num < 0.5) {
            effect = EffectManager.PlayEffect(EffectName.Emoji_poop, this.id, 3000, this.sprite3D.transform.position);
        } else if (num < 0.75) {
            effect = EffectManager.PlayEffect(EffectName.Emoji_dead, this.id, 3000, this.sprite3D.transform.position);
        } else {
            effect = EffectManager.PlayEffect(EffectName.Emoji_cry, this.id, 3000, this.sprite3D.transform.position);
        }
        this.sprite3D.addChild(effect);
        effect.transform.localPosition = new Laya.Vector3(0, 2, 0);
    }

    private PlayHappyEmoji() {
        if (Random.random > 0.1) {
            return;
        }
        let effect: Laya.Sprite3D = null;
        let num = Random.random;
        if (num > 0.5) {
            effect = EffectManager.PlayEffect(EffectName.Emoji_happy, this.id, 1000, this.sprite3D.transform.position);
        } else {
            effect = EffectManager.PlayEffect(EffectName.Emoji_cute, this.id, 1000, this.sprite3D.transform.position);
        }
        this.sprite3D.addChild(effect);
        effect.transform.localPosition = new Laya.Vector3(0, 2, 0);
    }

    private PlayAngryEmoji() {
        if (Random.random > 0.1) {
            return;
        }
        EffectManager.ClearAll(this.id);
        let effect = EffectManager.PlayEffect(EffectName.Emoji_angry, this.id, 1000, this.sprite3D.transform.position);
        this.sprite3D.addChild(effect);
        effect.transform.localPosition = new Laya.Vector3(0, 2, 0);
    }

    private DispatchTaskEvent() {
        if (TaskManager.instance.hasTask) {
            let data = new TaskEventData();
            data.evt = TaskEvent.Guest;
            MesManagerClass.instance.eventUI(EEventUI.TaskEvent, data);
        }
    }
}

interface IFunction {
    FunctionId: number,
    Animations: string[],
    SfxName: string,
    SfxSlotId: number,
}

//旅客派发出去后的信息
export interface _RuntimeFacPosInfo {
    //目标建筑的入口
    entrance: Laya.Vector3,
    //目标建筑的出口
    exit: Laya.Vector3,
    //目标设备的位置
    funcPos: Laya.Vector3
    //设备信息
    slot: SlotData;
    fac: FacilityPosition;
}

//动画状态名字
export enum StateName {
    Idle = "Idle",
    Clean = "Clean",
    Sweep = "Sweep",
    Running = "Running",
    Walk = "Walk",
    Verb = "Verb",
    Sit = "Sit",
    WashHand = "xishou",
    Pee = "Pee",
}


