import BaseConfigDataProxy from "../aTGame/Config/RootDataProxy";
import { GuideInfo } from "../dMyGame/_config/GuideInfo";

/**建造的配置表 */
export class GuideInfoConfigProxy extends BaseConfigDataProxy<GuideInfo.config>{
    private static _instance: GuideInfoConfigProxy;
    /** 单例 */
    public static get instance(): GuideInfoConfigProxy {
        if (this._instance == null) {
            this._instance = new GuideInfoConfigProxy();
        }
        return this._instance;
    }
    //
    private constructor() {
        super();
    }

    public initData() {
        this.m_dataList = GuideInfo.dataList;
    }
}