import {Clock} from "./Clock";
import MesManagerClass from "../../../dMyGame/Manager/MesManager";
import {ClockEvent} from "./ClockEvent";
import RootLocalStorageSaveNaWft from "../../../aTGame/Data/RootLocalStorageSave";
import RootLocalStorageData from "../../../aTGame/Data/RootLocalStorageData";

class ClockManagerData extends RootLocalStorageData {
    List: Clock[] = [];
    TickEvent: ClockEvent[] = [];
}

export class ClockManager extends RootLocalStorageSaveNaWft<ClockManagerData> {
    private static _instance: ClockManager;
    public static get instance(): ClockManager {
        if (this._instance == null) {
            this._instance = new ClockManager();
        }
        return this._instance;
    }

    constructor() {
        super();
        this.InitData();
    }

    private _data: ClockManagerData;

    protected get _saveName(): string {
        return "ClockManagerData";
    }

    protected getNewData(): ClockManagerData {
        return new ClockManagerData();
    }

    public InitData(): void {
        this._data = this._ReadFromFile();
        let arr = [];
        for (let c of this._data.List) {
            let table = this._clockEvtDict.get(c.event);
            if (table == null) {
                table = {};
                this._clockEvtDict.set(c.event, table);
            }
            c = table[c.localTimeString] = new Clock(new Date(c.time), c.event);
            arr.push(c);
        }
        this._data.List = arr;
    }

    get isTicking(): boolean {
        return this._isTicking;
    }

    set isTicking(value: boolean) {
        this._isTicking = value;
    }


    /**
     * 计时器运行状态，是否已经运行
     * @private
     */
    private _hasRun = false;
    /**
     * key 事件-ClockEvent
     * value 事件对应的clock table 对象{[索引：执行时间LocalTimeString]:计时器}
     * @private
     */
    private _clockEvtDict: Map<string, { [localTime: string]: Clock }> = new Map<string, { [p: string]: Clock }>();

    /**
     * 所有clock的list，离线存贮用
     * @private
     */
    private get _clockList(): Clock[] {
        return this._data.List;
    }

    /**
     * tick 事件列表 ，每秒派发事件
     * @private
     */
    private get _tickEventList(): ClockEvent[] {
        return this._data.TickEvent;
    }

    //准备增加的tick事件
    private _tempAdd: ClockEvent[] = [];
    //准备删除的tick时间
    private _tempSub: ClockEvent[] = [];
    //准备关闭的clock
    private _offClock: Clock[] = [];
    //准备增加的clock
    private _onClock: Clock[] = [];
    private _isDirty = false;

    public Start() {
        this._hasRun = true;
        console.log("计时器启动...");
        Laya.timer.loop(1000, this, this.Tick);
    }

    /**
     * 添加一个计时器
     * @param date 执行时刻
     * @param evt 事件
     * @constructor
     */
    public AddClock(date: Date, evt: ClockEvent) {
        let clock: Clock = null;
        let localTimeStr = date.toLocaleTimeString();
        //从evt表中取出clock，没有就实例化
        let table = this._clockEvtDict.get(evt);
        if (table == null) {
            table = {};
            this._clockEvtDict.set(evt, table);
            clock = table[localTimeStr] = new Clock(date, evt);
        } else {
            clock = table[localTimeStr];
            if (clock == null) {
                clock = table[localTimeStr] = new Clock(date, evt);
            }
        }
        //添加进入
        if (this.isTicking) {
            this._onClock.push(clock);
            this.SetDirty();
        } else {
            let index = this.indexOf(this._clockList,clock);
            if (index < 0) {
                this._clockList.push(clock);
                this.Save();
            }
        }
        return clock;
    }

    private _isTicking = false;

    private Tick() {
        this._isTicking = true;
        this.OnTick();
        this.AfterTick();
        this._isTicking = false;
    }

    private OnTick() {
        let date = new Date();
        for (let c of this._clockList) {
            c.Check(date.getTime());
            if (c.isDone) {
                this._offClock.push(c);
            }
        }
        for (let t of this._tickEventList) {
            MesManagerClass.instance.event(t);
        }
    }

    //在这里处理增加，删除事件，计时器 的操作
    private AfterTick() {
        this.SpliceElement(this._tempSub, this._tickEventList);
        this.SpliceElement(this._offClock, this._clockList);
        this.PushElement(this._tempAdd, this._tickEventList);
        this.PushElement(this._onClock, this._clockList);
        for (let c of this._offClock) {
            this.DisposeClock(c);
        }
        if (this._tempAdd.length > 0) {
            this._tempAdd = [];
        }
        if (this._tempSub.length > 0) {
            this._tempSub = [];
        }
        if (this._offClock.length > 0) {
            this._offClock = [];
        }
        if (this._onClock.length > 0) {
            this._onClock = [];
        }
        if (this._isDirty) {
            this.Save();
            this._isDirty = false;
        }
    }

    private indexOf(list:Clock[],clock:Clock){
        if(list==null || list.length<=0 || clock==null){
            return  -1;
        }
        for(let i = 0;i< list.length;i++){
            let c = list[i];
            if(c.event == clock.event && c.localTimeString == clock.localTimeString){
                return i;
            }
        }
        return -1;
    }

    private PushElement(a: any[], b: any[]) {
        if (a.length > 0) {
            for (let item of a) {
                let index = -1;
                if(item instanceof Clock){
                    index = this.indexOf(b,item);
                }else{
                    index = b.indexOf(item);
                }
                if (index < 0) {
                    b.push(item);
                }
            }
        }
    }

    private SpliceElement(a: any[], b: any[]) {
        if (a.length > 0 && b.length > 0) {
            for (let item of a) {
                if (b.length == 0) {
                    break;
                }
                let index = -1
                if(item instanceof Clock){
                    index = this.indexOf(b,item);
                }else{
                    index = b.indexOf(item);
                }
                if (index > -1) {
                    b.splice(index, 1);
                }
            }
        }
    }

    private SetDirty() {
        this._isDirty = true;
    }

    public Save() {
        this._SaveToDisk(this._data);
    }

    //标记未被使用的clock,在下次tick时会被清理掉
    public ClearUnusedClock(){
        for(let c of this._clockList){
            if(!MesManagerClass.instance.hasListener(c.event)){
                this._offClock.push(c);
            }
        }
    }

    /**
     * 计时器执行完毕后释放
     * @constructor
     * @private
     */
    private DisposeClock(clock: Clock) {
        if (clock) {
            let table = this._clockEvtDict.get(clock.event);
            delete table[clock.localTimeString];
            if (Object.keys(table).length <= 0) {
                this._clockEvtDict.delete(clock.event);
            }
            let index = this.indexOf(this._clockList,clock);
            if (index > -1) {
                this._clockList.splice(index, 1);
            }
        }
    }


    /**
     * 每秒派发事件
     * @param evt
     * @constructor
     */
    public static OnTick(evt: ClockEvent) {
        if (this.instance._isTicking) {
            this.instance._tempAdd.push(evt);
            this.instance.SetDirty();
        } else {
            let index = this._instance._tickEventList.indexOf(evt);
            if (index < 0) {
                this._instance._tickEventList.push(evt);
            }
            this.instance.Save()
        }
    }

    /**
     * 取消每秒派发的某个事件
     * @param evt
     * @constructor
     */
    public static OffTick(evt: ClockEvent) {
        if (this.instance._isTicking) {
            this.instance._tempSub.push(evt);
            this.instance.SetDirty();
        } else {
            let index = this._instance._tickEventList.indexOf(evt);
            if (index > -1) {
                this._instance._tickEventList.splice(index, 1);
            }
            ClockManager.instance.Save();
        }
    }


    /**
     * 添加一个计时器，执行时间格式 时 - 分 - 秒 ，使用24时制，到达执行时间后派发计时器中存贮的信号
     * @param hour 小时
     * @param min 分钟
     * @param sec 秒
     * @param evt
     * @constructor
     */
    public static AddClock(hour: number, min: number, sec: number, evt: ClockEvent) {
        let now = new Date();
        let date = new Date(now.getFullYear(), now.getMonth(), now.getDate(), hour, min, sec);
        return ClockManager.instance.AddClock(date, evt);
    }

    /**
     * 移除某时刻事件的计时器
     * @constructor
     */
    public static RemoveClock(hour: number, min: number, sec: number, evt: ClockEvent) {
        let clock = ClockManager.GetClock(hour, min, sec, evt);
        if (this.instance.isTicking) {
            this._instance._offClock.push(clock);
            this.instance.SetDirty();
        } else {
            this.instance.DisposeClock(clock);
            ClockManager.instance.Save();
        }
    }

    public static GetClock(hour: number, min: number, sec: number, evt: ClockEvent) {
        let now = new Date();
        let date = new Date(now.getFullYear(), now.getMonth(), now.getDate(), hour, min, sec);
        let str = date.toLocaleTimeString();
        let table = this.instance._clockEvtDict.get(evt);
        if (table) {
            return table[str];
        }
    }

    /**
     * 添加对事件的监听
     * @param evt
     * @param caller
     * @param listener
     * @param args
     * @constructor
     */
    public static AddListener(evt: ClockEvent, caller: any, listener: Function, args?: any[]) {
        MesManagerClass.instance.on(evt, caller, listener);
    }

    /**
     * 移除对事件的监听
     * @param evt
     * @param caller
     * @param listener
     * @param onceOnly
     * @constructor
     */
    public static RemoveListener(evt: ClockEvent, caller: any, listener: Function, onceOnly?: boolean) {
        MesManagerClass.instance.off(evt, caller, listener, onceOnly);
    }

    /**
     * 在新的一天派发此事件
     * @param event
     * @constructor
     */
    static OnNewDay(event: ClockEvent) {
        return ClockManager.AddClock(0, 0, 0, event);
    }
}

