import PlatformData from "src/aTGame/Platform/Data/PlatformData";
import VIVOData from "src/cFrameBridge/Platform/Data/VIVOData";

/**
 * vivo平台
 */
export default class VivoPlatform {
    private static _i: VivoPlatform;
    /** 单例 */
    public static get instance(): VivoPlatform {
        if (!this._i) {
            this._i = new VivoPlatform();
        }
        return this._i;
    }
    //
    private constructor() {
        this.m_platform = window['qg'] || window['vivo'];
        this.m_data = new VIVOData();
    }

    /** 平台对象 */
    private m_platform: any;
    /** 原生广告组件 */
    private m_nativeAd: INativeAd;

    /** 平台数据 */
    private m_data: PlatformData;

    /** 获取原生广告组件 */
    public get nativeAd(): INativeAd {
        return this.m_nativeAd;
    }

    /**
     * 创建原生广告组件，如果已经创建过原生广告组件，则返回已创建的广告组件
     * @param _o 参数对象
     */
    public createNativeAd() {
        this.m_nativeAd = this.m_platform.createNativeAd({
            /** 原生广告位标识 */
            adUnitId: this.m_data.nativeId,
        });
        //
    }

    /**
     * 异步加载原生广告
     */
    public nativeAdLoadAsync(): Promise<INativeAdOnLoadData[]> {
        return new Promise<INativeAdOnLoadData[]>((resolve) => {
            let onLoadFun = (res: {
                adList: INativeAdOnLoadData[],
            }) => {
                //移除加载完成事件
                this.m_nativeAd.offLoad(onLoadFun);
                //返回加载成功数据
                resolve(res.adList);
            };
            //注册加载完成事件
            this.m_nativeAd.onLoad(onLoadFun);
            let onErrorfun = (err) => {
                console.log("加载广告失败出错");
                this.m_nativeAd.offError(onErrorfun);
                this.m_nativeAd.offLoad(onLoadFun);
                // this.m_nativeAd.load();
                console.log("原生广告错误信息===>>>：" + err.errMsg);
                console.log("原生广告错误码==>>>：" + err.errCode);
            }
            this.m_nativeAd.onError(onErrorfun);
            this.m_nativeAd.load();
        });
    }

    /**
     * 销毁原生广告
     */
    public destroy() {
        try {
            console.warn('destroy 前', this.m_nativeAd);
            this.m_nativeAd && this.m_nativeAd.destroy && this.m_nativeAd.destroy();
            console.warn('destroy 后', this.m_nativeAd);
        } catch (e) {
            console.warn('destroy 异常');

            console.error(e);

        }

    }
}
/**
 * 原生广告组件
 * * 原生广告一定要确保调用nativeAd.reportAdShow()，然后调用nativeAd.reportAdClick()，并且保证顺序，否则影响收益！
 */
export interface INativeAd {
    /** 拉取广告数据，成功回调 onLoad，失败回调 onError */
    load();

    /** 上报广告曝光，一个广告只有一次上报有效，adId 为 load 方法获取的广告数据的 adId 字段 */
    reportAdShow(_o: {
        /** 广告信息标识，由   load 接口返回 */
        adId: string,
    });

    /** 上报广告点击，一个广告只有一次上报有效，adId 为 load 方法获取的广告数据的 adId 字段 */
    reportAdClick(_o: {
        /** 广告信息标识，由   load 接口返回 */
        adId: string,
    });

    /** 设置广告加载成功回调 */
    onLoad(_f: (res: {
        adList: INativeAdOnLoadData[],
    }) => void);

    /** 移除原生广告加载成功监听 */
    offLoad(_f: Function);

    /** 监听原生广告错误事件 */
    onError(_f: (res: {
        err: { errMsg: string, errCode: number },
    }) => void);

    /** 移除原生广告错误监听 */
    offError(_f: Function);

    /** 销毁原生广告 */
    destroy();
}

/**
 * 原生广告加载完成后的回调参数
 */
export interface INativeAdOnLoadData {
    /** 广告标识，用来上报曝光与点击 */
    adId: string,
    /** 广告标题 */
    title: string,
    /** 广告描述 */
    desc: string,
    /** 推广应用的Icon图标 */
    icon: string,
    /** 广告图片 */
    imgUrlList: string[],
    /** “广告”标签图片 */
    logoUrl: string,
    /** 点击按钮文本描述 */
    clickBtnTxt: string,
    /** 获取广告类型，取值说明：0：无 1：纯文字 2：图片 3：图文混合 4：视频 */
    creativeType: number,
    /** 获取广告点击之后的交互类型，取值说明： 0：无 1：浏览类 2：下载类 3：浏览器（下载中间页广告） 4：打开应用首页 5：打开应用详情页 */
    interactionType: number,
}