export namespace CameraConst {
    export class config {
        /** 摄像机正价尺寸 */ readonly orthographic_size: number;
        /** 摄像机投影方式 */ readonly orthographic: boolean;
        /** X轴旋转角度 */ readonly EulerX: number;
        /** Y轴旋转角度 */ readonly EulerY: number;
        /** Z轴旋转角度 */ readonly EulerZ: number;
        /** 坐标x */ readonly x: number;
        /** 坐标y */ readonly y: number;
        /** 坐标z */ readonly z: number;
        /** 移动范围 */ readonly maxX: number;
        /** 移动范围 */ readonly minX: number;
        /** 移动范围 */ readonly maxZ: number;
        /** 移动范围 */ readonly minZ: number;
        /** 摄像机正价尺寸 */ readonly orthographic_MaxSize: number;
        /** 摄像机正价尺寸 */ readonly orthographic_MinSize: number;
        /** 缩放速度 */ readonly scaleSpeed: number;
        /** 移动速度，手指移动距离相对于屏幕的距离 */ readonly moveSpeed: number;
        /** far */ readonly far: number;
        /** 相机移动缓动时间 */ readonly smoothTime: number;
        /** 移动阻力 */ readonly damp: number;
        /** 最大力 */ readonly maxForce: number;
        /** 最小力 */ readonly minForce: number;
    }
    export var data : CameraConst.config;
    export const path = "res/config/CameraConst.json";
}


