import BaseUIMediator from "../aTGame/UI/FGUI/BaseUIMediator";
import FGUI_PGameTalk from "../FGUI/Main/FGUI_PGameTalk";
import UIManagerProxy from "../dMyGame/Manager/Proxy/UIManagerProxy";
import { EUI } from "../dMyGame/Enum/EUI";
import { Random } from "../lty/class/Random";
import { EUILayer } from "../aTGame/UI/FGUI/EUILayer";
import { GuideDataProxy } from "../Data/GuideDataProxy";
//对话事件的面板
export class UIMediatorTalkEventPanel extends BaseUIMediator<FGUI_PGameTalk>{
    private static m_instance: UIMediatorTalkEventPanel;
    private _talkTextList =
        [
            "非常感谢你，我感觉舒服多了！",
            "你的厕所真的非常舒适，让我感觉好多了！",
            "太舒坦了！这间厕所真的很不错呢！",
            "看到有网红说这里很不错，果然名不虚传啊！",
            "在派上对突然肚子疼，能来到你这里解决真是太好了！",
            "我是慕名过来的，这里的服务和环境都是一流的呢！",
        ]
    private _repairText = "点击屏幕中的       /       让雇员前往处理，更快的恢复收益，多多招募雇员吧！"
    private _worldEvtText = "点击屏幕右侧跳动的按钮                             可以获得奖励或者增益效果哦！"
    private _helpText = "头顶有         的客人需要您的帮助，多次点击按钮来帮助他（她）们。"
    private _lastShowTime = 0;
    protected _layer = EUILayer.TalkPanel;
    private _timer = 20000;
    /** 单例 */
    public static get instance(): UIMediatorTalkEventPanel {
        if (!this.m_instance) {
            this.m_instance = new UIMediatorTalkEventPanel();
            this.m_instance._classDefine = FGUI_PGameTalk;
        }
        return this.m_instance;
    }

    private constructor() {
        super();
    }


    protected _OnShow() {
        console.warn("显示对话事件：", this._typeIndex);

        this.ui.m_type.selectedIndex = this._typeIndex
        this.ui.m_ani.play(Laya.Handler.create(this, this.ShowTalk));
        this.ui.m_talk.visible = false;
        this.ui.m_Repair.visible = false;
        this.ui.m_Sweep.visible = false;
        this.ui.m_Help.visible = false;
        this.ui.m_Bg.onClick(this, this.OnClickClose);
    }

    protected _OnHide() {
        this._lastShowTime = new Date().getTime();
    }

    private _typeIndex = 0;
    public ShowTalkPanel(typeIndex: number) {
        if (this.isShow || !GuideDataProxy.IsCompleteAllGuide) {
            return false;
        }
        let time = new Date().getTime();
        let deltaTime = time - this._lastShowTime
        if (deltaTime > this._timer) {
            this._typeIndex = typeIndex;
            UIManagerProxy.instance.setUIState([{ typeIndex: EUI.TalkPanel, state: true }], false);
        }
        else {
            console.log("时间差小于" + this._timer / 1000 + "秒，不显示对话事件:" + deltaTime)
        }
        return true;
    }

    private ShowTalk() {
        if (this.isShow) {
            this.ui.m_talk.visible = true;
            if (this._typeIndex == 0 || this._typeIndex == 1) {
                let index = Random.RangeInt(0, this._talkTextList.length);
                this.ui.m_talk.title = this._talkTextList[index];
            }
            else if (this._typeIndex == 2) {
                this.ui.m_Repair.visible = true;
                this.ui.m_Sweep.visible = true;
                this.ui.m_talk.text = this._repairText;
            }
            else if (this._typeIndex == 3) {
                this.ui.m_talk.text = this._worldEvtText;
            }
            else if (this._typeIndex == 4) {
                this.ui.m_Help.visible = true;
                this.ui.m_talk.text = this._helpText;
            }
            // this.ui.m_Bg.onClick(this, this.OnClickClose);
        }
    }

    private OnClickClose() {
        if (this.isShow) {
            UIManagerProxy.instance.setUIState([{ typeIndex: EUI.TalkPanel, state: false }], false);
        }
    }


}