import BaseConfigDataProxy from "../aTGame/Config/RootDataProxy";
import {TaskConfig} from "../dMyGame/_config/TaskConfig";

export class TaskConfigProxy extends BaseConfigDataProxy<TaskConfig.config>{
    private static _instance: TaskConfigProxy;
    /** 单例 */
    public static get instance(): TaskConfigProxy {
        if (this._instance == null) {
            this._instance = new TaskConfigProxy();
        }
        return this._instance;
    }
    //
    private constructor() {
        super();
    }

    public initData() {
        this.m_dataList = TaskConfig.dataList;
    }
}