import RootLocalStorageData from "../aTGame/Data/RootLocalStorageData";
import RootLocalStorageSaveNaWft from "../aTGame/Data/RootLocalStorageSave";

export class GuideData extends RootLocalStorageData {
  isCompleteGuide1: boolean = false;
  isCompleteGuide2: boolean = false;
  isCompleteGuide3: boolean = false;
  isCompleteGuide4: boolean = false;
  isCompleteGuide5: boolean = false;

  guideID: number = 0;
}

export class GuideDataProxy extends RootLocalStorageSaveNaWft<GuideData> {
  constructor() {
    super();
  }

  private static m_instance: GuideDataProxy;

  /** 单例 */
  public static get instance(): GuideDataProxy {
    if (!this.m_instance) {
      this.m_instance = new GuideDataProxy();
    }
    return this.m_instance;
  }

  private _data: GuideData;

  public get Data() {
    return this._data;
  }

  protected get _saveName(): string {
    return "GuideData";
  }

  protected getNewData(): GuideData {
    return new GuideData();
  }
  public SaveData() {
    this._SaveToDisk(this._data);
  }

  public InitData(): void {
    this._data = this._ReadFromFile();
  }

  /**
   * 是否完成全部引导
   * @constructor
   */
  public static get IsCompleteAllGuide() {
    return (
      this.instance._data.isCompleteGuide1 &&
      this.instance._data.isCompleteGuide2 &&
      this.instance._data.isCompleteGuide3 &&
      this.instance._data.isCompleteGuide4 &&
      this.instance._data.isCompleteGuide5
    );
  }
}
