import BaseUIMediator from "../aTGame/UI/FGUI/BaseUIMediator";
import EnvironmentManager from "../dMyGame/Manager/EnvironmentManager";
import FGUI_EventCommPanel from "../FGUI/EventPanel/FGUI_EventCommPanel";
import { Time } from "../lty/class/Time";
import MesManagerClass from "../dMyGame/Manager/MesManager";
import { EEventScene } from "../dMyGame/EventEnum/EEventScene";
import { Random } from "../lty/class/Random";
import FGUI_CommonButton from "../FGUI/EventPanel/FGUI_CommonButton";
import FGUI_HelpButton from "../FGUI/EventPanel/FGUI_HelpButton";
import { PeopleEvent } from "../GameComponent/Misc/PeopleEvent";
import FGUI_EventTip from "../FGUI/EventPanel/FGUI_EventTip";
import { EEventUI } from "../dMyGame/EventEnum/EEventUI";
import FGUI_Mosaic from "../FGUI/EventPanel/FGUI_Mosaic";
import {MiscDataProxy} from "../Data/MiscDataProxy";
import {GuideDataProxy} from "../Data/GuideDataProxy";
import {Uma, UmaEvent} from "../Uma";
import {UIMediatorTalkEventPanel} from "./UIMediatorTalkEventPanel";
import {EUILayer} from "../aTGame/UI/FGUI/EUILayer";


export class UIMediatorEventPanel extends BaseUIMediator<FGUI_EventCommPanel> {
    private static m_instance: UIMediatorEventPanel;
    private offset = new Laya.Vector3(-2, 2, 0);
    public static scale: number = 1;
    protected _layer = EUILayer.EventBar;
    private helpBtnList: PoolObject[] = [];
    private commomBtnList: CommonPoolObject[] = [];
    //维修 打扫 按钮的对象池
    private tipBtnList: TipPoolObject[] = [];
    //马赛克对象池
    private mosaicList: MosaicPoolObject[] = [];

    private _dict: { [index: number]: CommonPoolObject } = {};
    private _dictHelp: { [index: number]: PoolObject } = {};
    //字典 对应 按钮与 id  
    private _dictRepair: { [index: number]: TipPoolObject } = {};
    private _dictMosaic: { [index: number]: MosaicPoolObject } = {};
    private _fillSpeed = 0.05;

    //Tip 如果玩家没有点击红色按钮，进度走完就会提示玩家要进行点击，触发间隔大于60秒
    private _lastTipTime= 0;
    /** 单例 */
    public static get instance(): UIMediatorEventPanel {
        if (!this.m_instance) {
            this.m_instance = new UIMediatorEventPanel();
            this.m_instance._classDefine = FGUI_EventCommPanel;
        }
        return this.m_instance;
    }

    private constructor() {
        super();
    }

    _OnShow() {
        super.Show();
        this.InitBtnList();
    }

    private InitBtnList() {
        //帮助事件按钮
        this.helpBtnList.push({
            btn: this.ui.m_btn_help_1, screenPos: new Laya.Vector4()
        });
        this.helpBtnList.push({
            btn: this.ui.m_btn_help_2,
            screenPos: new Laya.Vector4()
        });
        this.helpBtnList.push({
            btn: this.ui.m_btn_help_3,
            screenPos: new Laya.Vector4()
        });
        this.helpBtnList.push({
            btn: this.ui.m_btn_help_4,
            screenPos: new Laya.Vector4()
        });
        this.helpBtnList.push({
            btn: this.ui.m_btn_help_5,
            screenPos: new Laya.Vector4()
        });
        for (let element of this.helpBtnList) {
            element.btn.visible = false;
        }
    }

    /**实例化CommonButton */
    private InstanceCommBtn() {
        let btn = <FGUI_CommonButton><any>(fairygui.UIPackage.createObject("EventPanel", "CommonButton"));
        // console.log(btn);
        this.ui.addChild(btn);
        let obj: CommonPoolObject = {
            btn: btn, screenPos: new Laya.Vector4()
        }
        this.commomBtnList.push(obj);
        obj.btn.visible = false;
    }

    /**实例化tip */
    private InstanceTipBtn() {
        let btn: FGUI_EventTip = FGUI_EventTip.createInstance();
        this.ui.addChild(btn);
        let obj: TipPoolObject = {
            btn: btn, screenPos: new Laya.Vector4()
        }
        // btn.onClick(this, () => btn.m_runtime.selectedIndex = 1);
        this.tipBtnList.push(obj);
        btn.m_fill_green.fillAmount = 0;
        obj.btn.visible = false;
        // console.log("tipBtn实例化");
    }

    /**实例化马赛克 */
    private InstanceMosaic() {
        let img: FGUI_Mosaic = FGUI_Mosaic.createInstance();
        this.ui.addChild(img);
        let obj: MosaicPoolObject = {
            img: img, screenPos: new Laya.Vector4()
        }
        obj.img.visible = false;
        this.mosaicList.push(obj);

    }


    public EventDistribute(position: Laya.Vector3, id: number, eventID: PeopleEvent, duration: number) {
        switch (eventID) {
            case PeopleEvent.Toilet:
                this.UpdateSpecialEven(position, id, duration);
                break;
            case PeopleEvent.Building:
            case PeopleEvent.Normal:
                this.UpdateCommEven(position, id, eventID, duration)
                break;
            case PeopleEvent.Repair:
            case PeopleEvent.Sweep:
            case PeopleEvent.Money:
            case PeopleEvent.Diamond:
                this.UpdateRepair(position, id, duration, eventID)
                break;
            case PeopleEvent.StartRepair:
                this.StartRepair(id);
                break;
            default:
                break;
        }
    }



    /**
     * 每帧更新人物事件按钮的位置
     * @param position 人物的世界坐标
     * @param id 人物的唯一标示id
     */
    public UpdateSpecialEven(position: Laya.Vector3, id: number, duration: number) {
        //马赛克
        let mosaicObj: MosaicPoolObject;
        mosaicObj = this._dictMosaic[id]
        if (mosaicObj) {
            //let img = mosaicObj.img;
            this.MosaicChangePos(position, mosaicObj);
        }
        //使用普通绿色进度条
        let obj
        obj = this._dict[id]
        if (obj) {
            let btn = obj.btn;
            // if (!btn.visible)
            //     return;
            this.ChangePos(position, obj.screenPos, btn);
            btn.m_fill_green.fillAmount += Time.deltaTime / duration;

            if (btn.m_fill_green.fillAmount >= 1) {
                //回收绿色进度条
                btn.visible = false;
                this._dict[id] = null;
                //回收马赛克
                this._dictMosaic[id].img.visible = false;
                this._dictMosaic[id] = null;
                // console.log("回收马赛克");
                //离开厕所
                MesManagerClass.instance.event3D(EEventScene.DoNext, id);
            }
            return;
        }

        obj = this._dictHelp[id];
        if (obj) {
            let btn = obj.btn as FGUI_HelpButton;
            // if (!btn.visible)
            //     return;
            this.ChangePos(position, obj.screenPos, btn);
            btn.m_fill_red.fillAmount += this._fillSpeed * Time.deltaTime;
            if (btn.m_fill_yellow.fillAmount > btn.m_fill_red.fillAmount) {
                btn.m_fill_yellow.fillAmount -= this._fillSpeed * Time.deltaTime * 2;
            } else
                btn.m_fill_yellow.fillAmount = btn.m_fill_red.fillAmount - 0.02;
            if (btn.m_fill_red.fillAmount >= 1 || btn.m_fill_yellow.fillAmount >= 1) {
                //如果没点击红色，红色长满提示玩家要点击红色帮助npc
                if(btn.m_fill_red.fillAmount >= 1){
                    let time = new Date().getTime();
                    if(time - this._lastTipTime > 60000){
                        let hasShow = UIMediatorTalkEventPanel.instance.ShowTalkPanel(4)
                        if(hasShow){
                            this._lastTipTime = time;
                        }
                    }
                }
                btn.off(Laya.Event.CLICK, this, this.OnClick);
                //回收马赛克
                this._dictMosaic[id].img.visible = false;
                this._dictMosaic[id] = null;
                //人物对话事件
                let isTalk = btn.m_fill_yellow.fillAmount >= 1
                //离开厕所 
                MesManagerClass.instance.event3D(EEventScene.DoNext, [id,isTalk]);
                //点满黄色进度条
                if (btn.m_fill_yellow.fillAmount >= 1) {
                    btn.m_switch.selectedIndex = 1;
                    Laya.timer.once(1000, this, () => {
                        btn.visible = false;
                        this._dictHelp[id] = null;
                    })
                    return;
                }
                //回收emoji按钮
                btn.visible = false;
                this._dictHelp[id] = null;
                // console.log("归还red按钮：" + obj.btn.name)
            }
            return;
        }
        if (this._dictMosaic[id] == null)
            this._dictMosaic[id] = this.GetMosaicImg();
        //this.MosaicChangePos(position, mosaicObj);
        //执行到这里说明没有
        let num = Random.Range(0, 8)
        if (num <1.5 && GuideDataProxy.IsCompleteAllGuide) {
            //  console.log(id + ":随机到了红色");
            obj = this.GetHelpObject(position);
            if (obj != null) {
                this._dictHelp[id] = obj;
            }
        } else {
            // console.log(id + ":随机到了绿色");
            obj = this.GetObject(position, PeopleEvent.Normal);
            if (obj != null) {
                this._dict[id] = obj
            }
        }
    }
    /**小便 洗手 */
    public UpdateCommEven(position: Laya.Vector3, id: number, eventID: PeopleEvent, duration: number) {
        let obj
        obj = this._dict[id]
        if (obj) {
            let btn = obj.btn;
            this.ChangePos(position, obj.screenPos, btn);
            btn.m_fill_green.fillAmount += Time.deltaTime / duration;
            if (btn.m_fill_green.fillAmount >= 1) {
                btn.visible = false;
                this._dict[id] = null;
                // console.log("归还green按钮：" + obj.btn.name)
                MesManagerClass.instance.event3D(EEventScene.DoNext, id);
            }
            return;
        }
        //执行到这里说明没有
        obj = this.GetObject(position, eventID);

        if (obj != null) {
            this._dict[id] = obj;
        }
    }
    /**维修 打扫 */
    private UpdateRepair(position: Laya.Vector3, id: number, duration: number, evtID?: PeopleEvent) {
        let obj: TipPoolObject
        obj = this._dictRepair[id]
        if (obj) {
            let btn = obj.btn;
            this.NoSkewChangePos(position, obj.screenPos, btn);
            if (obj.btn.m_runtime.selectedIndex == 2) {
                //开始计时         
                btn.m_fill_green.fillAmount += Time.deltaTime / duration;
                if (btn.m_fill_green.fillAmount >= 1) {
                    console.log("======维修结束====")
                    btn.visible = false;
                    this._dictRepair[id] = null;
                    MesManagerClass.instance.eventUI(EEventUI.RepairProgressComplete, id)
                }
            }
            return;
        }
        obj = this.GetTipObject(position, id, evtID);
        if (obj != null) {
            this._dictRepair[id] = obj;
        }
    }

    /**维修 打扫 等提示 的变换和偏移 */
    public NoSkewChangePos(position: Laya.Vector3, viewPos: Laya.Vector4, btn: fairygui.GButton) {
        if (this.isShow) {
            EnvironmentManager.instance.camera.worldToViewportPoint(position, viewPos);
            btn.x = viewPos.x //- 15;
            btn.y = viewPos.y - 70 * UIMediatorEventPanel.scale;
            btn.scaleX = UIMediatorEventPanel.scale;
            btn.scaleY = UIMediatorEventPanel.scale;
        }
    }
    /**Toilet进度条按钮的变换和偏移 */
    public ChangePos(position: Laya.Vector3, viewPos: Laya.Vector4, btn: fairygui.GButton) {
        if (this.isShow) {
            EnvironmentManager.instance.camera.worldToViewportPoint(position, viewPos);
            btn.x = viewPos.x //- 30;
            btn.y = viewPos.y - 80 * UIMediatorEventPanel.scale;
            btn.scaleX = UIMediatorEventPanel.scale;
            btn.scaleY = UIMediatorEventPanel.scale;
        }
    }
    /**马赛克的变换和偏移 */
    private MosaicChangePos(position: Laya.Vector3, mosaicObj: MosaicPoolObject) {
        if (this.isShow) {
            EnvironmentManager.instance.camera.worldToViewportPoint(position, mosaicObj.screenPos);
            mosaicObj.img.x = mosaicObj.screenPos.x //- 10* UIMediatorEventPanel.scale;
            mosaicObj.img.y = mosaicObj.screenPos.y - 20 * UIMediatorEventPanel.scale+15;
            mosaicObj.img.scaleX = UIMediatorEventPanel.scale;
            mosaicObj.img.scaleY = UIMediatorEventPanel.scale;
            //   console.log(mosaicObj.img.scaleX + "   " + UIMediatorEventPanel.scale);
        }
    }

    /**
     * 初始化emoji按钮
     * @param btn
     */
    private InitButton(btn: FGUI_HelpButton) {
        btn.m_fill_red.fillAmount = 0.05;
        btn.m_switch.selectedIndex = 0;
        btn.scaleX = 0.7;
        btn.scaleY = 0.7;
        btn.m_fill_yellow.fillAmount = btn.m_fill_red.fillAmount;
        btn.on(Laya.Event.CLICK, this, this.OnClick, [btn]);
    }
    /**初始化Tips */
    private InitTipButton(btn: FGUI_EventTip, id?: number) {
        btn.m_runtime.setSelectedIndex(0);
        btn.onClick(this, () => {
            // console.warn("click button!!!")
            switch (btn.m_state.selectedIndex) {
                case 0:
                case 1:
                    if (btn.m_runtime.selectedIndex == 0) {
                        MesManagerClass.instance.eventUI(EEventUI.OnClickRepairBtn);
                        btn.m_runtime.selectedIndex = 1;
                        // console.log("OnClickRepairBtn!!!")
                        Uma.trackEvent(UmaEvent.Repair)
                    }
                    break;
                case 2:
                case 3:
                    MesManagerClass.instance.eventUI(EEventUI.OnClickTip, id)
                    btn.visible = false;
                    break;
                default:
                    break;
            }
        });
    }
    /**emoji按钮的黄色进度条点击 */
    private OnClick(btn: FGUI_HelpButton) {
        btn.m_fill_yellow.fillAmount += 0.1;
        Uma.trackEvent(UmaEvent.Help)
    }

    /**从按钮对象池获取一个未显示的按钮 */
    public GetObject(position: Laya.Vector3, event: PeopleEvent): CommonPoolObject {
        let obj: CommonPoolObject;
        for (let ele of this.commomBtnList) {
            if (!ele.btn.visible) {
                obj = ele;
                if (event == PeopleEvent.Building) {
                    obj.btn.m_state.setSelectedIndex(1);
                    obj.btn.scaleX = 2;
                    obj.btn.scaleY = 2;
                } else {
                    obj.btn.m_state.setSelectedIndex(0);
                    obj.btn.scaleX = 1;
                    obj.btn.scaleY = 1;
                }
                obj.btn.m_fill_green.fillAmount = 0.05;
                this.ChangePos(position, obj.screenPos, obj.btn);
                obj.btn.visible = true;
                break;
            }
        }
        if (obj == null) {
            // console.log("commomBtnList为空去实例化一个")
            Laya.timer.once(0, this, this.InstanceCommBtn)
        }
        return obj;
    }
    /**从对象池拿emoji帮助按钮 */
    public GetHelpObject(position: Laya.Vector3): PoolObject {
        let obj: PoolObject = null;
        for (let ele of this.helpBtnList) {
            if (!ele.btn.visible) {
                obj = ele;
                this.InitButton(obj.btn);
                this.ChangePos(position, obj.screenPos, obj.btn);
                obj.btn.visible = true;
                break;
            }
        }
        return obj;
    }

    /**从对象池拿Tips按钮 */
    public GetTipObject(position: Laya.Vector3, id: number, eventId?: PeopleEvent): TipPoolObject {
        let obj: TipPoolObject;
        for (let ele of this.tipBtnList) {
            if (!ele.btn.visible) {
                obj = ele;
                this.InitTipButton(obj.btn, id)
                switch (eventId) {
                    case PeopleEvent.Repair:
                        obj.btn.m_state.selectedIndex = 0;
                        break;
                    case PeopleEvent.Sweep:
                        obj.btn.m_state.selectedIndex = 1;
                        break;
                    case PeopleEvent.Money:
                        obj.btn.m_state.selectedIndex = 2;
                        break;
                    case PeopleEvent.Diamond:
                        obj.btn.m_state.selectedIndex = 3;
                        break;
                    default:
                        break;
                }
                //obj.btn.m_state.selectedIndex = eventId && eventId == PeopleEvent.Sweep ? 1 : 0;
                obj.btn.m_runtime.setSelectedIndex(0);
                obj.btn.m_fill_green.fillAmount = 0;
                this.ChangePos(position, obj.screenPos, obj.btn);
                obj.btn.visible = true;
                break;
            }
        }
        if (obj == null) {
            console.log("tipBtnList没有可用的")
            Laya.timer.once(0, this, this.InstanceTipBtn)
        }
        return obj;
    }
    /**从对象池拿马赛克 */
    private GetMosaicImg() {
        let obj: MosaicPoolObject;
        for (let ele of this.mosaicList) {
            if (!ele.img.visible) {
                obj = ele;
                obj.img.visible = true;
                break;
            }

        }
        if (obj == null) {
            Laya.timer.once(0, this, this.InstanceMosaic);
            for (let ele of this.mosaicList) {
                if (!ele.img.visible) {
                    obj = ele;
                    obj.img.visible = true;
                    break;
                }
            }
        }
        if (obj == null)
            console.warn("警告！警告！返回了一个空的mosaic")
        return obj;
    }
    /**开始维修*/
    public StartRepair(id: number) {
        if (this._dictRepair[id]) {
            console.log("======开始维修=====");
            this._dictRepair[id].btn.m_runtime.selectedIndex = 2;
        } else {
            console.warn("_dictRepair[{0}]不存在", id)
        }
    }

    public RecycleBtn(id: number, event: PeopleEvent) {
        let obj = this._dictRepair[id]
        if (obj) {
            obj.btn.visible = false;
            this._dictRepair[id] = null;
            MesManagerClass.instance.eventUI(EEventUI.RepairProgressComplete, id)
            // console.log("回收")
            return;
        }
        console.error("回收失败" + id + event)

    }


    _OnHide() {
        super.Hide();
    }
}

class PoolObject {
    btn: FGUI_HelpButton;
    screenPos: Laya.Vector4;
}

class CommonPoolObject {
    btn: FGUI_CommonButton;
    screenPos: Laya.Vector4;
}

class TipPoolObject {
    btn: FGUI_EventTip;
    screenPos: Laya.Vector4;
}
class MosaicPoolObject {
    img: FGUI_Mosaic;
    screenPos: Laya.Vector4;
}
